import React, { Fragment, useEffect, useState } from 'react';
import _ from 'lodash';
import { 
  Button,
  Checkbox,
  DialogContent,
  DialogActions,
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Slider,
  OutlinedInput,
} from '@material-ui/core';
import DatePicker from 'src/components/presentational/DatePicker';
import * as async_actions from 'src/store/actions/async_fields.actions';
import AsyncSelect from 'src/components/presentational/AsyncSelect';
import EventExpressionBuilder from 'src/components/presentational/EventExpressionBuilder';
import ExpressionBuilder, { ExpressionBuilderDialog } from 'src/components/presentational/ExpressionBuilder';
import ScheduleBuilder from 'src/components/presentational/ScheduleBuilder';
import TransferList from 'src/components/presentational/TransferList';
import DurationSelect from 'src/components/presentational/DurationSelect';
import StageBuilder from 'src/components/presentational/StageBuilder';
import EscalationSchedule from 'src/components/presentational/EscalationSchedule';

const FormBody = props => {
  const [ onLoadFields, setOnLoadFields ] = useState({});
  const { formSchema } = props;

  useEffect(() => {
    const fieldsWithOnLoadDefined = formSchema().filter(field => field.onLoad);
    fieldsWithOnLoadDefined.forEach(field => {
      props.dispatch(async_actions[field.onLoad]())
        .then(() => {
          setOnLoadFields({
            ...onLoadFields,
            [field.id]: props[field.stateProp],
          });
        });
    });
  }, []);

  const onChangeCustomInput = (e, target, value) => {
    let shouldValidate = _.isNull(e) ? false : true;
    props.setFieldTouched(target, shouldValidate);
    props.setFieldValue(target, value, true);
  };

  const onStageChange = (stageIdx, name, value) => {
    if (_.isFinite(stageIdx) && name) {
      props.setFieldValue(`stages[${stageIdx}][${name}]`, value, true);
      if (name !== 'escalationSchedule') {
        props.setFieldTouched(`stages[${stageIdx}][${name}]`, true);
      }
    }
  };

  const handleEscalationScheduleChange = (target, value, escalationIdx) => {
    if (_.isFinite(escalationIdx)) {
      props.setFieldValue(target, value, true);
      props.setFieldTouched(target, true);
    } else if (target === 'escalationSchedule') {
      let lastEscalationIdx = value.length;
      if (value.length > props.values.escalationSchedule.length) {
        lastEscalationIdx = value.length - 1;
      }
      ['unit', 'period', 'escalationType', 'escalationId'].forEach(key => {
        props.setFieldTouched(`escalationSchedule[${lastEscalationIdx}].${key}`, false);
      });
      props.setFieldValue('escalationSchedule', value, false);
    }
  };

  const escalationHasError = (escalationIdx, escalationField) => {
    let escErrors = props.errors.escalationSchedule;
    let escTouched = props.touched.escalationSchedule;
    return Boolean(
      escErrors && escTouched && escTouched[escalationIdx] && escErrors[escalationIdx] && 
      escTouched[escalationIdx][escalationField] && escErrors[escalationIdx][escalationField]
    );
  };

  const {
    classes,
    t,
    values,
    touched,
    errors,
    handleChange,
    handleBlur,
    handleSubmit,
    handleClose,
    isValid,
    isSubmitting,
  } = props;
  
  return (
    <Fragment>
      <Grid container className={classes.mainFormGrid}>
        <Grid item xl={12} md={12} lg={8}>
          <DialogContent dividers className={classes.dialog}>
            <Grid item className={classes.formInput}>
              { formSchema(values).map(field => {
                if (!field.isDisplayed) return null;
                if (field.type === 'text') {
                  return (
                    <FormControl 
                      key={field.id}
                      variant='outlined'
                      className={classes.formControl}
                      fullWidth={true}
                      error={errors[field.id] && touched[field.id]}
                    >
                      <InputLabel className={classes.formLabel} htmlFor={field.id}>{field.label}</InputLabel>
                      <OutlinedInput
                        id={field.id}
                        value={values[field.id]}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        rowsMax='4'
                        name={field.id}
                        aria-describedby={`${field.id}-helper-text`}
                      />
                      {errors[field.id] && touched[field.id] && <FormHelperText id={`${field.id}-helper-text`}>{errors[field.id]}</FormHelperText>}
                    </FormControl>
                  );
                } else if (field.type === 'select') {
                  return (
                    <FormControl 
                      key={field.id}
                      variant='outlined'
                      className={classes.formControl}
                      fullWidth={true}
                      error={errors[field.id] && touched[field.id]}
                    >
                      <InputLabel className={classes.formLabel} htmlFor={field.id}>{field.label}</InputLabel>
                      <Select
                        id={field.id}
                        value={values[field.id]}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        rowsMax='4'
                        name={field.id}
                        aria-describedby={`${field.id}-helper-text`}
                      >
                        { field.options && field.options.map(opt => <MenuItem key={opt.key} value={opt.key} selected={opt.key === values[field.id]}>{opt.displayName}</MenuItem>) }
                        { field.stateProp && props[field.stateProp].map(opt => <MenuItem key={opt.key} value={opt.key}>{opt.displayName}</MenuItem>) }
                        { values[field.id] && field.stateProp && (!props[field.stateProp] || props[field.stateProp].length === 0) && <MenuItem key={values[field.id]} value={values[field.id]}>{values[field.id]}</MenuItem>}
                      </Select>
                      { 
                        errors[field.id] && touched[field.id] && 
                        <FormHelperText id={`${field.id}-helper-text`}>{errors[field.id]}</FormHelperText>
                      }
                    </FormControl>
                  );
                } else if (field.type === 'checkbox') {
                  return (
                    <FormControl 
                      key={field.id}
                      variant='outlined'
                      className={classes.formControl}
                      error={errors[field.id] && touched[field.id]}
                    >
                      <FormControlLabel
                        control={
                          <Checkbox 
                            name={field.id} 
                            value={values[field.id]} 
                            checked={values[field.id]} 
                            onChange={handleChange} 
                          />
                        }
                        className={classes.formLabel}
                        label={field.label}
                        labelPlacement='end'
                      />
                      { 
                        errors[field.id] && touched[field.id] && 
                        <FormHelperText id={`${field.id}-helper-text`}>{errors[field.id]}</FormHelperText>
                      }
                    </FormControl>
                  );
                } else if (field.type === 'range') {
                  return (
                    <FormControl 
                      key={field.id}
                      variant='outlined'
                      className={classes.formControl}
                      fullWidth={true}
                      error={errors[field.id] && touched[field.id]}
                    >
                      <FormLabel>{field.label}</FormLabel>
                      <Slider
                        id={field.id}
                        name={field.id}
                        className={classes.formSlider}
                        value={values[field.id]}
                        onChange={(event, value) => onChangeCustomInput(event, field.id, value)}
                        getAriaValueText={values[field.value]}
                        min={1}
                        max={10}
                        step={1}
                        marks
                        valueLabelDisplay='auto'
                      />
                      { 
                        errors[field.id] && touched[field.id] && 
                        <FormHelperText id={`${field.id}-helper-text`}>{errors[field.id]}</FormHelperText>
                      }
                    </FormControl>
                  );
                } else if (field.type === 'typeahead') {
                  return (
                    <FormControl 
                      key={field.id}
                      className={classes.formControl}
                      fullWidth={true}
                      error={errors[field.id] && touched[field.id]}
                    >
                      <AsyncSelect 
                        name={field.id}
                        variant='outlined'
                        fullWidth={true}
                        label={field.label} 
                        targetType={field.targetType}
                        value={values[field.id]}
                        onChange={onChangeCustomInput}
                        error={errors[field.id] && touched[field.id]}
                      />
                      { errors[field.id] && touched[field.id] && 
                      <FormHelperText id={`${field.id}-helper-text`}>{errors[field.id]}</FormHelperText>
                      }
                    </FormControl>
                  );
                } else if (field.type === 'expression') {
                  return (
                    <Fragment key={field.id}>
                      <FormControl 
                        className={classes.formControl}
                        fullWidth={true}
                        error={Boolean(errors[field.id] && touched[field.id])}
                      > 
                        <FormLabel>{field.label}</FormLabel>
                      </FormControl>
                      { field.dialogView ? 
                        <ExpressionBuilderDialog 
                          errors={errors[field.id]}
                          expression={values[field.id]}
                          targetType={values.targetObjectType || 'user'}
                          handleChange={(event, value) => onChangeCustomInput(event, field.id, value)}
                        /> :
                        <ExpressionBuilder 
                          expression={values[field.id]}
                          targetType={values.certObjectType === 'user' ? 'user' : values.targetObjectType}
                          showBasicFilters={true}
                          handleChange={(value) => onChangeCustomInput(event, field.id, value)}
                        />
                      }
                      { errors[field.id] && touched[field.id] && 
                      <FormControl 
                        className={classes.formControl}
                        fullWidth={true}
                        error={Boolean(errors[field.id])}
                      > 
                        <FormHelperText id={`${field.id}-helper-text`}>
                          {t('expr-builder-invalid')}
                        </FormHelperText>
                      </FormControl>
                      }
                    </Fragment>
                  );
                } else if (field.type === 'eventExpression') {
                  return (
                    <Fragment key={field.id}>
                      <FormControl 
                        className={classes.formControl}
                        fullWidth={true}
                        error={Boolean(errors[field.id] && touched[field.id])}
                      > 
                        <FormLabel>{field.label}</FormLabel>
                      </FormControl>
                      <EventExpressionBuilder 
                        expression={values[field.id]}
                        targetType={values.targetObjectType}
                        handleChange={(event, value) => onChangeCustomInput(event, field.id, value)}
                      />
                      { errors[field.id] && touched[field.id] && 
                      <FormControl 
                        className={classes.formControl}
                        fullWidth={true}
                        error={Boolean(errors[field.id])}
                      > 
                        <FormHelperText id={`${field.id}-helper-text`}>
                          {t('expr-builder-invalid')}
                        </FormHelperText>
                      </FormControl>
                      }
                    </Fragment>
                  );
                } else if (field.type === 'schedule') {
                  return (
                    <Fragment key={field.id}>
                      <ScheduleBuilder
                        field={field}
                        value={values[field.id]}
                        handleChange={(value) => onChangeCustomInput(event, field.id, value)}
                      />
                      <FormControl 
                        className={classes.formControl}
                        fullWidth={true}
                        error={Boolean(errors[field.id])}
                      > 
                        { errors[field.id] && touched[field.id] && 
                        <FormHelperText id={`${field.id}-helper-text`}>
                          {t('schedule-builder-invalid')}
                        </FormHelperText>
                        }
                      </FormControl>
                    </Fragment>
                  );
                } else if (field.type === 'transfer-list') {
                  return (
                    <FormControl 
                      key={field.id}
                      className={classes.formControl} 
                      fullWidth={true}
                      error={Boolean(errors[field.id] && touched[field.id])} 
                    >
                      <FormLabel className={classes.formLabel}>{field.label}</FormLabel>
                      <TransferList 
                        field={field}
                        value={values[field.id]}
                        handleChange={(value) => onChangeCustomInput(event, field.id, value)}
                      />
                      { errors[field.id] && touched[field.id] && 
                      <FormHelperText id={`${field.id}-helper-text`}>
                        {t(`${field.id}-transfer-list-invalid`)}
                      </FormHelperText>
                      }
                    </FormControl>
                  );
                } else if (field.type === 'duration') {
                  return (
                    <FormControl 
                      key={field.id}
                      className={classes.formControl} 
                      fullWidth={true}
                      error={Boolean(
                        errors[field.id] && touched[field.id] && touched[field.id].unit && touched[field.id].period &&
                        (_.isString(errors[field.id]) || errors[field.id].unit && errors[field.id].period)
                      )}
                    >
                      <FormLabel>{field.label}</FormLabel>
                      <DurationSelect 
                        durationType={props.formType}
                        errors={errors[field.id]}
                        field={field}
                        handleBlur={handleBlur}
                        handleChange={handleChange}
                        touched={touched[field.id]}
                        value={values[field.id]}
                      />
                      { errors[field.id] && touched[field.id] && _.isString(errors[field.id]) && 
                      <FormHelperText id={`${field.id}-helper-text`}>{errors[field.id]}</FormHelperText>
                      }
                    </FormControl>
                  );
                } else if (field.type === 'date') {
                  return (
                    <FormControl 
                      key={field.id}
                      className={classes.formControl} 
                      error={Boolean(errors[field.id] && touched[field.id])}
                    >
                      <FormLabel className={classes.formLabel}>{field.label}</FormLabel>
                      <DatePicker 
                        field={field}
                        disablePast={field.disablePast}
                        disableFuture={field.disableFuture}
                        error={Boolean(errors[field.id] && touched[field.id])}
                        value={values[field.id]}
                        onChange={onChangeCustomInput}
                      />
                      { errors[field.id] && touched[field.id] && 
                      <FormHelperText id={`${field.id}-helper-text`}>{errors[field.id]}</FormHelperText>
                      }
                    </FormControl>
                  );
                } 
                else if (field.type === 'stages') {
                  return (
                    <StageBuilder 
                      key={field.id}
                      field={field}
                      formType={props.formType}
                      certType={values.certObjectType === 'user' ? 'user' : values.targetObjectType}
                      frequency={values.frequency}
                      stages={values[field.id]}
                      errors={errors[field.id]}
                      touched={touched[field.id]}
                      setFieldTouched={props.setFieldTouched}
                      setFieldValue={props.setFieldValue}
                      handleStageChange={onStageChange}
                    />
                  );
                } else if (field.type === 'escalation') {
                  return (
                    <EscalationSchedule
                      key={field.id}
                      formType={props.formType}
                      errorObject={errors.escalationSchedule}
                      escalationSchedule={values.escalationSchedule} 
                      handleChange={handleEscalationScheduleChange}
                      hasError={escalationHasError}
                      touchedObject={touched.escalationSchedule}
                    />
                  );
                }
              }) }
            </Grid>
          </DialogContent>
          <DialogActions className={classes.dialogActions}>
            <Button
              size='large'
              variant='outlined'
              onClick={handleClose}
            >
              {t('btn-cancel')}
            </Button>
            <Button
              variant='contained'
              size='large'
              color='primary'
              className={classes.submitButton}
              disabled={!isValid || isSubmitting}
              onClick={handleSubmit}
            >
              {t('btn-save')}
            </Button>
          </DialogActions>
        </Grid>
      </Grid>
    </Fragment>
  );
};

export default FormBody;
