import React, { Component, Fragment } from 'react';
import _ from 'lodash';
import { v4 as uuidv4 } from 'uuid';
import { compose } from 'redux';
import { connect } from 'react-redux';
import clsx from 'clsx';
import moment from 'moment';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Button,
  ButtonGroup,
  FormControl,
  Grid,
  IconButton,
  InputLabel,
  LinearProgress,
  ListItem,
  ListItemText,
  Select,
  Switch,
  MenuItem,
} from '@material-ui/core';
import { 
  changeSummaryType, 
  changeSortKey,
  changeSearchKey,
  getSummary, 
  reassignTasks,
  updatePagination,
  updateUser, 
} from 'src/store/actions/summary.actions.js';
import{
  ChevronRight,
  ExpandMore,
} from '@material-ui/icons';
import { 
  Card as DashCard,
  Table as DashTable,
} from 'tabler-react';
import { changeActiveGovernanceDialog, changeDialogType, getModalObject, changeModalObject, updateTakingAction } from 'src/store/actions/generic.actions.js';
import PageTitle from 'src/components/presentational/PageTitle';
import AsyncSelect from 'src/components/presentational/AsyncSelect';
import GovernanceTable from 'src/components/presentational/GovernanceTable';
import GovernanceDialog from 'src/components/presentational/GovernanceDialog';
import IDG_CONSTANTS from 'src/utils/constants.js';

class UserSummary extends Component {

  state = {
    summaryTabs: [
      'summary',
      'user',
      'object',
      'violation',
    ],
    newButtonText: '',
    showNewButton: false,
    bulkActions: [],
    tableCols: [],
    interval: null,
    modalEntitlement: {},
    nestedLists: {},
    userId: null,
    userName: null,
    system: 'IDM',
    type: 'summary',
    viewCurrentOnly: false,
  }

  componentDidMount() {
    const { summary } = this.props;
    const { type } = summary;
    this._isMounted = true;
    if (type) {
      this.updateTableForSummaryType(type);
    } 
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.props.changeSearchKey(null, true);
    this.props.changeSummaryType(this.state.summaryTabs[0], IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.updateUser(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.changeActiveGovernanceDialog(null, true);
    this.props.updatePagination({
      pageNumber: 1,
      pageSize: this.props.summary.pageSize,
      sortBy: 'name',
    });
  }

  componentDidUpdate(prevProps, prevState) {
    const { summary } = this.props;
    const { type, queryingEntitlement } = summary;
    if (prevProps && prevProps.summary.queryingEntitlement && !queryingEntitlement) {
      this.openDialogModal();
    }
    
    if (prevState && prevState.system && prevState.system !== this.state.system) {
      this.props.getSummary(this.state.system);
    }
    else if (type !== prevProps.summary.type) {
      this.updateTableForSummaryType(type);
    }
  }

  handleClick(name) {
    let nestedLists = _.cloneDeep(this.state.nestedLists);
    if (!_.has(nestedLists, name)) {
      nestedLists[name] = false;
    }
    else {
      nestedLists[name] = !nestedLists[name];
    }
    this.setState({ nestedLists: nestedLists });
  }

  changeSelectedTab = tab => {
    const {type} = this.props.summary;
    this.props.changeSummaryType(tab, !(type === tab));
  };

  updateTableForSummaryType = type => {
    let newButtonText = '';
    let showNewButton = false;
    let tableTitle = '';
    let tableCols = [];
    let bulkActions = [];
    if (type === 'summary') {
      tableTitle = 'certification-summary';
    } else if (type === 'user') {
      tableTitle = 'user-tasks',
      bulkActions = ['reassign-all', 'reassign-tasks'];
      tableCols=[
        {
          'header': 'Campaign Name',
          'key': 'name',
        },
        {
          'header': 'Certifier',
          'key': 'openCertifiers',
        },
        {
          'header': 'Start Date',
          'key': 'startDate',
          'type': 'date',
        },
        {
          'header': 'Deadline',
          'key': 'deadline',
          'type': 'date',
        },
      ];
    } else if (type === 'object') {
      tableTitle = 'object-tasks',
      bulkActions = ['reassign-all', 'reassign-tasks'];
      tableCols=[
        {
          'header': 'Campaign Name',
          'key': 'name',
        },
        {
          'header': 'Certifier',
          'key': 'openCertifiers',
        },
        {
          'header': 'Total Event Count',
          'key': 'totalEventCount',
        },
        {
          'header': 'Start Date',
          'key': 'startDate',
          'type': 'date',
        },
        {
          'header': 'Deadline',
          'key': 'deadline',
          'type': 'date',
        },
      ];
    } else if (type === 'violation') {
      tableTitle = 'violation',
      bulkActions = ['reassign-all', 'reassign-tasks'];
      tableCols = [
        {
          'header': 'Policy',
          'key': 'policyName',
        },
        {
          'header': 'User',
          'key': 'targetUser',
        },
        {
          'header': 'Owner',
          'key': 'owner',
        },
        {
          'header': 'Expiration Date',
          'key': 'expirationDate',
          'type': 'date',
        },
      ];
    }
    this.setState({ bulkActions, showNewButton, newButtonText, tableTitle, tableCols });
    if (tableCols[0]) {
      this.props.changeSortKey(tableCols[0].key);
    }
    else{
      this.props.getSummary(this.state.system);
    }
  };

  onPaginationChange = (evt, page) => {
    if (evt.q != null) {
      this.props.changeSearchKey(evt.q);
    }
    else if (evt.sortBy) {
      // Sorting key changed
      const isDesc = evt.sortBy === this.props.summary.sortBy && this.props.summary.sortDir === 'asc';
      this.props.changeSortKey(evt.sortBy, isDesc);
    } else if (evt.target.value) {
      // Rows per page changed
      this.props.updatePagination({
        ...this.props.summary,
        pageNumber: 1,
        pageSize: evt.target.value,
      });
    } else if (_.isNumber(page)) {
      // Page number changed
      this.props.updatePagination({
        ...this.props.summary,
        pageNumber: page + 1,
      });
    }
  }
  actionFinally = () => {
    this.props.updateTakingAction(false);
    this.props.changeActiveGovernanceDialog(null);
    this.props.changeModalObject(null);
    this.props.changeDialogType(null);
  }

  handleActions = (actionType, selectedRows, newCertifierId) => {
    const{id} = this.props.summary.user;
    this.props.updateTakingAction(true);
    this.props.changeDialogType('actionSubmitting');
    if (actionType === 'reassign-tasks') {
      this.props.reassignTasks('managed/user/' + id, newCertifierId, selectedRows).finally(()=>{
        this.actionFinally();
      });
    }
    if (actionType === 'reassign-all') {
      this.props.reassignTasks('managed/user/' + id, newCertifierId, null).finally(()=>{
        this.actionFinally();
      });
    }
  }

  handleRowClick  = (evt, selectedRow) => {
    const { summary } = this.props;
    const { type } = summary;
    this.props.history.push('/adminCertList/' + type + '/' + selectedRow);
  }

  updateTableData = () => {
    const { results } = this.props.summary;
    this.setState({ tableData: results });
  }

  onSelectUser = (e, target, value) => {
    this.props.changeSummaryType('summary', IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.updateUser(value, !value);
  }

  onSelectSystem = (e, target) => {
    var value = target.props && target.props.value ? target.props.value : null;
    this.setState({ system: value });
  }

  getSummaryEntry = (entry) => {
    const { classes } = this.props;
    let result =
      <ListItem button className={classes.nested}>
        <Grid container justify-content='flex-start' className={classes.summaryEntry}>
          <Grid item xs={6}>
            <ListItemText primary={entry.entitlementDisplayName} />
          </Grid>
          <Grid item xs={3}>
            <ListItemText primary={!entry.completionDate ? '-' : moment(entry.completionDate).format('L')} />
          </Grid>
          <Grid item xs={3}>
            <ListItemText className={classes.listItemText} primary={this.getDisplayOutcome(entry)} />
          </Grid>
        </Grid>
      </ListItem>;
    return result;
  }

  getDisplayOutcome = (entry) => {
    let displayOutcome = '';
    switch (entry.outcome) {
    case 'certify':
      displayOutcome = 'Certified';
      break;
    case 'revoke':
      displayOutcome = 'Revoked';
      break;
    case 'abstain':
      displayOutcome = 'Abstained';
      break;
    case null:
      displayOutcome = 'Uncertified';
      break;
    case 'in-progress':
      return 'In-progress';
    default:
      displayOutcome = entry.outcome;
      break;
    }
    /*
    if (entry.outcome && entry.outcome) {
      displayOutcome += ' by ' + entry.certifierDisplayName
    }*/
    return displayOutcome;
  }

  getFlattenedData = data => {
    const { summary, config } = this.props;
    const { viewCurrentOnly } = this.state; 
    const { attrOrder } = summary;
    const { managed_objects } = config;
    const { user } = managed_objects;
    var flattenedData = [];
    var isIDM = this.state.system === 'IDM';
    var keys = isIDM ? attrOrder : _.keys(data);
    _.forEach(keys, function(attr) {
      if (!data[attr]) {
        return;
      }
      var value = data[attr];
      var category = isIDM ? (value[0].attributeName || value[0].attributeDisplayName || user[attr].title) : value[0].attributeName;
      flattenedData.push({
        category: category,
      });
      var sorted = _.sortBy(value, 'entitlementDisplayName');
      if (viewCurrentOnly) {
        _.forEach(sorted, function(item) {
          if (item.isCurrentEntitlement === true) {
            flattenedData.push(item);
          }
        });
      }
      else {
        flattenedData = _.concat(flattenedData, sorted);
      }
    });
    return flattenedData;
  }

  handleToggle = () => {
    this.setState({ viewCurrentOnly: !this.state.viewCurrentOnly });
  }

  getFlattenedEntry = (entry) => {
    var flattenedCertifications = [];
    _.forEach(entry.certifications, function(item) {
      item.parentId = item.campaign._id + '/' + item.completionDate;
      flattenedCertifications.push(item);
      if (item.comments.length > 0) {
        flattenedCertifications.push({
          comments: item.comments,
          parent: item.campaign._id + '/' + item.completionDate,
        });
      }
    });
    entry.certifications = flattenedCertifications;
    return entry;
  }

  openDialogModal = (entry) => {
    if (!entry.certifications || entry.certifications.length === 0) {
      return;
    }
    var data = _.cloneDeep(entry);
    var flattenedData = this.getFlattenedEntry(data);
    this.setState({ modalEntitlement: flattenedData });
    this.props.changeDialogType('entitlement-history');
    this.props.changeActiveGovernanceDialog('UserSummaryDialog');
  };

  getSubtitle = () => {
    const {user} = this.props.summary;
    if(!user || !user.id){
      return 'No User Selected';
    }
    else{
      return user.displayName;
    }
  }

  render() {
    const { classes, summary, generic, t } = this.props;
    const { summaryTabs, nestedLists, system, viewCurrentOnly } = this.state;
    const { modalObject, queryingModalObject } = generic;
    const { entitlements, querying } = summary;
    const { id } = summary.user;


    return (
      <Fragment>
        <PageTitle
          title={t('page-user-summary')}
          subtitle={this.getSubtitle()}
        />
        <Grid className={classes.userSearchContainer} display='flex' flex-direction='row' container>
          <Grid className={classes.summaryInput} item xs={3}>
            <AsyncSelect 
              name='userSelector'
              variant='outlined'
              fullWidth={true}
              onChange={this.onSelectUser}
              label='Select user' 
              targetType='userName'
              value={this.props.summary.user.displayName}
            />
          </Grid>
          {summary.type === 'summary' && id &&
            <Grid item xs={3}>
              <FormControl className={classes.selectSystemControl}>
                <InputLabel className={classes.selectSystemLabel}>{t('select-system')}</InputLabel>
                <Select
                  className={classes.systemSelector}
                  name='systemSelector'
                  variant='outlined'
                  fullWidth={true}
                  onChange={this.onSelectSystem}
                  label='Select system'
                  value={id ? (system || 'IDM') : ''}
                  disabled={!id}
                >
                  {
                    summary.systems.map(system => (
                      <MenuItem key={system} value={system} >{system}</MenuItem>
                    ))
                  }
                </Select>
              </FormControl>
            </Grid>
          }
        </Grid>
        { id &&
          <Grid className={classes.buttonContainer} item xs={12}>
            <ButtonGroup color='default' fullWidth aria-label="full width outlined button group">
              {
                summaryTabs.map(tab => (
                  <Button
                    key={tab}
                    className={clsx(classes.summaryTab, { 'selected': summary.type === tab })}
                    variant="contained"
                    onClick={() => this.changeSelectedTab(tab)}
                  >{t(tab !== 'summary' ? tab + '-tasks' : tab)}</Button>
                ))
              }
            </ButtonGroup>
          </Grid>
        }
        {
          summary.type === 'summary' && id &&
          <Grid>
            <Grid className={classes.switchContainer}>
              <InputLabel className={classes.switchLabel}>
                {t('view-current')}
              </InputLabel>
              <Switch name='currentItems' 
                className={classes.toggleButton}
                onChange={this.handleToggle} 
                checked={Boolean(viewCurrentOnly)}
                value={Boolean(viewCurrentOnly)}
              />
            </Grid>
            <DashCard>
              <DashTable>
                <DashTable.Header>
                  <DashTable.Row>
                    <DashTable.ColHeader>{t('header-entitlement')}</DashTable.ColHeader>
                    <DashTable.ColHeader>{t('header-decision')}</DashTable.ColHeader>
                    <DashTable.ColHeader>{t('header-date')}</DashTable.ColHeader>
                    <DashTable.ColHeader>{t('header-certifier')}</DashTable.ColHeader>
                  </DashTable.Row>
                </DashTable.Header>
                { querying ?
                  <DashTable.Body>
                    <DashTable.Row>
                      <DashTable.Col colSpan={4}>
                        <LinearProgress />
                      </DashTable.Col>
                    </DashTable.Row>
                  </DashTable.Body> 
                  :
                  <DashTable.Body>
                    {this.getFlattenedData(entitlements).map(entry => (
                      (entry.category ?
                        <DashTable.Row className={classes.categoryRow} key={uuidv4()}>
                          <DashTable.Col className={classes.categoryCell}>
                            <IconButton className={classes.expandButton} onClick={()=>{this.handleClick(entry.category);}}>
                              {(!_.has(nestedLists, entry.category) || nestedLists[entry.category] === true) ? <ExpandMore /> : <ChevronRight />}
                            </IconButton>
                            <strong>{entry.category}</strong>
                          </DashTable.Col>
                          <DashTable.Col></DashTable.Col>
                          <DashTable.Col></DashTable.Col>
                          <DashTable.Col></DashTable.Col>
                        </DashTable.Row>
                        :
                        (!_.has(nestedLists, (entry.attributeDisplayName || entry.attributeName)) || nestedLists[(entry.attributeDisplayName || entry.attributeName)] === true ? 
                          <DashTable.Row key={uuidv4()} className={clsx(entry.certifications[0] && classes.entitlementRow)} onClick={() => this.openDialogModal(entry)}>
                            <DashTable.Col className={clsx(classes.entitlementCell, classes.firstCell)}>
                              {entry.entitlementDisplayName}
                            </DashTable.Col>
                            <DashTable.Col className={classes.entitlementCell}>
                              {entry.certifications[0] ? t('summary-' + entry.certifications[0].outcome) : t('summary-not-certified')}
                            </DashTable.Col>
                            <DashTable.Col className={classes.entitlementCell}>
                              {entry.certifications[0] && entry.certifications[0].completionDate ? moment(entry.certifications[0].completionDate).format('L') : '-'}
                            </DashTable.Col>
                            <DashTable.Col className={classes.entitlementCell}>
                              {entry.certifications[0] ? entry.certifications[0].certifier.displayName : '-'}
                            </DashTable.Col>
                          </DashTable.Row> : null
                        )))
                    )}
                  </DashTable.Body>
                }
              </DashTable>
            </DashCard>
          </Grid>
        }
        { summary.type !== 'summary' && id &&
        <Grid container className={classes.container} justify='center'>
          <GovernanceTable 
            tableCols={this.state.tableCols}
            tableData={_.isArray(summary.results) ? summary.results : []}
            sortBy={summary.sortBy}
            sortDir={summary.sortDir}
            bulkActions={this.state.bulkActions}
            handleActions={this.handleActions}
            showNewButton={this.state.showNewButton}
            newButtonText={this.state.newButtonText}
            showCheckboxes={summary.type !== 'summary'}
            pageSize={summary.pageSize}
            pagedResultsOffset={summary.pageNumber-1}
            handlePagination={this.onPaginationChange}
            querying={summary.querying}
            handleRowClick={this.handleRowClick}
            getModalObject={this.props.getModalObject}
            queryingModalObject={queryingModalObject}
            modalObject={modalObject}
            tableType={summary.type}
          />
        </Grid>
        }
        <GovernanceDialog
          dialogId='UserSummaryDialog'
          onSubmitDialog={() => {
            this.props.changeActiveGovernanceDialog(null);
          }}
          modalObject={this.state.modalEntitlement}
          largeDialog={true}
        />
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const {
    summary,
    generic,
    config,
  } = state;
  return {
    summary,
    generic,
    config,
  };
};

const mapDispatchToProps = {
  changeSummaryType,
  changeSortKey,
  changeSearchKey,
  changeDialogType,
  changeActiveGovernanceDialog,
  getSummary,
  reassignTasks,
  updatePagination,
  getModalObject,
  updateUser,
  updateTakingAction,
  changeModalObject,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(UserSummary);
