import React, { Component, Fragment } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import clsx from 'clsx';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Button,
  ButtonGroup,
  Grid,
} from '@material-ui/core';
import { 
  changeCertType, 
  changeSortKey,
  changeSearchKey,
  cancelCertifications, 
  getCertifications, 
  updatePagination,
} from 'src/store/actions/certs.actions.js';
import { setIsCreateModalOpen, changeCreateModalType, updateTakingAction, changeDialogType,  changeActiveGovernanceDialog} from 'src/store/actions/generic.actions.js';
import PageTitle from 'src/components/presentational/PageTitle';
import GovernanceTable from 'src/components/presentational/GovernanceTable';
import IDG_CONSTANTS from 'src/utils/constants.js';

class Certifications extends Component {
  _isMounted = false;

  state = {
    newButtonText: `${this.props.match.params.type}-cert`,
    bulkActions: ['cancel-cert'],
    tableCols: [],
    tableData: [],
  }

  componentDidMount() {
    this._isMounted = true;
    if(this.props.location.pathname.includes(IDG_CONSTANTS.TASKS[0])){
      this.props.changeCertType(IDG_CONSTANTS.TASKS[0], IDG_CONSTANTS.DO_NOT_UPDATE);
      this.setTableColsForStatus(this.props.certs.status, IDG_CONSTANTS.TASKS[0]);
    }
    else{
      this.props.changeCertType(IDG_CONSTANTS.TASKS[1], IDG_CONSTANTS.DO_NOT_UPDATE);
      this.setTableColsForStatus(this.props.certs.status, IDG_CONSTANTS.TASKS[1]);
    }
    this.props.getCertifications();
  }

  componentDidUpdate(prevProps) {
    const { type } = this.props.match.params;
    if (type !== this.props.certs.type) {
      this.setState({ newButtonText: `${type}-cert` });
      this.props.changeCertType(type, IDG_CONSTANTS.DO_NOT_UPDATE);
      this.setTableColsForStatus(IDG_CONSTANTS.CERT_STATUSES[0], type);
      this.props.updatePagination({
        status: IDG_CONSTANTS.CERT_STATUSES[0],
        pageNumber: 1,
        sortBy: 'name',
        sortDir: 'asc',
        pageSize: this.props.certs.pageSize,
      }, IDG_CONSTANTS.DO_NOT_UPDATE);
      this.props.changeSearchKey(null);
    } 
  }

  setTableColsForStatus = (status, type) => {
    let tableCols = [];
    let bulkActions = [];
    if (status === IDG_CONSTANTS.CERT_STATUS.ACTIVE) {
      tableCols = [
        {
          'header': 'Campaign Name',
          'key': 'name',
        },
        {
          'header': 'Campaign Start Date',
          'key': 'startDate',
          'type': 'date',
        },
        {
          'header': 'Next Deadline',
          'key': 'nextDeadline',
          'type': 'date',
        },
        {
          'header': 'Progress',
          'key': 'progress',
          'type': 'linear',
          'noSort': true,
        },
      ];
    } else if (status === IDG_CONSTANTS.CERT_STATUS.CLOSED) {
      tableCols = [
        {
          'header': 'Campaign Name',
          'key': 'name',
        },
        {
          'header': 'Start Date',
          'key': 'startDate',
          'type': 'date',
        },
        {
          'header': 'Completed On',
          'key': 'completionDate',
          'type': 'date',
        },
        {
          'header': 'Total Events',
          'key': 'totalEventsComplete',
          'noSort': true,
        },

      ];
    } else if (status === IDG_CONSTANTS.CERT_STATUS.SCHEDULED) {
      tableCols = [
        {
          'header': 'Campaign Name',
          'key': 'name',
        },
        {
          'header': 'Next Run Date',
          'key': 'nextRunDate',
          'type': 'date',
        },
      ];
    } else if (status === IDG_CONSTANTS.CERT_STATUS.TRIGGERED) {
      tableCols = [
        {
          'header': 'Campaign Name',
          'key': 'name',
        },
        {
          'header': 'Description',
          'key': 'description',
        },
      ];
    }
    if (type && type === 'object') {
      tableCols.splice(1, 0, {
        'header': 'Object Type',
        'key': 'certObjectType',
      });
    }
    if (status !== IDG_CONSTANTS.CERT_STATUS.CLOSED) {
      bulkActions = ['cancel-cert'];
    }
    this.setState({ bulkActions, tableCols });
  };

  componentWillUnmount() {
    this._isMounted = false;
    this.props.changeSearchKey(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.changeCertType(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.updatePagination({
      status: IDG_CONSTANTS.CERT_STATUSES[0],
      pageNumber: 1,
      sortBy: 'name',
      sortDir: 'asc',
      pageSize: this.props.certs.pageSize,
    }, IDG_CONSTANTS.DO_NOT_UPDATE);
  }

  changeSelectedStatus = status => {
    const { type } = this.props.match.params;
    this.props.updatePagination({
      ...this.props.certs,
      pageNumber: 1,
      status,
    });
    this.setTableColsForStatus(status, type);
    this.props.changeSearchKey(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.changeSortKey('name', !IDG_CONSTANTS.IS_DESC, IDG_CONSTANTS.DO_NOT_UPDATE);
  }

  onPaginationChange = (evt, page) => {
    if (evt.q != null) {
      this.props.changeSearchKey(evt.q);
    }
    else if (evt.sortBy) {
      // Sorting key changed
      const isDesc = evt.sortBy === this.props.certs.sortBy && this.props.certs.sortDir === 'asc';
      this.props.changeSortKey(evt.sortBy, isDesc);
    } else if (evt.target && evt.target.value) {
      // Rows per page changed
      this.props.updatePagination({
        ...this.props.certs,
        pageNumber: 1,
        pageSize: evt.target.value,
      });
    } else if (_.isNumber(page)) {
      // Page number changed
      this.props.updatePagination({
        ...this.props.certs,
        pageNumber: page + 1,
      });
    }
  }

  handleActions = (actionType, selectedRows) => {
    if (actionType === 'cancel-cert') {
      this.props.updateTakingAction(true);
      this.props.changeDialogType('actionSubmitting');
      this.props.changeActiveGovernanceDialog('GovernanceTableDialog');
      this.props.cancelCertifications(selectedRows).finally(()=>{
        this.props.updateTakingAction(false);
        this.props.changeDialogType(null);
        this.props.changeActiveGovernanceDialog(null);
      });
    }
  }

  handleRowClick = (evt, selectedRow) => {
    const { certs, changeCreateModalType, setIsCreateModalOpen } = this.props;
    const { newButtonText } = this.state;
    if (certs.status === 'scheduled' || certs.status === 'triggered') {
      // newButtonText will be 'user-cert' or 'object-cert'
      changeCreateModalType(newButtonText, 'edit', selectedRow);
      setIsCreateModalOpen(true);
    } else {
      this.props.history.push('/adminCertList/' + this.props.certs.type + '/' + selectedRow);
    }
  }

  render() {
    const { certs, classes, t } = this.props;
    const { pageNumber, pageSize, querying, type } = certs;

    return (
      <Fragment>
        <PageTitle
          title={t(`page-${certs.type}-certs`)}
        />
        <Grid className={classes.buttonContainer} item xs={12}>
          <ButtonGroup color='default' fullWidth aria-label="full width outlined button group">
            {
              IDG_CONSTANTS.CERT_STATUSES.map(status => {
                return (
                  <Button
                    key={status}
                    className={clsx(classes.certStatusButton, { 'selected': status === certs.status })}
                    variant="contained"
                    onClick={() => this.changeSelectedStatus(status)}
                  >{t(status)}</Button>
                );
              })
            }
          </ButtonGroup>
        </Grid>

        <Grid container className={classes.container} justify='center'>
          <GovernanceTable 
            tableCols={this.state.tableCols}
            tableData={this.props.certs.results}
            sortBy={this.props.certs.sortBy}
            sortDir={this.props.certs.sortDir}
            q={this.props.certs.q}
            bulkActions={this.state.bulkActions}
            handleActions={this.handleActions}
            handleRowClick={this.handleRowClick}
            showNewButton={true}
            newButtonText={this.state.newButtonText}
            showCheckboxes={certs.status !== IDG_CONSTANTS.CERT_STATUS.CLOSED}
            pageSize={pageSize}
            pagedResultsOffset={pageNumber-1}
            handlePagination={this.onPaginationChange}
            querying={querying}
            tableType={type}
          />
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const { certs } = state;
  return {
    certs,
  };
};

const mapDispatchToProps = {
  changeCertType,
  changeSortKey,
  changeSearchKey,
  changeCreateModalType,
  setIsCreateModalOpen, 
  cancelCertifications,
  getCertifications,
  updatePagination,
  updateTakingAction, 
  changeDialogType,  
  changeActiveGovernanceDialog,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(Certifications);
