import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
import {
  Grid,
  LinearProgress,
  Typography,
} from '@material-ui/core';
import { 
  Table as DashTable,
} from 'tabler-react';
import {
  RemoveCircle,
  NotInterested, 
  CheckCircle, 
  RadioButtonUnchecked,
  Comment,
  ModeCommentOutlined,  
} from '@material-ui/icons';

import 'tabler-react/dist/Tabler.css';
import { withStyles } from '@material-ui/core/styles'; 
import styles from './styles';
import moment from 'moment';

class ViewObject extends Component {
  state = {
    expandedRows: {},
  }

  componentWillUnmount() {

  }

  componentDidUpdate() {

  }

  expandRow(name){
    let expandedRows = _.cloneDeep(this.state.expandedRows);
    if (!_.has(expandedRows, name)) {
      expandedRows[name] = true;
    }
    else {
      expandedRows[name] = !expandedRows[name];
    }
    _.forEach(_.keys(expandedRows), function(key) {
      if (key !== name) {
        expandedRows[key] = false;
      }
    });
    this.setState({ expandedRows: expandedRows });
  }

  navigateToCertification = () => {
    this.props.onSubmitDialog(this.props.dialogType, this.props.selectedRows, this.state.reassignee);
    this.props.handleClose();
  }

  getDisplayOrder = obj => {
    if (obj.order && obj.order.length > 0) {
      // Remove duplicates and non-existent keys from 'order' key, and removes order from display object
      let orderedKeys = _.uniq(obj.order);
      delete obj.order;
      orderedKeys = _.intersection(orderedKeys, _.keys(obj));

      _.forEach(_.keys(obj), function(key) {
        if (!_.includes(orderedKeys, key)) {
          orderedKeys.push(key);
        }
      });

      return orderedKeys;
    }
    else {
      if (obj.order) {
        delete obj.order;
      }
      return _.keys(obj).sort();
    }
  }

  getIconFromOutcome = (outcome) => {
    const { classes } = this.props;
    let icon = '';
    switch(outcome) {
    case 'certify': 
      icon = <CheckCircle className={classes.certifyIcon}/>;
      break;
    case 'revoke': 
      icon = <RemoveCircle className={classes.revokeIcon}/>;
      break;
    case 'abstain': 
      icon = <NotInterested className={classes.abstainIcon}/>;
      break;
    case null:
    default:
      icon = <RadioButtonUnchecked className={classes.incompleteIcon}/>;
      break;
    }
    return icon;
  }

  getDisplayValue = value => {
    const { t } = this.props;

    if (_.isArray(value)) {
      for (var i = 0; i < value.length; i++) {
        if (_.isObject(value[i])) {
          value[i] = value[i].displayName || value[i];
          if (value[i] === 'manager') {
            value[i] = t('user-manager');
          }
        }
      }
      return value.join(', ');
    }
    else if (_.isObject(value)) {
      return value.displayName || value.toString();
    }
    else {
      return value.toString();
    }
  }

  getIconButton = certification => {
    const { classes } = this.props;
    let icon = '';
    if (this.state.expandedRows[certification.parentId] === true) {
      icon = (
        <Comment className={classes.selectedComments} onClick={() => this.expandRow(certification.parentId)}/>
      );
    }
    else if (certification.comments.length > 0) {
      icon = (
        <Comment className={classes.selectableComments} onClick={() => this.expandRow(certification.parentId)}/>
      );
    }
    else {
      icon = <ModeCommentOutlined/>;
    }
    return icon;
  }

  render() {
    const { classes, dialogType, modalObject, t, queryingMetadata } = this.props;
    const { expandedRows } = this.state;
    return (
      queryingMetadata ?
        <Grid className={classes.linearProgressContainer}>
          <LinearProgress />
        </Grid> :
        <Grid container className={classes.certification} spacing={2}>
          <Grid className={classes.detailsContainer} item xs={12}>
            {modalObject && dialogType === 'glossary' &&
          _.keys(modalObject).length > 0 &&
            <DashTable>
              <DashTable.Header>
                <DashTable.Row>
                  <DashTable.ColHeader>{t('key')}</DashTable.ColHeader>
                  <DashTable.ColHeader>{t('value')}</DashTable.ColHeader>
                </DashTable.Row>
              </DashTable.Header>
              <DashTable.Body>
                {
                  this.getDisplayOrder(modalObject).map(key => {
                    return (
                      <DashTable.Row key={key} className={classes.detailsRow}>
                        <DashTable.Col className={classes.detailsCell}>{key}</DashTable.Col>
                        <DashTable.Col className={classes.detailsCell}>{this.getDisplayValue(modalObject[key])}</DashTable.Col>
                      </DashTable.Row>
                    );
                  })
                }
              </DashTable.Body>
            </DashTable>
            }
            { modalObject && modalObject.data && dialogType === 'targeted-attribute' &&
            <DashTable>
              <DashTable.Header>
                <DashTable.Row>
                  <DashTable.ColHeader>{t('label-name')}</DashTable.ColHeader>
                  <DashTable.ColHeader>{t('description')}</DashTable.ColHeader>
                </DashTable.Row>
              </DashTable.Header>
              <DashTable.Body>
                { modalObject.data.map(item => {
                  return (
                    <DashTable.Row key={item._id} className={classes.detailsRow}>
                      <DashTable.Col className={classes.detailsCell}>{item.displayName}</DashTable.Col>
                      <DashTable.Col className={classes.detailsCell}>{item.description}</DashTable.Col>
                    </DashTable.Row>
                  );
                }) }
              </DashTable.Body>
            </DashTable>
            }
            { modalObject && modalObject.certifications && dialogType === 'entitlement-history' &&
            <DashTable>
              <DashTable.Header>
                <DashTable.Row>
                  <DashTable.ColHeader>{t('header-date')}</DashTable.ColHeader>
                  <DashTable.ColHeader></DashTable.ColHeader>
                  <DashTable.ColHeader>{t('header-decision')}</DashTable.ColHeader>
                  <DashTable.ColHeader>{t('header-certifier')}</DashTable.ColHeader>
                  <DashTable.ColHeader>{t('header-campaign')}</DashTable.ColHeader>
                  <DashTable.ColHeader></DashTable.ColHeader>
                </DashTable.Row>
              </DashTable.Header>
              <DashTable.Body>
                { modalObject.certifications.map(certification => {
                  return certification.parent ? 
                    ( expandedRows[certification.parent] &&
                    <DashTable.Row key={uuidv4()} className={classes.detailsCommentRow}>
                      <DashTable.Col colSpan={6}>
                        <DashTable>
                          <DashTable.Body>
                            {certification.comments.map(comment => {
                              return (
                                <DashTable.Row key={comment.timeStamp}>
                                  <DashTable.Col colSpan={6} className={classes.detailsCommentCell}><strong>{comment.user.displayName}</strong>{' (' + moment(comment.timeStamp).format('L') + '): ' + comment.comment}</DashTable.Col>
                                </DashTable.Row>);
                            })}
                          </DashTable.Body>
                        </DashTable>
                      </DashTable.Col>
                    </DashTable.Row>
                    )
                    :
                    (
                      <DashTable.Row key={uuidv4()} className={classes.detailsRow}>
                        <DashTable.Col className={classes.detailsCell}>{certification.completionDate ? moment(certification.completionDate).format('L') : '-'}</DashTable.Col>
                        <DashTable.Col className={classes.detailsCell}>{this.getIconFromOutcome(certification.outcome)}</DashTable.Col>
                        <DashTable.Col className={classes.detailsCell}>{t('summary-' + certification.outcome)}</DashTable.Col>
                        <DashTable.Col className={classes.detailsCell}>{certification.certifier.displayName}</DashTable.Col>
                        <DashTable.Col className={classes.detailsCell}>{certification.campaign.displayName}</DashTable.Col>
                        <DashTable.Col className={classes.detailsCell}>
                          {this.getIconButton(certification)}
                        </DashTable.Col>
                      </DashTable.Row>
                    );
                }) }
              </DashTable.Body>
            </DashTable>
            }
            {
              modalObject && dialogType === 'glossary' &&
            _.keys(modalObject).length === 0 &&
            <Typography>{ t('no-glossary-entry') }</Typography>
            }
            {
              modalObject && dialogType === 'targeted-attribute' &&
            (!modalObject.data || modalObject.data.length === 0) &&
            <Typography>{ t('no-selected-attributes') }</Typography>
            }
          </Grid>
        </Grid>
    );
  }
}

ViewObject.propTypes = {
  modalObject: PropTypes.object,
};

export default compose(
  connect(),
  withTranslation(),
  withStyles(styles),
)(ViewObject);
