import { SUMMARY, GENERIC } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';
import { httpFailure, alert } from './generic.actions.js';
import _ from 'lodash';

export const getSummary = (systemtest) => (dispatch, getState) => {
  dispatch({
    type: SUMMARY.QUERY_SUMMARY,
    payload: null,
  });
  let { pageNumber, pageSize, sortBy, sortDir, type, user, q } = getState().summary;
  var isSummary = type === 'summary';
  let url = getSummaryEndpointByType(type, user.id);
  let params = {
    pageSize: pageSize,
    pageNumber: pageNumber,
    type: type,
    status: 'active',
    q,
  };
  if (params.q) {
    params.q = params.q.replace(/\\/g, '\\\\').replace(/"/g, '\\"');
  }
  if (isSummary) {
    if (user.id === null) {
      dispatch({
        type: SUMMARY.QUERY_SUMMARY_SUCCESS,
        payload: {
          results: [],
          certifiables: [],
        },
      });
      return;
    }
    //TODO parameterize user ID
    params = {};
    if (systemtest) {
      params.system = systemtest;
    }

    return httpService.request({
      url: url,
      method: 'get',
      params,
    }).then(({ data }) => {
      let result = data.result;
      if (result[0] && !result[0]._id) {
        result = data.result.map(item => ({
          ...item,
          _id: item.objectid,
        }));
      }
      dispatch({
        type: SUMMARY.QUERY_SUMMARY_SUCCESS,
        payload: {
          entitlements: result.profile.entitlements,
          userInfo: result.profile.userInfo,
          attrOrder: result.order,
          totalPagedResults: data.totalPagedResults,
        },
      });
    }).catch(error => {
      dispatch({
        type: SUMMARY.QUERY_SUMMARY_FAILURE,
        payload: null,
      });
      return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
    });
  }
  else {
    if (!sortBy) {
      sortBy = '-expirationDate';
    }
    params.sortBy = sortDir === 'asc' ? `+${sortBy}` : `-${sortBy}`;
    params.pageSize = pageSize || 10;
    return httpService.request({
      url: url,
      method: 'get',
      params,
    }).then(({ data }) => {
      let result = data.result;
      if (result[0] && !result[0]._id) {
        result = data.result.map(item => ({
          ...item,
          _id: item.objectid,
        }));
      }
      dispatch({
        type: SUMMARY.QUERY_SUMMARY_SUCCESS,
        payload: {
          results: result,
          totalPagedResults: data.totalPagedResults,
        },
      });
    }).catch(error => {
      dispatch({
        type: SUMMARY.QUERY_SUMMARY_FAILURE,
        payload: null,
      });
      return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
    });
  }
};

export const getEntitlementHistory = entitlementId => (dispatch, getState) => {
  if (!entitlementId) {
    dispatch({
      type: SUMMARY.QUERY_ENTITLEMENT_SUCCESS,
      payload: {
        singleEntitlementHistory: {},
      },
    });
    return;
  }
  dispatch({
    type: SUMMARY.QUERY_ENTITLEMENT,
    payload: null,
  });
  let { system, userId } = getState().summary;
  let url = constants.IDM_CONTEXT + '/governance/userEventData/user/' + userId + '/history';
  let params = {
    entitlementId: entitlementId,
  };
  if (system) {
    params.system = system;
  }
  return httpService.request({
    url: url,
    method: 'get',
    params,
  }).then(({ data }) => {
    dispatch({
      type: SUMMARY.QUERY_ENTITLEMENT_SUCCESS,
      payload: {
        singleEntitlementHistory: data.result,
      },
    });
  }).catch(error => {
    dispatch({
      type: SUMMARY.QUERY_ENTITLEMENT_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

//REPLACE
export const reassignTasks = (oldCertifierId, newCertifierId, taskIds) => (dispatch, getState) => {
  let { type } = getState().summary;
  const endpoint = getReassignEndpointByType(type);
  var data = {};
  if (type === 'violation-tasks' || type === 'violation') {
    data = {
      oldOwnerId: oldCertifierId,
      newOwnerId: newCertifierId,
      ids: taskIds,
    };
  }
  else {
    data = {
      oldCertifierId: oldCertifierId,
      newCertifierId: newCertifierId,
      campaignIds: taskIds,
    };
  }
  return httpService.request({
    url: endpoint,
    method: 'post',
    data: data,
  }).then(({ data: { result } }) => {
    dispatch(alert(result, 'success'));
    dispatch(getSummary());
    return data;
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const updateUser = (user, noUpdate) => dispatch => {
  dispatch({
    type: SUMMARY.UPDATE_USER,
    payload: user, 
  });
  dispatch(updateSystems(!user ? null : user.key));
  if(!noUpdate){
    dispatch(getSummary());
  }
};

