export const updateSystems = (userId) => dispatch => {
  if (userId) {
    return httpService.request({
      url: constants.IDM_CONTEXT + '/endpoint/linkedView/managed/user/' + userId,
      method: 'get',
    }).then(({ data }) => {
      let systems = getSystemsFromLinkedTo(data.linkedTo);
      dispatch({
        type: SUMMARY.UPDATE_SYSTEMS, 
        payload: {
          systems: systems,
        },
      });
    }).catch(error => {
      return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
    });
  }
  else {
    dispatch({
      type: SUMMARY.UPDATE_SYSTEMS,
      payload: {
        systems: [],
      },
    });
  }
};

const getSummaryEndpointByType = (type, userId) => {
  if (type === 'summary' || !type) {
    return constants.IDM_CONTEXT + '/governance/userEventData/user/' + userId;
  }
  else {
    return constants.IDM_CONTEXT + '/governance/dashboard/' + userId;
  }
};

const getReassignEndpointByType = type => {
  switch(type) {
  case 'user':
    return constants.IDM_CONTEXT + '/governance/certify/user/reassign';
  case 'object':
    return constants.IDM_CONTEXT + '/governance/certify/object/reassign';
  case 'violation':
    return constants.IDM_CONTEXT + '/governance/violation?_action=reassign';
  default:
    return '';
  }
};

const getSystemsFromLinkedTo = linkedTo => {
  let systems = ['IDM'];
  if (!linkedTo) {
    return [];
  }
  _.forEach(linkedTo, function(link) {
    if (link.resourceName.endsWith(link.content._id)) {
      systems.push(link.resourceName.substring(0, link.resourceName.indexOf(link.content._id) - 1));
    }
  });
  return systems;
};