import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import {
  Button,
  FormControl,
  FormControlLabel,
  Grid,
  Radio,
  RadioGroup,
  LinearProgress,
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import AsyncSelect from 'src/components/presentational/AsyncSelect';
import styles from './styles';

class ViewReassign extends Component {

  state = {
    chooseType: 'user',
    reassignee: null,
  }

  componentWillUnmount() {

  }

  componentDidUpdate() {

  }

  onSelectReassignee = (event, field, value) => {
    if (this.state.chooseType === 'user') {
      if (!value || !value.key) {
        this.setState({ reassignee: null });
      }
      else {
        var reassignee = 'managed/' + this.state.chooseType + '/' + value.key;
        this.setState({ reassignee: reassignee });
      }
    }
    else {
      if (!value || !value._id) {
        this.setState({ reassignee: null });
      }
      else {
        this.setState({ reassignee: value._id });
      }
    }
  }

  handleChooseTypeChange = (event, value) => {
    this.setState({ chooseType: value, reassignee: null });
  }

  handleSubmit = () => {
    this.props.onSubmitDialog(this.props.dialogType, this.props.selectedRows, this.state.reassignee);
  }
  
  render() {
    const { t, classes, takingAction } = this.props;

    return (
      <Grid container>
        {takingAction ? 
          <Grid container direction={'column'}>
            <Grid item><h6>Action Submited please stand by</h6> </Grid>
            <Grid item><LinearProgress /> </Grid>
          </Grid>
          : null}
        <Grid item className={classes.radioButtonContainer} xs={12}>
          <FormControl component="fieldset">
            <RadioGroup aria-label="reassignType" name="reassignType" row value={this.state.chooseType} onChange={this.handleChooseTypeChange}>
              <FormControlLabel disabled={takingAction} value="user" control={<Radio />} label="User" />
              <FormControlLabel disabled={takingAction} value="role" control={<Radio />} label="Role" />
            </RadioGroup>
          </FormControl>
        </Grid>
        <Grid item xs={12}>
          <AsyncSelect
            id="reassignSelector"
            targetType={this.state.chooseType === 'user' ? 'userName' : 'authzGroup'}
            fullWidth={true}
            label={t('choose-' + this.state.chooseType)}
            name='reassignSelector'
            variant='outlined'
            onChange={this.onSelectReassignee}
            disabled={takingAction}
          />
        </Grid>
        <Grid item xs={12} className={classes.actionButtonContainer}>
          <Button
            className={classes.actionButton}
            variant='contained'
            color='primary'
            onClick={this.handleSubmit}
            disabled={!this.state.reassignee || takingAction}
          >
            {t('btn-submit')}
          </Button>
        </Grid>
      </Grid>
    );
  }
}

ViewReassign.propTypes = {
  chooseType: PropTypes.string,
  reassignee: PropTypes.string,
  onSubmitDialog: PropTypes.func,
  handleClose: PropTypes.func,
  dialogType: PropTypes.string,
  selectedRows: PropTypes.array,
};

export default compose(
  connect(),
  withTranslation(),
  withStyles(styles),
)(ViewReassign);
