import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { compose } from 'redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import {
  Button,
  Card,
  CardContent,
  FormControl,
  FormHelperText,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from '@material-ui/core';
import {
  Add,
  Delete,
} from '@material-ui/icons';
import AsyncSelect from 'src/components/presentational/AsyncSelect';
import DurationSelect from 'src/components/presentational/DurationSelect';
import styles from './styles';

const EscalationSchedule = props => {

  const addEscalationDate = () => {
    const newEscalation = _.cloneDeep(props.escalationSchedule);
    const newSchedule = {
      unit: '',
      period: '',
      escalationType: '',
      escalationId: '',
    };
    newEscalation.push(newSchedule);
    props.handleChange('escalationSchedule', newEscalation, null);
  };

  const removeEscalationDate = () => {
    if (props.escalationSchedule.length > 0) {
      const newEscalation = _.cloneDeep(props.escalationSchedule);
      newEscalation.pop();
      props.handleChange('escalationSchedule', newEscalation, null);
    }
  };

  const {  
    classes, 
    errorObject, 
    escalationSchedule,
    hasError, 
    handleChange, 
    t,
    touchedObject,
  } = props;
  
  const isAddEscalationButtonDisabled = () => {
    if (!escalationSchedule || escalationSchedule.length === 0 || !errorObject) {
      return false;
    }

    return errorObject.some(err => err && (err.unit || err.period || err.escalationType || err.escalationId));
  };

  
  return (
    <Grid item xs={12} className={classes.formControl}>
      <Button
        variant='contained'
        color='primary'
        disabled={isAddEscalationButtonDisabled()}
        startIcon={<Add />}
        onClick={() => addEscalationDate()}
      >
        Add escalation date
      </Button>
      <Button
        variant='contained'
        className={classes.deleteEscalationEntryBtn}
        disabled={escalationSchedule.length === 0}
        startIcon={<Delete />}
        onClick={() => removeEscalationDate()}
      >
        Remove escalation date
      </Button>

      { escalationSchedule.map((escalation, escalationIdx) => (
        <Card key={escalationIdx} variant='outlined'>
          <CardContent className={classes.cardContent}>
            <FormControl
              className={classes.formControl}
              error={hasError(escalationIdx, 'unit') || hasError(escalationIdx, 'period')}
              fullWidth={true}
            >
              <DurationSelect 
                durationType={`escalation-${props.formType}`}
                errors={
                  hasError(escalationIdx, 'unit') || hasError(escalationIdx, 'period') ? 
                    errorObject[escalationIdx] : {}
                }
                field={`escalationSchedule.${escalationIdx}`}
                handleBlur={(evt) => handleChange(evt.target.name, evt.target.value, escalationIdx)}
                handleChange={(evt) => handleChange(evt.target.name, evt.target.value, escalationIdx)}
                touched={touchedObject ? touchedObject[escalationIdx] : false}
                value={escalation}
              />
            </FormControl>
            <Grid container spacing={1} className={classes.escalationScheduleEntry}>
              <Grid item xs={6} className={classes.certifierTypeGrid}>
                <FormControl 
                  variant='outlined'
                  className={classes.formControl}
                  fullWidth={true}
                  error={hasError(escalationIdx, 'escalationType')}
                >
                  <InputLabel className={classes.escOwnerLabel} id={`escalationOwnerType-${escalationIdx}`}>{t('escalation-ownerType')}</InputLabel>
                  <Select
                    variant='outlined'
                    name={`escalationSchedule.${escalationIdx}.escalationType`}
                    value={escalation.escalationType}
                    onChange={(evt, child) => handleChange(evt.target.name, child.props.value, escalationIdx)}
                    onBlur={(evt, child, c) => handleChange(evt.target.name, evt.target.value, escalationIdx)}
                    labelId={`escalationOwnerType-${escalationIdx}`}
                  >
                    <MenuItem value='user'>{t('stage-builder-certifierType-user')}</MenuItem>
                    <MenuItem value='authzGroup'>{t('stage-builder-certifierType-authzGroup')}</MenuItem>
                    <MenuItem value='manager'>
                      {
                        props.formType.includes('cert') ? 
                          t('escalation-owner-certifierManager') : 
                          t('escalation-owner-policyOwnerManager')
                      }
                    </MenuItem>
                  </Select>
                  { hasError(escalationIdx, 'escalationType') && 
                  <FormHelperText>{t('form-error-required')}</FormHelperText>
                  }
                </FormControl>
              </Grid>
              { (escalation.escalationType === 'user' || escalation.escalationType === 'authzGroup') &&
              <Grid item xs={6}>
                <FormControl 
                  variant='outlined'
                  className={classes.formControl}
                  fullWidth={true}
                  error={hasError(escalationIdx, 'escalationId')}
                >
                  <AsyncSelect 
                    name={`escalationSchedule.${escalationIdx}.escalationId`}
                    error={hasError(escalationIdx, 'escalationId') ? Boolean(errorObject[escalationIdx].escalationId) : null}
                    variant='outlined'
                    label={t('escalation-owner')}
                    fullWidth={true}
                    targetType={escalation.escalationType === 'user' ? 'userName' : 'authzGroup'}
                    value={escalation.escalationId}
                    onChange={(evt, target, value) => handleChange(target, value, escalationIdx)}
                    onBlur={(evt, target) => handleChange(target, escalation.escalationId, escalationIdx)}
                  />
                  { hasError(escalationIdx, 'escalationId') && 
                  <FormHelperText>{t('form-error-required')}</FormHelperText>
                  }
                </FormControl>
              </Grid>
              }
            </Grid>
          </CardContent>
        </Card>
      )) }
    </Grid>
  );
};

EscalationSchedule.propTypes = {
  errorObject: PropTypes.array,
  escalationSchedule: PropTypes.array,
  formType: PropTypes.string,
  handleChange: PropTypes.func,
  hasError: PropTypes.func,
  touchedObject: PropTypes.array,
};

export default compose(
  withTranslation(),
  withStyles(styles),
)(EscalationSchedule);