import { CERTLIST, GENERIC } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import { httpFailure, alert } from './generic.actions.js';
import IDG_CONSTANTS from 'src/utils/constants.js';

export const getCertificationList = () => (dispatch, getState) => {
  dispatch({
    type: CERTLIST.QUERY_CERT_LIST,
    payload: null,
  });
  let { ascOrder, actingId, pageNumber, pageSize,  sortDir, sortBy, q, certId, certType, isAdminView, stageSelected, status } = getState().certlist;

  let params = { ascOrder, pageNumber, pageSize, sortBy, q };
  if (params.q) {
    params.q = params.q.replace(/\\/g, '\\\\').replace(/"/g, '\\"');
  }
  params.sortBy = sortDir === 'asc' ? `+${sortBy}` : `-${sortBy}`;

  if(!isAdminView){
    params.actingId = actingId;
  }
  else {
    params.selected = stageSelected;
  }
  
  params.type = status === IDG_CONSTANTS.CERT_STATUS.ACTIVE ? 'open': 'closed';
  
  return httpService.request({
    url: getCertificationListEndpoint(certType, certId, isAdminView),
    method: 'get',
    params,
  }).then(({ data }) => {
    dispatch({
      type: CERTLIST.QUERY_CERT_LIST_SUCCESS,
      payload: data.result,
    });
    return data;
  }).catch(error => {
    dispatch({
      type: CERTLIST.QUERY_CERT_LIST_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const updateStatus = (status) => (dispatch) => {
  dispatch({
    type: CERTLIST.UPDATE_STATUS,
    payload: status,
  });
};
  
export const getEventDetails = (hideScreen) => (dispatch, getState) => {

  let {stageSelected, eventIndexSelected, certId, certType} = getState().certlist;

  if (!eventIndexSelected && eventIndexSelected !== 0) {
    return;
  }
  
  if (hideScreen) {
    dispatch({
      type: CERTLIST.QUERY_EVENT_DETAILS,
      payload: null,
    });
  }
  let params = {};

  if(!getState().certlist.isAdminView){
    params.actingId = getState().certlist.actingId;
  }

  return httpService.request({
    url: getEventDetailsEndpoint(certType, certId, getState().certlist.isAdminView, stageSelected, eventIndexSelected),
    method: 'get',
    params: params, 
  }).then((data)=>{
    dispatch({
      type: CERTLIST.QUERY_EVENT_DETAILS_SUCCESS,
      payload: data,
    });
    return data;
  }).catch(error => {
    dispatch({
      type: CERTLIST.QUERY_EVENT_DETAILS_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const removeEventDetails = () => (dispatch, getState) => {
  dispatch({
    type: CERTLIST.QUERY_EVENT_DETAILS_REMOVE,
    payload: null,
  });
};

export const setSelectedEventIndex = (id, fetchData) => (dispatch, getState) => {
  let {stageSelected, results} = getState().certlist;
  let selectedEvent = null;
  if (results && results.stages) {
    results.stages[stageSelected].events.forEach((event)=>{
      if(event._id === id){
        selectedEvent= event.eventIndex;
      }
    });
  }
  dispatch({
    type: CERTLIST.SET_SELECTED_EVENT_INDEX,
    payload: selectedEvent,
  });
};

export const certActionEvent = (comment, reset) => (dispatch, getState) => {
  const { certType, certId, eventIndexSelected, stageSelected, actingId, rowData, selectedAction } = getState().certlist;
  var endpointUrl = IDG_CONSTANTS.IDM_CONTEXT + '/governance/certify/' + certType + '/' + certId + '/' + stageSelected + '/' + eventIndexSelected;
  let params = {action:selectedAction};

  //build data
  const data = !comment || comment === undefined ? {} : {comment: comment};

  //build params
  if(rowData){
    const {dataGroup, dataIndex, attrIndex} = rowData;
    params['dataGroup'] = dataGroup;
    params['dataIndex'] = dataIndex;
    params['attrIndex'] = attrIndex;
  }
  else if (!getState().certlist.isAdminView) {
    params.actingId = actingId;
  }

  return httpService.request({
    url: endpointUrl,
    method: 'post',
    data: data,
    params,
  }).then(({data})=> {
    dispatch(getEventDetails(false));
    return data;
  }).catch(error=>{
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  }).finally(()=>{
    if(reset){
      dispatch(updateSelectedAction(''));
      dispatch(updateRowData(null));
    } 
  });
};

export const certActionStage = (action, queryFilter, data) => (dispatch, getState) => {
  const { certType, certId, stageSelected, actingId } = getState().certlist;
  const endpoint = IDG_CONSTANTS.IDM_CONTEXT + '/governance/certify/' + certType + '/' + certId + '/' + stageSelected;
  const params = {
    'action': getAction(action),
    queryFilter,
    actingId,
  };
  return httpService.request({
    url: endpoint,
    method: 'post',
    data: data || {},
    params,
  }).then(({ data: { result } }) => {
    dispatch(alert(result, 'success'));
    if (action !== 'sign-off') {
      dispatch(getCertificationList());
    }
    return data;
  }).catch(error=>{
    dispatch({
      type: CERTLIST.QUERY_CERT_LIST_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const updateCertId = (newCertId, certType, noUpdate) => dispatch =>{

  dispatch({
    type: CERTLIST.UPDATE_CERTID,
    payload: {
      certId: newCertId,
      certType: certType,
    },
  });
  if(!noUpdate){
    dispatch(getCertificationList());
  }
};

export const changeSelectedStage = (stageSelected, noUpdate) => dispatch => {

  dispatch({
    type: CERTLIST.CHANGE_SELECTED_STAGE,
    payload: stageSelected,
  });
  if(!noUpdate){
    dispatch(getCertificationList());
  }
};

export const incrementSelectedStage = () => (dispatch, getState) => {
  const {stageSelected} = getState().certlist;
  let newStage = stageSelected + 1;
  
  if(newStage + 1 > getState().certlist.eventDetails.stages.length){
    newStage = 0;
  }
  dispatch({
    type: CERTLIST.CHANGE_SELECTED_STAGE,
    payload: newStage,
  });
};

export const decrementSelectedStage = () => (dispatch, getState) => {
  const {stageSelected} = getState().certlist;
  let newStage = stageSelected - 1;
  if(newStage < 0){
    newStage = getState().certlist.eventDetails.stages.length - 1;
  }
  dispatch({
    type: CERTLIST.CHANGE_SELECTED_STAGE,
    payload: newStage,
  });
};

