export const getMetadata = (type, obj) => (dispatch, getState) => {
  dispatch({
    type: CERTLIST.QUERY_METADATA_OBJECT,
    payload: null,
  });
  const certEndpoint = IDG_CONSTANTS.IDM_CONTEXT + '/commons/glossary/managed';
  let params = {
    targetId: obj,
  };
  return httpService.request({
    url: certEndpoint,
    method: 'get',
    params,
  }).then(({ data }) => {
    let payload = data.result || {};
    dispatch({
      type: CERTLIST.QUERY_METADATA_OBJECT_SUCCESS,
      payload: payload,
    });
  }).catch(error => {
    dispatch({
      type: CERTLIST.QUERY_METADATA_OBJECT_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  }); 
};

const getAction = (oldAction) => {
  switch(oldAction){
  case 'claim-all':
  case 'claim-selected':
    return 'claim';
  case 'certify-all':
  case 'certify-selected':
    return 'certify-remaining';
  case 'reset-all':
  case 'reset-selected':
    return 'reset';
  case 'reassign-tasks':
  case 'reassign-all':
  case 'reassign-selected':
    return 'reassign';
  default:
    return oldAction;
  }
};

const getCertificationListEndpoint = (certType, certId, isAdmin) => {
  if (isAdmin) {
    return IDG_CONSTANTS.IDM_CONTEXT + '/governance/adminCertList/' + certType + '/' + certId;
  }
  else{
    return IDG_CONSTANTS.IDM_CONTEXT + '/governance/certificationList/' + certType + '/' + certId;
  }
};

const getEventDetailsEndpoint = (certType, certId, isAdmin, stage, eventIndex) => {
  if(isAdmin){
    return IDG_CONSTANTS.IDM_CONTEXT + '/governance/adminCertEventDetails/' + certType + '/' + certId + '/' + stage + '/' + eventIndex;
  }
  else{
    return IDG_CONSTANTS.IDM_CONTEXT + '/governance/certificationEventDetails/' + certType + '/' + certId + '/' + stage + '/' + eventIndex;
  }
};