import { CERTLIST } from '../actions/actionTypes.js';

const initialState = {
  certType: '',
  status: '',
  results: [],
  pageNumber: 1,
  pageSize: 10,
  sortBy: 'firstName',
  sortDir: 'asc',
  q: '',
  querying: false,
  ascOrder: true,
  certId: '',
  actingId: 'managed/user/658b016e-9203-4408-90f5-204be6fc1064',
  events: [],
  stageSelected: 0,
  eventIndexSelected: null,
  eventDetails: null,
  isAdminView: true,
  selectedAction : '',
  rowData: {},
  metadataObject: null,
};

export default (state = initialState, action) => {
  switch(action.type) {
  case CERTLIST.CHANGE_SELECTED_STAGE:
    return {
      ...state,
      stageSelected: action.payload,
    };
  case CERTLIST.UPDATE_PAGINATION:
    let { ascOrder, pageNumber, pageSize, sortBy, stageSelected } = action.payload;
    return {
      ...state,
      pageNumber,
      pageSize,
      sortBy,
      ascOrder,
      stageSelected,
    };
  case CERTLIST.CHANGE_SORTING:
    return {
      ...state,
      sortBy: action.payload.sortBy,
      sortDir: action.payload.isDesc ? 'desc' : 'asc',
    };
  case CERTLIST.QUERY_CERT_LIST:
    return {
      ...state,
      querying: true,
    };
  case CERTLIST.UPDATE_CERTID:
    return{
      ...state,
      certId: action.payload.certId,
      certType: action.payload.certType,
    };
  case CERTLIST.QUERY_CERT_LIST_SUCCESS:
    return {
      ...state,
      querying: false,
      results: action.payload,
    };
  case CERTLIST.QUERY_CERT_LIST_FAILURE:
    return {
      ...state,
      querying: false,
      results: [],
    };
  case CERTLIST.UPDATE_STATUS:
    return {
      ...state,
      status: action.payload,
    };
  case CERTLIST.CHANGE_SEARCH_KEY:
    return{
      ...state,
      q:action.payload.q,
    };
  case CERTLIST.SET_SELECTED_EVENT_INDEX:
    return{
      ...state,
      eventIndexSelected:action.payload,
    };
  case CERTLIST.QUERY_EVENT_DETAILS:
    return {
      ...state,
      queryingEventDetails: true,
    };
  case CERTLIST.QUERY_EVENT_DETAILS_SUCCESS:
    return{
      ...state,
      queryingEventDetails: false,
      eventDetails: action.payload.data,
    };
  case CERTLIST.QUERY_EVENT_DETAILS_REMOVE:
    return{
      ...state,
      queryingEventDetails: false,
      eventDetails: null,
    };
  case CERTLIST.QUERY_EVENT_DETAILS_FAILURE:
    return{
      ...state,
      queryingEventDetails: false,
      eventDetails: null,
    };
  case CERTLIST.QUERY_METADATA_OBJECT:
    return {
      ...state,
      queryingMetadata: true,
    };
  case CERTLIST.QUERY_METADATA_OBJECT_SUCCESS:
    return{
      ...state,
      queryingMetadata: false,
      metadataObject: action.payload,
    };
  case CERTLIST.QUERY_METADATA_OBJECT_FAILURE:
    return{
      ...state,
      queryingMetadata: false,
      metadataObject: null,
    };
  case CERTLIST.CHANGE_IS_ADMIN:
    return{
      ...state,
      isAdminView: action.payload,
    };
  case CERTLIST.UPDATE_ACTINGID:
    return {
      ...state,
      actingId: action.payload,
    };
  case CERTLIST.CHANGE_SELECTED_ACTION:
    return {
      ...state,
      selectedAction : action.payload,
    };
  case CERTLIST.CHANGE_ROW_DATA:
    return({
      ...state,
      rowData: action.payload,
    });
  default:
    return state;
  }
};
