import { CONFIG, SETTINGS, NOTIFICATIONS } from '../actions/actionTypes.js';

const initialState = {
  IDM_version: null,
  managed_objects: {},
  applications: {},
  certifiable_attributes: {
    user: [],
    application: [],
  },
  membership_properties: null,
  settings: {
    querying: false,
    results: [],
    about: {},
  },
  notifications: {
    querying: false,
    results: [],
    selected: {
      id: '',
      notification: {},
    },
  },
};

export default (state = initialState, action) => {
  switch(action.type) {
  case CONFIG.GET_IDM_VERSION: 
    return {
      ...state,
      IDM_version: action.payload,
    };
  case CONFIG.GET_CONFIG_MANAGED_OBJECTS:
    return {
      ...state,
      managed_objects: action.payload,
    };
  case CONFIG.GET_CONFIG_APPLICATIONS:
    return {
      ...state,
      applications: action.payload,
    };
  case CONFIG.GET_CERTIFIABLE_ATTRIBUTES:
    return {
      ...state,
      certifiable_attributes: {
        ...state.certifiable_attributes,
        [action.payload.classType]: action.payload.data,
      },
    };
  case CONFIG.GET_MEMBERSHIP_PROPERTIES:
    return {
      ...state,
      membership_properties: action.payload,
    };
  case SETTINGS.QUERY_SETTINGS:
    return {
      ...state,
      settings : {
        ...state.settings,
        querying: true,
      },
    };
  case SETTINGS.QUERY_SUCCESS:
    return {
      ...state,
      settings : {
        ...state.settings,
        querying: false,
        results: action.payload,
      },
    };
  case SETTINGS.QUERY_FAILURE:
    return {
      ...state,
      settings : {
        ...state.settings,
        querying: false,
        results: null,
      },
    };
  case SETTINGS.QUERY_INFO:
    return {
      ...state,
      settings: {
        ...state.settings,
        querying:true,
      },
    };
  case SETTINGS.QUERY_INFO_SUCCESS:
    return{
      ...state,
      settings: {
        ...state.settings,
        querying:false,
        about: action.payload,
      },
    };
  case SETTINGS.QUERY_INFO_FAILURE:
    return {
      ...state,
      settings: {
        ...state.settings,
        querying: false,
        about: null,
      },
    };
  case NOTIFICATIONS.QUERY_ALL:
    return {
      ...state,
      notifications:{
        ...state.notifications,
        querying: true,
      },
    };
  case NOTIFICATIONS.QUERY_ALL_SUCCESS:
    return {
      ...state,
      notifications: {
        ...state.notifications,
        results: action.payload,
        querying: false,
      },
    };
  case NOTIFICATIONS.QUERY_ALL_FAILURE:
    return {
      ...state,
      notifications:{
        ...state.notifications,
        results: null,
        querying: false,
      },
    };
  case NOTIFICATIONS.QUERY_SELECTED:
    return {
      ...state,
      notifications:{
        ...state.notifications,
        querying: true,
      },
    };
  case NOTIFICATIONS.SET_SELECTED:
    return {
      ...state,
      notifications:{
        ...state.notifications,
        selected: {
          ...state.notifications.selected,
          id:action.payload,
        },
      },
    };
  case NOTIFICATIONS.QUERY_SELECTED_SUCCESS:
    return {
      ...state,
      notifications: {
        ...state.notifications,
        selected: {
          ...state.notifications.selected,
          notification: action.payload,
        },
        querying: false,
      },
    };
  case NOTIFICATIONS.QUERY_SELECTED_FAILURE:
    return {
      ...state,
      notifications:{
        ...state.notifications,
        selected: {
          ...state.notifications.selected,
          notification: null,
        },
        querying: false,
      },
    };
  default:
    return state;
  }
};
