import { CONFIG, GENERIC, SETTINGS, NOTIFICATIONS} from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';
import { httpFailure, alert } from './generic.actions.js';
import i18n from 'src/i18n.js';

export const getIDMVersion = () => dispatch => {
  return httpService.request({
    url: constants.IDM_CONTEXT + '/info/version',
    method: 'get',
  }).then(({ data }) => {
    dispatch({
      type: CONFIG.GET_IDM_VERSION,
      payload: data.productVersion,
    });
    return data;
  }).catch(error => error);
};

export const getManagedObjectConfig = () => dispatch => {
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/managedObjectConfig',
    method: 'get',
  }).then(({ data }) => {
    const { applications, managedObjects } = data;
    dispatch({
      type: CONFIG.GET_CONFIG_APPLICATIONS,
      payload: applications,
    });
    dispatch({
      type: CONFIG.GET_CONFIG_MANAGED_OBJECTS,
      payload: managedObjects,
    });
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

