export const getSettings = () => dispatch => {
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/systemSettings',
    method: 'get',
  }).then(({ data }) => {
    dispatch({
      type: SETTINGS.QUERY_SUCCESS,
      payload: data.systemSettings,
    });
    return data.systenSettings;
  }).catch(error => { 
    dispatch({
      type: SETTINGS.QUERY_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const updateSettings = (updatedSettings) => (dispatch, getState) => {
  const successMessage = i18n.t('updateSettingsSuccess');
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/systemSettings',
    method: 'post',
    data: updatedSettings,
  }).then(({data})=>{
    dispatch(alert(successMessage, 'success'));
    dispatch({
      type: SETTINGS.QUERY_SUCCESS,
      payload: data.systemSettings,
    });
    return getSettings();
  }).catch(error => {
    dispatch({
      type: SETTINGS.QUERY_FAILURE,
      payload:null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

