export const setSelectedNotification = (id) => dispatch => {
  dispatch({
    type: NOTIFICATIONS.SET_SELECTED,
    payload: id,
  });
};

export const getSelectedNotification = () => (dispatch, getState) => {
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/notification/' + getState().config.notifications.selected.id,
    method: 'get',
  }).then(({data}) => {
    dispatch({
      type: NOTIFICATIONS.QUERY_SELECTED_SUCCESS,
      payload: data,
    });
    return data;
  }).catch((error) => {
    dispatch({
      type: NOTIFICATIONS.QUERY_SELECTED_FAILURE,
      payload: null,
    });
    let errorMessage='';
    error.response.status == 404 ? errorMessage = 'Could Not Find Data for id' : errorMessage=error;
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, errorMessage, 'error'));
  });
};

export const updateSelectedNotification = (updatedNotification) => (dispatch, getState) => {
  const successMessage = i18n.t('update-notification-success');
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/notification/' + getState().config.notifications.selected.id,
    method: 'put',
    data: updatedNotification,
  }).then(({data}) => {
    dispatch(alert(successMessage, 'success'));
    return(getSelectedNotification());
  }).catch((error) => {

  });
};

const getGlossaryMetadata = (type, systemName) => {
  let classType;

  if (type === 'user') {
    classType = 'identity';
  } else if (type === 'application') {
    classType = 'system';
  } else if (systemName) {
    classType = type;
  }

  let queryFilter = `class eq '${classType}' and certifiable eq true`;
  if (systemName) {
    queryFilter = queryFilter.concat(` and system eq '${systemName}'`);
  }

  return httpService.request({
    url: constants.IDM_CONTEXT + '/commons/glossary',
    method: 'get',
    params: {
      queryFilter,
    },
  }).then(({ data }) => {
    return data.result.map(metadata => {
      let entry = {
        _id: metadata._id, 
        certifiable: metadata.certifiable,
      };
      if (type === 'user') {
        entry.attributeName = metadata.attributeName;
      } else if (type === 'application') {
        entry.name = metadata.name;
        entry.riskLevel = metadata.riskLevel;
      } else {
        entry = { ...metadata };
      }
      return entry;
    });
  });
};

export const getCertifiableAttributes = type => (dispatch, getState) => {
  const { config: { certifiable_attributes } } = getState();

  if (type && certifiable_attributes && certifiable_attributes[type]) {
    return certifiable_attributes[type];
  } 

  return Promise.all([getGlossaryMetadata('user'), getGlossaryMetadata('application')])
    .then(([userAttrs, systemAttrs]) => {
      dispatch({
        type: CONFIG.GET_CERTIFIABLE_ATTRIBUTES,
        payload: { classType: 'user', data: userAttrs },
      });
      systemAttrs.forEach(system => {
        if (system.certifiable === true) {
          getGlossaryMetadata('system-attribute', system.name).then(attrs => {
            attrs.forEach(appAttr => {
              if (appAttr.certifiable === false) {
                return;
              }
              if (!system._systemAttributes) {
                system._systemAttributes = {};
              }
              if (!system._systemAttributes[appAttr.objectType]) {
                system._systemAttributes[appAttr.objectType] = {};
              }
              delete appAttr._rev;
              delete appAttr.constraints;
              system._systemAttributes[appAttr.objectType][appAttr.attributeName] = appAttr;
            });
            dispatch({
              type: CONFIG.GET_CERTIFIABLE_ATTRIBUTES,
              payload: { classType: 'application', data: systemAttrs },
            });
          });
        }
      });
    }).catch(error => {
      return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
    });

};