import React, { useState, Fragment, useEffect } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Button,
  Grid,
  OutlinedInput,
  Table,
  TableHead,
  TableBody,
  TableCell,
  TableRow,
  Typography,
} from '@material-ui/core';
import { 
  getSettings, updateSettings, getManagedObjectConfig,
} from 'src/store/actions/config.actions.js';
import {withFormik} from 'formik';
import LoadingIndicator from 'src/components/presentational/LoadingIndicator';
import {BooleanField} from 'src/components/containers/Settings/inputFields/BooleanField';
import {DropdownField} from 'src/components/containers/Settings/inputFields/DropdownField';
import {RiskLevel} from 'src/components/containers/Settings/inputFields/RiskLevelSlider';
import MenuLinks from 'src/components/containers/Settings/inputFields/MenuLinks';

const SimpleForm = props => {
  const [loading, setLoading] = useState(false);
  useEffect(()=>{
    setLoading(true);
    props.getSettings().then((settingData)=>{
      props.getManagedObjectConfig();
    }).finally(()=> {
      setLoading(false);
    });
  }, []);

  const handleSliderChange = (e, target, value) => {
    
    props.setFieldTouched(target, true);
    if (value[0] === value[1]) {
      if (value[0] === 1) {
        value[1] = 2;
      }
      else {
        value[0] = value[0] - 1;
      }
    }
    props.setFieldValue(target, value, true);
  };

  const handleDropDownChange =(e, target, value) => {
    props.setFieldTouched(target, true);
    props.setFieldValue(target, value, true);
  };

  const getOptionsFromManagedUsers = () => {
    const user_objects = config.managed_objects.user;
    const options = Object.keys(user_objects).map(key=>({
      'key': key,
      'label':user_objects[key].description,
    }));
    return options;
  };

  const getSettingSegment = (field) => {
    if(field.type == 'dropdown'){
      const options = getOptionsFromManagedUsers();
      if(field.attributes){
        return(
          <Fragment>
            {field.attributes.map(attribute => {
              return(
                <DropdownField key={attribute.id}
                  options={options} 
                  field={attribute} t={t} classes={classes} 
                  handleChange={(e, selection) => {handleDropDownChange(e, attribute.id, selection.props.value);}}
                  values={values}/>
              );
            })}
          </Fragment>
        );
      }
      else{
        return(
          <DropdownField options={options} 
            field={field} 
            t={t} 
            classes={classes} 
            handleChange={(e, selection) => {
              handleDropDownChange(e, field.id, selection.props.value);}}
            values={values}/>
        );
      }
    }
    else if(field.type == 'dblSlider'){
      return(
        <RiskLevel field={field} values={values} classes={classes}
          t={t} handleChange={(event, value) => { handleSliderChange(event, field.id, value);}}/>
      );
    }
    else if(field.type == 'string'){
      if(field.id == 'menuManagement'){
        return(
          <MenuLinks field={field} values={values} onChange={handleChange} classes={classes} t={t}
            setFieldValue={props.setFieldValue}/>
        );
      }
      else {
        return (
          <TableRow className={classes.settingGrid} key={field.id}>
            <TableCell key={field.id+'label'} className={classes.headerCell}>
              <Typography>{t(field.id)}:</Typography>
            </TableCell>
            <TableCell key={field.id+'input'} className={classes.headerCell}>
              <OutlinedInput onChange={handleChange} id={field.id} height='.75em' fullWidth={true} value={values[field.id]}></OutlinedInput>              
            </TableCell>
          </TableRow>
        );
      }
    }
    else if(field.type == 'boolean'){
      return(
        <BooleanField field={field} values={values} handleChange={handleChange} t={t} classes={classes}/>
      );
    }
  };

  const {
    values,
    handleChange,
    handleSubmit,
    config,
    classes,
    t,
    dirty,
  } = props;
  if(!config.settings.results || !config.managed_objects || _.isEmpty(config.managed_objects)){ //possible check config.managed_sers.schema as well
    return null;
  }

  if(loading){
    return(<LoadingIndicator />);
  }
  else{
    return(
      <Grid container className={classes.formContainer} direction={'column'}>
        <form onSubmit={handleSubmit} className={classes.formEntire}>
          {config.settings.results.map(setting=>{
            return(
              <Grid container item 
                key={setting.section} 
                className={classes.settingSection}
                direction={'column'}>
                <Grid item>
                  <Typography className={classes.sectionHeader} variant='h6'>{t(setting.section)}</Typography>
                </Grid>
                <Grid item>
                  <Table className={classes.settingTable}>
                    { setting.section !== 'Risk Level Management' &&
                      <TableHead className={classes.tableHeader}>
                        <TableRow>
                          <TableCell className={classes.headerCell}>Name</TableCell>
                          <TableCell colSpan={2} className={classes.headerCell}>Option</TableCell>
                        </TableRow>
                      </TableHead>
                    }
                    <TableBody className={classes.tableBody}>
                      {setting.fields.map(field=>{
                        const settingSegment = getSettingSegment(field);
                        return(
                          <Fragment key={field.id}>
                            {settingSegment}
                          </Fragment>
                        );
                      })}
                    </TableBody>
                  </Table>
                </Grid>
              </Grid>
            );
          })}
          <Button className={classes.settingsButton} type="submit" disabled={!dirty}>{t('submit')}</Button>
        </form>
      </Grid>
    );
  }
};

const Global = withFormik({
  mapPropsToValues: (props) => { 
    let formFields = {};
    if(props.config.settings.results != null && props.config.settings.results.length > 0){
      props.config.settings.results.forEach(setting => {
        setting.fields.forEach(field => {
          if(field.type == 'dblSlider'){
            formFields[field.id]=[field.value.lower, field.value.higher];
          }
          else if(field.id == 'userAttrMappings'){
            field.attributes.forEach(attribute => {
              formFields[attribute.id]=attribute.value;
            });
          }
          else if(field.type == 'boolean'){
            formFields[field.id] = field.value;
          }
          else if(field.id == 'menuManagement'){
            formFields[field.id] = [];
            field.value.forEach(value => {
              formFields[field.id].push(value);
            });
          }
          else{
            formFields[field.id] = field.value || '';
          }
        });
      });
      return formFields;
     
    }
  },
      
  enableReinitialize: true,
  // Custom sync validation
  validate: (values, props) => {
    const errors = {};

    values['menuManagement'].map(url => {
      if(url.name == '' || url.value == ''){
        errors.menuManagement = 'Required';
      }
    });

    return errors;
  },

  handleSubmit: (values, formikBag) => {
    const { props } = formikBag;
    var newSettings = _.cloneDeep(props.config.settings);
    newSettings.results.forEach(setting=>{
      setting.fields.forEach(field => {
        if(field.type == 'dblSlider'){
          field.value.lower = values[field.id][0];
          field.value.higher = values[field.id][1];
        }
        else if(field.id == 'userAttrMappings'){
          field.attributes.forEach(attribute => {
            attribute.value = values[attribute.id];
          });
        }
        else{
          field.value = values[field.id];
        }
      });
    });
    props.updateSettings({'updatedSettings': newSettings.results});
  },

  displayName: 'BasicForm',
})(SimpleForm);


const mapDispatchToProps = {
  getSettings,
  updateSettings,
  getManagedObjectConfig,
};

const mapStateToProps = state => {
  const { config, settings } = state;
  return {
    config, settings,
  };
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(Global);