import React, { Fragment, useEffect } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import {
  Button,
  Checkbox,
  Collapse, 
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Grid,
  IconButton,
  InputLabel,
  MenuItem,
  OutlinedInput,
  Paper,
  Select,
  Switch,
  TextField,
  Zoom,
} from '@material-ui/core';
import {
  ArrowUpward,
  ArrowDownward,
  Close,
  ExpandMore,
} from '@material-ui/icons';
import AsyncSelect from 'src/components/presentational/AsyncSelect';
import DatePicker from 'src/components/presentational/DatePicker';
import DurationSelect from 'src/components/presentational/DurationSelect';
import EntitlementFilter from 'src/components/presentational/EntitlementFilter';
import EscalationSchedule from 'src/components/presentational/EscalationSchedule';
import styles from './styles';

const StageBuilder = props => {
  const { certType, classes, errors, frequency, stages, t, touched } = props;
  const [showNewStageButtons, setShowNewStageButtons] = React.useState(false);
  
  useEffect(() => {
    if (stages.length === 0) {
      addNewStage();
    }
  }, []);

  const addNewStage = idx => {
    const { field, setFieldValue } = props;
    const newStages = _.cloneDeep(stages);
    let newStage = null;
    if (_.isFinite(idx)) {
      newStage = _.cloneDeep(newStages[idx]);
      newStage.name = t('stage-builder-new-stage-name', { num: newStages.length + 1 });
      newStage.deadline = null;
      newStage.deadlineDuration = {
        unit: '',
        period: '',
      };
    } else {
      newStage = {
        name: t('stage-builder-new-stage-name', { num: newStages.length + 1 }),
        entitlementFilter: { attributes: {} },
        useRiskLevel: false,
        riskLevel: {
          Low: false,
          Medium: false,
          High: false,
        },
        certifierName: '',
        certifierType: '',
        certifierKey: '',
        deadline: null,
        deadlineDuration: {
          unit: '',
          period: '',
        },
        escalationSchedule: [],
      };
      if (certType === 'user') {
        newStage.entitlementFilter.applications = {};
      } else {
        newStage.entitlementFilter.certifyMetadata = false;
      }
    }
    setShowNewStageButtons(false);
    newStages.push(newStage);
    setFieldValue(field.id, newStages, true);
  };

  const removeStage = stageIdx => {
    const { field, stages, setFieldValue } = props;
    const newStages = _.cloneDeep(stages);
    if (stageIdx !== 0) {
      _.pullAt(newStages, stageIdx);
      setFieldValue(field.id, newStages, true);
    }
  };

  const handleChange = (evt, value, idx) => {
    if (_.isFinite(idx)) {
      const target = evt && evt.target && evt.target.name ? evt.target.name : evt;
      props.handleStageChange(idx, target, value);
      if (target === 'certifierType' && value === 'authzGroup') {
        for (let i = idx + 1; i < props.stages.length; i++) {
          const futureStage = props.stages[i];
          if (futureStage.certifierType === 'prevManager') {
            props.handleStageChange(i, 'certifierType', '');
          }
        }
      }
    }
  };

  const handleEscalationScheduleChange = (target, value, escalationIdx, stageIdx) => {
    if (_.isFinite(stageIdx) && _.isFinite(escalationIdx)) {
      props.handleStageChange(stageIdx, target, value);
    } else if (target === 'escalationSchedule') {
      const newEscalationPath = `${props.field.id}[${stageIdx}].escalationSchedule`;
      let lastEscalationIdx = value.length;
      if (value.length > props.stages[stageIdx].escalationSchedule.length) {
        lastEscalationIdx = value.length - 1;
      }
      ['unit', 'period', 'escalationType', 'escalationId'].forEach(key => {
        props.setFieldTouched(`${newEscalationPath}[${lastEscalationIdx}].${key}`, false);
      });
      props.setFieldValue(newEscalationPath, value, false);
    }
  };

  const getCertifierTypesForStage = stageIdx => {
    return (
      <FormControl 
        variant='outlined'
        className={classes.formControl}
        fullWidth={true}
        error={hasError(stageIdx, 'certifierType')}
      >
        <InputLabel>{t('stage-builder-certifierType')}</InputLabel>
        <Select
          name='certifierType'
          value={props.stages[stageIdx].certifierType}
          onChange={(evt, child) => handleChange(evt, child.props.value, stageIdx)}
          label={t('stage-builder-certifierType')}
        >
          <MenuItem value='user'>{t('stage-builder-certifierType-user')}</MenuItem>
          <MenuItem value='authzGroup'>{t('stage-builder-certifierType-authzGroup')}</MenuItem>
          { certType === 'user' && <MenuItem value='manager'>{t('stage-builder-certifierType-manager')}</MenuItem> }
          { certType === 'user' && <MenuItem 
            disabled={stageIdx === 0 || props.stages[stageIdx-1].certifierType === 'authzGroup'} 
            value='prevManager'
          >
            {t('stage-builder-certifierType-prevManager')}
          </MenuItem> 
          }
          { certType === 'user' ?
            <MenuItem value='entitlementOwner'>{t('stage-builder-certifierType-entitlementOwner')}</MenuItem> :
            <MenuItem value='glossaryKey'>{t('stage-builder-certifierType-glossaryKey')}</MenuItem> 
          }
        </Select>
        {hasError(stageIdx, 'certifierType') && <FormHelperText>{t('form-error-required')}</FormHelperText>}
      </FormControl>
    );
  };

  const getCertifierNameFieldForStage = stageIdx => {
    const stage = props.stages[stageIdx];
    if (!_.includes(['user', 'authzGroup', 'glossaryKey'], stage.certifierType)) {
      return null;
    }
    if (stage.certifierType === 'glossaryKey') {
      return (
        <FormControl 
          variant='outlined'
          className={classes.formControl}
          fullWidth={true}
          error={hasError(stageIdx, 'certifierKey')}
        >
          <InputLabel className={classes.formLabel}>{t('stage-builder-certifierKey')}</InputLabel>
          <OutlinedInput
            name={'certifierKey'}
            value={stage.certifierKey}
            onChange={(evt) => handleChange(evt, evt.target.value, stageIdx)}
            onBlur={(evt) => handleChange(evt, evt.target.value, stageIdx)}
          />
          {hasError(stageIdx, 'certifierKey') && <FormHelperText>{t('form-error-required')}</FormHelperText>}
        </FormControl>
      );
    }
    return (
      <FormControl 
        variant='outlined'
        className={classes.formControl}
        fullWidth={true}
        error={hasError(stageIdx, 'certifierName')}
      >
        <AsyncSelect 
          name='certifierName'
          variant='outlined'
          fullWidth={true}
          label={t('stage-builder-certifierName')}
          targetType={stage.certifierType === 'user' ? 'userName' : 'authzGroup'}
          value={stage.certifierName}
          onChange={(evt, target, value) => handleChange(target, value, stageIdx)}
        />
      </FormControl>
    );
  };

  const getDeadlineFieldForStage = stageIdx => {
    const stage = props.stages[stageIdx];

    if (frequency === 'ad-hoc') {
      return (
        <FormControl 
          className={classes.formControl} 
          error={hasError(stageIdx, 'deadline')}
        >
          <FormLabel className={classes.deadlineLabel} >
            {t('stage-builder-deadline')}
          </FormLabel>
          <DatePicker 
            field='deadline'
            disablePast={true}
            disableFuture={false}
            error={hasError(stageIdx, 'deadline')}
            value={stage.deadline}
            onChange={(evt, target, value) => handleChange(target, value, stageIdx)}
          />
          { hasError(stageIdx, 'deadline') && 
          <FormHelperText>{errors[stageIdx].deadline}</FormHelperText>
          }
        </FormControl>
      );
    } else if (frequency === 'scheduled' || frequency === 'event-based') {
      return (
        <FormControl 
          className={classes.formControl}
          error={
            hasError(stageIdx, 'deadlineDuration') && 
            Boolean(errors[stageIdx].deadlineDuration.unit || errors[stageIdx].deadlineDuration.period)
          }
          fullWidth={true}
        >
          <FormLabel className={classes.deadlineLabel} >
            {t('stage-builder-deadline')}
          </FormLabel>
          <DurationSelect 
            durationType={props.formType}
            errors={errors && errors && errors[stageIdx] ? errors[stageIdx].deadlineDuration : ''}
            field='deadlineDuration'
            handleBlur={(evt) => handleChange(evt.target.name, evt.target.value, stageIdx)}
            handleChange={(evt) => handleChange(evt.target.name, evt.target.value, stageIdx)}
            touched={touched && touched[stageIdx] ? touched[stageIdx].deadlineDuration : false}
            value={stage.deadlineDuration}
          />
        </FormControl>
      );
    } else {
      return null;
    }
  };

  const hasError = (stageIdx, field, escalationIdx, escalationField) => {
    let errorFound = Boolean(
      touched && errors && touched[stageIdx] && errors[stageIdx] && touched[stageIdx][field] && errors[stageIdx][field]
    );
    if (_.isUndefined(escalationIdx) && _.isUndefined(escalationField)) {
      return errorFound;
    } 

    return Boolean(
      errorFound && touched[stageIdx][field][escalationIdx] && errors[stageIdx][field][escalationIdx] &&
      touched[stageIdx][field][escalationIdx][escalationField] && errors[stageIdx][field][escalationIdx][escalationField]
    );
  };
  
  return (
    <Fragment>
      { stages.map((stage, stageIdx) => (
        <Paper key={stageIdx} className={classes.root}>
          <ExpansionPanel defaultExpanded>
            <ExpansionPanelSummary
              expandIcon={<ExpandMore />}
              aria-label='Expand'
              className={classes.stageSummaryPanel}
            >
              <Grid item className={classes.expansionPanelSummaryGrid} onClick={(event) => event.stopPropagation()}>
                <TextField 
                  name='name'
                  label='Name' 
                  error={hasError(stageIdx, 'name')}
                  variant='outlined'
                  value={stage.name} 
                  onChange={(evt) => handleChange(evt, evt.target.value, stageIdx)} 
                />
                <IconButton 
                  disabled={stageIdx === 0} 
                  color='primary' 
                  aria-label={t('stage-builder-move-up')} 
                  onClick={handleChange}
                >
                  <ArrowUpward />
                </IconButton>
                <IconButton 
                  disabled={stageIdx === stages.length - 1} 
                  color='primary' 
                  aria-label={t('stage-builder-move-up')} 
                  onClick={handleChange}
                >
                  <ArrowDownward />
                </IconButton>
                <IconButton 
                  disabled={stageIdx === 0} 
                  className={classes.stageActionDelete} 
                  aria-label={t('stage-builder-delete-stage')} 
                  onClick={() => removeStage(stageIdx)}
                >
                  <Close />
                </IconButton>
              </Grid>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails>
              <Grid container justify='flex-start'>
                <Grid item xs={12}>
                  { certType && 
                  <EntitlementFilter
                    certType={certType}
                    filter={stage.entitlementFilter}
                    onChange={(filter) => handleChange('entitlementFilter', filter, stageIdx)}
                  />
                  }
                </Grid>
                <Grid item xs={3} className={classes.riskGrid}>
                  <FormControlLabel
                    className={classes.formControl}
                    control={
                      <Switch
                        checked={stage.useRiskLevel}
                        onChange={(evt, value) => handleChange(evt, value, stageIdx)}
                        name='useRiskLevel'
                        color='primary'
                      />
                    }
                    label={t('stage-builder-useRiskLevel')}
                  />
                </Grid>
                <Grid item xs={8} className={classes.riskGrid}>
                  <Collapse in={stage.useRiskLevel}>
                    <FormControl 
                      className={classes.formControlCheckbox}
                      fullWidth={true}
                      error={hasError(stageIdx, 'riskLevel')}
                    >
                      { Object.entries(stage.riskLevel).map(([key, value]) => (
                        <FormControlLabel
                          key={key}
                          className={classes.formControlCheckbox}
                          control={
                            <Checkbox
                              checked={value}
                              onChange={(evt, value) => handleChange(evt, value, stageIdx)}
                              name={`riskLevel.${key}`}
                              color='primary'
                            />
                          }
                          label={t(`stage-builder-risk-${key.toLowerCase()}`)}
                        />
                      ))}
                      { hasError(stageIdx, 'riskLevel') && 
                        <FormHelperText>{errors[stageIdx].riskLevel}</FormHelperText>}
                    </FormControl>
                  </Collapse>
                </Grid>
                <Grid item xs={12} className={classes.certifierGrid}>
                  {getCertifierTypesForStage(stageIdx)}
                </Grid>
                <Grid item xs={12}>
                  {getCertifierNameFieldForStage(stageIdx)}
                </Grid>
                <Grid item xs={12} className={classes.deadlineGrid}>
                  {getDeadlineFieldForStage(stageIdx)}
                </Grid>
                <EscalationSchedule
                  formType={props.formType}
                  errorObject={_.get(errors, `${stageIdx}.escalationSchedule`)}
                  escalationSchedule={stage.escalationSchedule} 
                  handleChange={(target, value, escalationIdx) => handleEscalationScheduleChange(target, value, escalationIdx, stageIdx)}
                  hasError={(escalationIdx, escalationField) => hasError(stageIdx, 'escalationSchedule', escalationIdx, escalationField)}
                  touchedObject={_.get(touched, `${stageIdx}.escalationSchedule`)}
                />
              </Grid>
            </ExpansionPanelDetails>
          </ExpansionPanel>
        </Paper>
      )) }
      <Grid container justify='flex-end' className={classes.stageBtnContainer}>
        { !showNewStageButtons &&
        <Grid item>
          <Button
            className={classes.stageActionBtn}
            size='large'
            variant='outlined'
            onClick={() => setShowNewStageButtons(true)}
          >
            {t('stage-builder-btn-add')}
          </Button>
        </Grid>
        }
        { showNewStageButtons &&
        <Zoom in={showNewStageButtons}>
          <Grid item>
            <Button
              className={classes.stageActionBtn}
              size='large'
              variant='outlined'
              onClick={() => addNewStage()}
            >
              {t('stage-builder-btn-blank')}
            </Button>
            { stages.map((stage, stageIdx) => (
              <Button
                key={`CopyStage${stageIdx}`}
                className={classes.stageActionBtn}
                size='large'
                variant='outlined'
                onClick={() => addNewStage(stageIdx)}
              >
                {t('stage-builder-btn-copy', { stage: stage.name })}
              </Button>
            ))
            }
            <Button
              className={classes.stageActionBtn}
              size='large'
              variant='outlined'
              onClick={() => setShowNewStageButtons(false)}
            >
              {t('btn-cancel')}
            </Button>
          </Grid>
        </Zoom>
        }
      </Grid>
    </Fragment>
  );
  
};

export default compose(
  withTranslation(),
  withStyles(styles),
)(StageBuilder);