import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import {
  Button,
  Container,
  Dialog,
  DialogTitle,
  Grid,
  List,
  ListItem,
  ListItemText,
  ListSubheader,
  IconButton,
  Typography,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import clsx from 'clsx';
import { withStyles } from '@material-ui/core/styles';
import { changeActiveGovernanceDialog, changeModalObject, changeDialogType } from 'src/store/actions/generic.actions.js';
import ViewReassign from 'src/components/presentational/GovernanceDialog/ViewReassign';
import ViewReactive from 'src/components/presentational/GovernanceDialog/ViewReactive';
import ViewCertification from 'src/components/presentational/GovernanceDialog/ViewCertification';
import ViewObject from 'src/components/presentational/GovernanceDialog/ViewObject';
import ViewViolation from 'src/components/presentational/GovernanceDialog/ViewViolation';
import ViewComments from 'src/components/presentational/GovernanceDialog/ViewComments';
import ViewActionSubmitting from 'src/components/presentational/GovernanceDialog/ViewActionSubmitting';
import styles from './styles';

class GovernanceDialog extends Component {

  handleClose = () => {
    if(this.props.takingAction){
      return null;
    }
    
    this.props.changeActiveGovernanceDialog(null);
    this.props.changeModalObject(null);

    if(this.props.onClose !== undefined){
      this.props.onClose();
    }
  }

  componentWillUnmount() {
    this.setState({ modalObject: null });
    this.props.changeDialogType(null);
  }

  getModalTitle = modalObject => {
    const { t } = this.props;
    let modalTitle = null;

    switch (this.props.dialogType) {
    case 'userCertification':
    case 'objectCertification':
    case 'certification':
      modalTitle = t('campaign-details');
      break;
    case 'policy':
      modalTitle = t('policy-details');
      break;
    case 'violation':
      modalTitle = t('violation-details');
      break;
    case 'glossary':
      modalTitle = modalObject && !this.props.queryingMetadata ? modalObject.displayName || modalObject.attributeValue : null;
      break;
    case 'entitlement-history':
      modalTitle = modalObject ? modalObject.entitlementDisplayName : null;
      break;
    case 'targeted-attribute':
      modalTitle = modalObject ? modalObject.title : null;
      break;
    case 'reassign-tasks':
    case 'reassign-all':
    case 'choose-certifier':
      return t(this.props.dialogType);
    case 'comment':
      modalTitle = 'Comments';
      break;
    case 'revoke':
      modalTitle = 'Revoke';
      break;
    case 'abstain':
      modalTitle = 'Abstain';
      break;
    case 'configure-reactive':
      modalTitle = t('btn-configure-reactive');
      break;
    case 'actionSubmitting':
      modalTitle = t('actionInProgress');
      break;
    default:
      modalTitle = null;
    }
    return modalTitle;
  }

  handleConfirmationSubmit = dialogType => {
    switch (dialogType) {
    case 'cancel-cert':
    case 'cancel-violation':
    case 'cancel-exception':
    case 'delete-scan':
    case 'delete-policy':
    case 'reset-all':
    case 'reset-selected':
    case 'sign-off':
      this.handleClose();
      this.props.onSubmitDialog(dialogType, this.props.selectedRows);
      break;
    case 'confirmation':
    default:
      this.props.onSubmitDialog();
    }
  }

  getConfirmationMessage = dialogType => {
    const { confirmationMessage, t } = this.props;
    if (confirmationMessage) {
      return confirmationMessage;
    }
    else {
      return t('confirm-' + dialogType);
    }
  }

  getModalContents = dialogType => {
    const { classes, modalObject, t } = this.props;

    switch (dialogType) {
    case 'confirmation':
    case 'cancel-cert':
    case 'cancel-violation':
    case 'cancel-exception':
    case 'delete-scan':
    case 'delete-policy':
    case 'reset-all':
    case 'reset-selected':
    case 'sign-off':
      return (
        <Grid>
          <Grid>
            <Typography>{this.getConfirmationMessage(dialogType)}</Typography>
          </Grid>
          <Grid item xs={12} className={classes.actionButtonContainer}>
            <Button
              className={classes.actionButton}
              variant='contained'
              onClick={this.handleClose}
            >
              {t('btn-cancel')}
            </Button>
            <Button
              className={classes.actionButton}
              variant='contained'
              color='primary'
              onClick={() => this.handleConfirmationSubmit(dialogType)}
            >
              {t('btn-ok')}
            </Button>
          </Grid>
        </Grid>
      );
    case 'choose-certifier':
      return (
        <Grid>
          <List 
            component="nav"      
            subheader={
              <ListSubheader component="div" id="nested-list-subheader">
                {t(dialogType + '-message')}
              </ListSubheader>}
          >
            {modalObject && modalObject.options && modalObject.options.map(option =>(
              <ListItem key={option._id} value={option._id} onClick={() => this.props.onSubmitDialog(null, modalObject.certId, option._id)} button>
                <ListItemText primary={option.displayName} />
              </ListItem>
            ))
            }
          </List>
        </Grid>
      );
    case 'reassign-tasks':
    case 'reassign-all':
      return (
        <ViewReassign 
          handleClose={this.handleClose} 
          onSubmitDialog={this.props.onSubmitDialog}
          selectedRows={this.props.selectedRows}
          dialogType={dialogType}
          takingAction={this.props.takingAction}
        />
      );
    case 'userCertification':
    case 'objectCertification':
    case 'certification':
      return (
        <ViewCertification 
          certification={modalObject}
          onSubmitDialog={this.props.onSubmitDialog}
          handleClose={this.handleClose}
        />
      );
    case 'violation':
      return (
        <ViewViolation
          violation={modalObject}
          onSubmitDialog={this.props.onSubmitDialog}
          handleClose={this.handleClose}
          dialogType={dialogType}
          isOwner={this.props.root === 'dashboard'}
        />
      );
    case 'glossary':
    case 'targeted-attribute':
    case 'entitlement-history':
      return (
        <ViewObject
          modalObject={modalObject}
          queryingMetadata={this.props.queryingMetadata || this.props.queryingModalObject}
          dialogType={dialogType}
        />
      );
    case 'comment':
    case 'abstain':
    case 'revoke':
      return(
        <ViewComments key={'comments'}
          showCommentHistory={this.props.selectedAction === 'comment' ? true : false}
          isNotCurrentStage={this.props.isNotCurrentStage}
          comments={modalObject}
          onSubmitDialog={this.props.onSubmitDialog}
        />
      );
    case 'configure-reactive':
      return (
        <ViewReactive
          handleClose={this.handleClose}
          dialogType={dialogType}
        />
      );
    case 'actionSubmitting':
      return (
        <ViewActionSubmitting />
      );
    }
  }

  handleSubmit = () => {
    this.handleClose();
  }

  render() {
    const { t, classes, dialogId, dialogType, activeGovernanceDialog, modalObject, largeDialog, takingAction } = this.props;
    const modalTitle = this.getModalTitle(modalObject);

    return (
      <Dialog
        className={classes.dialog}
        scroll='paper'
        PaperProps={{
          className: 'appBarShift',
        }}
        onClose={this.handleClose}
        open={activeGovernanceDialog === dialogId}
        aria-labelledby='dialog-title'
      >
        <DialogTitle id='dialog-title' disableTypography>
          <Typography variant='h6'>{modalTitle || ' '}</Typography>
          {!takingAction ? 
            <IconButton aria-label={t('label-close')} className={classes.closeButton} onClick={this.handleClose}>
              <CloseIcon />
            </IconButton>
            : null }
        </DialogTitle>
        <Container className={clsx(!largeDialog && classes.dialogContainer, largeDialog && classes.largeDialogContainer)} justify='center'>
          {
            <div>
              {this.getModalContents(dialogType)}
            </div>
          }
        </Container>
      </Dialog>
    );
  }
}

const mapStateToProps = state => {
  const {
    generic: {
      activeGovernanceDialog,
      dialogType,
      takingAction,
    },
    certlist:{
      selectedAction,
      rowData,
    },
  } = state;

  return  {
    activeGovernanceDialog,
    dialogType,
    selectedAction,
    rowData,
    takingAction,
  };
};

const mapDispatchToProps = {
  changeActiveGovernanceDialog,
  changeModalObject,
  changeDialogType,
};

GovernanceDialog.propTypes = {
  activeGovernanceDialog: PropTypes.string,
  dialogType: PropTypes.string,
  confirmationMessage: PropTypes.string,
  changeActiveGovernanceDialog: PropTypes.func,
  changeModalObject: PropTypes.func,
  changeDialogType: PropTypes.func,
  chooseType: PropTypes.string,
  onSubmitDialog: PropTypes.func,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(GovernanceDialog);
