import React, { Component } from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { Formik } from 'formik';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import { Button, Grid, TextField, Typography, InputAdornment, IconButton } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { Visibility, VisibilityOff } from '@material-ui/icons';


import { authenticateUser, logoutUser, openAmLogin } from 'src/store/actions/auth.actions.js';
import httpService from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';

const styles = theme => ({
  loginLogo: {
    width: '43px',
    marginBottom: '10px',
  },
  loginGrid: {
    backgroundColor: theme.palette.forgerock.white,
    border: '1px solid',
    borderColor: 'rgba(33, 37, 41, .125)',
    borderRadius: 'calc(.25rem - 1px) calc(.25rem - 1px) 0 0',
    maxWidth: '420px',
    minHeight: '411px',
    margin: 'auto',
    marginTop: '140px',
  },
  loginLabel: {
    lineHeight: '1.8',
  },
  loginText: {
    minWidth: '300px',
  },
  loginButton: {
    minWidth: '300px',
    backgroundColor: theme.palette.secondary.main,
    color: theme.palette.forgerock.white,
    '&:disabled': {
      backgroundColor: theme.palette.secondary.main,
      color: theme.palette.forgerock.white,
    },
    '&:hover': {
      backgroundColor: theme.palette.secondary.main,
      color: theme.palette.forgerock.white,
    },
  },
  showPasswordButton: {
    marginBottom: '5px',
  },
});

class Login extends Component {
  
  login = (values, actions) => {
    const { userName, password } = values;
    this.props.loginUser(userName, password);
  }

  openAmRedirect = (provider) => {
    return httpService.post(constants.IDM_CONTEXT.concat('/identityProviders'), {
      provider: provider,
      landingPage: constants.IDM_URL.concat('/governance/#/login'),
    }, {
      headers: {
        'X-OpenIDM-Username': 'anonymous',
        'X-OpenIDM-Password': 'anonymous',
        'X-OpenIDM-NoSession': 'true',
      },
      params: {
        '_action': 'getAuthRedirect',
      },
    }).then(({ data }) => {
      httpService.addDataStoreToken(data.token);
      return window.location.replace(data.redirect);
    }).then(response => {
      return authenticateUser('', '');
    }).catch(error => {
      const { logoutUser, history } = this.props;
      logoutUser().then(() => {
        history.push({ pathname: '/' });
        history.replace({ pathname: '/login/oidc' });
      });
    });
  }

  renderFormikForm = props => {
    const { classes, t, error, message } = this.props;
    const { setSubmitting, handleChange, handleBlur, handleSubmit, values, setValues, errors, touched, isSubmitting } = props;

    const handleClickShowPassword = () => {
      setValues({ ...values, showPassword: !values.showPassword });
    };

    const handleMouseDownPassword = event => {
      event.preventDefault();
    };
    
    if (error && message && isSubmitting) {
      setSubmitting(false);
    }

    return (
      <form onSubmit={handleSubmit}>
        <Grid 
          container 
          justify='center' 
          alignItems='center' 
          direction='column' 
          className={classes.loginGrid}
          spacing={2}
        >
          <img className={classes.loginLogo} src={process.env.PUBLIC_URL + '/img/loginlogo.svg'} alt='logo' />
          <Typography
            className={classes.loginLabel}
            component="h1"
            variant="h4"
            color="inherit"
            data-test-id="appBar-text-appTitle"
            noWrap
          >
            {t('sign-in')}
          </Typography>
          <Grid item xs={12} sm={10}>
            <TextField
              label={t('label-username')}
              type='text'
              variant='outlined'
              data-test-id="loginPage-textInput-userNameField"
              name='userName'
              fullWidth
              className={classes.loginText}
              error={Boolean(touched.userName && errors.userName)}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.userName}
              helperText={
                touched.userName &&
                errors.userName &&
                String(errors.userName)
              }
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              label={t('label-password')}
              type={values.showPassword ? 'text' : 'password'}
              data-test-id="loginPage-textInput-passwordField"
              variant='outlined'
              name='password'
              fullWidth
              className={classes.loginText}
              error={Boolean(touched.password && errors.password)}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.password}
              helperText={
                touched.password &&
                errors.password &&
                String(errors.password)
              }
              InputProps={{
                endAdornment:
                  <InputAdornment position="end">
                    <IconButton
                      className={classes.showPasswordButton}
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                    >
                      {values.showPassword ? <Visibility /> : <VisibilityOff />}
                    </IconButton>
                  </InputAdornment>,
              }}
            ></TextField>
          </Grid>
          <Grid item xs={12} sm={10}>
            <Button 
              variant='contained' 
              size='large' 
              type="submit"
              className={classes.loginButton}
              data-test-id="loginPage-button-loginButton" 
              fullWidth
            >
              {t('btn-login')}
            </Button>
          </Grid>
        </Grid>
      </form>
    );
  }

  validateFormikForm = values => {
    const { t } = this.props;
    let errors = {};
    if (!values.userName) {
      errors.userName = t('form-error-required');
    }
    if (!values.password) {
      errors.password = t('form-error-required');
    }
    return errors;
  }

  render() {
    const token = window.sessionStorage.getItem('amToken') || window.localStorage.getItem('dataStoreToken');
    const { isLoggedIn, authModule, openAmLogin } = this.props;
    if (isLoggedIn) {
      return <Redirect to='/' />;
    } else if (token) {
      openAmLogin(token);
    } else if (this.props.match.params.oidc) {
      this.openAmRedirect(authModule);
    }
    if (this.props.authModule === 'OPENAM') {
      this.openAmRedirect(authModule);
    }
    return (
      <div className='login'>
        <Formik
          initialValues={{ userName: '', password: '' }}
          validate={this.validateFormikForm}
          onSubmit={this.login}
        >
          {this.renderFormikForm}
        </Formik>
      </div>
    );
  }
}

Login.propTypes = {
  loginUser: PropTypes.func,
  logoutUser: PropTypes.func,
  history: PropTypes.object,
  authModule: PropTypes.string,
  isLoggedIn: PropTypes.bool,
  error: PropTypes.bool,
  message: PropTypes.string,
  openAmLogin: PropTypes.func,
  match: PropTypes.object,
};

const mapStateToProps = state => {
  const {
    auth: { authModule, isLoggedIn, error, message },
  } = state;

  return  {
    authModule,
    isLoggedIn,
    error,
    message,
  };
};

const mapDispatchToProps = dispatch => ({
  loginUser: (userName, password) => dispatch(authenticateUser(userName, password)),
  logoutUser: () => dispatch(logoutUser()),
  openAmLogin: openAmLogin,
});

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(Login);
