import _ from 'lodash';
import theme from 'src/styles/theme';

let IDM_URL = process.env.IDM_URL;
let IDM_CONTEXT = process.env.IDM_CONTEXT;

if (process.env.NODE_ENV === 'production') {
  IDM_URL = `${location.protocol}//${location.hostname}`;
  if (location.port) {
    IDM_URL += `:${location.port}`;
  }
  IDM_CONTEXT = window.IDM_CONTEXT;
}

// approvals progress statuses
const PROGRESS_STATUS = {
  IN_PROGRESS: 'in-progress',
  COMPLETE: 'complete',
};

// approvals outcome statuses
const APPROVAL_STATUS = {
  APPROVED: 'approved',
  REJECTED: 'rejected',
  PENDING: 'pending',
};

const CERT_STATUS = {
  ACTIVE: 'active',
  CLOSED: 'closed',
  SCHEDULED: 'scheduled',
  TRIGGERED: 'triggered',
};

const CERT_STATUSES = _.values(CERT_STATUS);
const DASHBOARD_STATUSES = _.remove(_.values(CERT_STATUS), function(n){
  return n === 'active' || n === 'closed';
});

const CERT_TYPE = {
  USER: 'user',
  OBJECT: 'object',
};

const CERT_TYPES = _.values(CERT_TYPE);

const VIEW_TYPE = {
  ADMIN: 'admin',
  USER: 'user',
};

const VIEW_TYPES = _.values(VIEW_TYPE);
const TASK = {
  USER_CERT : 'user',
  OBJECT_CERT : 'object',
  VIOLATION : 'violation',
};
const TASKS = _.values(TASK);

const SETTING = {
  GLOBAL : 'global',
  ABOUT : 'about',
};
const SETTINGS = _.values(SETTING);

const CHART_COLORS = [
  theme.palette.secondary.main,
  theme.palette.forgerock.blue,
  theme.palette.forgerock.orange,
];

const ENTITLEMENT_CHART_CATEGORIES = [
  'certify',
  'revoke',
  'abstain',
];

const POLICY_CHART_CATEGORIES = [
  'remediated',
  'exceptions',
  'cancelled',
  'expired',
];

const DO_NOT_UPDATE = true;
const IS_DESC = true;

const constants = {
  IDM_URL,
  IDM_CONTEXT,
  PROGRESS_STATUS,
  APPROVAL_STATUS,
  CERT_STATUS,
  CERT_STATUSES,
  DASHBOARD_STATUSES,
  CERT_TYPE,
  CERT_TYPES,
  VIEW_TYPE,
  VIEW_TYPES,
  TASKS,
  SETTINGS,
  CHART_COLORS,
  ENTITLEMENT_CHART_CATEGORIES,
  POLICY_CHART_CATEGORIES,
  DO_NOT_UPDATE,
  IS_DESC,
};

export default constants;

