import React, { Component, Fragment } from 'react';
import { compose } from 'redux';
import clsx from 'clsx';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Button,
  ButtonGroup,
  Grid,
} from '@material-ui/core';
import PageTitle from 'src/components/presentational/PageTitle';
import IDG_CONSTANTS from 'src/utils/constants.js';

import Global from './Global';
import About from './About';

class Settings extends Component {
  state = {
    selectedSetting : 'global',
    loading: false,
  }

  changeSelected(selectedSetting){
    this.setState({selectedSetting});
  }

  getView(){
    switch(this.state.selectedSetting){
    case 'global':
      return <Global />;
    case 'about':
      return <About />;
    }
  }

  render() {
    const { classes, t } = this.props;
    
    let view = this.getView();
    return (
      <Fragment>
        <PageTitle
          title={t('System Settings')}
        />
        <Grid className={classes.buttonContainer} item xs={12}>
          <ButtonGroup color='default' fullWidth aria-label="full width outlined button group">
            {
              IDG_CONSTANTS.SETTINGS.map(setting => (
                <Button
                  key={setting}
                  className={clsx(classes.statusButton, { 'selected': setting === this.state.selectedSetting})}
                  variant="contained"
                  onClick={() => this.changeSelected(setting)}
                >{t(setting)}</Button>
              ))
            }
          </ButtonGroup>
        </Grid>
        {view}
      </Fragment>
    );
  }
}

export default compose(
  withTranslation(),
  withStyles(styles)
)(Settings);
