import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { compose } from 'redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import {
  Stepper,
  Step,
  StepLabel,
} from '@material-ui/core';
import styles from './styles';

const StageStepper = props => {
  const { classes, changeSelectedStage, changeActiveStage, altLabel, stagesParent, onlyChangeActive, activeStage } = props;

  const steps = getSteps();

  const handleStageChange = (index) => {
    if (changeActiveStage) {
      changeActiveStage(index);
    }
    if (changeSelectedStage) {
      changeSelectedStage(index);
    }
  };

  function getSteps() {
    let steps = [];
    if (stagesParent && stagesParent.stages) {
      let nameProp = onlyChangeActive ? 'stageName': 'name';
      steps = _.map(stagesParent.stages, nameProp);
    }
    return steps;
  }
  return (
    <div className={classes.root}>
      <Stepper nonLinear className={classes.stageStepper} activeStep={activeStage} alternativeLabel={altLabel}>
        {steps.map((label, index) => (
          <Step key={label} className={classes.stageStep} value={index} onClick={() => handleStageChange(index)}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
    </div>
  );
};

StageStepper.propTypes = {
  changeActiveStage: PropTypes.func,
  changeSelectedStage: PropTypes.func,
  altLabel: PropTypes.bool,
};

export default compose(
  withTranslation(),
  withStyles(styles),
)(StageStepper);