import React from 'react';
import _ from 'lodash';
import {TableRow, TableCell, Typography} from '@material-ui/core';
import {Select, MenuItem } from '@material-ui/core';

export function DropdownField(props){
  const {field, t, values, handleChange, classes} = props;
  let attrObj = _.find(props.options, ['key', field.id]);
  return(
    <TableRow className={classes.settingGrid} key={field.id}>
      <TableCell key={field.id+'label'} className={classes.headerCell}>
        <Typography>{attrObj ? attrObj.label : t(field.id)}</Typography>
      </TableCell>
      <TableCell key={field.id+'input'} className={classes.headerCell}>
        <Select 
          variant='outlined'
          fullWidth={true}
          name={field.id}
          value={values[field.id] || ''}
          onChange={handleChange}>
          {props.options.map((option)=>{
            return(
              <MenuItem key={option.key} 
                value={option.key}>{option.key}
              </MenuItem>
            );}
          )} 
        </Select>
      </TableCell>
    </TableRow>
  );
}