import _ from 'lodash';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withFormik } from 'formik';
import { withStyles } from '@material-ui/core/styles';
import { 
  withWidth,
} from '@material-ui/core';
import * as yup from 'yup';
import { createYupSchema } from 'src/utils/schemas';
import FormBody from './FormBody';
import styles from './styles';

const NewUserCertForm = withFormik({
  mapPropsToValues: (props) => {
    const formFields = {};
    const existingFormData = _.cloneDeep(props.existingFormData);
    
    props.formSchema().forEach(item => {
      if (item.id === 'certObjectType') {
        formFields[item.id] = props.formType === 'user-cert' ? 'user' : 'object';
      } else if (existingFormData && existingFormData[item.id]) {
        if (item.id === 'stages') {
          const formStages = existingFormData.stages.map(stage => {
            let newStage = _.cloneDeep(stage);
            const [unit, period] = newStage.deadline.split(' ');
            newStage.deadlineDuration = { unit, period };
            newStage.deadline = null;
            newStage.useRiskLevel = stage.riskLevelFilter.length > 0;
            newStage.riskLevel = {
              Low: false,
              Medium: false,
              High: false,
            };
            newStage.riskLevelFilter.forEach(level => newStage.riskLevel[level] = true);
            delete newStage.riskLevelFilter;
            let attributes = newStage.entitlementFilter.attributes;
            const filterKeys = Object.keys(attributes);
            for (let key of filterKeys) {
              if (stage.entitlementFilter.attributes[key].targetFilter) {
                let targetFilter = [];
                let boolOperators = [];

                stage.entitlementFilter.attributes[key].targetFilter.forEach((filter, idx) => {
                  if (idx % 2 === 0) {
                    targetFilter.push(filter);
                  } else {
                    boolOperators.push(filter);
                  }
                });
                newStage.entitlementFilter.attributes[key].targetFilter = targetFilter;
                newStage.entitlementFilter.attributes[key].boolOperators = boolOperators;
              }
            }

            newStage.escalationSchedule = newStage.escalationSchedule.map(escalation => ({
              unit: escalation.interval,
              period: escalation.intervalType,
              escalationType: escalation.escalationType,
              escalationId: escalation.escalationId,
            }));
            return newStage;
          });
          formFields.stages = formStages;
        } else if (item.id === 'frequency') {
          formFields[item.id] = existingFormData[item.id].toLowerCase();
        } else if (item.isStringified && _.isString(props.existingFormData[item.id])) {
          formFields[item.id] = JSON.parse(props.existingFormData[item.id]);
        } else {
          formFields[item.id] = existingFormData[item.id];
        }
      } else if (item.id === 'targetObjectType') {
        formFields[item.id] = existingFormData ? existingFormData.certObjectType : '';
      } else if (_.isNull(item.value)) {
        formFields[item.id] = item.value;
      } else {
        formFields[item.id] = item.value || '';
      }
    });
    if (formFields.certObjectType === 'user') {
      formFields.targetObjectType = 'user';
    }

    return formFields;
  },
  enableReinitialize: true,
  isInitialValid: (props) => !_.isNull(props.existingFormData),
  handleSubmit: (values, { props, resetForm }) => {
    const initialValues = _.cloneDeep(values);

    values.certObjectType = values.targetObjectType;
    delete values.targetObjectType;
    
    if (values.frequency === 'ad-hoc') {
      delete values.schedule;
      delete values.expression;
    } else if (values.frequency === 'event-based') {
      delete values.schedule;
      values.expression = JSON.stringify(values.expression);
    }

    if (values.defaultCertifierType === 'none') {
      delete values.defaultCertifierType;
      delete values.defaultCertifierName;
    }
    else {
      values.defaultCertifierName = 
        values.defaultCertifierType === 'user' ?
          values.defaultCertifierName.userName || values.defaultCertifierName.displayName :
          values.defaultCertifierName._id;
    }
    
    for (let stage of values.stages) {
      if (values.frequency === 'ad-hoc') {
        delete stage.deadlineDuration;
        stage.deadline = stage.deadline.format();
      } else {
        stage.deadline = `${stage.deadlineDuration.unit} ${stage.deadlineDuration.period}`;
        delete stage.deadlineDuration;
      }
      if (stage.certifierType === 'user') {
        stage.certifierName = stage.certifierName.userName || stage.certifierName.displayName;
      } else if (stage.certifierType !== 'manager') {
        stage.certifierName = stage.certifierName._id;
      }
      // Build riskLevel array
      let riskLevel = [];
      _.keys(stage.riskLevel).forEach(level => {
        if (stage.useRiskLevel && stage.riskLevel[level]) {
          riskLevel.push(level);
        }
      });
      stage.riskLevelFilter = riskLevel;
      delete stage.riskLevel;
      delete stage.useRiskLevel;
      // Transform entitlementFilter for backend
      stage.entitlementFilter = findSelectedEntitlements(stage.entitlementFilter);
      // Transform escalationSchedule for backend
      stage.escalationSchedule.forEach(schedule => {
        schedule.interval = schedule.unit;
        schedule.intervalType = schedule.period;
        delete schedule.unit;
        delete schedule.period;
        schedule.escalationId = schedule.escalationId.key || schedule.escalationId._id;
      });
    }

    props.onSubmit(values).then(response => {
      if (values.frequency === 'ad-hoc') {
        resetForm();
        props.handleClose();
      } else {
        resetForm(initialValues);
      }
    });
  },
  validationSchema: props => yup.object().shape(props.formSchema().reduce(createYupSchema, {})),
  validateOnChange: false,
  displayName: 'New User Certification',
})(FormBody);

function findSelectedEntitlements(state, selectedEntitlements){
  var entitlements = selectedEntitlements ? selectedEntitlements : {};
  if (state) {
    let stateKeys = Object.keys(state);
    for (let key of stateKeys) {

      if (state[key]['selected'] === false) {
        delete state[key];
      }
      if (key && key !== 'isOpen' && key !== 'isDisabled' && key !== 'boolOperators') {
        if (key && key === 'targetFilter') {
          let joinedTargetFilter = [];
          for (let i in state[key]) {
            joinedTargetFilter.push(state[key][i]);
            if (state['boolOperators'] && state['boolOperators'][i]) {
              joinedTargetFilter.push(state['boolOperators'][i]);
            }
          }
          entitlements[key] = joinedTargetFilter;
        } else if (!_.isFinite(key) && _.isObject(state[key])) {
          entitlements[key] = findSelectedEntitlements(state[key]);
          if (Object.keys(entitlements[key]).length === 0) {
            delete entitlements[key];
          }
        } else {
          if (state[key]) {
            entitlements[key] = state[key];
          } else {
            delete entitlements[key];
          }
        }
      }
    }
  }
  return entitlements;
}

const mapStateToProps = state => {
  const { async_fields } = state;
  return async_fields;
};

export default compose(
  connect(mapStateToProps),
  withTranslation(),
  withStyles(styles),
  withWidth(),
)(NewUserCertForm);