import React, { Fragment } from 'react';
import { 
  Card as DashCard, 
  Table as DashTable,
} from 'tabler-react';
import moment from 'moment';
import _ from 'lodash';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import styles from './styles.js';

const ObjectInformation = props => {
  const { campaignInfo, classes, infoType, isAdmin} = props;

  const getClass = (key) => {
    if(key === 'Active Certifiers' || key === 'Completed Certifiers'){
      return classes.objectInfoTableCellNoTransform;
    }
    else{
      return classes.objectInfoTableCell;
    }
  };

  return (
    <Fragment>
      <DashCard>
        {infoType === 'stages' &&
        <DashTable className={classes.objectInformation}>
          <DashTable.Header className={classes.sectionHeader}>
            <DashTable.Row>
              <DashTable.Col className={classes.headerText}>Stage Name</DashTable.Col>
              <DashTable.Col className={classes.headerText}>Deadline</DashTable.Col>
              <DashTable.Col className={classes.headerText}>Progress</DashTable.Col>
            </DashTable.Row>
          </DashTable.Header>
          {isAdmin ?
            <DashTable.Body>
              { campaignInfo.stages.map(stage => (
                <DashTable.Row key={stage.name} className={classes.objectInfoTableRow} justify='flex-start'>
                  <DashTable.Col className={classes.objectInfoTableCell}>{stage.name}</DashTable.Col>
                  <DashTable.Col className={classes.objectInfoTableCell}>{moment(stage.deadline).format('L')}</DashTable.Col>
                  <DashTable.Col className={classes.objectInfoTableCell}>{stage.eventsComplete + ' of ' + stage.eventCount}</DashTable.Col>
                </DashTable.Row>
              ))
              }
            </DashTable.Body>
            :
            <DashTable.Body>
              { campaignInfo.stages.map(stage => (
                campaignInfo.totals &&
                campaignInfo.totals[stage.name] &&
              <DashTable.Row key={stage.name}>
                <DashTable.Col className={classes.objectInfoTableCell}>{stage.name}</DashTable.Col>
                <DashTable.Col className={classes.objectInfoTableCell}>{moment(stage.deadline).format('L')}</DashTable.Col>  
                <DashTable.Col className={classes.objectInfoTableCell}>
                  {_.keys(campaignInfo.totals[stage.name]).map(key => (
                    <span key={key}>{key}: {campaignInfo.totals[stage.name][key]}<br /></span>
                  ))
                  }
                </DashTable.Col>
              </DashTable.Row>
              ))}
            </DashTable.Body>
          }
        </DashTable>
        }
        {infoType !== 'stages' &&
        <DashTable className={classes.objectInformation}>
          <DashTable.Header className={classes.sectionHeader}>
            <DashTable.Row>
              <DashTable.Col colSpan={3} className={classes.headerText}>Campaign Information</DashTable.Col>
            </DashTable.Row>
          </DashTable.Header>
          <DashTable.Body>
            { campaignInfo.map(key => (
              <DashTable.Row key={key.key} className={classes.objectInfoTableRow} justify='flex-start'>
                <DashTable.Col className={classes.objectInfoTableCell}>{key.key}: </DashTable.Col>
                <DashTable.Col className={getClass(key.key)}>{key.value}</DashTable.Col>
              </DashTable.Row>
            ))
            }
          </DashTable.Body>
        </DashTable>
        }
      </DashCard>
    </Fragment>

  );
};

ObjectInformation.propTypes = {
  title: PropTypes.string,
  infoType: PropTypes.string,
  isAdmin: PropTypes.bool,
  pageActionButton: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.element,
  ]),
};

export default withStyles(styles, { withTheme: true })(ObjectInformation);
