import axios from 'axios';
import constants from './constants.js';
import { AUTH } from 'src/store/actions/actionTypes.js';

let axiosOptions = {
  responseType: 'json',
  withCredentials: true,
  headers: {
    'Accept': 'application/json, text/javascript, */*; q=0.01',
    'Cache-Control': 'no-cache',
    'X-Requested-With': 'OpenIDM Plugin',
  },
};

if (process.env.NODE_ENV === 'production') {
  axiosOptions.baseURL = constants.IDM_URL;
}
const instance = axios.create(axiosOptions);

instance.setupInterceptors = (store, history) => {

  instance.interceptors.request.use(config => {
    
    const token = window.sessionStorage.getItem('amToken') || window.localStorage.getItem('dataStoreToken');
    if (token) {
      if (config.headers['X-OpenIDM-Username'] === 'anonymous' && config.headers['X-OpenIDM-Password'] === 'anonymous') {
        delete config.headers['X-OpenIDM-Username'];
        delete config.headers['X-OpenIDM-Password'];
      }
      config.headers['X-OpenIDM-NoSession'] = 'false';
      config.headers['X-OpenIDM-DataStoreToken'] = token;
      config.headers['X-OpenIDM-OAuth-Login'] = 'true';
      config.headers['X-Requested-With'] = 'OpenIDM Plugin';
    }
    return config;
  });

  instance.interceptors.response.use(response => {
    return response;
  }, error => {
    if (error.response && error.response.status === 500) {
      instance.removeDataStoreToken();
      
      if (error.response.config.url.indexOf('info/uiconfig') > 0 && error.response.config.headers['X-OpenIDM-DataStoreToken']) {
        return instance.get(error.config.url, {
          headers: {
            'X-OpenIDM-Username': 'anonymous',
            'X-OpenIDM-Password': 'anonymous',
            'X-OpenIDM-NoSession': 'true',
          },
        });
      }
    } else if (error.response.status === 401 && error.response.config.url.indexOf(`${constants.IDM_CONTEXT}/authentication`) > 0) {
      instance.removeDataStoreToken();
    } else if (error.response.status === 401 && error.response.data) {
      store.dispatch({
        type: AUTH.AUTHENTICATION_FAILURE,
        payload: error.response.data,
      });
    }

    return Promise.reject(error);
  });
};

instance.addDataStoreToken = token => {
  window.localStorage.setItem('dataStoreToken', token);
};

instance.removeDataStoreToken = () => {
  window.sessionStorage.removeItem('amToken');
  window.localStorage.removeItem('dataStoreToken');
};

instance.getUiConfig = (store, tokensAvailableCallback) => {

  instance.get(constants.IDM_CONTEXT.concat('/info/uiconfig'), {
    headers: {
      'x-appauthhelper-anonymous': 'true',
    },
  }).then(({ data }) => {
    const { configuration } = data;
    const platformSettings = configuration.platformSettings;

    if (platformSettings && platformSettings.amUrl) {
      store.dispatch({
        type: AUTH.FETCH_AUTH_MODULE_SUCCESS,
        payload: 'AM',
      });
      const { AppAuthHelper } = window;
      const amUrl = platformSettings.amUrl;
      const calculatedAMUriLink = document.createElement('a');
      calculatedAMUriLink.href = amUrl;
      const authorizationEndpoint = amUrl + '/oauth2/authorize';

      AppAuthHelper.init({
        clientId: 'access-review-ui',
        authorizationEndpoint: authorizationEndpoint,
        tokenEndpoint: amUrl + '/oauth2/access_token',
        revocationEndpoint: amUrl + '/oauth2/token/revoke',
        endSessionEndpoint: amUrl + '/oauth2/connect/endSession',
        resourceServers: {
          [ location.origin + '/openidm' ]: platformSettings.adminOauthClientScopes,
        },
        tokensAvailableHandler: function (claims) {
          instance.post(constants.IDM_CONTEXT.concat('/authentication?_action=login')).then(({data}) => {
            const { authenticationId, authorization } = data;
            const roles = authorization.roles.map(role => {
              return role.length ? role.split('/').pop() : '';
            });

            store.dispatch({
              type: AUTH.AUTHENTICATION_SUCCESS,
              payload: {
                authenticationId,
                authorization,
                roles,
              },
            });
            tokensAvailableCallback();
          });
        },
      }).then(function (res) {
        // In this application, we want tokens immediately, before any user interaction is attempted
        AppAuthHelper.getTokens();
      });
    } else {
      tokensAvailableCallback();
    }
  }).catch(() => tokensAvailableCallback());
};

export default instance;
