import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { compose } from 'redux';
import { withTranslation } from 'react-i18next';
import { v4 as uuidv4 } from 'uuid';
import { withStyles } from '@material-ui/core/styles';
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormHelperText,
  FormLabel,
  Grid,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Typography,
} from '@material-ui/core';
import styles from './styles';
import chronStringBuilder from 'src/utils/chronStringBuilder';

class ScheduleBuilder extends Component {
  state = {
    repeatType: 'daily',
    dailyRate: 1,
    startDay: 1,
    daysOfWeek: { 
      'Monday': false,
      'Tuesday': false,
      'Wednesday': false,
      'Thursday': false,
      'Friday': false,
      'Saturday': false,
      'Sunday': false, 
    },
    monthsOfYear: [
      { name: 'January', id: 1 },
      { name: 'February', id: 2 },
      { name: 'March', id: 3 },
      { name: 'April', id: 4 },
      { name: 'May', id: 5 },
      { name: 'June', id: 6 },
      { name: 'July', id: 7 },
      { name: 'August', id: 8 },
      { name: 'September', id: 9 },
      { name: 'October', id: 10 },
      { name: 'November', id: 11 },
      { name: 'December', id: 12 },
    ],
    monthlyRate: 1,
    monthlyDate: 1,
    startMonth: 1,
    useLastDay: false,
  }
  
  componentDidMount() {
    if (this.props.value) {
      let newState = {};
      chronStringBuilder.toScheduleObject(this.props.value, newState);
      this.setState(newState);
    }
    this.setState({ hasError: false });
  }

  componentDidUpdate(prevProps, prevState) {
    const { repeatType, daysOfWeek } = this.state;
    let hasError = false;
    if (this.props.value === '' && prevProps.value === '') {
      const chron = chronStringBuilder.toCronString(this.state);
      this.props.handleChange(chron);
    } else if (repeatType === 'weekly' && !_.isEqual(daysOfWeek, prevState.daysOfWeek)) {
      hasError = _.chain(daysOfWeek).values().every(day => day === false).value();
      // _.values(daysOfWeek)
      this.setState({ hasError });
    } else if (repeatType !== 'weekly' && this.state.hasError) {
      this.setState({ hasError });
    }
  }

  handleScheduleChange = (evt, value, parent) => {
    let newState = null;
    if (parent) {
      newState = { 
        ...this.state,
        [parent]: {
          ...this.state[parent],
          [evt.target.name]: value,
        },
      };
    } else {
      const val = value.props ? value.props.value : value;
      if (evt && evt.target.name) {
        newState = { 
          ...this.state,
          [evt.target.name]: val,
        };
      }
    }
    if (!_.isNull(newState)) {
      const chron = chronStringBuilder.toCronString(newState);
      this.setState(newState);
      this.props.handleChange(chron);
    }
  }

  render() {
    const { t, classes, field } = this.props;
    const { 
      repeatType, 
      daysOfWeek, 
      dailyRate, 
      monthsOfYear, 
      monthlyRate, 
      monthlyDate, 
      startDay, 
      startMonth, 
      useLastDay, 
      hasError, 
    } = this.state;
    return (
      <Fragment>
        <FormControl error={hasError} className={classes.formControl} fullWidth={true}>
          <FormLabel className={classes.formLabel} component='legend'>{field.label}</FormLabel>
          <Grid container>
            <Grid item xs={2} className={classes.inlineLabelGrid}>
              <Typography className={classes.inlineLabel} component='span'>{t('schedule-builder-repeat')}</Typography>
            </Grid>
            <Grid item xs={10}>
              <RadioGroup 
                row 
                // aria-label='repeat-type' 
                name='repeatType' 
                // label='repeat'
                value={repeatType} 
                onChange={this.handleScheduleChange}
              >
                <FormControlLabel value='daily' control={<Radio />} label={t('schedule-builder-daily')} />
                <FormControlLabel value='weekly' control={<Radio />} label={t('schedule-builder-weekly')} />
                <FormControlLabel value='monthly' control={<Radio />} label={t('schedule-builder-monthly')} />
                {/* <FormControlLabel value="disabled" disabled control={<Radio />} label="(Disabled option)" /> */}
              </RadioGroup>
            </Grid>
            
          </Grid>
        </FormControl>
        {/* If a daily schedule is set */}
        { repeatType === 'daily' && 
        
        <Grid container className={classes.formControlRow}>
          <Grid item xs={2} className={classes.inlineLabelGrid}>
            <Typography className={classes.inlineLabel} component='span'>{t('schedule-builder-repeat-every')}</Typography>
          </Grid>
          <Grid item xs={10}>
            <FormControl 
              className={classes.formControl} 
            >
              <FormGroup row>
                <Select
                  name='dailyRate'
                  value={dailyRate}
                  onChange={this.handleScheduleChange}
                >
                  { Array(30).fill().map((x,i) => (<MenuItem key={i} value={i+1}>{i+1}</MenuItem>)) }
                </Select>
                <Typography className={classes.typographyLabel} component='span'>{t('schedule-builder-daily-rate')}</Typography>
              </FormGroup>
            </FormControl>
            <FormControl 
              className={classes.formControl} 
            >
              <FormGroup row>
                <Select
                  name='startDay'
                  value={startDay}
                  onChange={this.handleScheduleChange}
                >
                  { Array(31).fill().map((x,i) => (<MenuItem key={i} value={i+1}>{i+1}</MenuItem>)) }
                </Select>
                <Typography className={classes.typographyLabel} component='span'>{t('schedule-builder-start-day')}</Typography>
              </FormGroup>
            </FormControl>
          </Grid>
        </Grid>
        }
        { repeatType === 'weekly' && 
        <FormControl 
          className={classes.formControl} 
          value={daysOfWeek}
          fullWidth={true} 
        >
          {/* <FormLabel className={classes.formLabel} component='legend'>{field.label}</FormLabel> */}
          <Grid container>
            <Grid item xs={2} className={classes.inlineLabelGrid}>
              <Typography className={classes.inlineLabel} component='span'>{t('schedule-builder-repeat-on')}</Typography>
            </Grid>
            <Grid item xs={10}>
              <FormGroup row>
                { Object.entries(daysOfWeek).map(([key, value]) => (
                  <FormControlLabel
                    key={uuidv4()}
                    control={<Checkbox checked={value} name={key} onChange={(evt, val) => this.handleScheduleChange(evt, val, 'daysOfWeek')} />}
                    // name={key}
                    label={key}
                  />
                ))
                }
              </FormGroup>
            </Grid>
          </Grid>
        </FormControl>
        }
        { repeatType === 'monthly' && 
        <Grid container className={classes.formControlRow}>
          <Grid item xs={2} className={classes.inlineLabelGrid}>
            <Typography className={classes.inlineLabel} component='span'>{t('schedule-builder-repeat-every')}</Typography>
          </Grid>
          <Grid item xs={10}>
            <FormControl 
              className={classes.formControl} 
            >
              <FormGroup row>
                <Select
                  name='monthlyRate'
                  value={monthlyRate}
                  onChange={this.handleScheduleChange}
                >
                  { Array(30).fill().map((x,i) => (<MenuItem key={i} value={i+1}>{i+1}</MenuItem>)) }
                </Select>
                <Typography className={classes.typographyLabel} component='span'>{t('schedule-builder-monthly-rate')}</Typography>
              </FormGroup>
            </FormControl>
            <FormControl 
              className={classes.formControl} 
            >
              <FormGroup row>
                <Select
                  name='startMonth'
                  value={startMonth}
                  onChange={this.handleScheduleChange}
                >
                  { monthsOfYear.map(month => 
                    (<MenuItem key={month.id} value={month.id}>
                      {t('getMonthNameFromInt', { date: month.id - 1 })}
                    </MenuItem>)
                  ) }
                </Select>
              </FormGroup>
            </FormControl>
          </Grid>
          <Grid item xs={2} className={classes.inlineLabelGrid}>
            <Typography className={classes.inlineLabel} component='span'>{t('schedule-builder-repeat-on')}</Typography>
          </Grid>
          <Grid item xs={5}>
            <FormGroup row>
              <Select
                name='monthlyDate'
                value={monthlyDate}
                disabled={useLastDay}
                onChange={this.handleScheduleChange}
              >
                { Array(31).fill().map((x,i) => (<MenuItem key={i} value={i+1}>{i+1}</MenuItem>)) }
              </Select>
              <Typography disabled={useLastDay} className={classes.typographyLabel} component='span'>{t('schedule-builder-start-day')}</Typography>
            </FormGroup>
          </Grid>
          <Grid item xs={5}>
            <FormControlLabel
              className={classes.checkboxLabel}
              control={<Checkbox checked={useLastDay} onChange={this.handleScheduleChange} name='useLastDay' />}
              label={t('schedule-builder-last-month-day')}
            />
          </Grid>
        </Grid>
        }
        {hasError && <FormHelperText error={hasError}>{t('schedule-builder-invalid')}</FormHelperText>}
      </Fragment>
    );
  }
}

ScheduleBuilder.propTypes = {
  field: PropTypes.object,
  handleChange: PropTypes.func,
};

export default compose(
  withTranslation(),
  withStyles(styles)
)(ScheduleBuilder);
