import { AUTH } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import {httpFailure} from './generic.actions.js';
import constants from 'src/utils/constants.js';

export const getAuthenticationModule = () => (dispatch, getState) => {
  return httpService.get(constants.IDM_CONTEXT.concat('/info/uiconfig'), {
    headers: {
      'X-OpenIDM-Username': 'anonymous',
      'X-OpenIDM-Password': 'anonymous',
      'X-OpenIDM-NoSession': 'true',
    },
  }).then(({ data }) => {
    const { configuration } = data;
    const oauthClientAuth = configuration.amDataEndpoints;
    if (oauthClientAuth) {
      return dispatch(getOpenAmRedirect());
    } else {
      dispatch(setAuthenticationModule('MANAGED_USER'));
    }
  }).catch(error => {
    dispatch(authenticationModuleFailure(error));
  });
};

const getOpenAmRedirect = () => dispatch => {
  const anonymousHeaders = {
    'X-OpenIDM-Username': 'anonymous',
    'X-OpenIDM-Password': 'anonymous',
    'X-OpenIDM-NoSession': 'true',
  };

  return httpService.get(constants.IDM_CONTEXT.concat('/authentication'), {
    headers: anonymousHeaders,
  }).then(({ data }) => {
    const provider = data.providers[0].provider;
    dispatch(setAuthenticationModule(provider));
  }).catch(error => {
    dispatch(authenticationModuleFailure(error));
  });
};

export const setAuthenticationModule = authModule => {
  return {
    type: AUTH.FETCH_AUTH_MODULE_SUCCESS,
    payload: authModule,
  };
};

export const authenticationModuleFailure = error => dispatch => {
  return dispatch(httpFailure(AUTH.FETCH_AUTH_MODULE_FAILURE, error, 'error'));
};

export const checkIfAuthenticated = () => (dispatch, getState) =>  {
  const { authModule, authorization } = getState().auth;
  return httpService.get(constants.IDM_CONTEXT.concat('/info/login'))
    .then(({ data }) => {
      if (data && data.authenticationId === 'anonymous') {
        httpService.removeDataStoreToken();
        return dispatch(logoutUser());
      }
      if (authModule === 'AM' && authorization && authorization.id) {
        return httpService.get(constants.IDM_CONTEXT.concat('/managed/user/', authorization.id)).then(response => {
          if (response.data && response.data.userName) {
            data.authenticationId = response.data.userName;
          }
          return dispatch(authenticationSuccess(data));    
        });
      }
      return dispatch(authenticationSuccess(data));
    }).catch(() => {
      httpService.removeDataStoreToken();
      return dispatch(logoutUser());
    });
};

export const authenticateUser = (userName, password) => dispatch => {
  return httpService.request({
    url: constants.IDM_CONTEXT.concat('/authentication'),
    method: 'post',
    params: {
      '_action': 'login',
    },
    headers: {
      'X-OpenIDM-NoSession': 'false',
      'X-OpenIDM-Password': password,
      'X-OpenIDM-Username': userName,
    },
  }).then(({ data }) => {
    return dispatch(authenticationSuccess(data));
  }).catch(error => {
    return dispatch(authenticationFailure(error));
  });
};

export const openAmLogin = token => dispatch => {
  return httpService.request({
    url: constants.IDM_CONTEXT.concat('/authentication'),
    method: 'post',
    params: {
      '_action': 'login',
    },
    headers: {
      'X-OpenIDM-DataStoreToken': token,
      'X-OpenIDM-NoSession': 'false',
    },
  }).then(({data}) => {
    dispatch(authenticationSuccess(data));
  }).catch(error => {
    dispatch(authenticationFailure(error));
    if (error.response.status === 500) {
      return dispatch(logoutUser());
    }
  });
};

export const authenticationSuccess = response => {
  const { authenticationId, authorization } = response;
  const roles = authorization.roles.map(role => {
    return role.length ? role.split('/').pop() : '';
  });
  return {
    type: AUTH.AUTHENTICATION_SUCCESS,
    payload: {
      authenticationId,
      authorization,
      roles,
    },
  };
};

export const authenticationFailure = error => dispatch => {
  return dispatch(httpFailure(AUTH.AUTHENTICATION_FAILURE, error, 'error'));
};

export const logoutUser = () => (dispatch, getState) => {
  const auth = getState().auth;
  return httpService.post(constants.IDM_CONTEXT.concat('/authentication?_action=logout'), {}, {
    headers: {
      'Content-Type': 'application/json',
      'X-OpenIDM-NoSession': 'true',
      'X-OpenIDM-Password': 'anonymous',
      'X-OpenIDM-Username': 'anonymous',
    },
  }).then(({ data }) => {
    httpService.removeDataStoreToken();
    
    if (auth.authorization && auth.authorization.logoutUrl) {
      window.location.href = auth.authorization.logoutUrl;
    }
    dispatch(logoutSuccess(data));
    if (auth.authModule === 'AM') {
      const { AppAuthHelper } = window;
      AppAuthHelper.logout().then(() => {
        window.location.href = window.location.origin + window.location.pathname;
      });
    }
  }).catch(error => {
    dispatch(logoutFailure(error));
  });
};

export const logoutSuccess = response => {
  return {
    type: AUTH.LOGOUT_SUCCESS,
    payload: response,
  };
};

export const logoutFailure = error => {
  return httpFailure(AUTH.AUTHENTICATION_FAILURE, error, 'error');
};
