import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import clsx from 'clsx';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Button,
  ButtonGroup,
  Grid,
} from '@material-ui/core';
import { 
  getCertifications,
  updateStatus,
  updateType,
  changeSearchKey,
  changeSortKey,
  updatePagination,
  commentViolation,
  actOnViolation,
} from 'src/store/actions/dashboard.actions.js';
import { getModalObject, 
  setIsDialogOpen, 
  changeModalObject, 
  changeDialogType,
  changeActiveGovernanceDialog } from 'src/store/actions/generic.actions.js';
import { updateActingId } from 'src/store/actions/certlist.actions.js';
import PageTitle from 'src/components/presentational/PageTitle';
import GovernanceTable from 'src/components/presentational/GovernanceTable';
import IDG_CONSTANTS from 'src/utils/constants.js';
import LoadingIndicator from 'src/components/presentational/LoadingIndicator';


class Dashboard extends Component {
  _isMounted = false;
  state = {
    tableCols : [],
    tableData: [],
    loading: false,
    modalObject: null,
    translationMap:{
      'user' : 'page-user-certs',
      'object': 'page-object-certs',
      'violation': 'page-violations',
    },
  }

  componentDidMount() {
    this.props.updateActingId(''); //Note: This forces componentDidUpdate to run
  }

  componentDidUpdate(prevProps) {
    const {type,status} = this.props.dashboard;
    if(!type || !status){
      this.props.updateStatus('active', IDG_CONSTANTS.DO_NOT_UPDATE);
      this.props.updateType('user', IDG_CONSTANTS.DO_NOT_UPDATE);
      this.props.updateActingId('');

      this.props.getCertifications().finally(()=>{
        this.setTableColsForType('user', 'active');
      });
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
    const {status} = this.props.dashboard;
    this.props.updatePagination({
      pageNumber: 1,
      pageSize: this.props.dashboard.pageSize,
      sortBy: 'deadline',
      status: status,
    }, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.changeSearchKey(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.updateStatus(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.updateType(null, IDG_CONSTANTS.DO_NOT_UPDATE);
  }

  closeDialog = () => {
    this.props.setIsDialogOpen(false);
    this.props.changeModalObject(null);
  }


  openDialogModal = (dataType, modalObject) => {
    this.props.getModalObject(dataType, modalObject._id);
    this.props.changeDialogType(dataType);
    this.props.setIsDialogOpen(true);
  }

  setTableColsForType = (type, status) => {
    let tableCols = [];
    if(type === 'violation'){
      if (status === 'active') {
        tableCols=[
          {
            'header': 'User',
            'key': 'targetUser',
            'noSort': true,
          },
          {
            'header': 'Policy',
            'key': 'policyName',
          },
          {
            'header': 'Owner',
            'key': 'owner',
            'noSort': true,
          },
          {
            'header': 'Expiration Date',
            'key': 'expirationDate',
            'type': 'date',
          },
        ];
      }
      else if (status === 'closed') {
        tableCols=[
          {
            'header': 'User',
            'key': 'targetUser',
            'noSort': true,
          },
          {
            'header': 'Policy',
            'key': 'policyName',
          },
          {
            'header': 'Owner',
            'key': 'owner',
            'noSort': true,
          },
          {
            'header': 'Completion Date',
            'key': 'completionDate',
            'type': 'date',
          },
          {
            'header': 'Result',
            'key': 'status',
            'type': 'violationStatus',
          },
        ];
      }
    }
    else{
      if(status === 'active'){
        tableCols=[
          {
            'header': 'Campaign Name',
            'key': 'name',
          },
          {
            'header': 'Certifier',
            'key': 'openCertifiers',
            'noSort': true,
          },
          {
            'header': 'Start Date',
            'key': 'startDate',
            'type': 'date',
          },
          {
            'header': 'Deadline',
            'key': 'deadline',
            'type': 'date',
          },
        ];
      }
      else{
        tableCols=[
          {
            'header': 'Campaign Name',
            'key': 'name',
          },
          {
            'header': 'Certifier',
            'key': 'closedCertifiers',
            'noSort': true,
          },
          {
            'header': 'Start Date',
            'key': 'startDate',
            'type': 'date',
          },
          {
            'header': 'Completed On',
            'key': 'completionDate',
            'type': 'date',
          },
        ];
      }
      if (type === 'object') {
        tableCols.splice(2, 0, {
          'header': 'Object',
          'key': 'certObjectType',
        });
      }
    }
    this.setState({tableCols});
  }

  dropDownHandleChange = (id, event) =>{
    event.stopPropagation();
    this.props.updateActingId(event.target.value);
    this.props.history.push('/certificationList/' + this.props.dashboard.type + '/' + id);
  }

  changeSelectedType= type => {
    const { status } = this.props.dashboard;
    this.setTableColsForType(type, status);
    this.props.updateType(type);
  }

  changeSelectedStatus = (status) => {
    const { type } = this.props.dashboard;
    this.setTableColsForType(type, status);
    this.props.updateStatus(status);
  }

  onPaginationChange = (evt, page) => {
    if (evt.q != null) {
      this.props.changeSearchKey(evt.q);
    }
    else if (evt.sortBy) {
      // Sorting key changed
      const isDesc = evt.sortBy === this.props.dashboard.sortBy && this.props.dashboard.sortDir === 'asc';
      this.props.changeSortKey(evt.sortBy, isDesc);
    } else if (evt.target.value) {
      // Rows per page changed
      this.props.updatePagination({
        ...this.props.dashboard,
        pageNumber: 1,
        pageSize: evt.target.value,
      });
    } else if (_.isNumber(page)) {
      // Page number changed
      this.props.updatePagination({
        ...this.props.dashboard,
        pageNumber: page + 1,
      });
    }
  }

  handleRowClick = (evt, selectedRow, action, comments, exceptionDate) => {
    const { dashboard } = this.props;

    if (dashboard.type === 'violation') {
      this.props.actOnViolation(selectedRow, action, comments, exceptionDate);
    }
    else {
      this.props.changeActiveGovernanceDialog(null);
      this.props.changeDialogType(null);
      this.props.history.push({
        pathname: '/certificationList/' + this.props.dashboard.type + '/' + selectedRow,
        search: '?actingId=' + action + '&status=' + dashboard.status,
      });
    }
  }

  render() {
    const { dashboard, classes, t, generic } = this.props;
    const { pageNumber, pageSize, querying, type } = dashboard;
    const { modalObject, queryingModalObject } = generic;

    if(!dashboard.results){
      return null;
    }
    if(this.state.loading){
      return <LoadingIndicator />;
    }
    return (
      <Fragment>
        <PageTitle
          title={t('page-my-tasks')}
        />
        <Grid className={classes.buttonContainer} item xs={12}>
          <ButtonGroup color='default' fullWidth aria-label="full width outlined button group">
            {
              IDG_CONSTANTS.TASKS.map(taskType => (
                <Button
                  key={taskType}
                  className={clsx(classes.certTypeButton, { 'selected': taskType === dashboard.type })}
                  variant="contained"
                  onClick={() => this.changeSelectedType(taskType)}
                >{t(this.state.translationMap[taskType])}</Button>
              ))
            }
          </ButtonGroup>
        </Grid>

        <Grid className={classes.buttonContainer} item xs={12}>
          <ButtonGroup color='default' fullWidth aria-label="full width outlined button group">
            {
              IDG_CONSTANTS.DASHBOARD_STATUSES.map(status => (
                <Button
                  key={status}
                  className={clsx(classes.certStatusButton, { 'selected': status === dashboard.status })}
                  variant="contained"
                  onClick={() => this.changeSelectedStatus(status, type)}
                >{t(status)}</Button>
              ))
            }
          </ButtonGroup>
        </Grid>
        <Grid container className={classes.container} justify='center'>
          <GovernanceTable 
            tableCols={this.state.tableCols}
            tableData={this.props.dashboard.results}
            sortBy={this.props.dashboard.sortBy}
            sortDir={this.props.dashboard.sortDir}
            handleActions={this.handleRowClick}
            showNewButton={false}
            showCheckboxes={false}
            pageSize={pageSize}
            pagedResultsOffset={pageNumber-1}
            handlePagination={this.onPaginationChange}
            querying={querying}
            hideBulkActions={true}
            handleRowClick={this.handleRowClick}
            tableType={type + '-dashboard'}
            getModalObject={this.props.getModalObject}
            modalObject={modalObject}
            queryingModalObject={queryingModalObject}
            hideToolbar={true}
            onRowDropdownSelect={this.dropDownHandleChange}  
            root='dashboard'
          />
        </Grid>
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const { dashboard, generic, certlist } = state;
  return {
    dashboard,
    generic,
    certlist,
  };
};

const mapDispatchToProps = {
  getCertifications,
  updateStatus,
  updateType,
  changeSearchKey,
  changeSortKey,
  updatePagination,
  getModalObject,
  setIsDialogOpen,
  changeModalObject,
  commentViolation,
  actOnViolation,
  updateActingId,
  changeDialogType,
  changeActiveGovernanceDialog,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(Dashboard);
