import { ASYNC_FIELDS } from '../actions/actionTypes.js';

const initialState = {
  workflows: [],
  users: [],
  roles: [],
  results: [],
  scanPolicies: [],
};

export default (state = initialState, action) => {
  switch(action.type) {
  case ASYNC_FIELDS.QUERY_WORKFLOWS:
    return {
      ...state,
      workflows: [],
    };
  case ASYNC_FIELDS.QUERY_WORKFLOWS_SUCCESS:
    return {
      ...state,
      workflows: action.payload,
    };
  case ASYNC_FIELDS.QUERY_USERS:
    return {
      ...state,
      users: [],
    };
  case ASYNC_FIELDS.QUERY_USERS_SUCCESS:
    return {
      ...state,
      users: action.payload,
    };
  case ASYNC_FIELDS.QUERY_ROLES:
    return {
      ...state,
      roles: [],
    };
  case ASYNC_FIELDS.QUERY_ROLES_SUCCESS:
    return {
      ...state,
      roles: action.payload,
    };
  case ASYNC_FIELDS.QUERY_TYPEAHEAD:
    return {
      ...state,
      results: [],
    };
  case ASYNC_FIELDS.QUERY_TYPEAHEAD_SUCCESS:
    return {
      ...state,
      results: action.payload,
    };
  case ASYNC_FIELDS.QUERY_SCAN_POLICIES:
    return {
      ...state,
      scanPolicies: [],
    };
  case ASYNC_FIELDS.QUERY_SCAN_POLICIES_SUCCESS:
    return {
      ...state,
      scanPolicies: action.payload,
    };
  default:
    return state;
  }
};
