import React, { Component, Fragment } from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Grid,
  Typography,
} from '@material-ui/core';
import LoadingIndicator from 'src/components/presentational/LoadingIndicator';
import {getAboutInfo} from 'src/store/actions/config.actions';

class About extends Component {
  state={
    loading:false,
  }
  
  componentDidMount() {
    this.setState({loading:true});
    this.props.getAboutInfo().finally(()=> {
      this.setState({loading:false});
    });
  }
  
  getVersion(version){
    const majorVersion = version.major;
    const minorVersion = version.minor;
    return majorVersion + '.' + minorVersion;
  }

  parseCommit(commit){
    return commit.substring(commit.length - 7);
  }
  

  render(){
    const { classes, t, config } = this.props;
    let version = null;
    if(config.settings.about){
      version = config.settings.about.version;
    }

    if(!version){

      return null;
    }

    return(
      <Fragment>
        {this.state.loading ?
          <LoadingIndicator /> :
          <Grid className={classes.aboutContainer}>
            <Typography>{t('version')}: {this.getVersion(version)}</Typography>
            <Typography>{t('commit')}: {this.parseCommit(version.commit)}</Typography>
          </Grid>
        }
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const { config } = state;
  return {
    config,
  };
};

const mapDispatchToProps = {
  getAboutInfo,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(About);
