import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { compose } from 'redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { 
  FormControl,
  FormHelperText,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from '@material-ui/core';
import styles from './styles';

const DurationSelect = props => {
  const { classes, durationType, errors, field, handleBlur, handleChange, maxUnit, t, touched, value } = props;
  const fieldId = _.isString(field) ? field : field.id;
  const unitCeiling = maxUnit || 30;

  const getDurationLabel = () => {
    if (durationType === 'policy-scan' || durationType === 'configure-reactive') {
      return t('duration-select-after-violation');
    } else if (durationType === 'user-cert' || durationType === 'object-cert') {
      return (
        /escalationSchedule\.\d{1,}$/.test(fieldId) ? 
          t('duration-select-before-stage-deadline') : 
          t('duration-select-after-campaign')
      );
    } else if (durationType === 'escalation-policy-scan') {
      return t('duration-select-escalation-expiration');
    } else if (durationType === 'escalation-user-cert' || durationType === 'escalation-object-cert') {
      return t('duration-select-escalation-deadline');
    }
  };

  return (
    <Fragment>
      <Grid container spacing={1} alignItems='stretch'>
        <Grid item xs={4} lg={5}>
          <FormControl 
            name={`${fieldId}.unit`}
            fullWidth={true}
            variant='outlined'
            error={Boolean(errors && touched && (_.isString(errors) || errors.unit && touched.unit))} 
          >
            <InputLabel 
              id={`${fieldId}-unit-label`}
              className={classes.formControlLabel} 
            >
              {t('duration-select-amount')}
            </InputLabel>
            <Select
              labelId={`${fieldId}-unit-label`}
              name={`${fieldId}.unit`}
              value={value.unit}
              onBlur={handleBlur}
              onChange={handleChange}
            >
              { Array(unitCeiling).fill().map((x,i) => (<MenuItem key={i} value={i+1}>{i+1}</MenuItem>)) }
            </Select>
            { _.isObject(errors) && touched && errors.unit && touched.unit &&
            <FormHelperText id={`${fieldId}-helper-text`}>
              {errors.unit}
            </FormHelperText>
            }
          </FormControl>
        </Grid>
        <Grid item xs={4} lg={5}>
          <FormControl 
            name={`${fieldId}.period`}
            fullWidth={true}
            variant='outlined'
            error={Boolean(errors && touched && (_.isString(errors) || errors.period && touched.period))} 
          >
            <InputLabel 
              id={`${fieldId}-period-label`}
              className={classes.formControlLabel} 
            >
              {t('duration-select-period')}
            </InputLabel>
            <Select
              labelId={`${fieldId}-period-label`}
              name={`${fieldId}.period`}
              value={value.period}
              onBlur={handleBlur}
              onChange={handleChange}
            >
              <MenuItem value='days'>{t('duration-select-days')}</MenuItem>
              <MenuItem value='weeks'>{t('duration-select-weeks')}</MenuItem>
            </Select>
            { _.isObject(errors) && touched && errors.period && touched.period &&
            <FormHelperText id={`${fieldId}-helper-text`}>
              {errors.period}
            </FormHelperText>
            }
          </FormControl>
        </Grid>
        <Grid item xs={4} lg={2} className={classes.durationLabel}>
          <Typography component='span'>{ getDurationLabel() }</Typography>
        </Grid>
      </Grid>
    </Fragment>
  );
};

DurationSelect.propTypes = {
  durationType: PropTypes.string,
  errors: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  field: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  handleBlur: PropTypes.func,
  handleChange: PropTypes.func,
  maxUnit: PropTypes.number,
  touched: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),
  value: PropTypes.object,
};

export default compose(
  withTranslation(),
  withStyles(styles),
)(DurationSelect);