import { applyMiddleware, createStore } from 'redux';
import * as storage from 'redux-storage';
import createEngine from 'redux-storage-engine-localstorage';
import { createLogger } from 'redux-logger';
import thunk from 'redux-thunk';
import freeze from 'redux-freeze';
import _ from 'lodash';
import rootReducer from './reducers';
import { SHOULD_NOT_SAVE, SHOULD_SAVE } from './actions/actionTypes.js';
import {changeActiveGovernanceDialog, updateTakingAction, changeModalObject} from 'src/store/actions/generic.actions';
import { changeCertType } from './actions/certs.actions';

import {updateStatus, updateType}from 'src/store/actions/dashboard.actions.js';

let logger, redux_devtools = null;

// note that environment variables are strings not booleans
if (process.env.REDUX_LOGGER === 'true') {
  logger = createLogger();
}

if (process.env.NODE_ENV === 'development') {
  redux_devtools = window.__REDUX_DEVTOOLS_EXTENSION__;
}

const reducer = storage.reducer(rootReducer);
const engine = createEngine('GOVERNANCE');
const reactStorageMiddleware = storage.createMiddleware(engine, SHOULD_NOT_SAVE, SHOULD_SAVE);

const middleware = _.compact([thunk, freeze, reactStorageMiddleware, logger]);
const createStoreWithMiddleware = applyMiddleware(...middleware)(createStore);
const store = redux_devtools ? createStoreWithMiddleware(reducer, redux_devtools()) : createStoreWithMiddleware(reducer);

const load = storage.createLoader(engine);
load(store)
  .then((newState) => {
    store.dispatch(updateTakingAction(false));
    store.dispatch(changeActiveGovernanceDialog(null));
    store.dispatch(changeModalObject(null));
    store.dispatch(changeCertType(null, true));
    store.dispatch(updateType(null, true));
    store.dispatch(updateStatus(null, true));
    // store.dispatch({ type: GENERIC.RESET_LOADING_STATUS });
  });

export default store;
