import React, { Component, Fragment } from 'react';
import _ from 'lodash';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import {
  Button,
  Grid,
  TextField,
} from '@material-ui/core';
import { 
  Table as DashTable,
} from 'tabler-react';
import 'tabler-react/dist/Tabler.css';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';
import styles from './styles';
import { v4 as uuidv4 } from 'uuid';


class ViewComments extends Component {
  state={
    comments: '',
  }

  onUpdateComments = event => {
    this.setState({ comments: event.target.value || '' });
  }

  render(){
    const { t, comments, classes, isNotCurrentStage } = this.props;

    return(
      <Fragment>
        {this.props.showCommentHistory && comments ? 
          <DashTable>
            <DashTable.Header>
              <DashTable.Row>
                <DashTable.ColHeader>{t('date')}</DashTable.ColHeader>
                <DashTable.ColHeader>{t('comment')}</DashTable.ColHeader>
                <DashTable.ColHeader>{t('by')}</DashTable.ColHeader>
              </DashTable.Row>
            </DashTable.Header>
            <DashTable.Body>
              {comments.map(comment => (
                <DashTable.Row key={uuidv4()}>
                  <DashTable.Col>{moment(comment.timeStamp).format('lll')}</DashTable.Col>
                  <DashTable.Col>{comment.comment}</DashTable.Col>
                  <DashTable.Col>{comment.user ? comment.user.displayName : comment.username}</DashTable.Col>
                </DashTable.Row>
              ))
              }
            </DashTable.Body>
          </DashTable>
          : null}
        {!isNotCurrentStage &&
          <Fragment> 
            <Grid
              container 
              justify='center' 
              alignItems='center' 
              direction='row'
              className={classes.commentsForm}>
              <Grid item width='100%'>
                <TextField
                  label={t('add-new-comment')}
                  type='text'
                  variant='outlined'
                  name='commentInput'
                  id='commentInput'
                  value={this.state.comments}
                  fullWidth
                  onChange={(evt) => this.onUpdateComments(evt)}
                /> 
              </Grid>
              <Grid className={classes.commentButtonContainer} item xs={12} sm={10}>
                <Button 
                  disabled={!this.state.comments}
                  variant='contained' 
                  name='submit'
                  size='large' 
                  type="submit"
                  color='primary'
                  onClick={(event)=>{
                    if(!this.state.comments){
                      return null;
                    }
                    if(this.props.showCommentHistory){
                      this.props.onSubmitDialog('revokeAbstainSubmit', this.state.comments);
                    }
                    else if (comments && _.isEmpty(comments)){
                      this.props.onSubmitDialog('revokeAbstainSubmit', this.state.comments);
                    }
                    else{
                      this.props.onSubmitDialog('revokeAbstainSubmit', this.state.comments);
                    }
                    this.setState({ comments: '' });
                  }}
                >
                  {t('btn-submit-comments')}
                </Button>
              </Grid>
            </Grid> 
          </Fragment> 
        }
      </Fragment>
    );
  }
}
export default compose(
  connect(),
  withTranslation(),
  withStyles(styles),
)(ViewComments);