import is from 'check-types';
import _ from 'lodash';
import __ from '@hubcitymedia/commons-scripts/src/utils/jsUtils';
import clientConfig from '@hubcitymedia/commons-scripts/src/utils/clientConfig.js';
import httpService from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';

import { GENERIC } from './actionTypes.js';

// You should probably be using 'alert' action below instead.  Feel free to export this if you need the customization.
const enqueueSnackbar = (notification) => {
  const key = notification.options && notification.options.key;
  return {
    type: GENERIC.ENQUEUE_SNACKBAR,
    notification: {
      ...notification,
      key: key || new Date().getTime() + Math.random(),
    },
  };
};

export const closeSnackbar = key => ({
  type: GENERIC.CLOSE_SNACKBAR,
  dismissAll: !key, // dismiss all if no key has been defined
  key,
});

export const removeSnackbar = key => ({
  type: GENERIC.REMOVE_SNACKBAR,
  key,
});

/*
Expose an 'alert' function for alerting things like success and failure to the user with a message.

This points to whichever alert/toast library we choose.
*/
export const alert = (message, level) => (dispatch, getState) => {
  if (!is.string(message)) {
    __.requestError('Inputted argument `message` must be a string.');
  }
  const level_to_options = clientConfig.alert.levelToOptions;
  if (!level_to_options.hasOwnProperty(level)) {
    __.requestError('Inputted argument `level` is not a key in levelToOptions.');
  }
  const options = level_to_options[level];
  const { notifications } = getState().generic;
  if (_.some(notifications, (notification) => notification.message === message)) {
    return;
  }
  dispatch(enqueueSnackbar({
    message,
    options,
  }));
};

const getPayload = (obj) => {
  // the `response` could be a response from the server, or just a local JavaScript error object.  Nomatter what it is, we need to extract the error message, and a code if possible.
  if (obj.hasOwnProperty('response')) {
    // it's an error response object from the server
    const data = obj.response.data;
    const is_data_populated = !is.null(data);
    const code = is_data_populated ? data.code : obj.response.status;
    const message = is_data_populated ? data.message : obj.response.statusText;
    return {code, message};
  }
  else if (!obj.hasOwnProperty('response') && obj.hasOwnProperty('message')) {
    // it's probably a local error
    return {message: obj.message};
  }
  else if (!obj.hasOwnProperty('response') && !obj.hasOwnProperty('message')) {
    return {message: obj};
  }
  else {
    __.requestError('Could not extract error message from object.');
  }
};

export const httpFailure = (actionType, response, alert_level=false) => (dispatch) => {
  const payload = getPayload(response);
  if (alert_level) {
    dispatch(alert(payload.message, alert_level));
  }
  return {
    type: actionType,
    payload: payload,
  };
};

