export const getModalObject = (type, obj, isObject) => (dispatch, getState) => {
  // Use to pass in the modal object if already retrieved, rather than query for it
  if (isObject) {
    dispatch({
      type: GENERIC.QUERY_MODAL_OBJECT_SUCCESS,
      payload: obj,
    });
    return;
  }

  dispatch({
    type: GENERIC.QUERY_MODAL_OBJECT,
    payload: null,
  });
  const certEndpoint = getEndpointForType(type, obj);
  let params = {};
  if (type === 'glossary') {
    params.targetId = obj;
  }
  return httpService.request({
    url: certEndpoint,
    method: 'get',
    params,
  }).then(({ data }) => {
    let payload = data;
    if (type === 'glossary') {
      payload = data.result || {};
    }
    dispatch({
      type: GENERIC.QUERY_MODAL_OBJECT_SUCCESS,
      payload: payload,
    });
  }).catch(error => {
    dispatch({
      type: GENERIC.QUERY_MODAL_OBJECT_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  }); 
};

export const updateTakingAction = (newValue) => (dispatch) => {
  dispatch({
    type: GENERIC.UPDATE_TAKING_ACTION,
    payload: newValue,
  });
};

const getEndpointForType = (type, id) => {
  switch (type) {
  case 'userCertification':
    return constants.IDM_CONTEXT + '/governance/adminCertification/user/' + id;
  case 'objectCertification':
    return constants.IDM_CONTEXT + '/governance/adminCertification/object/' + id;
  case 'violation':
    return constants.IDM_CONTEXT + '/governance/violation/' + id;
  case 'glossary':
    return constants.IDM_CONTEXT + '/commons/glossary/managed';
  case 'configure-reactive':
    return constants.IDM_CONTEXT + '/governance/policyScan/reactive';
  }
};