import React, { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import clsx from 'clsx';
import _ from 'lodash';
import { 
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  IconButton,
  LinearProgress,
  Slide,
  Typography,
  Grid,
} from '@material-ui/core';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';
import styles from './styles';
import { 
  getEventDetails,
  changeSelectedStage,
  incrementSelectedStage,
  decrementSelectedStage,
  certActionEvent,
  setSelectedEventIndex,
  updateSelectedAction,
  updateRowData,
} from 'src/store/actions/certlist.actions.js';
import { changeDialogType, getModalObject, changeActiveGovernanceDialog } from 'src/store/actions/generic.actions.js';
import { v4 as uuidv4 } from 'uuid';
import EventDetailsList from 'src/components/containers/CertificationList/EventDetailsList';
import StageStepper from 'src/components/presentational/StageStepper';
import {
  ChevronRight,
  Close,
  ExpandMore,
} from '@material-ui/icons';
import { 
  Card as DashCard,
} from 'tabler-react';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction='down' ref={ref} {...props} />;
});

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 10,
    borderRadius: 5,
  },
  colorPrimary: {
    backgroundColor: theme.palette.forgerock.lightGray,
  },
  bar: {
    borderRadius: 5,
    backgroundColor: theme.palette.forgerock.blue,
  },
}))(LinearProgress);

class EventDetails extends Component {
  state={
    showSection: {
      'identity': true,
      'managedObject': true,
      'application': true,
      'metadata': true,
    },
    rowActions: ['certify', 'revoke', 'abstain'],
    managedObjectDisplayName: null,
    applicationDisplayName: null,
    activeStage: this.props.certlist.results.firstEventStage || this.props.certlist.selectedStage || 0,
    eventActionClicked: false,
  }

  componentDidMount(){
  }

  componentDidUpdate(prevProps){

  }

  componentWillUnmount() {
    this._isMounted = false;
    this.props.setSelectedEventIndex(null, false);
  }

  changeActiveStage = (activeStage) => {
    this.setState({ activeStage: activeStage });
  }

  toggleSection(section) {
    let showSection = _.cloneDeep(this.state.showSection);
    showSection[section] = !showSection[section];
    this.setState({ showSection: showSection });
  }

  handleClick(name){
    if(this.state.displayName === name){
      this.setState({displayName: null});
    }
    else{
      this.setState({displayName: name});
    }
  }

  takeAction=(action, comment)=>{
    const oldAction = this.props.certlist.selectedAction;
    if(action === 'certify' || action === 'reset' || action === 'claim'){
      this.props.updateSelectedAction(action);
      this.props.certActionEvent(null);
    }
    else if(action === 'revokeAbstainSubmit'){
      if(oldAction !== 'comment'){
        this.props.certActionEvent(comment, true);
        this.props.changeActiveGovernanceDialog(null);
        this.setState({eventActionClicked:false});
      }
      else{
        this.props.certActionEvent(comment, false);
      }
    }
    else{
      this.props.changeActiveGovernanceDialog('EventDetailsListDialog');
      this.props.changeDialogType(action);
      this.props.updateSelectedAction(action);
      this.setState(function(){
        return({eventActionClicked:true});
      });
    }
  }
  
  onDialogClose = (value)=> {
    this.props.updateSelectedAction('');
    this.props.updateRowData(null);
    this.setState({eventActionClicked:value});
  }

  pageTitle = () => {
    let { classes, certlist } = this.props;
    let { eventDetails } = certlist;
    let event = eventDetails.stages[0];

    return(
      event &&
      <DialogTitle className={classes.detailsTitle} id='create-modal-title' disableTypography>
        <Grid className={classes.titleContainer} container>
          <Grid item xs={1}>
          </Grid>
          <Grid item xs={4}>
            <Typography variant='h6' className={classes.certTitle}>{event.target.displayName}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Grid container justify-content='flex-end' display='flex'>
              <IconButton onClick={this.props.handleClose}>
                <Close />
              </IconButton>
            </Grid>
          </Grid>
        </Grid>
      </DialogTitle>
    );
  }

  getProgressBar = stage => {
    const { classes } = this.props;
    let totalProgress = (stage.complete / stage.total) * 100;
    return (
      <Grid container display='flex' flex-direction='row'>
        <Grid item className={classes.borderProgressContainer} xs={10}>
          <BorderLinearProgress variant='determinate' value={totalProgress} />
        </Grid>
        <Grid item xs={2}>
          <Typography className={classes.progressValue} variant="body1" color="textSecondary">{`${Math.round(
            totalProgress,
          )}%`}</Typography>
        </Grid> 
      </Grid>
    );
  }

  getCertifiersDisplayString = stage => {
    if (stage.openCertifiers) {
      return _.map(stage.openCertifiers, 'displayName').join(', ');
    }
    return stage.certifier.displayName;
  }

  render(){
    let {classes, certlist, t, queryingMetadata} = this.props;
    const {eventDetails, queryingEventDetails, certType, isAdminView} = certlist;
    if(!eventDetails || queryingEventDetails) {
      return null;
    }
    const selectedStage = eventDetails.stages[this.state.activeStage];
    const outcome = selectedStage.outcome;
    const eventData = selectedStage.eventData;
    let canTakeAction = _.includes(selectedStage.actions, 'certify');

    return(
      <Dialog
        maxWidth='lg'
        TransitionComponent={Transition}
        PaperProps={{
          className: 'appBarShift',
        }}
        fullScreen
        className={classes.eventDetailsDialog}
        onClose={this.handleClose}
        aria-labelledby='create-modal-title'
        scroll='paper'
        open={this.props.open}
      >
        {this.pageTitle()}
        <DialogContent className={classes.dialog}>
          <Grid className={classes.campaignInformation} container>
            <Grid className={classes.userInfoContainer} item xl={8} lg={8} md={12}>
              <Grid item className={classes.stageContainer}>
                {eventDetails.stages.length > 1 &&
                  <Grid container className={classes.stageActionContainer}>
                    <Grid item xs={2} className={classes.stageActionLabelContainer}>
                      <Typography className={classes.stageActionLabel}>{t('stage-selector')}:</Typography>
                    </Grid>
                    <Grid item xs={10}>
                      <StageStepper 
                        stagesParent={eventDetails}
                        changeActiveStage={this.changeActiveStage}
                        activeStage={this.state.activeStage}
                        onlyChangeActive={true}
                        altLabel={false}
                      />
                    </Grid>
                  </Grid>
                }
                {!isAdminView && selectedStage.actions && selectedStage.actions.length > 0 &&  
                  <Grid container className={classes.stageActionContainer}>
                    <Grid item xs={2} className={classes.stageActionLabelContainer}>
                      <Typography className={classes.stageActionLabel}>{t('event-actions')}:</Typography>
                    </Grid>
                    <Grid item xs={10} className={classes.stageActionButtonContainer}>
                      { selectedStage.actions &&
                          selectedStage.actions.map((action) => {
                            return(
                              <Button onClick={(event)=>{this.takeAction(action);}} 
                                key={uuidv4()}
                                className={clsx(classes.stageButton, classes.stageActionButton, { 'selected': action === outcome})}>
                                {action}
                              </Button>
                            );
                          })}
                    </Grid>
                  </Grid>
                }
                {
                  (selectedStage.openCertifiers || selectedStage.certifier) &&
                  <Grid container className={clsx(classes.stageActionContainer, classes.progressActionContainer)}>
                    <Grid item xs={2} className={classes.stageActionLabelContainer}>
                      <Typography className={classes.stageActionLabel}>
                        {selectedStage.openCertifiers ? t('stage-active-certifiers') : t('stage-certifier')}:
                      </Typography>
                    </Grid>
                    <Grid item xs={10} className={classes.stageActionProgressContainer}>
                      <Typography>
                        {this.getCertifiersDisplayString(selectedStage)}
                      </Typography>
                    </Grid>
                  </Grid>
                }
                {
                  selectedStage.claimedBy && selectedStage.claimedBy.displayName &&
                  <Grid container className={clsx(classes.stageActionContainer, classes.progressActionContainer)}>
                    <Grid item xs={2} className={classes.stageActionLabelContainer}>
                      <Typography className={classes.stageActionLabel}>
                        {t('claimed-by')}:
                      </Typography>
                    </Grid>
                    <Grid item xs={10} className={classes.stageActionProgressContainer}>
                      <Typography>
                        {selectedStage.claimedBy.displayName}
                      </Typography>
                    </Grid>
                  </Grid>
                }
                {
                  selectedStage.completionDate &&
                  <Grid container className={clsx(classes.stageActionContainer, classes.progressActionContainer)}>
                    <Grid item xs={2} className={classes.stageActionLabelContainer}>
                      <Typography className={classes.stageActionLabel}>
                        {t('completion-date')}:
                      </Typography>
                    </Grid>
                    <Grid item xs={10} className={classes.stageActionProgressContainer}>
                      <Typography>
                        {moment(selectedStage.completionDate).format('LLL')}
                      </Typography>
                    </Grid>
                  </Grid>
                }
                <Grid container className={clsx(classes.stageActionContainer, classes.progressActionContainer)}>
                  <Grid item xs={2} className={classes.stageActionLabelContainer}>
                    <Typography className={classes.stageActionLabel}>{t('stage-progress')}:</Typography>
                  </Grid>
                  <Grid item xs={10} className={classes.stageActionProgressContainer}>
                    {this.getProgressBar(selectedStage)}
                  </Grid>
                </Grid>
              </Grid>

              <Grid item xs={12} className={classes.sectionContainer}>
                {
                  _.keys(this.state.showSection).map(section => (
                    eventData[section].length > 0 ? (
                      <Grid item xs={12} key={section}>
                        <DashCard>
                          <DashCard.Header className={classes.sectionHeader}>
                            <Grid container className={classes.sectionHeaderGrid}>
                              <Grid>
                                <DashCard.Title>
                                  {certType === 'user' ? t('details-' + section) : t('details-' + section + '-object')}
                                </DashCard.Title>
                              </Grid>
                              <Grid>
                                <IconButton onClick={() => this.toggleSection(section)}>
                                  {this.state.showSection[section] ? <ExpandMore /> : <ChevronRight />}
                                </IconButton>
                              </Grid>
                            </Grid>
                          </DashCard.Header>
                          <Grid className={classes.sectionContainer}>
                            <Grid container item direction={'column'}>
                              {this.state.showSection[section] ? 
                                <Grid className={classes.eventDetailsListContainer}>
                                  <EventDetailsList 
                                    data={eventData[section]}
                                    section={section}
                                    queryingMetadata={queryingMetadata}
                                    canTakeAction={canTakeAction}
                                    activeStage={this.state.activeStage}
                                    getIconFromOutcome={this.props.getIconFromOutcome}
                                    eventActionClicked={this.state.eventActionClicked}
                                    eventAction={this.takeAction}
                                    eventActionOnDialogClose={this.onDialogClose}/>
                                </Grid> : null
                              }
                            </Grid>
                          </Grid>
                        </DashCard>
                      </Grid>
                    ) : null ))
                }
              </Grid>
            </Grid>
          </Grid>
        </DialogContent>
      </Dialog>
    );
  }
}

const mapStateToProps = state => {
  const { certlist, auth } = state;
  return {
    certlist,
    auth,
  };
};

const mapDispatchToProps = {
  getEventDetails,
  changeSelectedStage,
  incrementSelectedStage,
  decrementSelectedStage,
  certActionEvent,
  setSelectedEventIndex,
  changeDialogType, 
  getModalObject, 
  updateSelectedAction,
  updateRowData,
  changeActiveGovernanceDialog,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(EventDetails);