import React, { Fragment } from 'react';
import _ from 'lodash';
import styles from './styles.js';
import { withStyles } from '@material-ui/core/styles';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import {
  Menu,
  MenuItem,
  Button,
} from '@material-ui/core';


const BulkActionMenu = props => {
  const {actions, selected, classes, t} = props;
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleSubmit = (action) => {
    props.onActionClick(action);
    setAnchorEl(null);
  };

  let bulkActions = _.cloneDeep(actions);
  if(selected.length <= 0){
    bulkActions = _.remove(bulkActions, function(action){
      return(!action.includes('selected'));
    });
  }
  return(
    <Fragment>
      <Button
        aria-controls="customized-menu"
        aria-haspopup="true"
        variant="contained"
        color="primary"
        onClick={handleClick}
        className={classes.button}
        disabled={bulkActions.length === 0}
      >
        {t('btn-stage-actions')}
      </Button>

      <Menu 
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {bulkActions.map(action => {
          return(
            <MenuItem key={action} onClick={() => {handleSubmit(action);}}>{t('btn-' + action)}</MenuItem>
          );
        })}
      </Menu>
    </Fragment>
  );
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withTranslation(),
)(BulkActionMenu);
