import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { 
  Dialog,
  DialogTitle,
  IconButton,
  Slide,
  Typography,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import { setIsCreateModalOpen, changeCreateModalType } from 'src/store/actions/generic.actions';
import { createPolicy, createPolicyScan, editPolicy, editPolicyScan } from 'src/store/actions/policies.actions';
import { createCertification, editCertification } from 'src/store/actions/certs.actions';
import NewPolicyForm from 'src/components/presentational/CreateForms/NewPolicy';
import NewUserCertForm from 'src/components/presentational/CreateForms/NewUserCert';
import { policyForm, policyScanForm, userCertForm } from 'src/utils/schemas';
import styles from './styles';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction='down' ref={ref} {...props} />;
});

class CreateModal extends Component {
  state = {
    editObject: null,
    createdObject: null,
    updateModalTo: null,
  }

  componentDidUpdate(prevProps, prevState) {
    const { changeCreateModalType, createModalMode, createModalType, createModalTarget } = this.props;
    const { editObject, createdObject, updateModalTo } = this.state;
    if (updateModalTo && createdObject) {
      const newTarget = createdObject._id;
      changeCreateModalType(createModalType, updateModalTo, newTarget);
    } else if (createModalMode === 'edit' && (_.isNull(editObject) || editObject && editObject._id !== createModalTarget)) {
      this.getEditModeTarget();
    } else if (createModalMode === 'create' && _.isObject(this.state.editObject)) {
      this.setState({ editObject: null });
    }
  }

  componentWillUnmount() {
    this.setState({ editObject: null });
  }

  handleClose = () => {
    this.props.setIsCreateModalOpen(false);
    this.setState({ editObject: null, createdObject: null });
  }

  handleSubmit = form => {
    const { 
      createModalMode, 
      createModalTarget, 
      createModalType, 
      createCertification, 
      createPolicy, 
      createPolicyScan, 
      editCertification, 
      editPolicy, 
      editPolicyScan,
    } = this.props;
    if (createModalType === 'policy') {
      if (createModalMode === 'edit') {
        return editPolicy(form, createModalTarget);
      }
      return createPolicy(form).then(policy => {
        this.setState({ updateModalTo: 'edit', createdObject: policy });
        return policy;
      });
    }
    if (createModalType === 'policy-scan') {
      if (createModalMode === 'edit') {
        return editPolicyScan(form, createModalTarget);
      }
      return createPolicyScan(form).then(scan => {
        if (form.scanType === 'scheduled') {
          this.setState({ updateModalTo: 'edit', createdObject: form });
        }
        return scan;
      });
    }
    if (createModalType === 'user-cert' || createModalType === 'object-cert') {
      if (createModalMode === 'edit') {
        return editCertification(form, createModalTarget);
      }
      return createCertification(form).then(cert => {
        if (form.frequency === 'scheduled') {
          this.setState({ updateModalTo: 'edit', createdObject: form });
        } else {
          this.handleClose();
        }
        return cert;
      });
    }
  }

  getModalTitle = () => {
    const { t, createModalMode, createModalType } = this.props;
    return t(`${createModalMode}-modal`, {type: createModalType});
  }

  getEditModeTarget = () => {
    const { certs, createModalMode, createModalType, createModalTarget, policies } = this.props;
    const { createdObject } = this.state;
    if (createModalMode !== 'edit') {
      return;
    }
    if (
      createModalType === 'policy' || 
      createModalType === 'policy-scan' || 
      createModalType === 'user-cert' || 
      createModalType === 'object-cert'
    ) {
      const { results } = createModalType.indexOf('cert') > -1 ? certs : policies;
      const editObject = createdObject && createdObject._id === createModalTarget ? 
        createdObject :
        results.find(result => result._id === createModalTarget);
      if (editObject) {
        this.setState({ editObject });
      }
    }
  }

  getFormFromModalType = () => {
    const { createModalType } = this.props;
    let schema = null;
    if (createModalType === 'policy' || createModalType === 'policy-scan') {
      schema = createModalType === 'policy' ? policyForm : policyScanForm;
      return (
        <NewPolicyForm 
          formType={createModalType}
          formSchema={schema}
          handleClose={this.handleClose}
          onSubmit={this.handleSubmit}
          existingFormData={this.state.editObject}
        />
      );
    }
    if (createModalType === 'user-cert' || createModalType === 'object-cert') {
      schema = userCertForm;
      return (
        <NewUserCertForm 
          formType={createModalType}
          formSchema={schema}
          handleClose={this.handleClose}
          onSubmit={this.handleSubmit}
          existingFormData={this.state.editObject}
        />
      );
    }
    return null;
  }

  render() {
    const { t, classes, isCreateModalOpen } = this.props;
    const modalTitle = this.getModalTitle();
    
    return (
      <Dialog 
        maxWidth='lg'
        fullScreen
        disableEscapeKeyDown={true}
        TransitionComponent={Transition}
        scroll='paper'
        PaperProps={{
          className: 'appBarShift',
        }}
        onClose={this.handleClose}
        open={isCreateModalOpen}
        aria-labelledby='create-modal-title'
      >
        <DialogTitle className={classes.createModalTitle} id='create-modal-title' disableTypography>
          <Typography className={classes.createModalTitleText} variant='h6'>{modalTitle}</Typography>
          <IconButton aria-label={t('label-close')} className={classes.closeButton} onClick={this.handleClose}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        { this.getFormFromModalType() }
      </Dialog>
    );
  }
}

const mapStateToProps = state => {
  const {
    certs,
    policies,
    generic: {
      isCreateModalOpen,
      createModalType,
      createModalMode,
      createModalTarget,
    },
  } = state;

  return  {
    certs,
    policies,
    isCreateModalOpen,
    createModalType,
    createModalMode,
    createModalTarget,
  };
};

const mapDispatchToProps = {
  changeCreateModalType,
  setIsCreateModalOpen,
  createCertification,
  editCertification,
  createPolicy,
  createPolicyScan,
  editPolicy,
  editPolicyScan,
};

CreateModal.propTypes = {
  isCreateModalOpen: PropTypes.bool,
  createModalType: PropTypes.string,
  setIsCreateModalOpen: PropTypes.func,
  createCertification: PropTypes.func,
  createPolicy: PropTypes.func,
  createPolicyScan: PropTypes.func,
  editCertification: PropTypes.func,
  editPolicy: PropTypes.func,
  editPolicyScan: PropTypes.func,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(CreateModal);
