import React, { useState, useEffect, Fragment } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import clsx from 'clsx';
import {
  Button, OutlinedInput, Grid, Switch, TextareaAutosize, Typography,
} from '@material-ui/core';
import {getSelectedNotification, 
  setSelectedNotification,
  updateSelectedNotification} from 'src/store/actions/config.actions';
import PageTitle from 'src/components/presentational/PageTitle';
import LoadingIndicator from 'src/components/presentational/LoadingIndicator';
import { Formik } from 'formik';



const addCarbonCopyField = (data) => {
  let newData = Object.entries(data);
  newData.splice(4, 0, ['cc', '']);
  newData=newData.reduce((result, item) => {
    result[item[0]] = item[1];
    return result;
  }, {});
  return newData;
};

const NotificationDetails = props => {
  const [ data, setData ] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(()=>{
    const { id } = props.match.params;
    setLoading(true);
    props.setSelectedNotification(id);
    props.getSelectedNotification().finally(() => {
      setLoading(false);
    });
  }, []); 

  useEffect(() => {
    setData(props.config.notifications.selected.notification);
  }, [props.config.notifications.selected]);

  const { t, classes } = props;
  if(!(_.isEmpty(data)) && !('cc' in data)) {
    setData(addCarbonCopyField(data));
  }
  if(loading){
    return(<LoadingIndicator />);
  }
  return(
    <Fragment>
      <PageTitle
        title={t('notification_edit') + ' ' + t('notification')}
      />
      {
        loading ?
          <LoadingIndicator /> :
          <Fragment>
            <Grid className={classes.notificationContainer}>
              <Formik
                initialValues={data}
                validate={values => {
                  const errors ={};
                  if((_.isEqual(values, data))){
                    //TODO: Add alert for validation
                    errors.general = 'Data was not changed';
                  }
                  return errors;
                }}
                onSubmit={(values, { setSubmitting }) => {
                  props.updateSelectedNotification(values);
                }}>
                {({
                  values,
                  errors,
                  touched,
                  handleChange,
                  handleBlur,
                  handleSubmit,
                  isSubmitting,
                }) => (
                  <form onSubmit={handleSubmit}>
                    {Object.keys(data).map(key => {
                      let field = null;
                      if(key === 'enabled'){
                        field = <Switch name={key} 
                          onChange={handleChange} 
                          size='medium'
                          checked={values[key]}
                          value={values[key]}
                          onBlur={handleBlur}/>;
                      }
                      else if(key === 'body'){
                        field = <TextareaAutosize onChange={handleChange}
                          name={key}
                          value={values[key]}
                          className={clsx(classes.textArea, classes.notificationRow)}
                          rowsMax={10}
                          rowsMin={10}/>;
                      }
                      else{
                        field = <OutlinedInput onChange={handleChange}
                          name={key}
                          value={values[key]}
                          className={clsx(classes.textArea, classes.notificationRow)}
                          disabled={key === '_id'}
                          fullWidth={true}
                          onBlur={handleBlur}/>;
                      }
                      return(
                        <Grid container key={key} alignItems={'center'}>
                          <Grid item xs={1}>
                            <Typography>{t(key)}</Typography>
                          </Grid>
                          <Grid item xs={10}>
                            {field}
                          </Grid>
                        </Grid>
                      );
                    })}
                    <Button type="submit" disabled={isSubmitting} className={classes.editNotificationButton}>
                      {t('submit')}
                    </Button>
                    <Button type="button" onClick={() => {props.history.goBack();}} className={classes.editNotificationButton}>
                      {t('btn-cancel')}
                    </Button>
                  </form>
                )}
              </Formik>
            </Grid>
          </Fragment>
      }
    </Fragment>
  );
};

const mapStateToProps = state => {
  const { config } = state;
  return {
    config,
  };
};

const mapDispatchToProps = {
  getSelectedNotification,
  setSelectedNotification,
  updateSelectedNotification,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(NotificationDetails);
