import _ from 'lodash';
import { ASYNC_FIELDS, GENERIC } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import { httpFailure } from './generic.actions.js';
import constants from 'src/utils/constants.js';

export const getWorkflowOptions = () => (dispatch, getState) => {
  const { config } = getState();
  const IDM_version = _.get(config, 'IDM_version');
  dispatch({
    type: ASYNC_FIELDS.QUERY_WORKFLOWS,
    payload: null,
  });
  let params = {};
  if (IDM_version.indexOf('6.5') >= 0) {
    params._queryId = 'query-all-ids';
  } else {
    params._queryFilter = 'true';
  }
  return httpService.request({
    url: constants.IDM_CONTEXT.concat('/workflow/processdefinition'),
    method: 'get',
    params,
  }).then(({ data }) => {
    let workflows = [];
    if (data.result && data.result.length > 0) {
      workflows = _.chain(data.result).groupBy('key').map(_.last).value();
      workflows = workflows.map((workflow) => ({ key: workflow.key, displayName: workflow.name }));
    }
    dispatch({
      type: ASYNC_FIELDS.QUERY_WORKFLOWS_SUCCESS,
      payload: workflows,
    });
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const getPoliciesForScan = query => dispatch => {
  dispatch({
    type: ASYNC_FIELDS.QUERY_SCAN_POLICIES,
    payload: null,
  });
  const _queryFilter = query ? `name co "${query}"` : 'true';
  return httpService.request({
    url: constants.IDM_CONTEXT.concat('/managed/policy'),
    method: 'get',
    params: {
      _queryFilter,
      _sortKeys: 'name',
    },
  }).then(({ data }) => {
    dispatch({
      type: ASYNC_FIELDS.QUERY_SCAN_POLICIES_SUCCESS,
      payload: data.result,
    });
    return data.result;
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const queryUsers = (query, setting) => (dispatch) => {
  return queryForManagedObject(query, 'user', dispatch, setting);
};

export const queryRoles = (query, setting) => (dispatch) => {
  return queryForManagedObject(query, 'role', dispatch, setting);
};

const queryForManagedObject = (query, queryType, dispatch, setting) => {
  let resolvedData = [];
  let paramsAttr = null;
  let userFormat = null;
  if (setting && queryType === 'user') {
    userFormat = _.words(setting, /(?!\{\{)(\w+)(?=\}\})/g);
  }

  if (queryType === 'user') {
    paramsAttr = 'userName';
  } else if (queryType === 'role') {
    paramsAttr = 'name';
  } else {
    paramsAttr = 'name';
  }

  dispatch({
    type: ASYNC_FIELDS.QUERY_TYPEAHEAD,
    payload: null,
  });
  if (!query || query.length === 0) {
    dispatch({
      type: ASYNC_FIELDS.QUERY_TYPEAHEAD_SUCCESS,
      payload: [],
    });
    return Promise.resolve(resolvedData);
  }

  return httpService.request({
    url: `${constants.IDM_CONTEXT}/managed/${queryType}`,
    method: 'get',
    params: {
      [paramsAttr]: query,
      _queryFilter: `${paramsAttr} sw "${query}"` + getManagedObjectQuery(userFormat, query),
      _pageSize: '10',
      _sortKeys: paramsAttr,
    },
  }).then(({ data }) => {
    let results = null;
    if(setting && queryType === 'user'){
      results = data.result.map(obj => ({ key: obj._id, displayName: getDisplayName(obj, setting), userName: obj.userName}));
    }
    else{
      results = data.result.map(obj => ({ key: obj._id, displayName: obj[paramsAttr]}));
    }
    dispatch({
      type: ASYNC_FIELDS.QUERY_TYPEAHEAD_SUCCESS,
      payload: results,
    });
    resolvedData = results;
  }).catch(error => {
    dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  }).finally(() => Promise.resolve(resolvedData));
};

const queryForStoredEntitlement = (query, dispatch) => {
  let resolvedData = [];
  let actionType, actionSuccess = null;

  actionType = ASYNC_FIELDS.QUERY_TYPEAHEAD;
  actionSuccess = ASYNC_FIELDS.QUERY_TYPEAHEAD_SUCCESS;

  dispatch({
    type: actionType,
    payload: null,
  });
  if (!query || query.length === 0) {
    dispatch({
      type: actionSuccess,
      payload: [],
    });
    return Promise.resolve(resolvedData);
  }

  return httpService.request({
    url: constants.IDM_CONTEXT.concat('/governance/adminDashboard'),
    method: 'get',
    params: {
      q: query,
      action: 'getStoredEntitlements',
    },
  }).then(({ data }) => {
    dispatch({
      type: actionSuccess,
      payload: data.result,
    });
    resolvedData = data.result;
  }).catch(error => {
    dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  }).finally(() => Promise.resolve(resolvedData));
};

export const queryForEntitlement = (entitlementFilter, pageParams) => {
  let configObject = {};
  configObject.url = constants.IDM_CONTEXT.concat('/governance/getRelationshipObjects');
  configObject.params = {};
  configObject.method = 'get';
  configObject.params.managedObject = entitlementFilter.managedObject;
  configObject.params.attribute = entitlementFilter.attribute;
  if (entitlementFilter.query) {
    configObject.data = {
      query: entitlementFilter.query,
    };
    configObject.method = 'post';
  }
  if (pageParams) {
    configObject.params.ascOrder = pageParams.ascOrder;
    configObject.params.pageSize = pageParams.pageSize;
    configObject.params.pageNumber = pageParams.pageNumber;
    configObject.params.sortKey = pageParams.sortKey;
  }
  return httpService(configObject)
    .then(
      function(response) {
        return response.data;
      },
      function(error) {
        return Promise.reject(error);
      }
    );
};

export const resetTypeaheadOptions = () => dispatch => {
  dispatch({
    type: ASYNC_FIELDS.QUERY_TYPEAHEAD,
    payload: null,
  });
};

export const queryTypeaheadOptions = (targetObject, query, property, setting, userNameOnly) => (dispatch, getState) => {
  if (!property || !query) {
    return null;
  }
  if (property === 'userName' || targetObject !== 'user' && property === 'name') {
    return queryForManagedObject(query, targetObject, dispatch, userNameOnly ? null : setting);
  }
  else if (property === 'certificationTotals') {
    return queryForStoredEntitlement(query, dispatch);
  }
  else if (property === 'policyTotals') {
    return queryForManagedObject(query, 'policy', dispatch, setting);
  }

  let properties = [property];
  const { config } = getState();
  if (property === 'authzGroup' && config.membership_properties) {
    properties = _.cloneDeep(config.membership_properties);
  }
  const configState = config.managed_objects[targetObject];
  let entitlementQuery = {
    managedObject: targetObject,
    attribute: properties.join(),
    query: [],
  };
  const pageParams = {
    pageSize: 15,
    pageNumber: 1,
    sortBy: 'displayName',
  };

  let paths = [];
  for (let prop of properties) {
    const resourceCollection = configState[prop].items ? 
      configState[prop].items.resourceCollection : 
      configState[prop].resourceCollection;
    _.forEach(resourceCollection, coll => {
      if (paths.indexOf(coll.path) < 0) {
        let queryParam = {
          operator: 'co',
          path: coll.path,
          value: query,
        };
        if (coll.path === 'managed/user') {
          queryParam.operator = 'sw';
        }
        let newQueryParam = _.map(coll.query.fields, field => {
          let param = _.cloneDeep(queryParam);
          param.attribute = field;
          return param;
        });
        entitlementQuery.query = entitlementQuery.query.concat(newQueryParam);
        paths.push(coll.path);
      }
    });
  }

  if (entitlementQuery.query.length > 1) {
    let i = 0;
    while (i < entitlementQuery.query.length) {
      if (entitlementQuery.query[i + 1]) {
        entitlementQuery.query.splice(i + 1, 0, 'or');
      }
      i += 2;
    }
  }

  return queryForEntitlement(entitlementQuery, pageParams).then(response => {
    dispatch({
      type: ASYNC_FIELDS.QUERY_TYPEAHEAD_SUCCESS,
      payload: response.result,
    });
    return response.result;
  });
};

export const reverseQueryTypeahead = (targetType, value) => dispatch => {
  let resourceCollection, resourceId = null;
  if (value._refResourceCollection) {
    resourceCollection = value._refResourceCollection;
    resourceId = value._refResourceId;
  } else if (_.isString(value)) {
    let managed = '';
    [ managed, resourceCollection, resourceId ] = value.split('/');
    resourceCollection = managed.concat('/', resourceCollection);
  } 
  if (!resourceCollection || !resourceId) {
    return Promise.resolve([]);
  }
  return httpService.request({
    url: `${constants.IDM_CONTEXT}/${resourceCollection}`,
    method: 'get',
    params: {
      _queryFilter: `_id eq '${resourceId}'`,
      _pageSize: '10',
    },
  }).then(({ data }) => {
    const paramsAttr = resourceCollection === 'managed/user' ? 'userName' : 'name';
    const result = data.result.map(obj => ({ 
      _id: resourceCollection + '/' + obj._id,
      key: obj._id, 
      displayName: obj[paramsAttr] }));
    dispatch({
      type: ASYNC_FIELDS.QUERY_TYPEAHEAD_SUCCESS,
      payload: result,
    });
    return result;
  });
};

const getDisplayName = (data, setting) => {
  let newName = '';
  newName = setting.replace(/\{\{\w*\}\}/g, function(match){
    let str = _.trimStart(match, '{{');
    str = _.trimEnd(str, '}}');
    return data[str];
  });
  return newName;
};

const getManagedObjectQuery = (format, query) => {
  if(!format){
    return '';
  }
  let queryStr = ' or ';
  format.forEach((f, index)=>{
    if(index + 1 >= format.length){
      queryStr += `${f} sw "${query}"`;
    }
    else{
      queryStr += `${f} sw "${query}" or `;
    }
  });
  return queryStr;
};