import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { withFormik } from 'formik';
import {
  Grid,
} from '@material-ui/core';
import * as yup from 'yup';
import { configureReactiveScansForm, createYupSchema } from 'src/utils/schemas';
import { getReactiveScanConfiguration, configureReactiveScans } from 'src/store/actions/policies.actions';
import FormBody from 'src/components/presentational/CreateForms/FormBody';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';

class ViewReactive extends Component {

  componentDidMount() {
    this.props.getReactiveScanConfiguration();
  }

  handleSubmit = form => {
    this.props.configureReactiveScans(form).then(success => {
      this.props.handleClose();
    });
  }

  render() {
    const { t, classes, dialogType, reactiveConfig } = this.props;
    return (
      <Grid container spacing={2}>
        <ReactiveScanForm 
          classes={classes}
          formSchema={configureReactiveScansForm}
          existingFormData={reactiveConfig}
          formType={dialogType}
          onSubmit={this.handleSubmit}
          handleClose={this.props.handleClose}
          t={t}
        />
      </Grid>
    );
  }
}

const ReactiveScanForm = withFormik({
  mapPropsToValues: (props) => {
    const formFields = {};
    props.formSchema().forEach(item => {
      if (props.existingFormData && props.existingFormData[item.id]) {
        if (item.isStringified) {
          formFields[item.id] = JSON.parse(props.existingFormData[item.id]);
        } else if (item.type === 'duration') {
          const [ unit, period ] = props.existingFormData[item.id].split(' ');
          formFields[item.id] = { unit, period };
        } else {
          formFields[item.id] = props.existingFormData[item.id];
        }
      } else if (_.isNull(item.value)) {
        formFields[item.id] = item.value;
      } else {
        formFields[item.id] = item.value || '';
      }
    });
    
    return formFields;
  },
  enableReinitialize: true,
  isInitialValid: (props) => !_.isNull(props.existingFormData),
  handleSubmit: (values, { props, resetForm }) => {
    values.expirationDate = `${values.expirationDate.unit} ${values.expirationDate.period}`;
    props.onSubmit(values);
  },
  validationSchema: props => yup.object().shape(props.formSchema().reduce(createYupSchema, {})),
  displayName: 'ConfigureReactiveScans',
})(FormBody);

ViewReactive.propTypes = {
  escalationSchedule: PropTypes.array,
  expirationDate: PropTypes.string,
  onSubmitDialog: PropTypes.func,
  handleClose: PropTypes.func,
  dialogType: PropTypes.string,
};

const mapStateToProps = state => {
  const {
    policies: {
      reactiveConfig,
    },
  } = state;
  return { 
    reactiveConfig, 
  };
};

const mapDispatchToProps = {
  configureReactiveScans,
  getReactiveScanConfiguration,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(ViewReactive);
