import { ADMIN_DASHBOARD, GENERIC } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import { httpFailure } from './generic.actions.js';
import _ from 'lodash';
import constants from 'src/utils/constants.js';

export const getDashboard = () => (dispatch, getState) => {
  dispatch({
    type: ADMIN_DASHBOARD.QUERY_DASHBOARD,
    payload: null,
  });
  const certEndpoint = constants.IDM_CONTEXT.concat('/governance/adminDashboard');
  let params = {};
  return httpService.request({
    url: certEndpoint,
    method: 'get',
    params,
  }).then(({ data }) => {
    dispatch({
      type: ADMIN_DASHBOARD.QUERY_DASHBOARD_SUCCESS,
      payload: data,
    });
  }).catch(error => {
    dispatch({
      type: ADMIN_DASHBOARD.QUERY_DASHBOARD_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const getSingleDashboardStat = (adminProps, stat, page) => (dispatch, getState) => {
  let results = _.cloneDeep(adminProps.results);
  dispatch({
    type: ADMIN_DASHBOARD.QUERY_DASHBOARD_SINGLE,
    payload: null,
  });
  const certEndpoint = constants.IDM_CONTEXT.concat('/governance/adminDashboard/', stat);
  let params = {
    pageNumber: page + 1,
  };
  return httpService.request({
    url: certEndpoint,
    method: 'get',
    params,
  }).then(({ data }) => {
    let indexToUpdate = _.findIndex(results, ['label', stat]);
    results[indexToUpdate] = data.result;
    dispatch({
      type: ADMIN_DASHBOARD.QUERY_DASHBOARD_SINGLE_SUCCESS,
      payload: results,
    });
  }).catch(error => {
    dispatch({
      type: ADMIN_DASHBOARD.QUERY_DASHBOARD_SINGLE_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const getStoredEntitlement = id => (dispatch, getState) => {
  if (!id) {
    dispatch({
      type: ADMIN_DASHBOARD.QUERY_ENTITLEMENT_SUCCESS,
      payload: null,
    });
    return;
  }
  dispatch({
    type: ADMIN_DASHBOARD.QUERY_ENTITLEMENT,
    payload: null,
  });
  const certEndpoint = constants.IDM_CONTEXT.concat('/governance/userEventData/object');
  let params = {
    targetId: id,
  };
  return httpService.request({
    url: certEndpoint,
    method: 'get',
    params,
  }).then(({ data }) => {
    dispatch({
      type: ADMIN_DASHBOARD.QUERY_ENTITLEMENT_SUCCESS,
      payload: data,
    });
  }).catch(error => {
    dispatch({
      type: ADMIN_DASHBOARD.QUERY_ENTITLEMENT_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

