const getRandomIntInclusive = (min, max) => {
  const min1 = Math.ceil(min);
  const max1 = Math.floor(max);
  return Math.floor(Math.random() * (max1 - min1 + 1)) + min1;
};

const chronStringBuilder = {
  toCronString: scheduleObj => {
    let every = null,
      start = null,
      date = null;
    const seconds = getRandomIntInclusive(0, 59);
    const minutes = getRandomIntInclusive(0, 4);
    const secondsMinutes = seconds + ' ' + minutes;

    if (scheduleObj.repeatType === 'daily') {
      every = scheduleObj.dailyRate.toString();
      start = scheduleObj.startDay.toString();

      return secondsMinutes + ' 0 ' + (start + '/' + every) + ' * ?';
    }

    if (scheduleObj.repeatType === 'weekly') {
      const days = [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday',
      ];
      let daySched = [];

      for (let i = 0; i < days.length; i++) {
        if (scheduleObj.daysOfWeek[days[i]]) {
          daySched.push(i + 1);
        }
      }
      // If no days are selected, scheduleObj is invalid
      if (daySched.length === 0) return false;

      return secondsMinutes + ' 0 ? * ' + daySched.join(',');
    }

    if (scheduleObj.repeatType === 'monthly') {
      every = scheduleObj.monthlyRate.toString();
      start = scheduleObj.startMonth.toString();
      date = scheduleObj.monthlyDate.toString();

      return (
        secondsMinutes +
        ' 0 ' +
        (scheduleObj.useLastDay ? 'L' : date) +
        ' ' +
        (start + '/' + every) +
        ' ?'
      );
    }

    if (scheduleObj.repeatType === 'yearly') {
      start = scheduleObj.startMonth.toString();
      date = scheduleObj.monthlyDate.toString();
      return (
        '0 0 0 ' +
        (scheduleObj.useLastDay ? 'L' : date) +
        ' ' +
        (start + '/0 ?')
      );
    }

    return false;
  },
  toScheduleObject: (cronString, scheduleObj) => {
    const fields = cronString.split(' ');

    if (fields[4] === '*') {
      if (fields[5] === '?') {
        scheduleObj.repeatType = 'daily';
        const daySched = fields[3].split('/');
        scheduleObj.startDay = parseInt(daySched[0]);
        scheduleObj.dailyRate = parseInt(daySched[1]);
      } else if (fields[3] === '?') {
        scheduleObj.repeatType = 'weekly';
        const days = [
          'Sunday',
          'Monday',
          'Tuesday',
          'Wednesday',
          'Thursday',
          'Friday',
          'Saturday',
        ];
        const cronStringDays = fields[5].split(',');
        scheduleObj.daysOfWeek = {
          Sunday: false,
          Monday: false,
          Tuesday: false,
          Wednesday: false,
          Thursday: false,
          Friday: false,
          Saturday: false,
        };
        for (let i = 0; i < cronStringDays.length; i++) {
          scheduleObj.daysOfWeek[
            days[parseInt(cronStringDays[i]) - 1]
          ] = true;
        }
      }
    } else {
      scheduleObj.repeatType = 'monthly';

      if (fields[3] === 'L') {
        scheduleObj.useLastDay = true;
      } else {
        scheduleObj.monthlyDate = parseInt(fields[3]);
      }

      const monthSched = fields[4].split('/');
      scheduleObj.startMonth = parseInt(monthSched[0]);
      scheduleObj.monthlyRate = parseInt(monthSched[1]);
    }
  },
};

export default chronStringBuilder;
