import { SUMMARY } from '../actions/actionTypes.js';

const initialState = {
  type: null,
  results: [],
  entitlements: {},
  userInfo: {},
  attrOrder: [],
  systems: [],
  pageNumber: 1,
  pageSize: 10,
  totalPagedResults: 0,
  sortBy: 'name',
  sortDir: 'asc',
  // q: '',
  querying: false,
  queryingEntitlement: false,
  user : {
    id : null,
    displayName: '',
  },
};

export default (state = initialState, action) => {
  switch(action.type) {
  case SUMMARY.QUERY_SUMMARY:
    return {
      ...state,
      querying: true,
    };
  case SUMMARY.QUERY_SUMMARY_SUCCESS:
    return {
      ...state,
      querying: false,
      ...action.payload,
      // results: action.payload,
    };
  case SUMMARY.QUERY_SUMMARY_FAILURE:
    return {
      ...state,
      querying: false,
      results: [],
      totalPagedResults: 0,
    };
  case SUMMARY.QUERY_ENTITLEMENT:
    return {
      ...state,
      queryingEntitlement: true,
    };
  case SUMMARY.QUERY_ENTITLEMENT_SUCCESS:
    return {
      ...state,
      queryingEntitlement: false,
      ...action.payload,
      // results: action.payload,
    };
  case SUMMARY.QUERY_ENTITLEMENT_FAILURE:
    return {
      ...state,
      queryingEntitlement: false,
      singleEntitlementHistory: {},
    };
  case SUMMARY.CHANGE_SORTING:
    return {
      ...state,
      sortBy: action.payload.sortBy,
      sortDir: action.payload.isDesc ? 'desc' : 'asc',
    };
  case SUMMARY.CHANGE_SEARCH_KEY:
    return {
      ...state,
      q: action.payload.q,
    };
  case SUMMARY.UPDATE_PAGINATION:
    let { pageNumber, pageSize, sortBy, status } = action.payload;
    return {
      ...state,
      pageNumber,
      pageSize,
      sortBy,
      status,
    };
  case SUMMARY.CHANGE_SUMMARY_TYPE:
    return {
      ...state,
      type: action.payload,
      results: [],
      totalPagedResults: 0,
    };
  case SUMMARY.UPDATE_USER:
    let user = initialState.user;
    if(action.payload){
      user={id:action.payload.key, displayName: action.payload.displayName};
    }
    return {
      ...state,
      user: user,
    };
  case SUMMARY.UPDATE_SYSTEMS:
    return {
      ...state,
      systems: action.payload.systems,
    };
  default:
    return state;
  }
};