import { POLICIES, GENERIC } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';
import { httpFailure, alert } from './generic.actions.js';
import i18n from 'src/i18n.js';

export const getPolicies = () => (dispatch, getState) => {
  dispatch({
    type: POLICIES.QUERY_POLICIES,
    payload: null,
  });
  let { pageNumber, pageSize, q, sortBy, sortDir, type } = getState().policies;

  const endpointResource = getPolicyEndpointByType(type);
  let status = getStatusByType(type);
  let url = constants.IDM_CONTEXT + '/governance/adminPolicy/' + endpointResource;
  if (endpointResource === 'policy-scans') {
    url = constants.IDM_CONTEXT + '/governance/policyScan';
  }
  else if (endpointResource.startsWith('violations')) {
    url = constants.IDM_CONTEXT + '/governance/violation/admin';
  }
  let params = {
    pageSize: pageSize,
    pageNumber: pageNumber,
    status: status,
  };
  if (sortBy) {
    params.sortBy = sortDir === 'asc' ? `+${sortBy}` : `-${sortBy}`;
  }
  if (q) {
    params.q = q.replace(/\\/g, '\\\\').replace(/"/g, '\\"');
  }
  return httpService.request({
    url: url,
    method: 'get',
    params,
  }).then(({ data }) => {
    let result = data.result;
    if (result[0] && !result[0]._id) {
      result = data.result.map(item => ({
        ...item,
        _id: item.objectid,
      }));
    }
    dispatch({
      type: POLICIES.QUERY_POLICIES_SUCCESS,
      payload: {
        results: result,
        totalPagedResults: data.totalPagedResults,
      },
    });
  }).catch(error => {
    dispatch({
      type: POLICIES.QUERY_POLICIES_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const getActivePolicyScans = () => dispatch => {
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/activePolicyScan',
    method: 'get',
  }).then(({ data: { activePolicyScans } }) => {
    const policyScans = activePolicyScans.map(scan => {
      return {
        ...scan,
        progress: {
          complete: scan.complete,
          total: scan.total,
          percent: Math.ceil(scan.complete / scan.total * 100),
        },
      };
    });
    dispatch({
      type: POLICIES.QUERY_ACTIVE_SCANS,
      payload: policyScans,
    });
    if (!activePolicyScans || activePolicyScans.length === 0) {
      dispatch({
        type: POLICIES.CHANGE_ACTIVE_SCANS_STATUS,
        payload: false,
      });
    }
  }).catch(error => {
    dispatch({
      type: POLICIES.CHANGE_ACTIVE_SCANS_STATUS,
      payload: false,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const deletePolicies = policyIds => (dispatch, getState) => {
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/adminPolicy/policies?action=delete',
    method: 'post',
    data: {
      ids: policyIds,
    },
  }).then(({ data: { result } }) => {
    dispatch(alert(result, 'success'));
    dispatch(getPolicies());
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

