export const cancelViolations = violationIds => (dispatch, getState) => {
  let { type } = getState().policies;
  switch (type) {
  case 'violations-active':
    type = 'cancel';
    break;
  case 'exceptions-active':
    type = 'cancelexception';
  }
  return httpService.request({
    url: constants.IDM_CONTEXT + '/governance/violation?_action=' + type,
    method: 'post',
    data: {
      ids: violationIds,
    },
  }).then(({ data: { result } }) => {
    dispatch(alert(result, 'success'));
    dispatch(getPolicies());
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

