import React, { Component, Fragment } from 'react';
import _ from 'lodash';
import IDG_CONSTANTS from 'src/utils/constants.js';
import { compose } from 'redux';
import { connect } from 'react-redux';
import queryString from 'query-string';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Grid,
  Tooltip,
} from '@material-ui/core';
import {
  RemoveCircle,
  RemoveCircleOutline,
  HourglassEmpty, 
  Help, 
  HelpOutline,
  CheckCircle,
  CheckCircleOutline,  
  RadioButtonUnchecked,
  NotInterested,
  Clear,
  TimerOff,
} from '@material-ui/icons';
import { 
  changeSelectedStage, 
  changeSortKey,
  getCertificationList,
  updatePagination,
  updateCertId,
  changeSearchKey,
  setSelectedEventIndex,
  removeEventDetails,
  setUserAdmin,
  certActionStage,
  updateActingId,
  getEventDetails,
  updateStatus,
} from 'src/store/actions/certlist.actions.js';
import PageTitle from 'src/components/presentational/PageTitle';
import GovernanceTable from 'src/components/presentational/GovernanceTable';
import ObjectInformation from 'src/components/presentational/ObjectInformation';
import LoadingIndicator from 'src/components/presentational/LoadingIndicator';
import EventDetails from 'src/components/containers/CertificationList/EventDetails';
import StageStepper from 'src/components/presentational/StageStepper';

import { changeActiveGovernanceDialog, changeModalObject, changeDialogType, updateTakingAction } from 'src/store/actions/generic.actions.js';
class CertificationList extends Component {
  state = {
    newButtonText: 'stage-actions',
    bulkActions: ['cancel-cert'],
    rowActions: ['certify', 'revoke', 'abstain', 'comment', 'reset'],
    campaignInfo:[],
    tableCols: [],
    tableData: [],
    openComments: false,
    loadingData: false,
    showEventDetails: false,
    setInitialStage: false,
    loading: false,
  }

  componentDidMount() {
    let isAdminView = this.props.match.path.includes('admin');
    let params = queryString.parse(this.props.location.search);
    this.props.changeSelectedStage(0, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.setUserAdmin(isAdminView);
    if (!isAdminView && params.actingId && params.status) {
      this.props.updateStatus(params.status);
      this.props.updateActingId(params.actingId);
      this.props.updateCertId(this.props.match.params.id, this.props.match.params.type, IDG_CONSTANTS.DO_NOT_UPDATE);
    }
    else{
      this.props.updateCertId(this.props.match.params.id, this.props.match.params.type, IDG_CONSTANTS.DO_NOT_UPDATE);
    }
    
    this.setState({loading:true});
    this.props.getCertificationList().finally(()=>{
      this.setState({loading:false});
      this.setTableCols();
      this.props.changeSelectedStage(this.props.certlist.results.firstEventStage || 0, IDG_CONSTANTS.DO_NOT_UPDATE);
    });
  }

  componentDidUpdate(prevProps){
    let params = queryString.parse(this.props.location.search);
    if (this.props.certlist.actingId !== params.actingId) {
      this.props.updateActingId(params.actingId);
    }
  }
  
  componentWillUnmount() {
    this._isMounted = false;
    this.props.changeSearchKey(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.updatePagination({
      pageNumber: 1,
      pageSize: this.props.certlist.pageSize,
      stageSelected: 0,
      ascOrder: !IDG_CONSTANTS.IS_DESC,
      sortBy: 'firstName',
    }, IDG_CONSTANTS.DO_NOT_UPDATE);
  }

  getCampaignInformation(){
    const { t } = this.props;
    let {results, stageSelected} = this.props.certlist;
    var currentStage = results.stages[stageSelected];
    let campaignInfo = [
      {
        'key': t('campaign-status'),
        'value': results.status,
      },
    ];
    if (this.isAdminView()) {
      campaignInfo.push(
        {
          'key': t('progress'),
          'value': results.totalEventsComplete + ' ' +  t('of') + ' ' + results.totalEventCount,
        }
      );
      if (results.systemMessages && results.systemMessages.info) {
        _.forEach(results.systemMessages.info, function (error) {
          campaignInfo.push(
            {
              'key': t('error'),
              'value': t(error.message),
            }
          );
        });
      }
      var openCertifierValue = _.map(currentStage.openCertifiers, 'displayName').join(', ');
      var closedCertifierValue = _.map(currentStage.closedCertifiers, 'displayName').join(', ');
      if (openCertifierValue !== '') {
        campaignInfo.push(
          {
            'key': 'Active Certifiers',
            'value': openCertifierValue, // Change to stage
          }
        );
      }
      if (closedCertifierValue !== '') {
        campaignInfo.push(
          {
            'key': 'Completed Certifiers',
            'value': closedCertifierValue, // Change to stage
          }
        );
      }
    }
    return campaignInfo;
  }

  getStageInformation() {
    let {results} = this.props.certlist;
    if (this.isAdminView()) {
      return results.stages;
    }
    else {
      return results.totals;
    }
  }

  setTableCols = status => {
    const { certlist, t } = this.props;
    const { results, actingId } = certlist;
    const { stages, stageSelected } = results;
    let events = [];
    if (stages && stageSelected && stages[stageSelected]) {
      events = stages[stageSelected].events; 
    }
    else if (stages) {
      events = stages[0].events;
    }
    else {
      events = [];
    }
    let tableCols = [];
    if (events.length === 0) {
      if (this.state.tableCols.length > 0) {
        return;
      }
      tableCols = [];
    }
    else {
      let event = events[0];
      _.forEach(event.eventData.identity, function(attribute) {
        tableCols.push({
          'header': attribute.displayName,
          'key': attribute.field,
        });
      });
      if (_.includes(actingId, 'managed/role') || _.includes(actingId, 'internal/role')) {
        tableCols.push({
          'header': t('header-claimed-by'),
          'key': 'claimedBy',
        });
      }
      else {
        tableCols.push({
          'header': t('header-certifier'),
          'key': 'certifier',
        });
      }
    }

    this.setState({ tableCols });
  };

  isAdminView() {
    if(this.props.match == undefined){
      return false;
    }
    const url = this.props.match.url;
    if (_.startsWith(url, '/certificationList')) {
      return false;
    }
    else if (_.startsWith(url, '/adminCertList')) {
      return true;
    }
  }

  changeSelectedStage = (stageSelected) => {
    this.setState({showEventDetails:false});
    this.props.updatePagination({
      ...this.props.certlist,
      pageNumber: 1,
      stageSelected,
    });
  }

  onPaginationChange = (evt, page) => { 
    if (evt.q != null) {
      this.props.changeSearchKey(evt.q);
    }else if (evt.sortBy) {
      // Sorting key changed
      const isDesc = evt.sortBy === this.props.certlist.sortBy && this.props.certlist.sortDir === 'asc';
      this.props.changeSortKey(evt.sortBy, isDesc);
    } else if (evt.target.value) {
      // Rows per page changed
      this.props.updatePagination({
        ...this.props.certlist,
        pageNumber: 1,
        pageSize: evt.target.value,
      });
    } else if (_.isNumber(page)) {
      // Page number changed
      this.props.updatePagination({
        ...this.props.certlist,
        pageNumber: page + 1,
      });
    }
    this.setState({tableChanged: true});
  }

  handleActions = (actionType, selectedRows, input) => {
    let queryFilter = '';
    let data = null;
    //TODO: figure out what to do with certify-remaining
    if(actionType.includes('all')){
      queryFilter = 'true';
    }
    else{

      selectedRows.map((row, index)=>{
        if(index + 1 === selectedRows.length){
          queryFilter += `(_id eq "${row}")`;
        }
        else{
          queryFilter += `(_id eq "${row}") or`;
        }
      });
    }
    if (actionType === 'reassign-tasks' || actionType === 'reassign-all') {
      data = {
        newCertifierId: input,
      };
    }
    this.props.updateTakingAction(true);
    this.props.changeActiveGovernanceDialog('GovernanceTableDialog');
    this.props.changeDialogType('actionSubmitting');

    this.props.certActionStage(actionType, queryFilter, data).finally(()=>{
      this.props.changeActiveGovernanceDialog(null);
      this.props.updateTakingAction(false);
      this.props.changeDialogType(null);
      if (actionType === 'sign-off') {
        this.props.history.push('/my-tasks');
      }
    });
   
  }

  handleRowActions = (evt, id, noShow) => {
    evt.stopPropagation();
    if (!noShow) {
      this.props.setSelectedEventIndex(id, true);
      this.props.getEventDetails();
      this.setState({showEventDetails:true});
    }
  }

  updateTableData = () => {
    const { results, stageSelected } = this.props.certlist;
    this.setState({ tableData: results.stages[stageSelected].events });
  }

  handleClose= () => {
    this.setState({showEventDetails:false});
    this.props.removeEventDetails();
    this.props.getCertificationList();
  }
    
  getIconFromOutcome = (outcome, status) => {
    const { classes, certlist, t } = this.props;
    const { isAdminView } = certlist;
    let icon = '';
    let tooltipText = '';
    if (!outcome && !status) {
      tooltipText = 'incomplete';
      icon = <RadioButtonUnchecked visibility='hidden' className={classes.incompleteIcon}/>;
    }
    else if (status === 'in-progress' || status === 'reviewed' || status === 'signed-off') {
      var signedOffIcon = !isAdminView || (isAdminView && status === 'signed-off');
      var signedOffText = status === 'signed-off';
      switch(outcome) {
      case null:
        tooltipText = 'incomplete';
        icon = <RadioButtonUnchecked className={classes.incompleteIcon}/>;
        break;
      case 'certify':
        tooltipText = signedOffText ? 'certify-signed-off' : 'certify';
        icon = signedOffIcon ? <CheckCircle className={classes.certifyIcon}/> : <CheckCircleOutline className={classes.certifyIcon}/>;
        break;
      case 'revoke': 
        tooltipText = signedOffText ? 'revoke-signed-off' : 'revoke';
        icon = signedOffIcon ? <RemoveCircle className={classes.revokeIcon}/> : <RemoveCircleOutline className={classes.revokeIcon}/>;
        break;
      case 'abstain': 
        tooltipText = signedOffText ? 'abstain-signed-off' : 'abstain';
        icon = signedOffIcon ? <Help className={classes.abstainIcon}/> : <HelpOutline className={classes.abstainIcon}/>;
        break;
      default:
      }
    }
    else {
      tooltipText = status;
      switch(status) {
      case 'certify':
        icon = <CheckCircle className={classes.certifyIcon}/>;
        break;
      case 'revoke':
        icon = <RemoveCircle className={classes.revokeIcon}/>;
        break;
      case 'abstain':
        icon = <NotInterested className={classes.abstainIcon}/>;
        break;
      case 'cancelled':
        icon = <Clear className={classes.incompleteIcon}/>;
        break;
      case 'no-certifier':
        icon = <HelpOutline className={classes.incompleteIcon}/>;
        break;
      case 'pending':
        icon = <HourglassEmpty className={classes.incompleteIcon}/>;
        break;
      case 'expired':
        icon = <TimerOff className={classes.incompleteIcon}/>;
        break;
      }
    }
    icon = <Tooltip placement="top" title={t('tooltip-' + tooltipText)} aria-label="iconTip">{icon}</Tooltip>;

    return icon;
  };

  getBulkActions = () =>{
    const { certlist } = this.props;
    const { stageSelected, isAdminView } = certlist;
    const stages = certlist.results.stages;

    if (isAdminView && stages[stageSelected].certifierType !== 'entitlementOwner') {
      return [ 'reassign-all', 'reassign-tasks' ];
    }
    if (!stages[stageSelected].actions) {
      return [];
    }
    
    let actions = _.cloneDeep(stages[stageSelected].actions);

    if(actions.includes('claim')){
      _.remove(actions, function(n){
        return n === 'claim';
      });
      actions.push('claim-all');
      actions.push('claim-selected');
    }
    if(actions.includes('certify-remaining')){
      _.remove(actions, function(n){
        return n === 'certify-remaining';
      });
      actions.push('certify-all');
      actions.push('certify-selected');
    }
    if(actions.includes('reset')){
      _.remove(actions, function(n){
        return n === 'reset';
      });
    }
    actions.push('reset-all');
    actions.push('reset-selected');
    return actions;
  }

  render() {
    const { certlist, classes, t } = this.props;
    const { status } = certlist.results;
    const { pageNumber, pageSize, querying, queryingMetadata, results } = certlist;
    const stages = certlist.results.stages;
    const stageSelected = certlist.stageSelected;
    if(results.stages === undefined){
      return null;
    }
    if(this.state.loading){
      return <LoadingIndicator />;
    }
    return (
      <Fragment>
        <PageTitle
          title={results.name}
          subtitle={results.description}
        />
        <Grid className={classes.campaignInformation} container>
          <Grid item xs={5}>
            <ObjectInformation
              title={t('campaign-information')}
              infoType='campaign'
              isAdmin={this.isAdminView()}
              campaignInfo={this.getCampaignInformation()}
            />
          </Grid>
          <Grid item xs={5}>
            <ObjectInformation
              title={t('stages-information')}
              infoType='stages'
              isAdmin={this.isAdminView()}
              campaignInfo={results}
            />
          </Grid>
        </Grid>
        {stages.length > 1 &&
          <Grid className={classes.buttonContainer} item xs={12}>
            <StageStepper 
              stagesParent={certlist.results}
              onlyChangeActive={false}
              activeStage={certlist.stageSelected}
              changeSelectedStage={this.changeSelectedStage}
              altLabel={true}
            />
          </Grid>
        }
        <Grid container className={classes.container} justify='center'>
          <GovernanceTable 
            tableCols={this.state.tableCols}
            tableData={stages[stageSelected].events}
            sortBy={this.props.certlist.sortBy}
            sortDir={this.props.certlist.sortDir}
            bulkActions={this.getBulkActions()}
            useBulkActionMenu={!this.props.certlist.isAdminView}
            rowActions={this.state.rowActions}
            handleActions={this.handleActions}
            handleRowActions={this.handleRowActions}
            showNewButton={false}
            newButtonText={this.state.newButtonText}
            showCheckboxes={status === 'in-progress'}
            hideToolbar={status !== 'in-progress'}
            showOutcome={true}
            pageSize={pageSize}
            pagedResultsOffset={pageNumber-1}
            handlePagination={this.onPaginationChange}
            querying={querying}
            openComments={false}
            handleRowClick={this.handleRowActions}
            tableType={'certList'}
            getIconFromOutcome={this.getIconFromOutcome}
          />
        </Grid>
        <EventDetails 
          open={this.state.showEventDetails}
          eventDetails={this.props.eventDetails}
          queryingEventDetails={this.props.queryingEventDetails}
          queryingMetadata={queryingMetadata}
          handleClose={this.handleClose}
          events={stages[stageSelected].events}
          getIconFromOutcome={this.getIconFromOutcome} />
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const { certlist, auth } = state;
  return {
    certlist, auth,
  };
};

const mapDispatchToProps = {
  changeSelectedStage,
  changeSortKey,
  getCertificationList,
  updatePagination,
  updateCertId,
  changeSearchKey,
  setSelectedEventIndex,
  removeEventDetails,
  setUserAdmin,
  certActionStage,
  updateActingId,
  getEventDetails,
  changeActiveGovernanceDialog,
  changeModalObject,
  updateTakingAction,
  changeDialogType,
  updateStatus,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(CertificationList);
