import React, { Component, Fragment } from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import _ from 'lodash';
import {
  Grid,
} from '@material-ui/core';
import {getNotifications} from 'src/store/actions/config.actions';
import PageTitle from 'src/components/presentational/PageTitle';
import GovernanceTable from 'src/components/presentational/GovernanceTable';
import LoadingIndicator from 'src/components/presentational/LoadingIndicator';

class Notifications extends Component {
  state = {
    tableCols: [
      {
        'header': this.props.t('notificationName'),
        'key': 'displayName',
      },
    ],
    loading: false,
    sortAsc: true,
  }

  componentDidMount() {
    this.setState({loading:true});
    this.props.getNotifications().finally(() => {
      this.setState({loading:false});
    });
  }

  action = (evt, selectedRowId) => {
    this.props.history.push('/admin/notification-templates/' + selectedRowId);
  };

  sortNotifications = data => {
    let { sortAsc } = this.state;
    if (sortAsc) {
      return _.sortBy(data, 'displayName');
    } else {
      return _.sortBy(data, 'displayName').reverse();
    }
  }

  handleSortChange = () => {
    this.setState({ sortAsc: !this.state.sortAsc});
  }

  render() {
    const { classes, t } = this.props;
    const tableData = this.props.config.notifications.results;
    if(!tableData){
      return null;
    }
    let sortedData = this.sortNotifications(tableData);

    return (
      <Fragment>
        <PageTitle
          title={t('page-notification-templates')}
        />
        {this.state.loading ?
          <LoadingIndicator /> :
          <Grid container className={classes.container} justify='center'>
            <GovernanceTable 
              tableCols={this.state.tableCols}
              tableData={sortedData}
              bulkActions={null}
              handleActions={this.handleActions}
              showNewButton={false}
              showCheckboxes={false}
              handlePagination={this.handleSortChange}
              hideToolbar={true}
              hideSearchBar={true}  
              doNotPaginate={true}
              handleRowClick= {this.action}
              tableType='notifications'
            />
          </Grid>
        }
      </Fragment>
    );
  }
}
const mapStateToProps = state => {
  const { config } = state;
  return {
    config,
  };
};

const mapDispatchToProps = {
  getNotifications,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(Notifications);

