import React, { Fragment } from 'react';
import Clear from '@material-ui/icons/Clear';
import {
  Button,
  IconButton,
  TableRow,
  TableCell,
  TextField,
} from '@material-ui/core';
import {FieldArray} from 'formik';

export default function MenuLinks(props) {
  let {field, values, classes, t, onChange} = props;
  const newURL = {
    'name': '',
    'url': '',
  };
  const URLS = values[field.id] || [];
  return (
    <FieldArray name={field.id} render={arrayHelpers => (
      <Fragment>
        {URLS.map((value,index)=>{
          return(
            <TableRow key={index} className={classes.settingGrid}>
              <TableCell className={classes.menuHeaderCell}>
                <TextField key={index+'textfield1'} onChange={onChange}
                  name={`${field.id}[${index}].name`} 
                  value={values[field.id][index].name} 
                  variant='outlined'
                  className={classes.menuLabel}
                  fullWidth={true}/>
              </TableCell>
              <TableCell className={classes.menuHeaderCell}>
                <TextField onChange={onChange}
                  name={`${field.id}[${index}].url`} 
                  variant='outlined'
                  value={values[field.id][index].url}
                  className={classes.menuLink} 
                  fullWidth={true}/>
              </TableCell>
              <TableCell className={classes.menuCloseCell}>
                <IconButton aria-label={t('label-close')} className={classes.closeButton} onClick={() => arrayHelpers.remove(index)}>
                  <Clear />
                </IconButton>
              </TableCell>
            </TableRow>
          );
        })}
        
        <TableRow className={classes.buttonContainer}>
          <TableCell className={classes.innerSettingsCell}>
            <Button
              className={classes.settingsButton}
              size='large'
              variant='outlined'
              type='button'
              onClick={() => arrayHelpers.push(newURL)}
            >
              {t('add')} 
            </Button>
            <Button 
              className={classes.settingsButton}
              size='large'
              variant='outlined'
              type="button"
              onClick={() => props.setFieldValue(field.id, field.value, true)} 
            >
              {t('reset')}
            </Button>
          </TableCell>
        </TableRow>
      </Fragment>
    )} />
  );
}