import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import { connect } from 'react-redux';
import _ from 'lodash';
import {
  CircularProgress,
  TextField,
} from '@material-ui/core';
import { Autocomplete } from '@material-ui/lab';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import { 
  queryUsers, 
  queryRoles, 
  queryTypeaheadOptions, 
  reverseQueryTypeahead, 
  resetTypeaheadOptions, 
} from 'src/store/actions/async_fields.actions';
import {getSettings} from 'src/store/actions/config.actions';

const AsyncSelect = (props) => {
  const isMounted = React.useRef(null);
  const [open, setOpen] = React.useState(false);
  const [options, setOptions] = React.useState([]);
  const [input, setInput] = React.useState('');
  const [querying, setQuerying] = React.useState(false);
  const queryFunction = React.useRef(_.debounce(props.queryTypeaheadOptions, 250));
  const queryStore = props.results;
  const targetType = React.useRef(props.targetType);
  const userNameOnly = props.userNameOnly;

  React.useEffect(() => {
    // When component mounted
    isMounted.current = true;
    return () => {
      // executed on unmount
      isMounted.current = false;
    };
  }, []);
  
  React.useEffect(() => {
    setQuerying(true);
    if (event && event.type === 'input') {
      const targetObject = props.objectToTarget || 'user';
      queryFunction.current(targetObject, input, props.targetType, getDisplaySettings(), userNameOnly);
    } else {
      setQuerying(false);
    }
  }, [input, props.targetType]);
  
  React.useEffect(() => {
    if (targetType.current !== props.targetType) {
      props.resetTypeaheadOptions();
      if(props.targetType === 'userName'){
        props.queryUsers('');
      }
      else{
        props.queryRoles('');
      }
      setInput('');
      if (props.value) {
        props.onChange(null, props.name, '');
      }
      targetType.current = props.targetType;
    }
  }, [props.targetType]);

  React.useEffect(() => {
    setQuerying(false);
    setOptions(queryStore);
  }, [queryStore]);
  
  React.useEffect(() => {
    if (props.value) {
      props.reverseQueryTypeahead(props.targetType, props.value).then(result => {
        if (!isMounted.current) return null;

        if (result && result[0] && result[0].displayName) {
          setOptions(result);
          setInput(result[0].displayName);
          props.onChange(null, props.name, result[0]);
        } else {
          const newInput = props.value && props.value.displayName ? props.value.displayName : props.value;
          setInput(newInput);
        }
      });
    }

    if(!props.settingResults){
      props.getSettings();
    }
  }, []);

  const getDisplaySettings = () => {
    const setting = _.get(props, 'settingResults[1].fields[0].value', undefined);
    return setting;
  };

  return (
    <Autocomplete
      id={props.name}
      classes={{
        endAdornment: props.classes.endAdornment,
      }}
      inputValue={input}
      open={open}
      onOpen={() => setOpen(true)}
      onClose={(evt) => {
        props.queryUsers('');
        setOpen(false);
      }}
      onBlur={(evt) => props.onBlur && props.onBlur(evt, props.name)}
      onChange={(evt,val) => props.onChange(evt, props.name, val)}
      onInputChange={(evt,val,reason) => setInput(val)}
      getOptionSelected={(option, value) => option.key === value.key}
      getOptionLabel={option => option.displayName}
      options={options}
      multiple={props.isMulti}
      loading={querying}
      disabled={props.disabled}
      renderInput={params => (
        <TextField
          {...params}
          name={props.name}
          label={props.label}
          fullWidth={props.fullWidth}
          variant={props.variant}
          error={props.error}
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <Fragment>
                {querying ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </Fragment>
            ),
          }}>{input}</TextField>
        // />
      )}
    />
  );
};

const mapStateToProps = state => {
  const {async_fields, config} = state;
  const settingResults = config.settings.results;
  return {...async_fields, settingResults};
};

const mapDispatchToProps = {
  queryUsers,
  queryRoles,
  queryTypeaheadOptions,
  reverseQueryTypeahead,
  resetTypeaheadOptions,
  getSettings,
};

AsyncSelect.propTypes = {
  name: PropTypes.string,
  fullWidth: PropTypes.bool,
  targetType: PropTypes.string,
  onChange: PropTypes.func,
  workflows: PropTypes.array,
  results: PropTypes.array,
  userNameOnly: PropTypes.bool,
  queryUsers: PropTypes.func,
  queryRoles: PropTypes.func,
  queryTypeaheadOptions: PropTypes.func,
  resetTypeaheadOptions: PropTypes.func,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withStyles(styles)
)(AsyncSelect);
