import React, {Component} from 'react';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { Route, Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Grid } from '@material-ui/core';
import clsx from 'clsx';
import Notifier from 'src/utils/Notifier.js';

import { getAuthenticationModule, checkIfAuthenticated, logoutUser } from 'src/store/actions/auth.actions.js';
import { setIsDrawerOpen } from 'src/store/actions/generic.actions.js';
import { getIDMVersion, getManagedObjectConfig, getMembershipProperties } from 'src/store/actions/config.actions';
import NavBar from 'src/components/containers/NavBar';
import Dashboard from 'src/components/containers/Dashboard';
import Login from 'src/components/containers/Login.js';
import PrivateRoute from 'src/components/containers/PrivateRoute.js';
import CreateModal from 'src/components/presentational/CreateModal';
import Frame from 'src/components/presentational/Frame';
import constants from 'src/utils/constants.js';
import AdminDashboard from 'src/components/containers/AdminDashboard';
import Certifications from 'src/components/containers/Certifications';
import AccessDenied from 'src/components/containers/AccessDenied';
import Policies from 'src/components/containers/Policies';
import Settings from 'src/components/containers/Settings';
import Notifications from 'src/components/containers/Notifications';
import NotificationsDetails from 'src/components/containers/NotificationDetails/NotificationDetails';
import UserSummary from 'src/components/containers/UserSummary';
import CertificationList from 'src/components/containers/CertificationList';

class App extends Component {
  componentDidMount() {
    if (!this.props.authModule || this.props.authModule !== 'AM') {
      this.props.getAuthenticationModule().then(() => {
        this.props.checkIfAuthenticated();
      });
    } else {
      this.props.checkIfAuthenticated();
    }
  }

  componentDidUpdate(prevProps) {
    const { props } = this;
    const { 
      IDM_version,
      isLoggedIn, 
      checkIfAuthenticated, 
      getIDMVersion, 
      getManagedObjectConfig, 
      getMembershipProperties, 
      setIsDrawerOpen, 
      history, 
      managed_objects,
    } = props;
    
    if (!IDM_version) {
      getIDMVersion();
    }
    if (!prevProps.isLoggedIn && isLoggedIn) {
      if (prevProps.location.pathname === '/login') {
        getMembershipProperties();
        getManagedObjectConfig();
        setIsDrawerOpen(true);
      }
      if(this.props.authorization.component === 'internal/user'){
        setIsDrawerOpen(false);
        history.push('/access-denied');
      }
    } else if (!isLoggedIn && props.location.pathname !== '/login') {
      checkIfAuthenticated()
        .then(result => {
          if (!result) {
            setIsDrawerOpen(false);
            history.push('/login');
          }
        });
    }
    if (prevProps.location !== this.props.location){
      if(this.props.authorization.component === 'internal/user'){
        history.push('/access-denied');
      }
    }
    if (!managed_objects) {
      getManagedObjectConfig();
    }
  }

  render() {
    const { isLoggedIn, t } = this.props;
    if(this.props.auth && this.props.auth.authorization && this.props.auth.authorization.component === 'internal/user'){
      this.props.history.push('/access-denied');
    }

    return (
      <Grid container className='App'>
        <Grid item xs={12}>
          <NavBar />
        </Grid>
        <Grid item>
        </Grid>
        <Grid item className={clsx('mainContent', this.props.isDrawerOpen && 'contentShift')}>
          <main>
            <PrivateRoute path='/login/:oidc?' component={Login} />
            {isLoggedIn && <CreateModal />}
            <PrivateRoute path='/my-tasks' component={Dashboard} />
            {/* <PrivateRoute path='/policies' component={Tasks} /> */}
            <PrivateRoute path='/admin/dashboard' component={AdminDashboard} />
            <PrivateRoute path='/admin/certifications/:type' component={Certifications} />
            <PrivateRoute path='/admin/policies' component={Policies} />
            <PrivateRoute path='/admin/notification-templates/:id' component={NotificationsDetails} />
            <PrivateRoute path='/admin/system-settings' component={Settings} />
            <PrivateRoute exact path='/admin/notification-templates' component={Notifications} />
            <PrivateRoute exact path='/admin/user-summary' component={UserSummary} />
            <PrivateRoute path='/admin/user-summary/:id' component={UserSummary} />
            <PrivateRoute path='/access-denied' component={AccessDenied} />
            <PrivateRoute path='/certificationList/:type/:id' component={CertificationList} />
            <PrivateRoute path='/adminCertList/:type/:id' component={CertificationList} />
            {/* <PrivateRoute path='admin/certification/:type/:id' component={Certification} /> */}
            <PrivateRoute path='/glossaryAdmin/glossary-editor' component={Frame} props={{
              url: constants.IDM_URL.concat('/commons'),
              title: t('page-glossary-editor'),
            }} />
            <Route exact path='/' render={() => <Redirect to='/my-tasks' />} />
          </main>
        </Grid>
        <Notifier />
      </Grid>
    );
  }
}

App.propTypes = {
  getAuthenticationModule: PropTypes.func,
  checkIfAuthenticated: PropTypes.func,
  isLoggedIn: PropTypes.bool,
  logoutUser: PropTypes.func,
  history: PropTypes.object,
  authenticationId: PropTypes.string,
  roles: PropTypes.array,
};

const mapStateToProps = state => {
  const {
    auth: { authModule, isLoggedIn, authenticationId, roles, authorization },
    generic: { isDrawerOpen },
    config: { IDM_version, managed_objects },
  } = state;
  return  {
    authModule,
    isLoggedIn,
    authenticationId,
    roles,
    isDrawerOpen,
    authorization,
    IDM_version,
    managed_objects,
  };
};

const mapDispatchToProps = {
  getIDMVersion,
  getAuthenticationModule,
  checkIfAuthenticated,
  logoutUser,
  setIsDrawerOpen,
  getManagedObjectConfig,
  getMembershipProperties,
};

export default connect(mapStateToProps, mapDispatchToProps)(withTranslation()(App));
