import React, { Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import clsx from 'clsx';
import { v4 as uuidv4 } from 'uuid';
import {
  Button,
  Checkbox,
  Collapse,
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  FormControlLabel,
  Grid,
  ListItem,
  ListItemIcon,
  ListItemText,
  MenuItem,
  Paper,
  Select,
  TextField,
  Typography,
} from '@material-ui/core';
import {
  Add,
  ChevronRight,
  ExpandMore,
  Remove,
} from '@material-ui/icons';
import { withStyles, useTheme } from '@material-ui/core/styles';
import styles from './styles';
import { queryForEntitlement } from 'src/store/actions/async_fields.actions.js';
import { getCertifiableAttributes } from 'src/store/actions/config.actions.js';
import { changeActiveGovernanceDialog, changeDialogType, getModalObject } from 'src/store/actions/generic.actions.js';
import GovernanceDialog from 'src/components/presentational/GovernanceDialog';

const EntitlementFilter = props => {
  const { classes, certifiable_attributes, certType, filter, managed_objects, t } = props;
  const theme = useTheme();
  const isMounted = React.useRef(null);
  const [expanded, setExpanded] = React.useState({ attributes: false, applications: false });
  const [checked, setChecked] = React.useState(filter);
  const [attributeOptions, setAttributeOptions] = React.useState({});
  const [queryResults, setQueryResults] = React.useState({});
  const [attrFilterTargets, setAttrFilterTargets] = React.useState({});
  const [modalObj, setModalObj] = React.useState({});
  const queryOperators = [
    { 'label': t('expr-builder-contains'), 'operator': 'co'},
    { 'label': t('expr-builder-equals'), 'operator': 'eq'},
    { 'label': t('expr-builder-startsWith'), 'operator': 'sw'},
    { 'label': t('expr-builder-doesNotContain'), 'operator': '!co'},
    { 'label': t('expr-builder-doesNotEqual'), 'operator': '!eq'},
  ];
  const boolOperators = ['or','and'];

  const debouncedQueryForEntitlements = React.useRef(_.debounce(async attr => {
    let newAttrQueryResults = {
      pageNumber: 1,
      pageSize: 0,
      result: [],
      resultCount: 0,
      totalPagedResults: -1,
    };
    try {
      const entitlements = await queryForEntitlement(attr);
      const { _id, ...rest } = entitlements;
      newAttrQueryResults = rest;
    } catch(err) { } // eslint-disable-line no-empty
    if (isMounted.current) {
      setAttrFilterTargets({
        [attr.attribute]: newAttrQueryResults,
      });
    }
  }, 250));

  useEffect(() => {
    // When component mounted
    isMounted.current = true;
    return () => {
      // executed on unmount
      isMounted.current = false;
    };
  }, []);

  useEffect(() => {
    props.getCertifiableAttributes();
  }, []);

  useEffect(() => {
    if (checked && checked.attributes) {
      let newAttributeOptions = _.cloneDeep(attributeOptions);
      _.keys(checked.attributes).map(key => {
        if (managed_objects[certType][key] && managed_objects[certType][key].items) {
          newAttributeOptions[key] = _.cloneDeep(managed_objects[certType][key].items);
        }
      });
      setAttributeOptions(newAttributeOptions);
    }
  }, []);

  useEffect(() => {
    setQueryResults({
      ...queryResults,
      ...attrFilterTargets,
    });
  }, [attrFilterTargets]);

  useEffect(() => {
    if (props.modalObject) {
      setModalObj(props.modalObject);
    }
  }, [props.modalObject]);

  const usePrevious = (value) => {
    const ref = React.useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
  };
  const prevChecked = usePrevious(checked);
  useEffect(() => {
    const attribute = _.findKey(checked.attributes, (value, key) => {
      return !prevChecked || !prevChecked.attributes[key] || !_.isEqual(value, prevChecked.attributes[key]);
    });
    if (attribute && checked.attributes[attribute].selected && attributeIsExpandable('attributes', attribute)) {
      queryEntitlements(attribute);
    }
    props.onChange(checked);
  }, [checked.attributes]);

  useEffect(() => {
    props.onChange(checked);
  }, [checked.certifyMetadata]);

  const handleChange = panel => (event, newExpanded) => {
    event.stopPropagation();
    setExpanded({
      ...expanded,
      [panel]: newExpanded,
    });
  };

  const handleToggle = (type, value, propToSet) => {
    event.stopPropagation();
    let newChecked = _.cloneDeep(checked);
    const valuePathArray = value.split('.');

    const checkedAttr = _.get(newChecked, `${type}.${value}.${propToSet}`);
    _.set(newChecked, `${type}.${value}.${propToSet}`, !checkedAttr);
    if (type === 'attributes' && propToSet === 'isOpen' && newChecked.attributes[value].isOpen) {
      _.set(newChecked, `${type}.${value}.selected`, true);
    }
    if (type === 'applications' && propToSet === 'selected' && valuePathArray.length > 1) {
      for (let i = 1; i < valuePathArray.length; i++) {
        const nodeInPath = valuePathArray.slice(0, valuePathArray.length - i).join('.');
        if (_.get(`${type}.${nodeInPath}.selected`)) {
          continue;
        }
        if (!checkedAttr) {
          _.set(newChecked, `${type}.${nodeInPath}.selected`, !checkedAttr);
        }
        _.set(newChecked, `${type}.${nodeInPath}.isDisabled`, !checkedAttr);
      }
    }
    setChecked(newChecked);
  };

  const attributeIsExpandable = (type, attr) => {
    if (type === 'attributes') {
      const schemaProps = managed_objects[certType][attr];
      if (!schemaProps) {
        return false;
      }
      if (schemaProps.items && schemaProps.items.type === 'relationship') {
        return true;
      }
    } else if (type === 'applications') {
      const application = _.find(props.certifiable_attributes.application, { name: attr });
      if (application && application._systemAttributes && Object.keys(application._systemAttributes).length > 0) {
        return true;
      }
    }
    return false;
  };

  const addNewQueryExpression = (type, attr) => {
    let newChecked = _.cloneDeep(checked);
    let newAttributeOptions = _.cloneDeep(attributeOptions);
    let targetFilter = _.get(newChecked, `${type}.${attr}.targetFilter`, []);
    let queryBoolOperators = _.get(newChecked, `${type}.${attr}.boolOperators`, []);
    if (!newAttributeOptions[attr]) {
      newAttributeOptions[attr] = _.cloneDeep(managed_objects[certType][attr].items);
      setAttributeOptions(newAttributeOptions);
    }
    if (targetFilter.length >= 1) {
      _.set(newChecked, `${type}.${attr}.boolOperators`, [...queryBoolOperators, boolOperators[0]]);

    }
    targetFilter.push({
      path: newAttributeOptions[attr].resourceCollection[0].path,
      attribute: newAttributeOptions[attr].resourceCollection[0].query.fields[0],
      operator: queryOperators[0].operator,
      value: '',
    });
    _.set(newChecked, `${type}.${attr}.targetFilter`, targetFilter);
    setChecked(newChecked);
  };

  const removeQueryExpression = (type, attr) => {
    let newChecked = _.cloneDeep(checked);
    let attribute = _.get(newChecked, `${type}.${attr}`);
    attribute.targetFilter.pop();
    if (attribute.queryBoolOperators) {
      attribute.queryBoolOperators.pop();
    }
    _.set(newChecked, `${type}.${attr}`, attribute);
    setChecked(newChecked);
  };

  const updateAttributeFilter = (attr, filterIdx, evt, elem) => {
    const newChecked = _.cloneDeep(checked);
    
    if (evt.target.name === 'boolOperator') {
      newChecked.attributes[attr].boolOperators[filterIdx - 1] = evt.target.value;
    } else {
      newChecked.attributes[attr].targetFilter[filterIdx] = {
        ...newChecked.attributes[attr].targetFilter[filterIdx],
        [evt.target.name]: evt.target.value,
      };
    }
    setChecked(newChecked);
  };

  const getFieldsFromCollection = (attr, filter) => {
    if (!attributeOptions[attr]) {
      return null;
    }
    const fields = _.find(attributeOptions[attr].resourceCollection, { path: filter.path }).query.fields;
    return fields.map(field => (
      <MenuItem key={field} value={field}>{field}</MenuItem>
    ));
  };

  const joinQueryExpressions = attribute => {
    const expressions = attribute.targetFilter;
    const attributeBoolOperators = attribute.boolOperators;
    let joinedArray = [];
    for (let i in expressions) {
      joinedArray.push(expressions[i]);
      if (attributeBoolOperators && attributeBoolOperators[i]) {
        joinedArray.push(attributeBoolOperators[i]);
      }
    }
    return joinedArray;
  };

  const queryEntitlements = async attr => {
    let attributeToQuery = _.get(checked, `attributes.${attr}`);
    let entitlementFilter = {
      managedObject: certType,
      attribute: attr,
    };
    if (attributeToQuery && attributeToQuery.targetFilter && attributeToQuery.targetFilter.length > 0) {
      entitlementFilter.query = joinQueryExpressions(attributeToQuery);
    }
    
    debouncedQueryForEntitlements.current(entitlementFilter);
  };

  const showTargetedEntitlementsDialog = (evt, attr) => {
    evt.stopPropagation();
    const newModalObject = {
      title: attr,
      data: queryResults[attr] ? queryResults[attr].result : [],
    };
    props.changeDialogType('targeted-attribute');
    props.getModalObject('targeted-attribute', newModalObject, true);
    props.changeActiveGovernanceDialog('EntitlementFilterDialog');
  };

  const getUserEntitlementFilter = attr => {
    const attrBoolOperators = _.get(checked, `attributes.${attr}.boolOperators`, []);
    const targetFilter = _.get(checked, `attributes.${attr}.targetFilter`, []);
    return (
      <Paper className={classes.paper}>
        <Typography variant='subtitle1' className={classes.criteriaLabel}>Add criteria to target specific entitlements:</Typography>
        <Button
          variant='contained'
          color='secondary'
          className={clsx(classes.btnEntitlement, classes.btnAddEntitlement)}
          onClick={() => addNewQueryExpression('attributes', attr)}
          startIcon={<Add />}
        >
          Add
        </Button>
        <Button
          variant='contained'
          color='secondary'
          disabled={targetFilter.length === 0}
          className={clsx(classes.btnEntitlement, classes.btnRemoveEntitlement)}
          onClick={() => removeQueryExpression('attributes', attr)}
          startIcon={<Remove />}
        >
          Remove
        </Button>
        { targetFilter.map((filter, i) => (
          <Grid key={uuidv4()} container spacing={1} className={classes.entitlementFilterRow}>
            <Grid item xs={2}>
              { i >= 1 && 
              <Select
                name='boolOperator'
                variant='outlined'
                fullWidth={true}
                value={attrBoolOperators[i - 1]}
                onChange={(evt, elem) => updateAttributeFilter(attr, i, evt, elem)}
              >
                { boolOperators.map(operator => (
                  <MenuItem key={operator} value={operator}>{t(`expr-builder-bool-${operator}`)}</MenuItem>
                )) }
              </Select>
              }
            </Grid>
            <Grid item xs={2}>
              { attributeOptions[attr] && attributeOptions[attr].resourceCollection.length > 1 && 
              <Select
                name='path'
                variant='outlined'
                fullWidth={true}
                value={filter.path}
                renderValue={value => _.find(attributeOptions[attr].resourceCollection, { path: value }).label}
                onChange={(evt, elem) => updateAttributeFilter(attr, i, evt, elem)}
              >
                { attributeOptions[attr].resourceCollection.map(coll => (
                  <MenuItem key={coll.path} value={coll.path}>{coll.label}</MenuItem>
                )) }
              </Select>
              }
            </Grid>
            <Grid item xs={2}>
              <Select
                name='attribute'
                variant='outlined'
                fullWidth={true}
                value={filter.attribute}
                onChange={(evt) => updateAttributeFilter(attr, i, evt)}
              >
                { getFieldsFromCollection(attr, filter) }
              </Select>
            </Grid>
            <Grid item xs={2}>
              <Select
                name='operator'
                variant='outlined'
                fullWidth={true}
                value={filter.operator}
                onChange={(evt, elem) => updateAttributeFilter(attr, i, evt, elem)}
              >
                { queryOperators.map(({ label, operator }) => (
                  <MenuItem key={operator} value={operator}>{label}</MenuItem>
                )) }
              </Select>
            </Grid>
            <Grid item xs={2}>
              <TextField 
                name='value'
                // autoFocus={true}
                variant='outlined'
                fullWidth={true}
                defaultValue={filter.value}
                placeholder='search'
                onChange={(evt) => {
                  filter.value = evt.target.value;
                  queryEntitlements(attr);
                }}
                onBlur={() => {
                  props.onChange(checked);
                }}
              />
            </Grid>
          </Grid>
        ))}
      </Paper>
    );
  };

  const getNestedApplicationAttributes = (appName, appAttributePath, checkedPath) => {
    const appAttrs = _.find(props.certifiable_attributes.application, { name: appName });
    if (!appAttrs || !appAttrs._systemAttributes) {
      return null;
    }
    let nestedAttributes = null;
    if (_.isUndefined(appAttributePath)) {
      appAttributePath = '_systemAttributes';
    }
    nestedAttributes = _.get(appAttrs, appAttributePath);
    if (_.isUndefined(nestedAttributes)) {
      return null;
    }
    
    if (!checkedPath) {
      checkedPath = `${appName}`;
    }

    return Object.entries(nestedAttributes).map(([key, values]) => {
      const localCheckedPath = checkedPath.concat('.', key);
      const valueInChecked = _.get(checked, `applications.${localCheckedPath}`, { selected: false });
      return (
        <Fragment key={key}>
          <ListItem 
            button 
            className={classes.listItem} 
            style={{
              marginLeft: theme.spacing(appAttributePath.split('.').length),
            }}
            divider={_.get(checked, `applications.${appName}.${key}.isOpen`)}
          >
            <ListItemIcon className={classes.checkboxIcon}>
              <Checkbox
                edge='start'
                checked={_.get(checked, `applications.${localCheckedPath}.selected`, false)}
                disabled={valueInChecked.isDisabled}
                onChange={() => handleToggle('applications', `${localCheckedPath}`, 'selected')}
                tabIndex={-1}
                disableRipple
                inputProps={{ 'aria-labelledby': key }}
              />
            </ListItemIcon>
            { !nestedAttributes[key].hasOwnProperty('_id') ?
              <FormControlLabel
                onClick={(event) => handleToggle('applications', `${localCheckedPath}`, 'isOpen')}
                control={_.get(checked, `applications.${localCheckedPath}.isOpen`) ? <ExpandMore /> : <ChevronRight />}
                label={key}
              /> :
              <ListItemText id={key} primary={key} />
            }
          </ListItem>
          <Collapse in={_.get(checked, `applications.${localCheckedPath}.isOpen`)} className={classes.collapse}>
            { !nestedAttributes[key].hasOwnProperty('_id') && 
            getNestedApplicationAttributes(appName, appAttributePath.concat(`.${key}`), localCheckedPath)
            }
          </Collapse>
        </Fragment>
      );
    });
  };

  const renderAttributes = (type) => {
    let attrType, attributes, name = null;
    if (type === 'attributes' || type === 'applications') {
      attrType = type === 'attributes' ? certType : 'application';
      if (certType === 'user') {
        attributes = certifiable_attributes[attrType];
      } else if (managed_objects[certType]) {
        attributes = Object.entries(managed_objects[certType]).map(([key]) => key);
        _.remove(attributes, attr => attr === '_id');
      }
      name = type === 'attributes' ? 'attributeName' : 'name';
    }
    if (!attributes) {
      return null;
    }
    return attributes.map((attr, i) => {
      const attrName = _.isObject(attr) ? attr[name] : attr;
      const valueInChecked = _.get(checked, `${type}.${attrName}`, { selected: false });
      return (
        <Fragment key={attrName}>
          <ListItem button className={classes.listItem} divider={_.get(checked, `${type}.${attrName}.isOpen`)}>
            <ListItemIcon className={classes.checkboxIcon}>
              <Checkbox
                edge='start'
                checked={_.get(checked, `${type}.${attrName}.selected`, false)}
                disabled={valueInChecked.isDisabled}
                onChange={() => handleToggle(type, attrName, 'selected')}
                tabIndex={-1}
                disableRipple
                inputProps={{ 'aria-labelledby': attrName }}
              />
            </ListItemIcon>
            { attributeIsExpandable(type, attrName) ?
              <Fragment>
                <FormControlLabel
                  className={classes.relationshipAttrLabel}
                  onClick={() => handleToggle(type, attrName, 'isOpen')}
                  control={_.get(checked, `${type}.${attrName}.isOpen`) ? <ExpandMore /> : <ChevronRight />}
                  label={<Typography>{attrName}</Typography>}
                />
                { type === 'attributes' && _.get(checked, `${type}.${attrName}.selected`) &&
                <Fragment>
                  <Button 
                    className={classes.selectedCountLink}
                    onClick={(evt) => showTargetedEntitlementsDialog(evt, attrName)}
                  >
                    {t('entitlement-filter-selected-count', { 
                      count: _.get(queryResults, `${attrName}.resultCount`, 0), 
                      entitlement: attrName, 
                    })}
                  </Button>
                  <GovernanceDialog
                    dialogId='EntitlementFilterDialog'
                    onSubmitDialog={() => {
                      props.changeActiveGovernanceDialog(null);
                    }}
                    modalObject={modalObj}
                  />
                </Fragment>
                }
              </Fragment> :
              <ListItemText id={attrName} primary={attrName} />
            }
          </ListItem>
          <Collapse in={_.get(checked, `${type}.${attrName}.isOpen`)} className={classes.collapse}>
            { type === 'attributes' ? getUserEntitlementFilter(attr[name] || attrName) : getNestedApplicationAttributes(attr[name]) }
          </Collapse>
        </Fragment>
      );
    });
  };

  return (
    <div>
      <ExpansionPanel 
        TransitionProps={{ unmountOnExit: true }} 
        square 
        className={classes.root} 
        expanded={expanded.attributes} 
        onChange={handleChange('attributes')}
      >
        <ExpansionPanelSummary className={classes.panelHeader}>
          <Typography>{ expanded.attributes ? <ExpandMore /> : <ChevronRight />} Attributes</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails className={classes.panelContent}>
          { renderAttributes('attributes') }
        </ExpansionPanelDetails>
      </ExpansionPanel>
      { certType === 'user' ?
        <ExpansionPanel 
          TransitionProps={{ unmountOnExit: true }}
          square 
          className={classes.root} 
          expanded={expanded.applications} 
          onChange={handleChange('applications')}
        >
          <ExpansionPanelSummary className={classes.panelHeader}>
            <Typography>{ expanded.applications ? <ExpandMore /> : <ChevronRight />} {t('details-application')}</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails className={classes.panelContent}>
            { renderAttributes('applications') }
          </ExpansionPanelDetails>
        </ExpansionPanel> :
        <ExpansionPanel
          TransitionProps={{ unmountOnExit: true }}
          square 
          className={classes.root} 
          expanded={true} 
        >
          <ExpansionPanelDetails className={classes.panelContent}>
            <FormControlLabel
              // aria-label="Acknowledge"
              onClick={(event) => event.stopPropagation()}
              onFocus={(event) => event.stopPropagation()}
              value={checked.certifyMetadata}
              onChange={() => setChecked({ ...checked, certifyMetadata: !checked.certifyMetadata })}
              control={<Checkbox checked={checked.certifyMetadata} />}
              label={t('certify') + ' ' + t('details-metadata-object')}
            />
          </ExpansionPanelDetails>
        </ExpansionPanel>
      
      }
    </div>
  );
};

EntitlementFilter.propTypes = {
  certType: PropTypes.string,
  certifiable_attributes: PropTypes.object,
  getCertifiableAttributes: PropTypes.func,
  managed_objects: PropTypes.object,
};

const mapStateToProps = state => {
  const {
    config: {
      certifiable_attributes,
      managed_objects,
    },
    generic: {
      modalObject,
    },
  } = state; 

  return {
    certifiable_attributes,
    managed_objects,
    modalObject,
  };
};

const mapDispatchToProps = {
  getCertifiableAttributes,
  getModalObject,
  changeActiveGovernanceDialog,
  changeDialogType,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(EntitlementFilter);