import { CERTS, GENERIC } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import { httpFailure, alert } from './generic.actions.js';
import IDG_CONSTANTS from 'src/utils/constants.js';
import i18n from 'src/i18n.js';

export const getCertifications = () => (dispatch, getState) => {
  dispatch({
    type: CERTS.QUERY_CERTS,
    payload: null,
  });
  let { status, pageNumber, pageSize, sortBy, sortDir, q, type } = getState().certs;
  if(!type){
    return;
  }
  const certEndpoint = getEndpointForCertType(type, status);
  let params = { status, pageNumber, pageSize, sortBy, q };
  if (params.q) {
    params.q = params.q.replace(/\\/g, '\\\\').replace(/"/g, '\\"');
  }
  params.sortBy = sortDir === 'asc' ? `+${sortBy}` : `-${sortBy}`;
  return httpService.request({
    url: certEndpoint,
    method: 'get',
    params,
  }).then(({ data }) => {
    dispatch({
      type: CERTS.QUERY_CERTS_SUCCESS,
      payload: data.result,
    });
  }).catch(error => {
    dispatch({
      type: CERTS.QUERY_CERTS_FAILURE,
      payload: null,
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const cancelCertifications = certIds => (dispatch, getState) => {
  // CANCEL_CERTS
  let requestUrl = '';
  let { status, type } = getState().certs;

  if (status === IDG_CONSTANTS.CERT_STATUS.ACTIVE) {
    requestUrl = IDG_CONSTANTS.IDM_CONTEXT + '/governance/adminCancelCert/' + type;
  } else {
    requestUrl = getEndpointForCertType(type, status);
    requestUrl += '?_action=delete';
  }
  return httpService.request({
    url: requestUrl,
    method: 'post',
    data: {
      ids: certIds,
    },
  }).then(({ data: { result } }) => {
    dispatch(alert(result, 'success'));
    dispatch(getCertifications());
    return result;
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const createCertification = certForm => dispatch => {
  let creationEndpoint = null;
  const objectType = certForm.certObjectType === 'user' ? 'user' : 'object';
  if (certForm.frequency === 'scheduled') {
    creationEndpoint = `scheduledCertification/${objectType}`;
  } else if (certForm.frequency === 'event-based') {
    creationEndpoint = `triggeredCertification/${objectType}`;
  } else {
    creationEndpoint = 'certification/create';
  }
  const successMessage = i18n.t('create-cert-success');
  return httpService.request({
    url: `${IDG_CONSTANTS.IDM_CONTEXT}/governance/${creationEndpoint}`,
    method: 'post',
    params: {
      _action: 'create',
    },
    data: certForm,
  }).then(({ data }) => {
    dispatch(alert(successMessage, 'success'));
    dispatch(getCertifications());
    return data.content;
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const editCertification = (certForm, certId) => dispatch => {
  let creationEndpoint = null;
  const successMessage = i18n.t('update-cert-success');
  const objectType = certForm.certObjectType === 'user' ? 'user' : 'object';
  if (certForm.frequency === 'scheduled') {
    creationEndpoint = `scheduledCertification/${objectType}`;
  } else if (certForm.frequency === 'event-based') {
    creationEndpoint = `triggeredCertification/${objectType}`;
  }
  if (!creationEndpoint || !certId) {
    return null;
  }
  return httpService.request({
    url: `${IDG_CONSTANTS.IDM_CONTEXT}/governance/${creationEndpoint}/${certId}`,
    method: 'post',
    params: {
      _action: 'update',
    },
    data: certForm,
  }).then(({ data }) => {
    dispatch(alert(successMessage, 'success'));
    dispatch(getCertifications());
    return data;
  }).catch(error => {
    dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
    return error;
  });
};

export const changeCertType = (type, noUpdate) => dispatch => {
  dispatch({
    type: CERTS.CHANGE_CERT_TYPE,
    payload: type,
  });
  if(!noUpdate){
    dispatch(getCertifications());
  }
};

export const changeCertStatus = (status, noUpdate) => dispatch => {
  dispatch({
    type: CERTS.CHANGE_CERT_STATUS,
    payload: status,
  });
  if(!noUpdate){
    dispatch(getCertifications());
  }
};

export const changeSortKey = (sortBy, isDesc, noUpdate) => dispatch => {
  dispatch({
    type: CERTS.CHANGE_SORTING,
    payload: {
      sortBy,
      isDesc,
    },
  });
  if (!noUpdate) {
    dispatch(getCertifications());
  }
};

export const changeSearchKey = (q, noUpdate) => dispatch => {
  dispatch({
    type: CERTS.CHANGE_SEARCH_KEY,
    payload: {
      q,
    },
  });
  if(!noUpdate){
    dispatch(getCertifications());
  }
};

export const updatePagination = (params, noUpdate) => dispatch => {
  dispatch({
    type: CERTS.UPDATE_PAGINATION,
    payload: params,
  });
  if(!noUpdate){
    dispatch(getCertifications());
  }
};

const getEndpointForCertType = (type, status) => {
  if (type === IDG_CONSTANTS.CERT_TYPE.USER) {
    switch (status) {
    case IDG_CONSTANTS.CERT_STATUS.ACTIVE:
    case IDG_CONSTANTS.CERT_STATUS.CLOSED:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/adminCertification/user';
    case IDG_CONSTANTS.CERT_STATUS.SCHEDULED:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/scheduledCertification/user';
    case IDG_CONSTANTS.CERT_STATUS.TRIGGERED:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/triggeredCertification/user';
    default:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/adminCertification/user';
    }
  }

  else if (type === IDG_CONSTANTS.CERT_TYPE.OBJECT) {
    switch (status) {
    case IDG_CONSTANTS.CERT_STATUS.ACTIVE:
    case IDG_CONSTANTS.CERT_STATUS.CLOSED:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/adminCertification/object';
    case IDG_CONSTANTS.CERT_STATUS.SCHEDULED:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/scheduledCertification/object';
    case IDG_CONSTANTS.CERT_STATUS.TRIGGERED:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/triggeredCertification/object';
    default:
      return IDG_CONSTANTS.IDM_CONTEXT + '/governance/adminCertification/object';
    }
  }
};

