import { ADMIN_DASHBOARD } from '../actions/actionTypes.js';

const initialState = {
  results: [],
  querying: false,
  queryingSingle: false,
  queryingEntitlement: true,
  queryingModalObject: false,
};

export default (state = initialState, action) => {
  switch(action.type) {
  case ADMIN_DASHBOARD.QUERY_DASHBOARD:
    return {
      ...state,
      querying: true,
    };
  case ADMIN_DASHBOARD.QUERY_DASHBOARD_SUCCESS:
    return {
      ...state,
      querying: false,
      results: action.payload.result.stats,
    };
  case ADMIN_DASHBOARD.QUERY_DASHBOARD_FAILURE:
    return {
      ...state,
      querying: false,
      pagedResult: [],
    };
  case ADMIN_DASHBOARD.QUERY_DASHBOARD_SINGLE:
    return {
      ...state,
      queryingSingle: true,
    };
  case ADMIN_DASHBOARD.QUERY_DASHBOARD_SINGLE_SUCCESS:
    return {
      ...state,
      queryingSingle: false,
      results: action.payload,
    };
  case ADMIN_DASHBOARD.QUERY_DASHBOARD_SINGLE_FAILURE:
    return {
      ...state,
      queryingSingle: false,
      results: action.payload,
    };
  case ADMIN_DASHBOARD.QUERY_ENTITLEMENT:
    return {
      ...state,
      queryingEntitlement: true,
    };
  case ADMIN_DASHBOARD.QUERY_ENTITLEMENT_SUCCESS:
    let entitlement = action && action.payload ? action.payload.result : null;
    return {
      ...state,
      queryingEntitlement: false,
      storedEntitlement: entitlement,
    };
  case ADMIN_DASHBOARD.QUERY_ENTITLEMENT_FAILURE:
    return {
      ...state,
      queryingEntitlement: false,
      storedEntitlement: null,
    };
  case ADMIN_DASHBOARD.QUERY_POLICY:
    return {
      ...state,
      queryingEntitlement: true,
    };
  case ADMIN_DASHBOARD.QUERY_POLICY_SUCCESS:
    let policy = action && action.payload ? action.payload.result : null;
    return {
      ...state,
      queryingEntitlement: false,
      storedPolicy: policy,
    };
  case ADMIN_DASHBOARD.QUERY_POLICY_FAILURE:
    return {
      ...state,
      queryingEntitlement: false,
      storedPolicy: null,
    };
  default:
    return state;
  }
};
