import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import {
  Button,
  Grid,
  LinearProgress,
  TextField,
  Typography,
} from '@material-ui/core';
import { 
  Table as DashTable,
} from 'tabler-react';
import 'tabler-react/dist/Tabler.css';
import { withStyles } from '@material-ui/core/styles';
import DatePicker from 'src/components/presentational/DatePicker';
import moment from 'moment';
import styles from './styles';

class ViewViolation extends Component {

  state = {
    extraView: '',
    comments: null,
    expirationDate: null,
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  componentDidMount() {
    this.setState({ extraView: '' });
  }

  componentDidUpdate() {

  }

  handleAction = evt => {
    const { violation } = this.props;
    let action = evt.currentTarget.name;
    if ((action === 'approve' || action === 'comment') && this.state.extraView !== action) {
      this.setState({ extraView: action });
    }
    else if (action === 'submit') {
      this.props.onSubmitDialog(evt, violation._id, this.state.extraView, this.state.comments, this.state.expirationDate);
      if (this.state.expirationDate) {
        this.props.handleClose();
      }
      else {
        this.setState({ 
          extraView: '',
          comments: null,
          expirationDate: null,
        });
      }
    } 
    else if (action === 'remediate') {
      this.props.onSubmitDialog(evt, violation._id, action);
      this.props.handleClose();
    }
    else {
      this.setState({ 
        extraView: '',
        comments: null,
        expirationDate: null,
      });
    }
  }

  onUpdateComments = event => {
    if (event.target && event.target.value) {
      this.setState({ comments: event.target.value });
    }
  }

  onUpdateExpirationDate = (target, value) => {
    if (value && value._d) {
      this.setState({ expirationDate: value._d });
    }
  }

  getViolationDetailsRow = (label, value) => {
    const { classes, t } = this.props;
    return (
      <DashTable.Row className={classes.detailsRow}>
        <DashTable.Col>{t(label) + ':'}</DashTable.Col>
        <DashTable.Col>{value}</DashTable.Col>
      </DashTable.Row>
    );
  }

  render() {
    const { t, classes, violation, isOwner } = this.props;

    return violation ? (
      <Grid container className={classes.violationViewContainer}>
        <Grid className={classes.detailsContainer} item xs={12}>
          <DashTable>
            <DashTable.Body>
              {this.getViolationDetailsRow('target-user', violation.targetUser ? violation.targetUser.displayName : null)}
              {this.getViolationDetailsRow('policy-owner',violation.owner ? violation.owner.displayName : null)}
              {this.getViolationDetailsRow('violation-detected', moment(violation.startDate).format('L'))}
              { violation.status === 'in-progress' &&
                this.getViolationDetailsRow('expiration-date', moment(violation.expirationDate).format('L'))}
              { (violation.status === 'exception' || violation.status === 'exception-expired') &&
                this.getViolationDetailsRow('exception-start-date', moment(violation.exceptionStartDate).format('L'))}
              { (violation.status === 'exception' || violation.status === 'exception-expired') &&
                this.getViolationDetailsRow('exception-end-date', moment(violation.exceptionEndDate).format('L'))}
              { violation.status !== 'in-progress' && violation.status !== 'exception' &&
                this.getViolationDetailsRow('completed-by', violation.completedBy ? violation.completedBy.displayName : null)}
              { violation.status !== 'in-progress' && violation.status !== 'exception' &&
                this.getViolationDetailsRow('completion-date', moment(violation.completionDate).format('L'))}
              {this.getViolationDetailsRow('policy-name', violation.policyName)}
              {this.getViolationDetailsRow('policy-description', violation.policyDescription)}
            </DashTable.Body>
          </DashTable>
        </Grid>
        { violation.comments &&
            <Grid item xs={12} className={classes.commentsContainer}>
              <DashTable>
                <DashTable.Header>
                  <DashTable.Row>
                    <DashTable.ColHeader>{t('date')}</DashTable.ColHeader>
                    <DashTable.ColHeader>{t('comment')}</DashTable.ColHeader>
                    <DashTable.ColHeader>{t('by')}</DashTable.ColHeader>
                  </DashTable.Row>
                </DashTable.Header>
                <DashTable.Body>
                  {violation.comments.map(comment => (
                    <DashTable.Row key={comment.timeStamp}>
                      <DashTable.Col>{moment(comment.timeStamp).format('lll')}</DashTable.Col>
                      <DashTable.Col>{comment.comment}</DashTable.Col>
                      <DashTable.Col>{comment.user ? comment.user.displayName : null}</DashTable.Col>
                    </DashTable.Row>
                  ))
                  }
                </DashTable.Body>
              </DashTable>
            </Grid>
        }
        { isOwner &&
            violation.status === 'in-progress' &&
            <Grid item xs={12} className={classes.violationButtonContainer}>
              <Button
                name='comment'
                className={classes.violationButton}
                variant='contained'
                color='primary'
                onClick={this.handleAction}
                disabled={this.state.extraView !== '' && this.state.extraView !== 'comment'}
              >
                {this.state.extraView === 'comment' ? t('btn-hide-comment') : t('btn-add-comment')}
              </Button>
              <Button
                name='remediate'
                className={classes.violationButton}
                variant='contained'
                color='primary'
                onClick={this.handleAction}
                disabled={this.state.extraView !== ''}
              >
                {t('btn-remediate')}
              </Button>
              <Button
                name='approve'
                className={classes.violationButton}
                variant='contained'
                color='primary'
                onClick={this.handleAction}
                disabled={this.state.extraView !== '' && this.state.extraView !== 'approve'}
              >
                {this.state.extraView === 'approve' ? t('btn-hide-exception') : t('btn-grant-exception')}
              </Button>
            </Grid>
        }
        {
          this.state.extraView !== '' &&
            <Grid 
              container 
              className={classes.violationFormContainer}
              spacing={4}
            >
              <Grid item xs={12} sm={12}>
                <TextField
                  label={this.state.extraView === 'approve' ? t('reason') : t('comment')}
                  type='text'
                  variant='outlined'
                  name='commentInput'
                  id='commentInput'
                  fullWidth
                  onChange={(evt) => this.onUpdateComments(evt)}
                />
              </Grid>
              { this.state.extraView === 'approve' &&
                <Grid item xs={12} sm={12}>
                  <Grid container className={classes.exceptionContainer}>
                    <Typography className={classes.exceptionLabel}>
                      {t('exception-expiration-date')}: 
                    </Typography>
                    <DatePicker 
                      name='dateInput'
                      field='dateInput'
                      variant='outlined'
                      disablePast={true}
                      disableFuture={false}
                      selectedDate={null}
                      onChange={(evt, target, value) => this.onUpdateExpirationDate(target, value)}
                    />
                  </Grid>
                </Grid>
              }
              <Grid>
                <Button 
                  variant='contained' 
                  name='submit'
                  size='large' 
                  type="submit"
                  color='primary'
                  className={classes.violationButton}
                  onClick={this.handleAction}
                  disabled={!this.state.comments || (!this.state.expirationDate && this.state.extraView === 'approve')}
                >
                  {t('btn-submit-' + this.state.extraView)}
                </Button>
              </Grid>
            </Grid>
        } 
      </Grid>
    ) : (<LinearProgress />);
  }
}

ViewViolation.propTypes = {
  onSubmitDialog: PropTypes.func,
  handleClose: PropTypes.func,
  dialogType: PropTypes.string,
};

export default compose(
  connect(),
  withTranslation(),
  withStyles(styles),
)(ViewViolation);
