import { AUTH } from '../actions/actionTypes.js';

const initialState = {
  authModule: '',
  isLoggedIn: false,
  authenticationId: '',
  authorization: {},
  roles: [],
  error: false,
  message: '',
};

export default (state = initialState, action) => {
  switch(action.type) {
  case AUTH.FETCH_AUTH_MODULE_SUCCESS:
    return {
      ...state,
      authModule: action.payload,
    };
  case AUTH.FETCH_AUTH_MODULE_FAILURE:
    return {
      ...state,
      message: `${action.payload.code}: ${action.payload.message}`,
      error: true,
    };
  case AUTH.AUTHENTICATION_SUCCESS:
    // eslint-disable-next-line no-case-declarations
    const { authenticationId, authorization, roles } = action.payload;
    return {
      ...state,
      authenticationId,
      authorization,
      roles,
      isLoggedIn: true,
    };
  case AUTH.AUTHENTICATION_FAILURE:
    return {
      ...state,
      isLoggedIn: false,
      message: `${action.payload.code}: ${action.payload.message}`,
      error: true,
    };
  case AUTH.LOGOUT_SUCCESS:
    return {
      ...initialState,
      authModule: state.authModule,
    };
  case AUTH.LOGOUT_FAILURE:
    return {
      ...state,
      message: `${action.payload.code}: ${action.payload.message}`,
      error: true,
    };
  default:
    return state;
  }
};
