import * as yup from 'yup';
import _ from 'lodash';
import i18n from 'src/i18n.js';
import moment from 'moment';
import store from 'src/store';

yup.addMethod(yup.date, 'format', function() {
  return this.transform(function(value, originalValue) {
    if (this.isType(value)) return value;

    value = moment(originalValue);

    return value.isValid() ? value.toDate() : new Date('');
  });
});

const validateEscalationDuration = (compareDuration, toCheckDuration) => {
  if (compareDuration && _.isInteger(compareDuration.unit) && compareDuration.period.length) {
    if (toCheckDuration.unit && toCheckDuration.period) {
      if (compareDuration.period === toCheckDuration.period) {
        return toCheckDuration.unit < compareDuration.unit;
      } else if (compareDuration.period === 'weeks') {
        return toCheckDuration.unit < compareDuration.unit * 7;
      } else if (compareDuration.period === 'days') {
        return toCheckDuration.unit * 7 < compareDuration.unit;
      }
    } else if (!toCheckDuration.unit && !toCheckDuration.period) {
      return true;
    } else {
      return false;
    }
  }
  return true;
};

const validateExpression = (expr) => {
  const objSchema = yup.object({
    targetName: yup.string().required(),
    targetValue: yup.string().required(),
  });
  if (_.isArray(expr)) {
    const schema = yup.array().of(yup.object({
      operator: yup.string().oneOf(['ALL','AND','OR','NOT','EQUALS','CONTAINS','LINKEDTO']).required(),
      operand: yup.lazy((value) => {
        return _.isArray(value) ? schema : objSchema;
      }),
    }));
    return schema;
  } else {
    return objSchema;
  }
};

