function getTargetTypeOptionsFromStore(certObjectType) {
  let targetTypeOptions = [{ key: '', displayName: '' }];
  const config = store.getState().config;
  if (certObjectType && certObjectType === 'user') {
    targetTypeOptions.push({ key: 'user', displayName: 'user' });
  } else if (config && config.managed_objects) {
    Object.entries(config.managed_objects).map(([key]) => {
      if (key !== 'user') {
        targetTypeOptions.push({ key, displayName: key });
      }
    });
  }
  return targetTypeOptions;
}

function stageSchema(frequency) {
  let objSchema = yup.object().shape({
    name: yup.string().required(),
    entitlementFilter: yup.object().required(),
    useRiskLevel: yup.bool(),
    certifierType: yup.string().oneOf(['user', 'authzGroup', 'manager', 'prevManager', 'entitlementOwner', 'glossaryKey']).required(),
    certifierName: yup.string().when('certifierType', (certifierType, schema) => {
      return certifierType === 'user' || certifierType === 'authzGroup' ? schema.required() : schema.notRequired();
    }),
    certifierKey: yup.string().when('certifierType', (certifierType, schema) => {
      return certifierType === 'glossaryKey' ? schema.required() : schema.notRequired();
    }),
    riskLevel: yup.object().shape({
      Low: yup.bool(),
      Medium: yup.bool(),
      High: yup.bool(),
    }).when('useRiskLevel', (useRiskLevel, schema) => {
      if (useRiskLevel) {
        return schema.test({
          name: 'risk-level-valid',
          test: function(value) {
            const { Low, Medium, High } = value;
            if (!Low && !Medium && !High) {
              return this.createError({ 
                message: i18n.t('stage-builder-risk-level-error'),
              });
            }
            return true;
          },
        });
      }
      return schema;
    }),
    escalationSchedule: yup.array().of(yup.object().shape({
      unit: yup.number().typeError(i18n.t('datepicker-invalid-expiration-unit')).min(1, i18n.t('datepicker-invalid-expiration-unit')).required(i18n.t('form-error-required')),
      period: yup.string().oneOf(['days', 'weeks'], i18n.t('datepicker-invalid-expiration-period')).required(i18n.t('datepicker-invalid-expiration-period')),
      escalationType: yup.string().oneOf(['user', 'authzGroup', 'manager']).required(),
      escalationId: yup.object().when('escalationType', {
        is: (val) => val === 'user' || val === 'authzGroup',
        then: yup.object().required(),
        otherwise: yup.object().nullable(),
      }),
    })).test({
      name: 'stage-escalation-valid',
      test: function(value) {
        for (let i = value.length - 1; i >= 0; i--) {
          const escSchedule = value[i];
          var prevIdx = i - 1;
          if (prevIdx >= 0 && escSchedule.unit && escSchedule.period) {
            const isValid = validateEscalationDuration(value[prevIdx], escSchedule);
            if (!isValid) {
              return this.createError({ 
                path: `${this.path}[${i}].unit`, 
                message: i18n.t('datepicker-invalid-subsequent-escalation'),
              });
            }
          }
        }
        return true;
      },
    }),
  });
  
  let deadlineSchema = null;

  if (frequency === 'ad-hoc') {
    deadlineSchema = yup.object().shape({
      deadline: yup.date()
        .required(i18n.t('form-error-required')).min(moment(), i18n.t('datepicker-min-expiration-date')),
    });
  } else {
    deadlineSchema = yup.object().shape({
      deadlineDuration: yup.object().shape({
        unit: yup.number().typeError(i18n.t('datepicker-invalid-expiration-unit')).required(),
        period: yup.string().min(1).required(i18n.t('datepicker-invalid-expiration-period')),
      }),
    });
  }

  if (deadlineSchema) {
    objSchema = objSchema.concat(deadlineSchema);
  }

  return objSchema;
}

export function createYupSchema(schema, config) {
  const { id, validationType, validations = [] } = config;
  if (!yup[validationType]) {
    return schema;
  }
  let validator = yup[validationType]();
  validations.forEach(validation => {
    const { params, type } = validation;
    if (!validator[type]) {
      return;
    }
    validator = validator[type](...params);
  });
  schema[id] = validator;
  return schema;
}