import { DASHBOARD} from '../actions/actionTypes.js';
import IDG_CONSTANTS from 'src/utils/constants.js';

const initialState = {
  type: IDG_CONSTANTS.CERT_TYPE.USER,
  status: IDG_CONSTANTS.CERT_STATUS.ACTIVE,
  results: [],
  pageNumber: 1,
  pageSize: 10,
  sortBy: 'deadline',
  sortDir: 'desc',
  q: '',
  querying: false,
};

export default (state=initialState, action) => {
  switch(action.type){
  case DASHBOARD.QUERY_DASHBOARD:
    return{
      ...state,
      querying: true,
    };
  case DASHBOARD.QUERY_DASHBOARD_SUCCESS:
    return{
      ...state,
      querying: false,
      results: action.payload,
    };
  case DASHBOARD.QUERY_DASHBOARD_FAILURE:
    return{
      ...state,
      querying: false,
      results: action.payload,
    };
  case DASHBOARD.UPDATE_STATUS:
    return{
      ...state,
      pageNumber: 1,
      status: action.payload,
    };
  case DASHBOARD.UPDATE_TYPE:
    return{
      ...state,
      pageNumber: 1,
      type:action.payload,
    };
  case DASHBOARD.CHANGE_SEARCH_KEY:
    return{
      ...state,
      q: action.payload.q,
    };
  case DASHBOARD.CHANGE_SORTING:
    return {
      ...state,
      sortBy: action.payload.sortBy,
      sortDir: action.payload.isDesc ? 'desc' : 'asc',
    };
  case DASHBOARD.UPDATE_PAGINATION:
    let { pageNumber, pageSize, sortBy, status } = action.payload;
    return {
      ...state,
      pageNumber,
      pageSize,
      sortBy,
      status,
    };
  case DASHBOARD.ACT_ON_VIOLATION:
    return {

    };
  case DASHBOARD.COMMENT_VIOLATION:
    return {

    };
  default:
    return state;
  }
};