import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { KeyboardDatePicker } from '@material-ui/pickers';
import moment from 'moment';

const DatePicker = props => {
  const [selectedDate, handleDateChange] = React.useState(moment());
  const [input, setInput] = React.useState('');
  const format = moment.localeData()._longDateFormat.L;
  const { 
    disablePast, 
    disableFuture, 
    error,
    field, 
    value, 
  } = props;

  React.useEffect(() => {
    handleDateChange(value);
  }, [value]);

  const handleChange = (date, input) => {
    setInput(input);
    const id = _.isString(field) ? field : field.id;
    props.onChange(event, id, date);
  };

  return (
    <KeyboardDatePicker
      disablePast={disablePast}
      disableFuture={disableFuture}
      variant='dialog'
      inputVariant='outlined'
      format={format}
      placeholder={format}
      error={error}
      helperText={null}
      value={selectedDate}
      inputValue={input}
      onChange={handleChange}
    />
  );
};

DatePicker.propTypes = {
  disablePast: PropTypes.bool, 
  disableFuture: PropTypes.bool, 
  error: PropTypes.bool,
  field: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
  onChange: PropTypes.func,
  value: PropTypes.object, 
};

export default DatePicker;