import certs from './certs.reducer.js';
import certlist from './certlist.reducer.js';
import policies from './policies.reducer.js';
import summary from './summary.reducer.js';
import async_fields from './async_fields.reducer.js';
import config from './config.reducer.js';
import dashboard from './dashboard.reducer.js';

export default combineReducers({
  generic,
  auth,
  admin_dashboard,
  certs,
  certlist,
  policies,
  summary,
  async_fields,
  config,
  dashboard,
});

