import React from 'react';
import clsx from 'clsx';
import {
  TableRow,
  TableCell,
  Typography,
  Switch,
} from '@material-ui/core';

export function BooleanField(props) {
  const {field, handleChange, values, t, classes} = props;
  const fieldHeader = <Typography>{t(field.id)}:</Typography>;
  return(
    <TableRow className={classes.settingGrid} key={field.id}>
      <TableCell key={field.id+'label'} className={classes.headerCell}>
        {fieldHeader}
      </TableCell>
      <TableCell key={field.id+'input'} className={clsx(classes.headerCell, classes.menuHeaderCell)}>
        <Switch name={field.id} 
          onChange={handleChange} 
          checked={Boolean(values[field.id])}
          value={Boolean(values[field.id])}
        />
      </TableCell>
    </TableRow>
  );
}
