import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import {
  Grid,
  List,
  ListItem,
  ListItemText,
  ListSubheader,
  Button,
  Paper,
  TextField,
  Typography,
} from '@material-ui/core';
import {
  ArrowBack,
  ArrowForward,
} from '@material-ui/icons';
import styles from './styles';
import { getPoliciesForScan } from 'src/store/actions/async_fields.actions';

const TransferList = props => {
  const [policyList, setPolicyList] = React.useState([]); // eslint-disable-line no-unused-vars
  const [left, setLeft] = React.useState([]);
  const [right, setRight] = React.useState([]);
  const [input, setInput] = React.useState('');
  const [filteredByValue, setFilteredByValue] = React.useState(false);
  const { t, classes, value, handleChange, scanPolicies } = props;
  const filterPolicies = React.useRef(_.debounce(props.getPoliciesForScan, 250));
  
  React.useEffect(() => {
    filterPolicies.current(input);
  }, [input]);

  React.useEffect(() => {
    if (props.value && props.value.length > 0) {
      const value = props.value.map(policy => policy.replace('managed/policy/', ''));
      const policyList = scanPolicies.map(policy => ({ _id: policy._id, displayName: policy.name }));
      const selected = _.filter(policyList, (policy) => value.indexOf(policy._id) >= 0);
      setRight(selected);
      setFilteredByValue(true);
    }
  }, []);

  React.useEffect(() => {
    const policyList = scanPolicies.map(policy => ({ _id: policy._id, displayName: policy.name }));
    let selected = right;
    if (props.value && props.value.length > 0 && !filteredByValue) {
      const value = props.value.map(policy => policy.replace('managed/policy/', ''));
      selected = _.filter(policyList, (policy) => value.indexOf(policy._id) >= 0);
      setRight(selected);
      setFilteredByValue(true);
    }
    setPolicyList(policyList);
    setLeft(_.differenceBy(policyList, selected, '_id'));
  }, [scanPolicies]);

  React.useEffect(() => {
    if (!_.isEqual(value, right)) {
      handleChange(right);
    }
  }, [right]);

  const handleInputChange = (evt) => {
    setInput(evt.target.value);
  };

  const handleToggle = (value, array) => () => {
    let fromArray, fromUpdate, toArray, toUpdate = null;
    if (array === 'left') {
      fromArray = _.cloneDeep(left);
      fromUpdate = setLeft;
      toArray = _.cloneDeep(right);
      toUpdate = setRight;
    } else {
      fromArray = _.cloneDeep(right);
      fromUpdate = setRight;
      toArray = _.cloneDeep(left);
      toUpdate = setLeft;
    }
    
    fromUpdate(_.filter(fromArray, (policy) => policy._id !== value._id));
    toUpdate(_.sortBy(toArray.concat(value), 'displayName'));
  };

  const handleAllRight = () => {
    setRight(right.concat(left));
    setLeft([]);
  };

  const handleAllLeft = () => {
    setLeft(left.concat(right));
    setRight([]);
  };

  const customList = (policies, array) => (
    <Paper className={classes.paper}>
      <List 
        dense 
        component='div' 
        role='list'
        subheader={
          <ListSubheader className={classes.listHeader}>
            <Typography className={classes.headerTitle} component='span'>{ array === 'left' ? t('policies') : t('selected')} </Typography>
            {`${policies.length}/${scanPolicies.length}`}
          </ListSubheader>
        }
      >
        {policies.map((policy) => {
          const labelId = `transfer-list-item-${policy.displayName}-label`;

          return (
            <ListItem key={policy._id} role='listitem' button onClick={handleToggle(policy, array)}>
              <ListItemText id={labelId} primary={policy.displayName} />
            </ListItem>
          );
        })}
        <ListItem />
      </List>
    </Paper>
  );
  
  return (
    <Fragment>
      <Grid container>
        <Grid item xs={5}>
          <TextField
            id='policy-filter'
            label={t('search-filter')}
            placeholder={t('filter')}
            fullWidth
            margin='normal'
            value={input} 
            onChange={handleInputChange}
          />
        </Grid>
      </Grid>
      <Grid container justify='center' alignItems='center' className={classes.root}>
        <Grid item xs={12} sm={5}>{customList(left, 'left')}</Grid>
        <Grid item xs={12} sm={2}>
          <Grid container direction='column' alignItems='center'>
            <Button
              variant='outlined'
              size='small'
              className={classes.button}
              onClick={handleAllRight}
              disabled={left.length === 0}
              aria-label='move all right'
            >
              <ArrowForward />
            </Button>
            <Button
              variant='outlined'
              size='small'
              className={classes.button}
              onClick={handleAllLeft}
              disabled={right.length === 0}
              aria-label='move all left'
            >
              <ArrowBack />
            </Button>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={5}>{customList(right, 'right')}</Grid>
      </Grid>
    </Fragment>
  );
};

TransferList.propTypes = {
  field: PropTypes.object,
  handleChange: PropTypes.func,
};

const mapStateToProps = state => {
  const {
    async_fields,
  } = state;

  return async_fields;
};

const mapDispatchToProps = {
  getPoliciesForScan,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(TransferList);