import { POLICIES } from '../actions/actionTypes.js';

const initialState = {
  type: null,
  // status: IDG_CONSTANTS.CERT_STATUS.ACTIVE,
  results: [],
  activeScans: [],
  pageNumber: 1,
  pageSize: 10,
  totalPagedResults: 0,
  sortBy: 'name',
  sortDir: 'asc',
  // q: '',
  querying: false,
  shouldQueryActiveScans: false,
  reactiveConfig: null,
};

export default (state = initialState, action) => {
  switch(action.type) {
  case POLICIES.QUERY_POLICIES:
    return {
      ...state,
      querying: true,
    };
  case POLICIES.QUERY_POLICIES_SUCCESS:
    return {
      ...state,
      querying: false,
      ...action.payload,
      // results: action.payload,
    };
  case POLICIES.QUERY_POLICIES_FAILURE:
    return {
      ...state,
      querying: false,
      results: [],
      totalPagedResults: 0,
    };
  case POLICIES.CHANGE_SORTING:
    return {
      ...state,
      sortBy: action.payload.sortBy,
      sortDir: action.payload.isDesc ? 'desc' : 'asc',
    };
  case POLICIES.CHANGE_SEARCH_KEY:
    return {
      ...state,
      q: action.payload.q,
    };
  case POLICIES.UPDATE_PAGINATION:
    let { pageNumber, pageSize, sortBy, status } = action.payload;
    return {
      ...state,
      pageNumber,
      pageSize,
      sortBy,
      status,
    };
  case POLICIES.CHANGE_POLICY_TYPE:
    return {
      ...state,
      type: action.payload,
      results: [],
      totalPagedResults: 0,
    };
  case POLICIES.CHANGE_ACTIVE_SCANS_STATUS:
    return {
      ...state,
      shouldQueryActiveScans: action.payload,
    };
  case POLICIES.QUERY_ACTIVE_SCANS:
    return {
      ...state,
      activeScans: action.payload,
    };
  case POLICIES.GET_REACTIVE_SCAN_CONFIG:
    return {
      ...state,
      reactiveConfig: action.payload,
    };
  default:
    return state;
  }
};

