import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

import themeToStyles from './styles.js';

class Frame extends React.Component {
  render() {
    return (
      <iframe
        title={this.props.title}
        src={this.props.url}
        className={clsx({
          [this.props.classes['iframe']]: true,
          [this.props.classes['open']]: this.props.isDrawerOpen,
        })}
        // sandbox the iframe for maximum security, only turning on the things we know we need for full functionality
        sandbox={'allow-same-origin allow-scripts'}
      ></iframe>
    );
  }
}

Frame.propTypes = {
  // for accessibility reasons / for screen readers, this is required
  title: PropTypes.string.isRequired,
};

const mapStateToProps = state => {
  const {
    generic: { isDrawerOpen },
  } = state;
  return  {
    isDrawerOpen,
  };
};

const mapDispatchToProps = {
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(themeToStyles)(Frame));
