import React from 'react';
import {
  Grid,
  TableCell,
  TableRow,
  Typography,
} from '@material-ui/core';
import {Slider} from '@material-ui/core';

export function RiskLevel(props){
  const {field, classes, handleChange, values} = props;
  return(
    <TableRow>
      <TableCell className={classes.innerSettingsCell}>
        <Grid container direction={'column'} className={classes.slider}>
          <Grid item>
            <Slider
              name={field.id}
              value={values[field.id]} 
              aria-labelledby="continuous-slider"
              onChange={handleChange}
              min={1}
              max={9}
              valueLabelDisplay="on"/>
          </Grid>

          <Grid item>
            <Typography>Low: {values[field.id][0] == 1 ? 1 : '1 - ' + values[field.id][0]}</Typography>
          </Grid>
          <Grid item>
            <Typography>Medium: {values[field.id][1] - values[field.id][0] === 1 ?  values[field.id][1] : values[field.id][0] + 1 + ' - ' + values[field.id][1]}</Typography>
          </Grid>
          <Grid item>
            <Typography>High: {values[field.id][1] + 1 == 10 ? 10 : values[field.id][1] + 1 + ' - 10'}</Typography> 
          </Grid>
        </Grid>
      </TableCell>
    </TableRow>
  );
}