import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import _ from 'lodash';
import { v4 as uuidv4 } from 'uuid';
import clsx from 'clsx';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormLabel,
  Grid,
  IconButton,
  MenuItem,
  OutlinedInput,
  Select,
  Typography,
} from '@material-ui/core';
import { Add, Close, Remove } from '@material-ui/icons';
import { withStyles } from '@material-ui/core/styles';
import AsyncSelect from 'src/components/presentational/AsyncSelect';
import { getManagedObjectConfig } from 'src/store/actions/config.actions';
import styles from './styles';

class ExpressionBuilderTree extends Component {
  state = {
    expression: {},
    operators: [
      {
        text: this.props.t('expr-builder-and'),
        value: 'and',
        nested: true,
      },
      {
        text: this.props.t('expr-builder-or'),
        value: 'or',
        nested: true,
      },
      {
        text: this.props.t('expr-builder-not'),
        value: 'not',
        nested: true,
      },
      {
        text: this.props.t('expr-builder-attr', { type: this.props.targetType }),
        value: 'ATTR',
        nested: false,
      },
    ],
    operands: {
      'and': [{'operator': '', 'operand': []}],
      'or': [{'operator': '', 'operand': []}],
      'not': [{'operator': '', 'operand': []}],
      'ATTR': { 'operator': '', 'field': '', 'value': '' },
    },
    isValid: false,
  }; // state

  componentDidMount() {
    const { expression } = this.props;
    this.setState({expression});
  }

  componentDidUpdate(prevProps, prevState) {
    const { managed_objects, targetType } = this.props;
    const { managedObjAttrs } = this.state;

    if (targetType && (!managedObjAttrs || targetType !== prevProps.targetType)) {
      if (targetType === 'object' || !managed_objects[targetType]) {
        return null;
      }
      const attrs = Object.entries(managed_objects[targetType]).map(([key, value]) => {
        const objAttribute = {
          value: key,
          label: value.title || key,
          type: value.type,
          isVirtual: value.isVirtual,
        };
        return objAttribute;
      });
      _.remove(attrs, attr => (attr.value === '_id' || attr.isVirtual));
      this.setState({ managedObjAttrs: attrs });
    }
  }

  getOperands = operator => {
    switch (operator) {
    case 'and':
    case 'or':
    case 'not':
      return [{'operator': '', 'operand': []}];
    case 'ATTR':
      return { 'comparator': '', 'field': '', 'value': '' };
    default:
      return [];
    }
  };

  getSearchTypeFromTarget = (targetKey, targetVal) => {
    let searchType = 'text';
    if (targetKey === 'manager' || targetKey === 'userName' || this.props.targetType !== 'user' && targetKey === 'name') {
      searchType = 'typeahead';
    } else if (targetKey) {
      if (['array', 'relationship'].indexOf(targetVal.type) >= 0) {
        searchType = 'typeahead';
      }
    }
    return searchType;
  };

  updateExpression = (newExpression, path) => {
    let expression = null;
    if (path) {
      expression = _.cloneDeep(this.state.expression);
      _.set(expression, path, newExpression);
    } else {
      expression = newExpression;
    }
    const isValid = this.validateExpression(expression);
    this.setState({ expression, isValid });
    this.props.setIsValid(isValid);
    this.props.handleChange(expression);
  };

  removeNode = path => {
    let expression = _.cloneDeep(this.state.expression);
    const index = _.lastIndexOf(path, '.');
    const targetPath = path.slice(0, index);
    const targetNode = path.slice(index + 1);
    if (targetPath && targetNode) {
      let operand = _.get(expression, targetPath);
      _.pullAt(operand, targetNode);
      _.set(expression, targetPath, operand);
    }
    const isValid = this.validateExpression(expression);
    this.setState({ expression, isValid });
    this.props.setIsValid(isValid);
    this.props.handleChange(expression);
  };

  addNode = path => {
    let expression = _.cloneDeep(this.state.expression);
    let newOperand = { operator: '', operand: []};
    if (path) {
      let operand = _.get(expression, `${path}.operand`);
      operand.push(newOperand);
      _.set(expression, `${path}.operand`, operand);
    } else {
      expression.operand.push(newOperand);
    }
    const isValid = this.validateExpression(expression);
    this.setState({ expression, isValid });
    this.props.setIsValid(isValid);
    this.props.handleChange(expression);
  };

  validateExpression = expr => {
    let expression = expr || this.state.expression;
    if (!expression.operator && !expression.comparator) {
      return false;
    } else if (expression.operator && ['and', 'or', 'not'].indexOf(expression.operator) >= 0) {
      return _.every(expression.operand, this.validateExpression);
    } else if (_.has(expression, 'operand.field')) {
      const { operand } = expression;
      return (operand.comparator && operand.field && (operand.comparator === 'changed' || operand.value));
    }
    return false;
  }

  render() {
    const { t, targetType, theme } = this.props;
    const { expression, managedObjAttrs, operators, operands } = this.state;
    return (
      <EventExpressionNode
        addNode={this.addNode}
        expression={expression}
        getOperands={this.getOperands}
        objAttributes={managedObjAttrs}
        operators={operators}
        operands={operands}
        removeNode={this.removeNode}
        t={t}
        theme={theme}
        targetType={targetType}
        updateExpression={this.updateExpression}
      />
    );
  }
}

const EventExpressionNode = withStyles(theme => ({
  expressionNode: props => {
    return {
      maxWidth: '100%',
      paddingTop: theme.spacing(),
      marginLeft: _.has(props.expression, 'field') ? 0 : theme.spacing(1.5),
      borderLeft: props.expression.operator && ['and', 'or', 'not'].indexOf(props.expression.operator) >= 0 ? `3px solid ${theme.palette.forgerock.blue}` : 'none',
    };
  },
  nodeButton: {
    color: theme.palette.forgerock.white,
    margin: theme.spacing(),
  },
  addButton: {
    backgroundColor: theme.palette.forgerock.blue,
  },
  removeButton: {
    marginTop: 0,
    backgroundColor: theme.palette.forgerock.danger,
  },
  removeButtonGrid: {
    display: 'flex',
  },
  operatorText: {
    marginTop: theme.spacing(),
    marginBottom: theme.spacing(),
    position: 'relative',
    right: theme.spacing(),
    backgroundColor: theme.palette.forgerock.white,
    textTransform: 'uppercase',
  },
}))((props) => {
  const { classes, expression, getOperands, objAttributes, operators, path, t, targetType, updateExpression } = props;

  const handleChange = event => {
    const newOperator = _.find(operators, (op) => op.value === event.target.value);
    if (newOperator) {
      const operand = getOperands(newOperator.value);
      const newExpression = {
        operator: newOperator.value,
        operand,
      };
      updateExpression(newExpression, path);
    }
  };

  const handleLeafNodeChange = evt => {
    const { name, value } = evt.target;
    let newExpression = {
      ...expression,
      [name]: value,
    };
    if (evt.target.value === 'changed') {
      newExpression.value = '';
    } else if (expression.field && name === 'field') {
      newExpression = {
        ...newExpression,
        comparator: '',
        value: '',
      };
    }
    updateExpression(newExpression, path);
  };

  const handleTypeaheadChange = (evt, name, val) => {
    const newExpression = {
      ...expression,
      [name]: val && val._id ? val._id : val,
    };
    updateExpression(newExpression, path);
  };

  const isNested = op => {
    const operatorData = _.find(operators, operator => operator.value === op);
    return operatorData ? operatorData.nested : false;
  };

  const getMenuItemsForComparator = () => {
    const selected = _.find(objAttributes, attr => attr.value === expression.field);
    if (!selected) {
      return <MenuItem value=''></MenuItem>;
    } else if (selected.type === 'relationship') {
      return (
        <MenuItem value='changed'>{t('expr-builder-changed')}</MenuItem>
      );
    } else if (selected.type === 'array') {
      return ['contains', 'contained'].map(comp => <MenuItem key={uuidv4()} value={comp}>{t(`expr-builder-${comp}`)}</MenuItem>);
    } else {
      return ['is', 'was', 'changed'].map(comp => <MenuItem key={uuidv4()} value={comp}>{t(`expr-builder-${comp}`)}</MenuItem>);
    }
  };

  const getInputFromSearchType = () => {
    const selected = _.find(objAttributes, attr => attr.value === expression.field);
    if (selected && selected.type === 'array') {
      return (
        <AsyncSelect 
          id={uuidv4()}
          name='value'
          fullWidth={true}
          variant='outlined'
          userNameOnly={true}
          objectToTarget={targetType}
          targetType={expression.field}
          value={expression.value}
          onChange={handleTypeaheadChange}
        />
      );
    } else if (selected && selected.type === 'relationship') {
      return null;
    }
    return (
      <OutlinedInput
        id={uuidv4()}
        name='value'
        fullWidth={true}
        variant='outlined'
        value={expression.value}
        onChange={handleLeafNodeChange}
        rowsMax='4'
      />
    );
  };

  return (
    <Grid container spacing={1} className={classes.expressionNode}>
      { objAttributes && _.has(expression, 'field') ?
        <Fragment>
          <Grid item xs={3}>
            <Select
              name='field'
              value={expression.field}
              onChange={handleLeafNodeChange}
              variant='outlined'
              fullWidth={true}
            >
              { objAttributes && objAttributes.map(attr => (
                <MenuItem key={uuidv4()} value={attr.value}>{attr.label}</MenuItem>
              ))}
            </Select>
          </Grid>
          { expression.field &&
        <Grid item xs={3}>
          <Select
            name='comparator'
            fullWidth={true}
            onChange={handleLeafNodeChange}
            variant='outlined'
            value={expression.comparator}
          >
            { getMenuItemsForComparator() }
          </Select>
        </Grid>
          }
          { expression.field && expression.comparator && expression.comparator !== 'changed' &&
        <Grid item xs={3}>
          { getInputFromSearchType() }
        </Grid>
          }
        </Fragment> :
        <Grid item xs={10}>
          <Select
            name='operator'
            value={expression.operator || ''}
            onChange={handleChange}
            variant='outlined'
            fullWidth={true}
          >
            { operators && operators.map(op => (
              <MenuItem key={op.value} value={op.value}>{op.text}</MenuItem>
            ))}
          </Select>
        </Grid>
      }
      <Grid item xs={2} className={classes.removeButtonGrid}>
        { _.isFinite(props.index) && !_.has(expression, 'field') &&
          <Button
            className={clsx(classes.nodeButton, classes.removeButton)}
            variant='contained' 
            size='small' 
            aria-label='Remove' 
            onClick={() => props.removeNode(path)}
          >
            <Remove />
          </Button>
        }
      </Grid>
      { expression.operator && isNested(expression.operator) &&
        <Grid item xs={6}>
          <Button 
            className={clsx(classes.nodeButton, classes.addButton)}
            variant='contained' 
            size='small' 
            aria-label='Add' 
            onClick={() => props.addNode(path)}
          >
            <Add />
          </Button>
        </Grid>
      }
      { _.isArray(expression.operand) ? expression.operand.map((op, i) => {
        return (
          <Fragment key={i}>
            <EventExpressionNode 
              {...props}
              index={i}
              expression={op}
              path={path ? `${path}.operand.${i}` : `operand.${i}`}
            />
            { i < expression.operand.length - 1 && 
            <Typography className={classes.operatorText} variant='h6'>{expression.operator}</Typography> }
          </Fragment>
        );
      }) : expression.operand &&
        <EventExpressionNode 
          {...props}
          expression={expression.operand}
          path={path ? `${path}.operand` : 'operand'}
        />
      }
    </Grid>
  );
});

const EventExpressionBuilder = props => {
  const [open, setOpen] = React.useState(false);
  const [isValid, setIsValid] = React.useState(false);
  const [expression, setExpression] = React.useState({});
  const { t, handleChange, classes } = props;

  React.useEffect(() => {
    const expr = _.cloneDeep(props.expression);
    unpackExpression(expr);
    setExpression(expr);
  }, [props.expression]);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const packExpression = expr => {
    if (_.isArray(expr.operand)) {
      expr.operand.map(packExpression);
    } else if (expr.operator === 'ATTR') {
      expr.operator = expr.operand.comparator;
      delete expr.operand.comparator;
    }
  };

  const unpackExpression = expr => {
    if (_.isArray(expr.operand)) {
      expr.operand.map(unpackExpression);
    } else if (expr.operand) {
      expr.operand.comparator = expr.operator;
      expr.operator = 'ATTR';
    }
  };

  const handleSave = () => {
    const packedExpression = _.cloneDeep(expression);
    packExpression(packedExpression);
    handleChange(event, packedExpression);
    handleClose();
  };

  return (
    <div className={classes.componentRoot}>
      <Button 
        variant='contained' 
        color='primary' 
        onClick={handleClickOpen}
        disabled={!props.targetType}
      >
        {t('btn-open-expression-builder')}
      </Button>
      <FormLabel error={_.isEmpty(props.expression)}>
        {_.isEmpty(props.expression) ? t('expr-builder-unsaved') : t('expr-builder-saved')}
      </FormLabel>
      <Dialog 
        maxWidth='md'
        fullWidth={true}
        className={props.classes.dialogContainer}
        onClose={handleClose} 
        aria-labelledby='expression-builder-title' 
        open={open}
        PaperProps={{
          className: 'appBarShift',
        }}
      >
        <DialogTitle className={classes.builderTitle} id='expression-builder-title' disableTypography>
          <Typography className={classes.builderTitleText} variant='h6'>{t('expression-builder')}</Typography>
          <IconButton aria-label='close' className={props.classes.closeButton} onClick={handleClose}>
            <Close />
          </IconButton>
          
        </DialogTitle>
        <DialogContent dividers>
          <ExpressionBuilderTree 
            {...props}
            expression={expression}
            handleChange={setExpression}
            setIsValid={setIsValid}
          />
        </DialogContent>
        <DialogActions>
          <Button
            variant='contained'
            size='large'
            color='primary'
            disabled={!isValid}
            className={props.classes.submitButton}
            onClick={handleSave}
          >
            {props.t('btn-save')}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

EventExpressionBuilder.propTypes = {
  expression: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
  targetType: PropTypes.string,
  handleChange: PropTypes.func,
};

const mapStateToProps = state => {
  const {
    config,
  } = state;
  return config;
};

const mapDispatchToProps = {
  getManagedObjectConfig,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles, { withTheme: true }),
)(EventExpressionBuilder);