import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';
import _ from 'lodash';
import {
  Grid,
  Button,
  LinearProgress,
  Typography,
} from '@material-ui/core';
import { 
  Table as DashTable,
} from 'tabler-react';
import 'tabler-react/dist/Tabler.css';
import { withStyles } from '@material-ui/core/styles'; 
import styles from './styles';

class ViewCertification extends Component {

  componentWillUnmount() {

  }

  componentDidUpdate() {

  }

  navigateToCertification = cert => {
    let certType = cert.certObjectType === 'user' ? 'user' : 'object';
    this.props.onSubmitDialog('/adminCertList/' + certType + '/' + cert._id);
    this.props.handleClose();
  }

  getCertifierList = list => {
    let certifierList = _.map(list, 'displayName').join(', ');
    return certifierList;
  }

  getProgressOfStage = stage => {
    const { t } = this.props;
    if (!stage || !stage.totalEvents || !stage.eventStatuses) {
      return null;
    }
    let inProgress = stage.eventStatuses['in-progress'] || 0;
    let pending = stage.eventStatuses['pending'] || 0;
    let complete = stage.totalEvents - (inProgress + pending);
    return complete + ' ' + t('of') + ' ' + stage.totalEvents;
  }

  render() {
    const { classes, certification, t } = this.props;
    return certification ? (
      <Grid container className={classes.certification} spacing={2}>
        <Grid item xs={12}>
          <DashTable>
            <DashTable.Body>
              <DashTable.Row key={uuidv4()}>
                <DashTable.Col>{t('name')}</DashTable.Col>
                <DashTable.Col>{certification.name}</DashTable.Col>
              </DashTable.Row>
              <DashTable.Row key={uuidv4()}>
                <DashTable.Col>{t('description')}</DashTable.Col>
                <DashTable.Col>{certification.description}</DashTable.Col>
              </DashTable.Row>
              <DashTable.Row key={uuidv4()}>
                <DashTable.Col>{t('start-date')}</DashTable.Col>
                <DashTable.Col>{moment(certification.startDate).format('L')}</DashTable.Col>
              </DashTable.Row>
              <DashTable.Row key={uuidv4()}>
                <DashTable.Col>{t('open-certifiers')}</DashTable.Col>
                <DashTable.Col>{this.getCertifierList(certification.openCertifiers) }</DashTable.Col>
              </DashTable.Row>
            </DashTable.Body>
          </DashTable>        
        </Grid>
        <Grid item xs={12}>
          <DashTable>
            <DashTable.Header>
              <DashTable.Row>
                <DashTable.ColHeader>{t('stage')}</DashTable.ColHeader>
                <DashTable.ColHeader>{t('deadline')}</DashTable.ColHeader>
                <DashTable.ColHeader>{t('progress')}</DashTable.ColHeader>
              </DashTable.Row>
            </DashTable.Header>
            <DashTable.Body>
              {certification.stages.map(stage => (
                <DashTable.Row key={stage.name}>
                  <DashTable.Col>{stage.name}</DashTable.Col>
                  <DashTable.Col>{moment(stage.deadline).format('L')}</DashTable.Col>
                  <DashTable.Col>{this.getProgressOfStage(stage)}</DashTable.Col>
                </DashTable.Row>
              ))
              }
            </DashTable.Body>
          </DashTable>
        </Grid>
        < Grid className={classes.navigateButtonContainer} item xs={12}>
          <Typography>  
            <Button className={classes.navigateButton} onClick={() => this.navigateToCertification(certification)}>
              {t('go-to-campaign')}
            </Button>
          </Typography>
        </Grid>
      </Grid>
    ) : (<LinearProgress />);
  }
}

ViewCertification.propTypes = {
  certification: PropTypes.object,
  onSubmitDialog: PropTypes.func,
};

export default compose(
  connect(),
  withTranslation(),
  withStyles(styles),
)(ViewCertification);
