import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import clsx from 'clsx';
import { compose } from 'redux';
import { 
  IconButton,
  Button,
  ButtonGroup,
} from '@material-ui/core';
import{
  ChevronRight,
  ExpandMore,
  Comment,
  ModeCommentOutlined,
}from '@material-ui/icons';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import { 
  certActionEvent,
  getMetadata,
  updateSelectedAction,
  updateRowData,
} from 'src/store/actions/certlist.actions.js';
import { changeActiveGovernanceDialog, changeDialogType, getModalObject } from 'src/store/actions/generic.actions.js';
import GovernanceDialog from 'src/components/presentational/GovernanceDialog';
import { v4 as uuidv4 } from 'uuid';
import { 
  Table as DashTable,
} from 'tabler-react';

class EventDetailsList extends Component {
  state={
    rowActions: ['certify', 'revoke', 'abstain'],
    nestedLists: {},
    action : 'revoke',
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.setState({ nestedLists: {}});
  }

  handleClick(name){
    let nestedLists = _.cloneDeep(this.state.nestedLists);
    if (!_.has(nestedLists, name)) {
      nestedLists[name] = _.startsWith(name, 'managed/assignment');
    }
    else {
      nestedLists[name] = !nestedLists[name];
    }
    if (name === 'assignments') {
      _.forEach(_.keys(nestedLists), function(key) {
        if (_.startsWith(key, 'managed/assignment/')) {
          nestedLists[key] = false;
        }
      });
    }
    this.setState({ nestedLists: nestedLists });
  }

  takeAction = (action, comment, dataGroup, dataIndex, attrIndex) => {
    const oldAction = this.props.certlist.selectedAction;
    if(dataGroup !== undefined && dataIndex !== undefined){
      let rowData = {dataGroup, dataIndex, attrIndex};
      this.props.updateRowData(rowData);
    }
    
    if(action === 'certify' || action === 'reset' || action === 'claim'){
      this.props.updateSelectedAction(action);
      this.props.certActionEvent(null, true);
    }
    else if(action === 'revokeAbstainSubmit'){
      if(oldAction === 'comment'){
        this.props.certActionEvent(comment, false);
      }
      else{
        this.props.certActionEvent(comment, true);
        this.props.changeActiveGovernanceDialog(null);
      }
    }
    else{
      this.props.updateSelectedAction(action);
      this.props.changeActiveGovernanceDialog('EventDetailsListDialog');
      this.props.changeDialogType(action);
    }
  }
  
  actionButtons(dataIndex, attrIndex, dataGroup, outcome, disabled){
    const { classes } = this.props;
    return(
      <ButtonGroup key={uuidv4()} color="primary" aria-label="outlined primary button group">
        {this.state.rowActions.map((action) => {
          return(
            <Button size='small' onClick={(event)=>{
              this.takeAction(action === outcome ? 'reset' : action, null, dataGroup, dataIndex, attrIndex);}} 
            key={uuidv4()}
            disabled={disabled}
            className={clsx(classes.statusButton, 
              { 'certify-selected': action === outcome && outcome === 'certify' },
              { 'revoke-selected': action === outcome && outcome === 'revoke' },
              { 'abstain-selected': action === outcome && outcome === 'abstain' },)}>
              {action}
            </Button>
          );
        })}
      </ButtonGroup>
    );
  }

  getFlattenedData(data) {
    var flattenedData = [];
    _.forEach(data, function(entry) {
      flattenedData.push(entry);
      var subCategory = entry.mapping ? 'attributes' : 'values';
      if (entry[subCategory] && entry[subCategory].length > 0) {
        _.forEach(entry[subCategory], function(value) {
          flattenedData.push(value);
          if (value.parent && value.parent === 'assignments') {
            _.forEach(value.assignmentAttributes, function(attribute) {
              var attributeToPush = _.cloneDeep(attribute);
              attributeToPush.parent = value.fieldValue;
              attributeToPush.isAssignmentAttribute = true;
              flattenedData.push(attributeToPush);
            });
          }
        });
      }
    });
    return flattenedData;
  }


  getAttributeDisplayName(attribute, section, isLabel) {
    const { classes, certlist } = this.props;
    const { nestedLists } = this.state;
    const { certType } = certlist;
    let isUser = certType === 'user';
    if (section === 'identity') {
      if (isLabel) {
        return <span className={clsx(isUser && classes.clickableMetadata)} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, isUser, true))}><strong>{attribute.displayName}</strong></span>;
      }
      else {
        return <span className={clsx(isUser && classes.clickableMetadata)} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, isUser))}>{attribute.fieldValue}</span>;
      }
    }
    else if (section === 'managedObject') {
      if (attribute.values && attribute.values.length === 0) {
        let split = attribute.displayName.split(': ');
        return ( 
          <span>
            <span className={clsx(isUser && classes.clickableMetadata)} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, isUser, true))}><strong>{split[0]}</strong></span>
            <span textDecoration='none'>: </span>
            <span className={clsx((isUser || attribute.objectId) && classes.clickableMetadata)} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, (isUser || attribute.objectId)))}> {split[1]} </span>
          </span>);
      }
      else if (attribute.certifiable === 0 && attribute.values.length > 0) {
        return (
          <span>
            <IconButton className={classes.toggleButton} onClick={()=>{this.handleClick(attribute.objectType);}}>
              {(!_.has(nestedLists, attribute.objectType) || nestedLists[attribute.objectType] === true) ? <ExpandMore /> : <ChevronRight />}
            </IconButton>
            <span className={clsx(isUser && classes.clickableMetadata)} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, isUser, true))}><strong>{attribute.displayName}</strong></span>
          </span>
        );
      }
      else if (attribute.isAssignmentAttribute) {
        return (
          <span><i>{attribute.name}</i>{': ' + attribute.value}</span>
        );
      }
      else {
        return (
          <span>
            {attribute.parent && attribute.parent === 'assignments' && attribute.assignmentAttributes.length > 0 &&
              <IconButton className={classes.toggleButton} onClick={()=>{this.handleClick(attribute.fieldValue);}}>
                {(nestedLists[attribute.fieldValue] === true) ? <ExpandMore /> : <ChevronRight />}
              </IconButton>
            }
            <span className={classes.clickableMetadata} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, true))}>{attribute.displayName}</span>
          </span>
        );
      }
    }
    else if (section === 'application') {
      if (attribute.attributes) {
        let identifier = attribute.displayName + '_' + attribute.objectType;
        return (
          <span>
            <IconButton className={classes.toggleButton} onClick={()=>{this.handleClick(identifier);}}>
              {(!_.has(nestedLists, identifier) || nestedLists[identifier] === true) ? <ExpandMore /> : <ChevronRight />}
            </IconButton>
            <span className={classes.clickableMetadata} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, true, false, true))}><strong>{attribute.displayName + ' - ' + attribute.objectType}</strong></span>
          </span>
        );
      }
      else {
        return (
          <span>
            <span className={classes.clickableMetadata} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, true, true))}><strong>{attribute.attributeName}</strong>
            </span><span textDecoration='none'>: </span>
            <span className={classes.clickableMetadata} onClick={(evt)=>(this.showMetadataModal(evt, attribute, section, true))}>{attribute.attributeValue}</span>
          </span>
        );
      }
    }
    else if (section === 'metadata') {
      return <span><strong>{attribute.attributeName + ': '}</strong> {attribute.displayName.toString()}</span>;
    }
  }

  getDataGroupIndex(attribute, section){
    const {data} = this.props;
    let group = null;
    
    switch (section) {
    case 'application':
      group = attribute.connector ? attribute.connector + '_' + attribute.objectType : attribute.parent;
      break;
    case 'managedObject':
      group = attribute.objectType || attribute.parent;
      break;
    case 'metadata':
      group = attribute.objectType;
    }

    for(let i=0; i < data.length; i++){
      if (section === 'application' && data[i].connector + '_' + data[i].objectType === group) {
        return i;
      }
      else if(section !== 'application' && data[i].objectType === group){
        return i;
      }
    }
  }

  getMetadataIdFromRow = (data, section, isField, isSystem) => {
    switch (section) {
    case 'identity': 
      if (isField) {
        return data.field === 'email' ? 'mail' : data.field; // Temporary fix for email issue with creation
      }
      else {
        return data.field + '/' + data.fieldValue;
      }
    case 'managedObject':
      // Get the metadata id for the attribute itself (glossary class - identity)
      if (isField) {
        return data.objectType;
      }
      // Get the metadata id for the managed object value of the entry (glossary class- object)
      else if (data.objectId) {
        // Single relationship attribute (e.g. manager)
        return data.objectId;
      }
      else if (data.relationshipId) {
        // Multi valued relationship attribute (e.g. roles)
        return data.fieldValue;
      }
      // Get the metadata for the identity value itself (glossary class - identity-value)
      else {
        return data.objectType + '/' + data.attributeValue;
      }
    case 'application':
      if (isSystem) {
        return 'system/' + data.connector;
      }
      else if (isField) {
        return 'system-attribute/' + data.displayName + '/' + data.attributeName;
      }
      else {
        return 'system-value/' + data.displayName + '/' + data.attributeName + '/' + data.attributeValue;
      }
    }
  }

  showMetadataModal = (evt, data, section, isClickable, isField, isSystem) => {
    evt.stopPropagation();
    if (!isClickable) {
      return;
    }
    let metadataId = this.getMetadataIdFromRow(data, section, isField, isSystem);
    this.props.getMetadata('glossary', metadataId);
    this.props.changeDialogType('glossary');
    this.props.changeActiveGovernanceDialog('EventDetailsListDialog');
  }

  handleClose = () => {

  }

  getEventDetailsModalObject = () => {
    const { certlist, activeStage } = this.props;
    const { selectedAction, rowData, eventDetails, metadataObject } = certlist;

    if(this.props.eventActionClicked) {
      return selectedAction === 'comment' ? eventDetails.stages[activeStage].comments : [];
    }
    else {
      if (selectedAction === 'comment') {
        if(!rowData){
          return null;
        }
        const {dataGroup, dataIndex, attrIndex} = rowData;
        let comments = [];
        if(attrIndex === undefined){
          comments = eventDetails.stages[activeStage].eventData[dataGroup][dataIndex].comments;
        }
        else{
          let identifier = dataGroup === 'application' ? 'attributes' : 'values';
          let values = eventDetails.stages[activeStage].eventData[dataGroup][dataIndex][identifier];
          let entitlement = dataGroup === 'managedObject' ? _.find(values, { 'attrIndex' : attrIndex }) : values[attrIndex];
          comments = entitlement ? entitlement.comments : [];
        }
        return comments;
      }
      else {
        return metadataObject || rowData;
      }
    }
  }

  showRow = attribute => {
    const { nestedLists } = this.state;
    return (!attribute.parent || (!_.has(nestedLists, attribute.parent) || nestedLists[attribute.parent] === true));
  }

  render(){
    const { data, section, classes, certlist, canTakeAction, queryingMetadata, activeStage } = this.props;
    const { isAdminView, selectedAction, stageSelected } = certlist;
    const { nestedLists } = this.state;
    let eventDetailsModalObject = this.getEventDetailsModalObject();
    let isNotCurrentStage = activeStage !== stageSelected;

    return(
      <Fragment>
        {section === 'identity' ?
          <DashTable>
            <DashTable.Body>
              {data.map(attribute => (
                <DashTable.Row key={attribute.field} >
                  <DashTable.Col>{this.getAttributeDisplayName(attribute, section, true)}</DashTable.Col>
                  <DashTable.Col>{this.getAttributeDisplayName(attribute, section)}</DashTable.Col>
                </DashTable.Row>
              ))
              }
            </DashTable.Body>
          </DashTable> :
          <DashTable>
            <DashTable.Body>
              {this.getFlattenedData(data).map(attribute => (
                this.showRow(attribute) &&
                <DashTable.Row key={uuidv4()}>
                  {attribute.isAssignmentAttribute ?
                    nestedLists[attribute.parent] &&
                    <DashTable.Col colSpan={3} className={clsx(classes.entitlementCell, classes.nameCell, classes.assignmentAttribute)}>
                      {this.getAttributeDisplayName(attribute, section)}
                    </DashTable.Col>
                    :
                    <DashTable.Col className={clsx(classes.entitlementCell, classes.nameCell)}>
                      {this.getAttributeDisplayName(attribute, section)}
                    </DashTable.Col>
                  }
                  { !attribute.isAssignmentAttribute &&
                    <DashTable.Col className={classes.entitlementCell}>
                      { attribute.certifiable === 1 &&
                        this.actionButtons(this.getDataGroupIndex(attribute, section), attribute.entitlementIndex, section, attribute.outcome, (isAdminView || !canTakeAction))
                      }
                    </DashTable.Col>
                  }
                  { !attribute.isAssignmentAttribute &&
                    <DashTable.Col className={classes.entitlementCell}>
                      {attribute.certifiable === 1 && (!isNotCurrentStage || (attribute.comments && attribute.comments.length > 0)) &&
                        <IconButton onClick={()=>{this.takeAction('comment', null, section, this.getDataGroupIndex(attribute, section), attribute.entitlementIndex);}}>
                          {
                            !attribute.comments || attribute.comments.length <= 0 ? <ModeCommentOutlined/> : <Comment /> 
                          }
                        </IconButton>
                      }
                    </DashTable.Col>
                  }
                </DashTable.Row>
              ))
              }
            </DashTable.Body>
          </DashTable>
        }
        <GovernanceDialog 
          dialogId='EventDetailsListDialog'
          modalObject={eventDetailsModalObject}
          isNotCurrentStage={isNotCurrentStage}
          queryingMetadata={queryingMetadata}
          onSubmitDialog={this.props.eventActionClicked ? this.props.eventAction : this.takeAction}
          onClose={(this.props.eventActionClicked || selectedAction === 'comment') ? this.props.eventActionOnDialogClose : this.handleClose}
        />
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const { certlist } = state;
  return {
    certlist,
  };
};

const mapDispatchToProps = {
  certActionEvent,
  changeActiveGovernanceDialog, 
  changeDialogType, 
  getModalObject,
  getMetadata,
  updateSelectedAction,
  updateRowData,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
)(EventDetailsList);
