import _ from 'lodash';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withFormik } from 'formik';
import { withStyles } from '@material-ui/core/styles';
import { withWidth } from '@material-ui/core';
import * as yup from 'yup';
import { createYupSchema } from 'src/utils/schemas';
import FormBody from 'src/components/presentational/CreateForms/FormBody';
import styles from './styles';

const NewPolicyForm = withFormik({
  mapPropsToValues: (props) => {
    const formFields = {};
    props.formSchema().forEach(item => {
      if (props.existingFormData && props.existingFormData[item.id]) {
        if (item.isStringified && _.isString(props.existingFormData[item.id])) {
          formFields[item.id] = JSON.parse(props.existingFormData[item.id]);
        } else if (item.type === 'duration') {
          const [ unit, period ] = props.existingFormData[item.id].split(' ');
          formFields[item.id] = { unit, period };
        } else if (item.type === 'escalation') {
          formFields[item.id] = props.existingFormData[item.id].map(schedule => ({
            escalationType: schedule.escalationType,
            escalationId: schedule.escalationId,
            unit: schedule.interval,
            period: schedule.intervalType,
          }));
        } else {
          formFields[item.id] = props.existingFormData[item.id];
        }
      } else if (_.isNull(item.value)) {
        formFields[item.id] = item.value;
      } else {
        formFields[item.id] = item.value || '';
      }
    });
    return formFields;
  },
  enableReinitialize: true,
  isInitialValid: (props) => !_.isNull(props.existingFormData),
  handleSubmit: (values, { props, resetForm }) => {
    const initialValues = _.cloneDeep(values);

    if (props.formType === 'policy') {
      if (values.ownerType === 'user') {
        values.owner = { _ref: values.owner._ref || 'managed/user/' + values.owner.key };
      } else {
        values.owner = { _ref: values.owner._id || values.owner._ref };
      }
      props.formSchema().forEach(item => {
        if (item.isStringified) {
          values[item.id] = JSON.stringify(values[item.id]);
        }
      });
    } else if (props.formType === 'policy-scan') {
      values.policies = values.policies.map(policy => (`managed/policy/${policy._id}`));
      if (values.scanType === 'ad-hoc') {
        values.expirationDate = values.expirationDate.format('MM/DD/YYYY');
        delete values.expirationDuration;
      } else if (values.scanType === 'scheduled') {
        values.expirationDuration = `${values.expirationDuration.unit} ${values.expirationDuration.period}`;
        delete values.expirationDate;
      }
      // Transform escalationSchedule for backend
      values.escalationSchedule.forEach(schedule => {
        schedule.interval = schedule.unit;
        schedule.intervalType = schedule.period;
        if (schedule.escalationType === 'manager') {
          schedule.escalationId = '';
        } else if (schedule.escalationType === 'authzGroup') {
          schedule.escalationId = schedule.escalationId._id;
        } else if (schedule.escalationType === 'user') {
          schedule.escalationId = schedule.escalationId.key;
        }
        delete schedule.unit;
        delete schedule.period;
      });
    }
    
    props.onSubmit(values).then(response => {
      if (values.scanType === 'ad-hoc') {
        resetForm();
        props.handleClose();
      } else {
        resetForm(initialValues);
      }
    });
  },
  validationSchema: props => yup.object().shape(props.formSchema().reduce(createYupSchema, {})),
  displayName: 'NewPolicy',
})(FormBody);

const mapStateToProps = state => {
  const { async_fields } = state;
  return async_fields;
};

export default compose(
  connect(mapStateToProps),
  withTranslation(),
  withStyles(styles),
  withWidth(),
)(NewPolicyForm);