import React, { Component, Fragment } from 'react';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { withTheme } from '@material-ui/core/styles';
import PageTitle from 'src/components/presentational/PageTitle';
import styles from './styles';
import moment from 'moment';
import clsx from 'clsx';
import C3Chart from 'react-c3js';
import constants from 'src/utils/constants.js';
import 'tabler-react/dist/Tabler.css';
import { v4 as uuidv4 } from 'uuid';
import { 
  Grid,
  LinearProgress,
  TablePagination,
} from '@material-ui/core';
import { 
  StatsCard, 
  Card as DashCard, 
  Grid as DashGrid,
  Table as DashTable,
} from 'tabler-react';
import { 
  getDashboard,
  getSingleDashboardStat,
  getStoredEntitlement,
  getStoredPolicy,
} from 'src/store/actions/admin_dashboard.actions.js';
import { changeActiveGovernanceDialog, changeDialogType, getModalObject } from 'src/store/actions/generic.actions.js';
import LoadingIndicator from 'src/components/presentational/LoadingIndicator';
import GovernanceDialog from 'src/components/presentational/GovernanceDialog';
import 'tabler-react/dist/Tabler.css';
import AsyncSelect from 'src/components/presentational/AsyncSelect/AsyncSelect';

class AdminDashboard extends Component {
  _isMounted = false;

  state = {
    tableCols: [],
    tableData: [],
    modalObject: null,
    pageNums: {},
    paginatedStat: null,
  }

  componentDidMount() {
    this._isMounted = true;
    this.getDashboard();
  }

  componentDidUpdate(prevProps) {
    const { admin_dashboard } = this.props;
    const { querying, results } = admin_dashboard;
    if (prevProps.admin_dashboard.querying && !querying) {
      let pageNums = _.cloneDeep(this.state.pageNums);
      let isChanged = false;
      _.forEach(results, function(stat) {
        if (stat.displayType === 'table' && !pageNums[stat.label]) {
          pageNums[stat.label] = 0;
          isChanged = true;
        }
      });
      if (isChanged) {
        this.setState({ pageNums: pageNums });
      }
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  getDashboard = () => {
    this.props.getDashboard();
  }

  getTableHeaders = tableType => {
    const { t } = this.props;
    switch(tableType) {
    case('certification'):
    case('objectCertification'):
    case('userCertification'):
      return [t('header-name'),t('header-deadline')];
    case('violation'):
      return [t('header-name'), t('header-policy'), t('header-deadline')];
    case('entitlementsCertified'):
    case('entitlementsRevoked'):
      return [t('header-entitlement'), t('header-percentage')];
    case('policy'):
      return [t('header-policy'), t('header-total-violations')];
    default:
      return [];
    }
  }

  getTableColumnKeys = tableType => {
    switch(tableType) {
    case('certification'):
    case('objectCertification'):
    case('userCertification'):
      return [
        {key: 'name', type: 'string'},
        {key: 'nextDeadline', type: 'date'},
      ];
    case('violation'):
      return [
        {key: 'target', type: 'string'},
        {key: 'policyName', type: 'string'},
        {key: 'expirationDate', type: 'date'},
      ];
    case('entitlementsCertified'):
      return [
        {key: 'displayName', type: 'string'},
        {key: 'certifyPercentage', type: 'string'},
      ];
    case('entitlementsRevoked'):
      return [
        {key: 'displayName', type: 'string'},
        {key: 'revokePercentage', type: 'string'},
      ];
    case('policy'):
      return [
        {key: 'name', type: 'string'},
        {key: 'violations', type: 'string'},
      ];
    default:
      return [];
    }
  }

  openDialogModal = (dataType, modalObject) => {
    dataType = this.convertTypeToBackendObject(dataType);
    if (!dataType) {
      return;
    }
    this.props.getModalObject(dataType, modalObject._id);
    this.props.changeDialogType(dataType);
    this.props.changeActiveGovernanceDialog('AdminDashboardDialog');
  }

  convertTypeToBackendObject = dataType => {
    switch (dataType) {
    case 'entitlementsCertified':
    case 'entitlementsRevoked':
      return 'glossary';
    case 'userCertification':
    case 'objectCertification':
    case 'certification':
    case 'violation':
      return dataType;
    default:
      return null;
    }
  }

  onClickNavigationLink = path => {
    this.props.history.push(path);
  }

  onSelectStoredItem = (e, target, value) => {
    if (value && value.key) {
      if (target === 'certificationTotals') {
        this.props.getStoredEntitlement(value.key);
      }
      else if (target === 'policyTotals') {
        this.props.getStoredPolicy('managed/policy/' + value.key);
      }
    }
    else {
      if (target === 'certificationTotals') {
        this.props.getStoredEntitlement(null);
      }
      else if (target === 'policyTotals') {
        this.props.getStoredPolicy(null);
      }
    }
  }

  getStoredItem = type => {
    const { admin_dashboard } = this.props;
    const { storedEntitlement, storedPolicy } = admin_dashboard;
    switch(type) {
    case 'certificationTotals':
      return storedEntitlement;
    case 'policyTotals':
      return storedPolicy;
    default:
      return null;
    }
  }

  getColorsForColumns = columns => {
    let colors = {};
    let idx = 0;
    _.forEach(columns, function(column) {
      if (idx < constants.CHART_COLORS.length) {
        colors[column[0]] = constants.CHART_COLORS[idx];
      }
      idx++;
    });
    return colors;
  }

  getDonutData = (stat, dataType, isStored) => {
    const { t } = this.props;
    let columns = [];
    let categories = null;
    if (dataType === 'certificationTotals') {
      categories = constants.ENTITLEMENT_CHART_CATEGORIES;
    }
    else if (dataType === 'policyTotals') {
      categories = constants.POLICY_CHART_CATEGORIES;
    }

    if (isStored) {
      _.forEach(categories, function(category) {
        columns.push([category, stat[category]]);
      });
    }
    else {
      _.forEach(stat.value, function(entry) {
        if (entry.value > 0) {
          columns.push([entry.key, entry.value]);
        }
      });
    }
    return {
      columns: columns,
      type: 'donut',
      size: {
        height: '10px',
        width: '10px',
      },
      colors: this.getColorsForColumns(columns),
      empty: { label: { text: t('no-data') } },
    };
  }

  onPaginationChange = (evt, page, stat) => {
    if (_.isNumber(page)) {
      // Page number changed
      let pageNums = _.cloneDeep(this.state.pageNums);
      pageNums[stat] = page;
      this.props.getSingleDashboardStat({
        ...this.props.admin_dashboard,
      },
      stat,
      pageNums[stat],
      );
      this.setState({ 
        pageNums: pageNums,
        paginatedStat: stat,
      });
    }
  }

  getLastUpdatedTime = () => {
    const { admin_dashboard } = this.props;
    const { results } = admin_dashboard;
    let stats = _.cloneDeep(results);
    stats = _.sortBy(stats, 'lastUpdated');
    let lastUpdated = null;
    if (stats && stats.length > 0) {
      lastUpdated = stats[0].lastUpdated;
    }
    return lastUpdated;
  }

  render() {
    const { t, classes, admin_dashboard, generic } = this.props;
    const { results, querying, queryingSingle } = admin_dashboard;
    const { modalObject, queryingModalObject } = generic;
    const paginatedStat = this.state.paginatedStat;

    return (
      <Fragment>
        <PageTitle
          title={t('admin-dashboard')}
        />
        {
          querying ?
            <Grid className={classes.loadingGrid}>
              <LoadingIndicator  /> 
            </Grid> :
            <Grid className={classes.dashboardGrid}>
              <DashGrid.Row cards className={classes.dashboardRow}>
                {
                  results &&
                  results.map(stat => {
                    return stat.displayType === 'statCard' ?
                      <DashGrid.Col key={stat.label}>
                        <StatsCard layout={1} movement={parseInt(stat.change)} total={stat.value} label={t(stat.label)}/>
                      </DashGrid.Col>
                      : null;
                  })
                }
              </DashGrid.Row>
              <DashGrid.Row cards deck className={classes.dashboardRow}>
                {
                  results &&
                    results.map(stat => {
                      return stat.displayType === 'table' ?
                        <DashGrid.Col key={stat.label} className={classes.tableCol}>
                          <DashCard className={classes.tableCard} title={t(stat.label)}>
                            <DashTable>
                              <DashTable.Header>
                                <DashTable.Row>
                                  {this.getTableHeaders(stat.dataType).map(header => (
                                    <DashTable.ColHeader key={header}>{header}</DashTable.ColHeader>
                                  ))}
                                </DashTable.Row>
                              </DashTable.Header>
                              {stat.value.length === 0 ?
                                <DashTable.Body>
                                  <DashTable.Row>
                                    <DashTable.Col>{t('no-data')}</DashTable.Col>
                                  </DashTable.Row>
                                </DashTable.Body> :
                                <DashTable.Body>
                                  {queryingSingle && paginatedStat === stat.label ?
                                    <DashTable.Row>
                                      <DashTable.Col colSpan={this.getTableHeaders(stat.dataType).length}>
                                        <LinearProgress />
                                      </DashTable.Col>
                                    </DashTable.Row> : null
                                  }
                                  {stat.value.map(row => (
                                    <DashTable.Row className={clsx(stat.dataType !== 'policy' && classes.tableRow)} key={uuidv4()} onClick={() => this.openDialogModal(stat.dataType, row)}>
                                      {this.getTableColumnKeys(stat.dataType).map(columnKey => (
                                        <DashTable.Col key={uuidv4()}>
                                          { 
                                            columnKey['type'] === 'date' ?
                                              moment(row[columnKey['key']]).format('L') :
                                              row[columnKey['key']]
                                          }
                                        </DashTable.Col>
                                      ))}
                                    </DashTable.Row>
                                  ))}
                                </DashTable.Body>
                              }
                            </DashTable>
                            <DashCard.Footer className={classes.tableFooter}>
                              <Grid container className={classes.footerGrid}>
                                <Grid>
                                  <TablePagination
                                    className={classes.tablePagination}
                                    component='div'
                                    count={-1}
                                    labelDisplayedRows={({ from, to, count }) => t('displaying') + ' ' + `${from}-${to}`}
                                    rowsPerPage={5}
                                    rowsPerPageOptions={[5]}
                                    stat={stat.label}
                                    page={this.state.pageNums[stat.label] || 0}
                                    backIconButtonProps={{
                                      'aria-label': t('paging-prev'),
                                      'disabled': this.state.pageNums[stat.label] === 0,
                                    }}
                                    nextIconButtonProps={{
                                      'aria-label': t('paging-next'),
                                      'disabled': stat.value.length === 0,
                                    }}
                                    onChangePage={(evt, page) => this.onPaginationChange(evt, page, stat.label)}
                                    onChangeRowsPerPage={(evt, page) => this.onPaginationChange(evt, page, stat.label)}
                                  />
                                </Grid>
                              </Grid>
                            </DashCard.Footer>
                          </DashCard>
                        </DashGrid.Col>
                        : null;
                    })
                }
              </DashGrid.Row>
              <DashGrid.Row cards deck className={classes.dashboardRow}>
                {
                  results &&
                  results.map(stat => {
                    return stat.displayType === 'pie' ?
                      <DashGrid.Col key={stat.label} className={classes.pieCol}>
                        <DashCard>
                          <DashCard.Header>
                            <DashCard.Title>{t(stat.label)}</DashCard.Title>
                          </DashCard.Header>
                          <DashCard.Body>
                            <AsyncSelect 
                              name={stat.dataType}
                              targetType={stat.dataType}
                              fullWidth={true}
                              label={t('choose-' + stat.dataType)}
                              variant='outlined'
                              onChange={this.onSelectStoredItem}
                            />
                            {this.getStoredItem(stat.dataType) ?
                              <C3Chart data={this.getDonutData(this.getStoredItem(stat.dataType), stat.dataType, true)} /> : 
                              <C3Chart data={this.getDonutData(stat, stat.dataType)}/>
                            }
                          </DashCard.Body>
                        </DashCard>
                      </DashGrid.Col>
                      : null;
                  })
                }
              </DashGrid.Row>
            </Grid>
        }
        <GovernanceDialog
          dialogId='AdminDashboardDialog'
          modalObject={modalObject}
          queryingModalObject={queryingModalObject}
          onSubmitDialog={this.onClickNavigationLink}
        />
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const { admin_dashboard, generic } = state;
  return {
    admin_dashboard,
    generic,
  };
};

const mapDispatchToProps = {
  getDashboard,
  getSingleDashboardStat,
  getStoredEntitlement,
  getStoredPolicy,
  getModalObject,
  changeActiveGovernanceDialog,
  changeDialogType,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles),
  withTheme,
)(AdminDashboard);
