import { createMuiTheme } from '@material-ui/core/styles';

const muiBaseTheme = createMuiTheme();

const drawerWidth = muiBaseTheme.spacing() * 30;
// note(ML): The nav bar gets a property of min-height: 64px under .MuiToolbar-regular class, so i'm setting the same value here
const navBarHeight = '64px';

const renameProp = (
  oldProp,
  newProp,
  { [oldProp]: old, ...others }
) => {
  return {
    [newProp]: old,
  };
};

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#314054',
    },
    secondary: {
      main: '#007bff',
    },
    forgerock: {
      blue: '#109cf1',
      darkGray: '#324054',
      lightGray: '#f6f8fa',
      white: '#fff',
      orange: '#f96600',
      lightBlue: '#109cf1',
      success: '#2ed47a',
      warning: '#ffb946',
      danger: '#f7685b',
    },
    background: {
      default: '#f6f8fa',
    },
  },
  overrides: {
    MuiAppBar: {
      root: {
        display: 'flex',
        flexGrow: 1,
        '&.appBar': {
          justifyContent: 'space-between',
          transition: muiBaseTheme.transitions.create(['margin', 'width'], {
            easing: muiBaseTheme.transitions.easing.easeOut,
            duration: muiBaseTheme.transitions.duration.leavingScreen,
          }),
        },
        '&.appBarShift': {
          width: `calc(100% - ${drawerWidth}px)`,
          transition: muiBaseTheme.transitions.create('width', {
            easing: muiBaseTheme.transitions.easing.easeOut,
            duration: muiBaseTheme.transitions.duration.enteringScreen,
          }),
        },
      },
    },
    MuiButtonBase: {
      root: {
        height: '40px',
        '&.hide': {
          display: 'none',
        },
        '&:focus': {
          outline: 'none',
        },
      },
    },
    MuiButton: {
      root: {
        fontWeight: '400',
        textTransform: 'capitalize',
      },
    },
    MuiToolbar: {
      root: {
        '&.appToolbar': {
          justifyContent: 'center',
          flexGrow: 1,
          transition: muiBaseTheme.transitions.create(['margin', 'width'], {
            easing: muiBaseTheme.transitions.easing.easeOut,
            duration: muiBaseTheme.transitions.duration.leavingScreen,
          }),
        },
        '&.appToolbarLoggedIn': {
          justifyContent: 'space-between',
          height: '72px',
        },
        '&.menuButton': {
          marginRight: muiBaseTheme.spacing(2),
        },
        '&.hide': {
          display: 'none',
        },
      },
    },
    MuiDrawer: {
      root: {
        '& div.drawer': {
          width: drawerWidth,
          flexShrink: 0,
        },
        '& div.drawerHeader': {
          display: 'flex',
          alignItems: 'center',
          padding: '0 8px',
          height: '64px',
          overflow: 'visible',
          ...muiBaseTheme.mixins.toolbar,
          justifyContent: 'flex-start',
        },
        '& img.drawerLogo': {
          paddingLeft: '10px',
          width: '150px',
        },
      },
      paper: {
        '&.drawerPaper': {
          width: drawerWidth,
        },
      },
    },
    MuiDialog: {
      paper: {
        maxWidth: '100%!important',
        overflowY: 'overlay',
        '&.appBarShift': {
          // marginLeft: drawerWidth,
        },
      },
    },
    MuiGrid: {
      item: {
        flexGrow: 1,
        '&.mainContent': {
          ...renameProp('minHeight', 'marginTop', muiBaseTheme.mixins.toolbar),
          marginTop: '90px',
          padding: muiBaseTheme.spacing(3),
          marginLeft: 0,
          transition: muiBaseTheme.transitions.create('margin', {
            easing: muiBaseTheme.transitions.easing.easeOut,
            duration: muiBaseTheme.transitions.duration.leavingScreen,
          }),
        },
        '&.mainContent.contentShift': {
          marginLeft: drawerWidth,
          transition: muiBaseTheme.transitions.create('margin', {
            easing: muiBaseTheme.transitions.easing.easeOut,
            duration: muiBaseTheme.transitions.duration.enteringScreen,
          }),
        },
      },
    },
  },
});

export default theme;
export {
  muiBaseTheme,
  drawerWidth,
  navBarHeight,
};
