import React, { Component, Fragment } from 'react';
import _ from 'lodash';
import IDG_CONSTANTS from 'src/utils/constants.js';
import { compose } from 'redux';
import { connect } from 'react-redux';
import clsx from 'clsx';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import {
  Button,
  ButtonGroup,
  Grid,
  Typography,
} from '@material-ui/core';
import { 
  setIsCreateModalOpen, 
  changeCreateModalType, 
  changeDialogType, 
  changeActiveGovernanceDialog, 
  updateTakingAction, 
} from 'src/store/actions/generic.actions.js';
import { 
  changeActiveScansStatus,
  changePolicyType, 
  changeSortKey,
  changeSearchKey,
  getActivePolicyScans, 
  getPolicies, 
  deletePolicies,
  deleteScans,
  cancelViolations,
  reassignViolations,
  configureReactiveScans,
  updatePagination, 
} from 'src/store/actions/policies.actions.js';
import { getModalObject } from 'src/store/actions/generic.actions.js';
import PageTitle from 'src/components/presentational/PageTitle';
import GovernanceTable from 'src/components/presentational/GovernanceTable';

class Policies extends Component {

  state = {
    policyTabs: [
      'policies',
      'policy-scans',
      'violations-active',
      'exceptions-active',
      'violations-history',
    ],
    newButtonText: '',
    showNewButton: false,
    bulkActions: [],
    altActions: [],
    tableCols: [],
    activeScanCols: [
      {
        'header': 'Name',
        'key': 'name',
      },
      {
        'header': 'Type',
        'key': 'scanType',
      },
      {
        'header': 'Progress',
        'key': 'progress',
      },
    ],
    interval: null,
  }

  componentDidMount() {
    this._isMounted = true;
    if (this.props.policies.type) {
      this.updateTableForPolicyType(this.props.policies.type);
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.props.changePolicyType('policies', IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.changeSearchKey(null, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.changeSortKey('name', !IDG_CONSTANTS.IS_DESC, IDG_CONSTANTS.DO_NOT_UPDATE);
    this.props.updatePagination({
      pageNumber: 1,
      pageSize: this.props.policies.pageSize,
    }, IDG_CONSTANTS.DO_NOT_UPDATE);
  }

  componentDidUpdate(prevProps) {
    const { type, shouldQueryActiveScans } = this.props.policies;
    if (!type) {
      this.props.changePolicyType(this.state.policyTabs[0]);
    } else if (type !== prevProps.policies.type) {
      this.updateTableForPolicyType(type);
    }
    if (shouldQueryActiveScans && !prevProps.policies.shouldQueryActiveScans) {
      const interval = setInterval(this.props.getActivePolicyScans, 1500);
      this.setState({ interval });
    } else if (!shouldQueryActiveScans && this.state.interval) {
      clearTimeout(this.state.interval);
      this.setState({ interval: null });
    }
  }

  changeSelectedTab = tab => {
    const {type} = this.props.policies;
    this.props.changePolicyType(tab, !(type === tab));
  };

  updateTableForPolicyType = type => {
    let newButtonText = '';
    let showNewButton = false;
    let tableTitle = '';
    let tableCols = [];
    let bulkActions = [];
    let altActions = [];
    if (type === 'policies') {
      showNewButton = true;
      newButtonText = 'policy';
      bulkActions = ['delete-policy'];
      tableCols = [
        {
          'header': 'Name',
          'key': 'name',
        },
        {
          'header': 'Description',
          'key': 'description',
        },
      ];
    } else if (type === 'policy-scans') {
      showNewButton = true;
      newButtonText = 'policy-scan';
      altActions = ['configure-reactive'];
      bulkActions = ['delete-scan'];
      tableCols = [
        {
          'header': 'Name',
          'key': 'name',
        },
        {
          'header': 'Next Run Date',
          'key': 'nextRunDate',
          'type': 'date',
        },
      ];
    } else if (type === 'violations-active') {
      bulkActions = ['cancel-violation', 'reassign-tasks'];
      tableCols = [
        {
          'header': 'User',
          'key': 'targetUser',
          'noSort': true,
        },
        {
          'header': 'Policy',
          'key': 'policyName',
        },
        {
          'header': 'Owner',
          'key': 'owner',
        },
        {
          'header': 'Expiration Date',
          'key': 'expirationDate',
          'type': 'date',
        },
      ];
    } else if (type === 'exceptions-active') {
      bulkActions = ['cancel-exception'];
      tableCols = [
        {
          'header': 'User',
          'key': 'targetUser',
          'noSort': true,
        },
        {
          'header': 'Policy',
          'key': 'policyName',
        },
        {
          'header': 'Owner',
          'key': 'owner',
        },
        {
          'header': 'Approver',
          'key': 'completedBy',
        },
        {
          'header': 'Expiration Date',
          'key': 'expirationDate',
          'type': 'date',
        },
      ];
    } else if (type === 'violations-history') {
      bulkActions = [];
      tableCols = [
        {
          'header': 'User',
          'key': 'targetUser',
          'noSort': true,
        },
        {
          'header': 'Policy',
          'key': 'policyName',
        },
        {
          'header': 'Completed By',
          'key': 'completedBy',
        },
        {
          'header': 'Completion Date',
          'key': 'completionDate',
          'type': 'date',
        },
        {
          'header': 'Result',
          'key': 'status',
          'type': 'violationStatus',
        },
      ];
    }
    this.setState({ bulkActions, altActions, showNewButton, newButtonText, tableTitle, tableCols });
    if (tableCols[0]) {
      this.props.changeSortKey(tableCols[0].key);
    }
  };

  onPaginationChange = (evt, page) => {
    if (evt.q != null) {
      this.props.changeSearchKey(evt.q);
    }
    else if (evt.sortBy) {
      // Sorting key changed
      const isDesc = evt.sortBy === this.props.policies.sortBy && this.props.policies.sortDir === 'asc';
      this.props.changeSortKey(evt.sortBy, isDesc);
    } 
    else if (evt.target && evt.target.value) {
      // Rows per page changed
      this.props.updatePagination({
        ...this.props.policies,
        pageNumber: 1,
        pageSize: evt.target.value,
      });
    } else if (_.isNumber(page)) {
      // Page number changed
      this.props.updatePagination({
        ...this.props.policies,
        pageNumber: page + 1,
      });
    }
  }

  handleActions = (actionType, targetData, newOwnerId) => {
    this.props.updateTakingAction(true);
    this.props.changeDialogType('actionSubmitting');
    switch(actionType) {
    case 'delete-selected':
    case 'delete-policy':
      this.props.deletePolicies(targetData).finally(()=>{
        this.props.updateTakingAction(false);
        this.props.changeDialogType(null);
        this.props.changeActiveGovernanceDialog(null);
      });
      break;
    case 'delete-scan':
      this.props.deleteScans(targetData).finally(()=>{
        this.props.updateTakingAction(false);
        this.props.changeDialogType(null);
        this.props.changeActiveGovernanceDialog(null);
      });
      break;
    case 'cancel-violation':
    case 'cancel-exception':
      this.props.cancelViolations(targetData).finally(()=>{
        this.props.updateTakingAction(false);
        this.props.changeDialogType(null);
        this.props.changeActiveGovernanceDialog(null);
      });
      break;
    case 'reassign-tasks':
      this.props.reassignViolations(targetData, newOwnerId).finally(()=>{
        this.props.updateTakingAction(false);
        this.props.changeDialogType(null);
        this.props.changeActiveGovernanceDialog(null);
      });
      break;
    case 'configure-reactive':
      this.props.configureReactiveScans(targetData).finally(()=>{
        this.props.updateTakingAction(false);
        this.props.changeDialogType(null);
        this.props.changeActiveGovernanceDialog(null);
      });
      break;
    }
  }

  openEditModal = (evt, rowId) => {
    const { policies, changeCreateModalType, setIsCreateModalOpen } = this.props;
    
    switch (policies.type) {
    case 'policies':
      changeCreateModalType('policy', 'edit', rowId);
      setIsCreateModalOpen(true);
      break;
    case 'policy-scans':
      changeCreateModalType('policy-scan', 'edit', rowId);
      setIsCreateModalOpen(true);
      break;
    default:
      return;
    }
  }

  updateTableData = () => {
    const { results } = this.props.policies;
    this.setState({ tableData: results });
  }

  render() {
    const { classes, policies, generic, t } = this.props;
    const { policyTabs } = this.state;
    const { modalObject, queryingModalObject } = generic;

    return (
      <Fragment>
        <PageTitle
          title={t('page-policies')}
        />
        <Grid className={classes.buttonContainer} item xs={12}>
          <ButtonGroup color='default' fullWidth aria-label="full width outlined button group">
            {
              policyTabs.map(tab => (
                <Button
                  key={tab}
                  className={clsx(classes.policyTab, { 'selected': policies.type === tab })}
                  variant="contained"
                  onClick={() => this.changeSelectedTab(tab)}
                >{t(tab)}</Button>
              ))
            }
          </ButtonGroup>
        </Grid>
        { this.state.tableTitle && 
        <Typography variant='h6' id='tableTitle'>{t(this.state.tableTitle)}</Typography>
        }
        <Grid container className={classes.container} justify='center'>
          <GovernanceTable 
            tableCols={this.state.tableCols}
            tableData={policies.results}
            sortBy={policies.sortBy}
            sortDir={policies.sortDir}
            q={policies.q}
            bulkActions={this.state.bulkActions}
            altActions={this.state.altActions}
            handleActions={this.handleActions}
            handleRowClick={this.openEditModal}
            hideToolbar={this.state.bulkActions.length === 0}
            showNewButton={this.state.showNewButton}
            newButtonText={this.state.newButtonText}
            showCheckboxes={true}
            pageSize={policies.pageSize}
            pagedResultsOffset={policies.pageNumber-1}
            totalPagedResults={policies.totalPagedResults}
            handlePagination={this.onPaginationChange}
            querying={policies.querying}
            getModalObject={this.props.getModalObject}
            queryingModalObject={queryingModalObject}
            modalObject={modalObject}
            tableType={policies.type}
          />
        </Grid>
        { policies.type === 'policy-scans' && 
        <Fragment>
          <Typography className={classes.policyScanHeader} variant='h6' id='tableTitle'>{t('policy-scan-active')}</Typography>
          <Grid container className={classes.container} justify='center'>
            <GovernanceTable 
              tableCols={this.state.activeScanCols}
              tableData={policies.activeScans}
              showCheckboxes={false}
              pageSize={10}
              pagedResultsOffset={0}
              totalPagedResults={policies.activeScans.length}
              hideToolbar={true}
              hideSearchBar={true}
              handlePagination={this.onPaginationChange}
            />
          </Grid>
        </Fragment>
        }
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  const {
    policies,
    generic,
  } = state;
  return {
    policies,
    generic,
  };
};

const mapDispatchToProps = {
  changeActiveScansStatus,
  changeCreateModalType,
  changePolicyType,
  changeSortKey,
  changeSearchKey,
  getActivePolicyScans,
  getPolicies,
  deletePolicies,
  deleteScans,
  cancelViolations,
  reassignViolations,
  configureReactiveScans,
  setIsCreateModalOpen, 
  updatePagination,
  getModalObject,
  changeDialogType,
  changeActiveGovernanceDialog,
  updateTakingAction,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(Policies);
