import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import clsx from 'clsx';
import moment from 'moment';
import { v4 as uuidv4 } from 'uuid';
import styles from './styles.js';
import { withStyles } from '@material-ui/core/styles';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import { connect } from 'react-redux';
import {
  Button,
  Checkbox,
  Grid,
  InputAdornment,
  LinearProgress,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableSortLabel,
  TextField,
  Toolbar,
  Typography,
  Select,
  MenuItem,
} from '@material-ui/core';
import { Delete, 
  Add, 
  HighlightOff, 
  CheckCircle, 
  Search,
  Settings,
  Block,
  AssignmentInd,
  RotateLeft,
  Assignment,
} from '@material-ui/icons';
import { setIsCreateModalOpen, 
  changeCreateModalType, 
  changeActiveGovernanceDialog,
  changeDialogType} from 'src/store/actions/generic.actions.js';
import GovernanceDialog from 'src/components/presentational/GovernanceDialog';
import BulkActionMenu from 'src/components/presentational/BulkActionMenu';

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 10,
    borderRadius: 5,
  },
  colorPrimary: {
    backgroundColor: theme.palette.forgerock.lightGray,
  },
  bar: {
    borderRadius: 5,
    backgroundColor: theme.palette.forgerock.blue,
  },
}))(LinearProgress);


const GovernanceTable = props => {
  const [selectedRows, setSelectedRows] = React.useState([]);
  const [allSelected, setAllSelected] = React.useState(false);

  useEffect(() => {
    setSelectedRows([]);
    setAllSelected(false);
  }, [props.tableData]);

  const handleCheckboxClick = (event, rowId, isDisabled) => {
    event.stopPropagation();
    if (isDisabled) {
      return;
    }
    const selectedIndex = selectedRows.indexOf(rowId);
    let newSelected = [];
    if (selectedIndex > -1) {
      newSelected = _.without(selectedRows, rowId);
    } else {
      newSelected = _.concat(selectedRows, rowId);
    }
    if (props.tableData && newSelected.length === props.tableData.length) {
      setAllSelected(true);
    } else {
      setAllSelected(false);
    }
    setSelectedRows(newSelected);
  };

  const handleCheckAll = () => {
    if (!props.tableData || props.tableData.length === 0) {
      setAllSelected(false);
    } else if (selectedRows.length === props.tableData.filter(row => !row.isRowInactive).length) {
      setSelectedRows([]);
      setAllSelected(false);
    } else {
      const newSelected = props.tableData.filter(row => !row.isRowInactive).map(row => row._id);
      setSelectedRows(newSelected);
      setAllSelected(true);
    }
  };

  const getIconFromAction = (action) => {
    let icon = '';
    switch(action) {
    case 'cancel': 
    case 'cancel-violation':
    case 'cancel-exception':
    case 'cancel-cert':
      icon = <Block className={classes.buttonIcon}/>;
      break;
    case 'reassign-tasks':
    case 'reassign-all':
      icon = <AssignmentInd className={classes.buttonIcon}/>;
      break;
    case 'delete-selected':
    case 'delete-scan':
    case 'delete-policy':
      icon = <Delete className={classes.buttonIcon}/>;
      break;
    case 'configure-reactive':
      icon = <Settings className={classes.buttonIcon}/>;
      break;
    case 'certify':
      icon = <CheckCircle className={classes.buttonIcon}/>;
      break;
    case 'revoke':
      icon = <HighlightOff className={classes.buttonIcon}/>;
      break;
    case 'sign-off':
      icon = <Assignment className={classes.buttonIcon}/>;
      break;
    case 'reset':
      icon = <RotateLeft className={classes.buttonIcon}/>;
      break;
    default:
      icon = <Add className={classes.buttonIcon}/>;
      break;
    }
    return icon;
  };

  const openDialogModal = (action, data) => {
    if (data && props.getModalObject) {
      if (action !== 'choose-certifier' && action !== 'configure-reactive') {
        props.getModalObject(action, data, false);
      }
      else {
        props.getModalObject(action, data, true);
      }
    }
    props.changeDialogType(action);
    props.changeActiveGovernanceDialog('GovernanceTableDialog');
  };

  const handleTableActions = (action, taskId, value) => {
    switch(action) {
    case 'cancel-cert':
    case 'cancel-violation':
    case 'cancel-exception':
    case 'delete-scan':
    case 'delete-policy':
    case 'reset-all':
    case 'reset-selected':
    case 'sign-off':
      openDialogModal(action);
      break;
    case 'reassign-tasks':
    case 'reassign-all':
    case 'configure-reactive':
      openDialogModal(action);
      break;
    default:
      props.handleActions(action, selectedRows);
    }
  };

  const handleRowClick = (evt, rowId) => {
    const { tableData } = props;
    let data = null;
    if (props.handleRowClick) {
      switch (props.tableType) {
      case 'violation':
      case 'violations-active':
      case 'exceptions-active':
      case 'violations-history':
      case 'violation-tasks':
      case 'violation-dashboard':
        openDialogModal('violation', rowId);
        break;
      case 'user-dashboard':
      case 'object-dashboard':
        data = _.find(tableData, ['_id', rowId]);
        let options = data.status === 'in-progress' ? data.openCertifiers : data.closedCertifiers;
        if (options && options.length > 1) {
          let data = {
            certId: rowId,
            options: options,
          };
          openDialogModal('choose-certifier', data);
        }
        else if (options) {
          props.handleRowClick(evt, rowId, options[0]._id);
        }
        break;
      case 'certList':
        data = _.find(tableData, ['_id', rowId]);
        let viewable = data && (!data.isRowInactive || data.isViewable);
        props.handleRowClick(evt, rowId, !viewable);
        break;
      default:
        props.handleRowClick(evt, rowId);
      }
    }
  };

  const openCreateModal = () => {
    props.changeCreateModalType(props.newButtonText, 'create');
    props.setIsCreateModalOpen(true);
  };

  const getRowDropDown = (row, options) => {
    if(_.isUndefined(options)){
      return 'bad data';
    }
    else if(options.length == 1){
      return options[0].displayName;
    }
    else{
      return(
        <Select onChange={(event)=>{props.onRowDropdownSelect(row._id, event);}}
          value={options[0]._id}
          name={options[0].displayName}
          onClose={(event)=> {event.stopPropagation();}}
        >
          {options.map(option =>{
            return(
              <MenuItem key={uuidv4()} value={option._id} name={option.displayName}>{option.displayName}</MenuItem>
            );
          })}
        </Select>);
    }
  };

  const getTableCellData = (row, columnKey, type) => {
    const { getIconFromOutcome, t } = props;
    var value = row[columnKey];
    if (value && _.isArray(value) && type !== 'dropdown') {
      if (value.length === 1) {
        value = value[0];
      }
      else {
        value = t('multiple');
      }
    }
    if (columnKey === 'progress') {
      if (row.status === 'creating') {
        value = t('creating');
      }
      else {
        return (
          <Grid container display='flex' flex-direction='row'>
            <Grid item className={classes.borderProgressContainer} xs={8}>
              <BorderLinearProgress variant='determinate' value={value} />
            </Grid>
            <Grid item xs={4}>
              <Typography className={classes.progressValue} variant="body1" color="textSecondary">
                {row.totalEventsComplete + '/' + row.totalEventCount}
              </Typography>
            </Grid> 
          </Grid>
        );
      }
    } else if (columnKey === 'outcome') {
      return getIconFromOutcome(row.ownerOutcome || row.outcome, row.status);
    } else if (columnKey === 'claimedBy') {
      return value ? value.displayName : t('unclaimed');
    } else if (_.isObject(value) && value._ref) {
      return value._ref;
    } else if (_.isObject(value) && type != 'dropdown') {
      return value.displayName || null;
    } else if (type === 'date' && value) {
      return moment(value).format('L');
    } else if (type === 'dropdown') {
      return getRowDropDown(row, value);
    } else if (type === 'violationStatus') {
      return t(value);
    }
    else {
      return value;
    }
  };

  const {
    bulkActions,
    altActions,
    classes,
    tableCols,
    sortBy,
    sortDir,
    showCheckboxes,
    showOutcome,
    showNewButton,
    newButtonText,
    querying,
    tableData,
    pageSize,
    pagedResultsOffset,
    totalPagedResults,
    handlePagination,
    t,
    doNotPaginate,
    hideToolbar,
    hideSearchBar,
    useBulkActionMenu,
  } = props;
  let count = 0;
  if (totalPagedResults) {
    count = totalPagedResults;
  } else if (tableData.length < pageSize || tableData.length === 0) {
    count = tableData.length + pageSize * pagedResultsOffset;
  } else {
    count = pageSize * (pagedResultsOffset + 1) + 1;
  } 

  return (
    <Paper className={classes.root}>
      {!hideToolbar ?
        <Toolbar className={classes.tableToolbar}>
          <Grid container direction='row' justify='space-between'>
            <Grid item xs={8} className={classes.bulkActionGrid}>
              {
                showNewButton && newButtonText &&
              <Button
                variant='outlined'
                key='createActionButton'
                className={classes.tableCreateButton}
                onClick={() => openCreateModal()}
              > <Add className={classes.buttonIcon}/>
                {t(`btn-new-${newButtonText}`)}
              </Button>
              }
              {useBulkActionMenu ? 
                <BulkActionMenu actions={bulkActions} onActionClick={handleTableActions}
                  selected={selectedRows}/> :
                (bulkActions || []).map(action => (
                  <Button
                    id='bulk-action-button'
                    aria-controls='bulk-menu'
                    aria-haspopup='true'
                    variant='outlined'
                    disabled={selectedRows.length === 0 && !action.endsWith('-all')}
                    key={action}
                    className={classes.tableActionButton}
                    onClick={() => handleTableActions(action)}
                  > {getIconFromAction(action)}
                    {t('btn-' + action)}
                  </Button>
                ))
              }
            </Grid>
            <Grid item xs={4} className={classes.altActionGrid}>
              { altActions && altActions.map(action => (
                <Button
                  id='alt-action-button'
                  aria-controls='alt-menu'
                  aria-haspopup='true'
                  variant='outlined'
                  key={action}
                  className={classes.tableActionButton}
                  onClick={() => handleTableActions(action)}
                > {getIconFromAction(action)}
                  {t('btn-' + action)}
                </Button>
              ))}
            </Grid>
          </Grid>
        </Toolbar> : ''}
      { props.handleActions && 
        <GovernanceDialog
          dialogId='GovernanceTableDialog'
          onSubmitDialog={props.handleActions}
          selectedRows={selectedRows}
          modalObject={props.modalObject}
          queryingModalObject={props.queryingModalObject}
          root={props.root}
        />
      }
      {!hideSearchBar &&
        <Toolbar className={classes.tableToolbar}>
          <Grid container direction='row' justify='space-between'>
            <TextField 
              className={classes.tableSearchBar} 
              variant='outlined' 
              label={t('filter-table')}
              onChange={val => handlePagination({ q: val.target.value })}
              InputProps={{
                startAdornment: <InputAdornment className={classes.searchIcon} position="start"><Search /></InputAdornment>,
              }}>
            </TextField>
          </Grid>
        </Toolbar>
      }
      <Table size='small' className={classes.table}>
        <TableHead>
          {tableData && tableData.length > 0 &&
          <TableRow className={classes.tableHeadRow}>
            <TableCell
              padding='checkbox'
              className={classes.tableHeaderCheckbox}
              onClick={() => handleCheckAll()}
            >
              {_.isUndefined(showCheckboxes) || showCheckboxes &&
                <Checkbox
                  className={clsx(!showCheckboxes && classes.hidden)}
                  checked={allSelected}
                  indeterminate={tableData && selectedRows.length > 0 && !allSelected}
                />
              }
            </TableCell>
            <TableCell
              colSpan={1}
              padding='checkbox'
              className={classes.tableCheckbox}
            >{showOutcome && getTableCellData({}, 'outcome', null)}
            </TableCell>
            {
              tableCols.map(column => (
                <TableCell 
                  className={clsx(classes.tableHeader, column.noSort && classes.tableLabel)} 
                  key={column.key}
                  sortDirection={sortBy === column.key ? sortDir : false}
                >
                  {!column.noSort ?
                    <TableSortLabel
                      active={sortBy === column.key}
                      direction={sortBy === column.key ? sortDir : 'desc'}
                      onClick={() => handlePagination({ sortBy: column.key })}
                    >
                      {column.header}
                      {sortBy === column.key ? (
                        <span className={classes.visuallyHidden}>
                          {sortDir === 'asc' ? 'sorted ascending' : 'sorted descending'}
                        </span>
                      ) : null}
                    </TableSortLabel>
                    :
                    <span>{column.header}</span>
                  }
                </TableCell>
              ))
            }
          </TableRow>
          }
        </TableHead>
        <TableBody>
          {querying ?
            <TableRow>
              <TableCell colSpan={tableCols.length}>
                <LinearProgress />
              </TableCell>
            </TableRow> :
            tableData && tableData.length > 0 && tableData.map(tableRow => (
              <TableRow key={tableRow._id} className={classes.tableBodyRow}
                onClick= {(evt) => handleRowClick(evt, tableRow._id)}>
                <TableCell
                  colSpan={1}
                  padding='checkbox'
                  className={classes.tableCheckbox}
                  onClick={(event) => handleCheckboxClick(event, tableRow._id, tableRow.isRowInactive)}
                >
                  {_.isUndefined(showCheckboxes) || showCheckboxes &&
                    <Checkbox
                      className={clsx(!showCheckboxes && classes.hidden)}
                      disabled={tableRow.isRowInactive}
                      checked={selectedRows.indexOf(tableRow._id) > -1}
                    />
                  }
                </TableCell>
                <TableCell
                  colSpan={1}
                  padding='checkbox'
                  className={classes.tableCheckbox}
                >{showOutcome && getTableCellData(tableRow, 'outcome', null)}
                </TableCell>
                {
                  tableCols.map(column => (
                    <TableCell key={uuidv4()}
                      colSpan={tableCols.length - 1} 
                      className={clsx(classes.root, classes.tableCellGeneric, tableRow.isRowInactive && classes.tableCellInactive)} 
                      onClick={(evt) => handleRowClick(evt, tableRow._id)}
                    >
                      <Grid className={classes.tableCellGrid}>
                        <Typography component='span' className={classes.column} key={column.key}>
                          <span className={clsx(classes.cellItem, classes.cellItemIcon)}>
                            {getTableCellData(tableRow, column.key, column.type)}
                          </span>
                        </Typography>
                      </Grid>
                    </TableCell>
                  ))
                }
              </TableRow>
            ))
          }
          {
            tableData && <TableRow>
              <TableCell colSpan={tableCols.length}>
                {doNotPaginate ? ''
                  :
                  <TablePagination
                    className={classes.tablePagination}
                    rowsPerPageOptions={[10, 25, 50]}
                    component='div'
                    count={count}
                    rowsPerPage={pageSize}
                    page={pagedResultsOffset}
                    backIconButtonProps={{
                      'aria-label': t('paging-prev'),
                    }}
                    nextIconButtonProps={{
                      'aria-label': t('paging-next'),
                    }}
                    onChangePage={handlePagination}
                    onChangeRowsPerPage={handlePagination}
                  />
                }
              </TableCell>
            </TableRow>
          }
        </TableBody>
      </Table>
    </Paper>
  );
};

const mapStateToProps = state => {
  const {
    generic,
  } = state;

  return  {
    generic,
  };
};

const mapDispatchToProps = {
  setIsCreateModalOpen,
  changeActiveGovernanceDialog,
  changeCreateModalType,
  changeDialogType,
};

GovernanceTable.propTypes = {
  tableCols: PropTypes.array,
  tableData: PropTypes.array,
  sortBy: PropTypes.string,
  sortDir: PropTypes.string,
  bulkActions: PropTypes.array,
  altActions: PropTypes.array,
  rowActions: PropTypes.array,
  handleActions: PropTypes.func,
  handleRowActions: PropTypes.func,
  showNewButton: PropTypes.bool,
  newButtonText: PropTypes.string,
  showCheckboxes: PropTypes.bool,
  showOutcome: PropTypes.bool,
  pageSize: PropTypes.number,
  pagedResultsOffset: PropTypes.number,
  totalPagedResults: PropTypes.number,
  handlePagination: PropTypes.func,
  querying: PropTypes.bool,
  updateSearch: PropTypes.func,
  tableType: PropTypes.string,
  getModalObject: PropTypes.func,
  getIconFromOutcome: PropTypes.func,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withStyles(styles, { withTheme: true }),
  withTranslation(),
)(GovernanceTable);
