import { DASHBOARD, GENERIC } from './actionTypes.js';
import httpService from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';
import { httpFailure, alert, getModalObject } from './generic.actions.js';
import i18n from 'src/i18n.js';

export const getCertifications = () => (dispatch, getState) => {
  dispatch({
    type: DASHBOARD.QUERY_DASHBOARD,
    payload: null,
  });
  const endpoint = constants.IDM_CONTEXT + '/governance/dashboard';
  let { status, pageNumber, pageSize, sortBy, sortDir, q, type } = getState().dashboard;
  let params = { status, pageNumber, pageSize, sortBy, q, type };
  params.sortBy = sortDir === 'asc' ? `+${sortBy}` : `-${sortBy}`;
  if (params.q) {
    params.q = params.q.replace(/\\/g, '\\\\').replace(/"/g, '\\"');
  }
  return httpService.request({
    url: endpoint,
    method: 'get',
    params,
  }).then(({data})=>{
    dispatch({
      type: DASHBOARD.QUERY_DASHBOARD_SUCCESS,
      payload: data.result,
    });
    return data;
  }).catch((error)=>{
    dispatch({
      type: DASHBOARD.QUERY_DASHBOARD_FAILURE,
      payload: [],
    });
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const updateStatus = (newStatus, noUpdate) => (dispatch) => {
  dispatch({
    type: DASHBOARD.UPDATE_STATUS,
    payload: newStatus,
  });
  if(!noUpdate){
    dispatch(getCertifications());
  }
};

