export const actOnViolation = (id, action, comments, expirationDate) => dispatch => {
  let endpoint = constants.IDM_CONTEXT + '/governance/violation/' + id;
  let payload = {};
  if (comments && expirationDate) {
    payload = {
      comments,
      expirationDate,
    };
  }
  else if (comments) {
    payload = {
      comments,
    };
  }
  let params = {
    _action: action,
  };
  return httpService.request({
    url: endpoint,
    method: 'post',
    data: payload,
    params: params,
  }).then( ({data})=> {
    const successMessage = i18n.t('action-successful');
    dispatch(alert(successMessage, 'success'));
    if (action === 'comment') {
      dispatch(getModalObject('violation', id));
    }
    else {
      dispatch(getCertifications());
    }
  }).catch(error => {
    return dispatch(httpFailure(GENERIC.HTTP_FAILURE, error, 'error'));
  });
};

export const commentViolation = params => dispatch => {
  dispatch({
    type: DASHBOARD.COMMENT_VIOLATION,
    payload: params,
  });
  dispatch(getCertifications());
};