// SHOULD_SAVE: an array of whitelisted action types
// that trigger calls to redux-storage's engine.save
export const SHOULD_SAVE = _.concat(
  Object.values(AUTH),
  GENERIC.CHANGE_IS_DRAWER_OPEN,
  GENERIC.CHANGE_IS_CREATE_MODAL_OPEN,
  GENERIC.REMOVE_SNACKBAR,
  GENERIC.CLOSE_SNACKBAR,
  CONFIG.GET_CONFIG_MANAGED_OBJECTS,
  CONFIG.GET_MEMBERSHIP_PROPERTIES,
  GENERIC.CHANGE_ACTIVE_GOVERNANCE_DIALOG,
  CERTS.UPDATE_PAGINATION,
  CERTLIST.UPDATE_CERTID,
  CERTLIST.CHANGE_CERT_TYPE,
);

// SHOULD_NOT_SAVE: an array of blacklisted action
// types that don't trigger engine.save
export const SHOULD_NOT_SAVE = _.concat(
  _.difference(GENERIC, SHOULD_SAVE, CERTS, POLICIES, ASYNC_FIELDS),
);
