import React, { Fragment, useEffect } from 'react';
import clsx from 'clsx';
import _ from 'lodash';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import { withTranslation } from 'react-i18next';
import PropTypes from 'prop-types';
import { 
  AppBar,
  Button,
  Divider,
  Drawer,
  Grid,
  Link,
  List,
  ListItem,
  ListItemText,
  Menu,
  MenuItem,
  IconButton,
  Toolbar,
  Typography,
} from '@material-ui/core';
import {
  AccountCircle,
  BookOutlined,
  Dashboard,
  DoneAll, 
  Email,
  Policy,
  Settings,
  SupervisedUserCircleOutlined,
  Category,
} from '@material-ui/icons';  
import MenuIcon from '@material-ui/icons/Menu';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

import { logoutUser } from 'src/store/actions/auth.actions.js';
import { setIsDrawerOpen } from 'src/store/actions/generic.actions.js';
import {getSettings} from 'src/store/actions/config.actions.js';
import NavLinkButton from 'src/components/containers/NavLinkButton.js';
import CONSTANT from '@hubcitymedia/commons-scripts/src/utils/globalConstants';

const NavBar = props => {
  useEffect(()=>{
    props.getSettings();
  }, []);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    handleClose();
    props.logoutUser();
  };

  const handleDrawerToggle = () => {
    props.setIsDrawerOpen(!props.isDrawerOpen);
  };

  const { t, classes, settings, isLoggedIn, isDrawerOpen, userId, userRoles, authorization } = props;
  let config = {};
  let menu = _.find(settings.results, function(o){return o.section == 'Menu Management';});
  if(!_.isUndefined(menu)){
    config.customNavlinks=menu.fields[0].value;
  }
  return (
    <Fragment>
      <AppBar
        variant='outlined'
        position="fixed"
        className={clsx('appBar', isDrawerOpen && 'appBarShift', classes.appBar )}
      >
        {
          props.isLoggedIn &&
          <Toolbar
            className={clsx({
              'appToolbar': !isLoggedIn,
              'appToolbarLoggedIn': isLoggedIn,
            })}
          >
            {authorization.component !== 'internal/user' ?
              <IconButton
                edge="start"
                color="inherit"
                data-test-id="appBar-button-navBarMenu"
                aria-label="Open drawer"
                onClick={handleDrawerToggle}
                className={clsx('menuButton', !isLoggedIn && 'hide')}
              >
                <MenuIcon />
              </IconButton> : 
              <IconButton>
              </IconButton>
            }
            { !isLoggedIn &&
            <Typography
              component="h1"
              variant="h4"
              color="inherit"
              data-test-id="appBar-text-appTitle"
              noWrap
            >
              {t('governance')}
            </Typography>
            }
            {
              isLoggedIn &&
              <Fragment>
                <Button
                  className={classes.frgNavButton}
                  data-test-id="appBar-button-frgUserButton"
                  color='primary'
                  variant='text'
                  onClick={handleClick}
                >
                  <AccountCircleIcon className={classes.buttonIcon} />
                  {userId}
                  <ArrowDropDownIcon />
                </Button>
                <Menu
                  keepMounted
                  elevation={0}
                  anchorEl={anchorEl}
                  getContentAnchorEl={null}
                  open={Boolean(anchorEl)}
                  onClose={handleClose}
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                  }}
                >
                  {
                    config && config.customNavlinks && config.customNavlinks.map(navlink => (
                      <Link
                        key={navlink.url}
                        color='inherit'
                        data-test-id="appBar-button-logoutButton"
                        href={navlink.url}
                      >
                        <MenuItem>
                          {navlink.name}
                        </MenuItem>
                      </Link>
                    ))
                  }
                  <Link
                    color='inherit'
                    data-test-id="appBar-button-logoutButton"
                    onClick={handleLogout}
                  >
                    <MenuItem> 
                      {t('btn-logout')}
                    </MenuItem>
                  </Link>
                </Menu>
              </Fragment>
            }
          </Toolbar>
        }
      </AppBar>
      <Drawer
        className={'drawer'}
        variant="persistent"
        anchor="left"
        open={isDrawerOpen}
        classes={{
          paper: clsx('drawerPaper', classes.drawer),
        }}
      >
        <div className={'drawerHeader'}>
          <img className={'drawerLogo'} src={process.env.PUBLIC_URL + '/img/mainlogo.png'} alt='logo' />
        </div>
        <Divider className={classes.navDivider}/>
        <List className={classes.navList}>
          <NavLinkButton className={classes.navButton} data-test-id="navBar-link-myTasks" color="inherit" fullWidth exact to="/my-tasks">
            <DoneAll className={classes.navLinkIcon} />
            <ListItem>
              <ListItemText primary={t('page-my-tasks')} />
            </ListItem>
          </NavLinkButton>
        </List>
        
        {
          userRoles.indexOf(CONSTANT.ROLE.GOV_ADMIN) > -1 &&
          <List className={classes.navList}>
            <Divider className={classes.navDivider}/>
            <ListItem>
              <Typography
                color="inherit"
                gutterBottom
                variant="h6"
                className={classes.navHeader} 
              >
                {t('page-admin')}
              </Typography>
            </ListItem>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-admin-dashboard" color="inherit" fullWidth exact to="/admin/dashboard">
              <Dashboard className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-admin-dashboard')} />
              </ListItem>
            </NavLinkButton>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-user-certifications" color="inherit" fullWidth exact to="/admin/certifications/user">
              <SupervisedUserCircleOutlined className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-user-certs')} />
              </ListItem>
            </NavLinkButton>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-object-certifications" color="inherit" fullWidth exact to="/admin/certifications/object">
              <Category className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-object-certs')} />
              </ListItem>
            </NavLinkButton>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-policies" color="inherit" fullWidth exact to="/admin/policies">
              <Policy className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-policies')} />
              </ListItem>
            </NavLinkButton>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-user-summary" color="inherit" fullWidth exact to="/admin/user-summary">
              <AccountCircle className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-user-summary')} />
              </ListItem>
            </NavLinkButton>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-system-settings" color="inherit" fullWidth exact to="/admin/system-settings">
              <Settings className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-system-settings')} />
              </ListItem>
            </NavLinkButton>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-notification-templates" color="inherit" fullWidth exact to="/admin/notification-templates">
              <Email className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-notification-templates')} />
              </ListItem>
            </NavLinkButton>
          </List>
        }
        {
          userRoles.indexOf(CONSTANT.ROLE.GLOSSARY_ADMIN) > -1 &&
          <List className={classes.navList}>
            <Divider className={classes.navDivider}/>
            <ListItem>
              <Typography
                color="inherit"
                gutterBottom
                variant="h6"
                className={classes.navHeader} 
              >
                {t('page-glossary')}
              </Typography>
            </ListItem>
            <NavLinkButton className={classes.navButton} data-test-id="navBar-link-adminGlossaryEditorPage" color="inherit" fullWidth exact to="/glossaryAdmin/glossary-editor">
              <BookOutlined className={classes.navLinkIcon} />
              <ListItem>
                <ListItemText primary={t('page-glossary-editor')} />
              </ListItem>
            </NavLinkButton>
          </List>
        }
        <Grid className={classes.drawerFooter}>
          <List>
            <ListItem>
            </ListItem>
          </List>
        </Grid>
      </Drawer>
    </Fragment>
  );

};

NavBar.propTypes = {
  isLoggedIn: PropTypes.bool,
  logoutUser: PropTypes.func,
  userId: PropTypes.string,
  userRoles: PropTypes.array,
  config: PropTypes.object,
  isDrawerOpen: PropTypes.bool,
  setIsDrawerOpen: PropTypes.func,
};

const mapStateToProps = state => {
  const {
    auth: { isLoggedIn, authenticationId, roles, authorization },
    generic: { isDrawerOpen},
    config: {settings},
  } = state;
  return  {
    userId: authenticationId,
    userRoles: roles,
    settings,
    isLoggedIn,
    isDrawerOpen,
    authorization,
  };
};

const mapDispatchToProps = {
  logoutUser,
  setIsDrawerOpen,
  getSettings,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withStyles(styles, { withTheme: true }),
  withTranslation(),
)(NavBar);
