import React from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import { withTranslation } from 'react-i18next';
import _ from 'lodash';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormLabel,
  IconButton,
  Typography,
} from '@material-ui/core';
import { Close } from '@material-ui/icons';
import { withStyles } from '@material-ui/core/styles';
import ExpressionBuilder from './ExpressionBuilder';
import styles from './styles';

const ExpressionBuilderDialog = props => {
  const [open, setOpen] = React.useState(false);
  const { t, errors, expression, handleChange, classes } = props;

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = value => {
    setOpen(false);
  };

  const handleExpressionBuilderChange = expr => {
    handleChange(event, expr);
  };

  const handleSave = () => {
    handleChange(event, expression);
    handleClose();
  };

  return (
    <div className={classes.componentRoot}>
      <Button variant='contained' color='primary' onClick={handleClickOpen} className={classes.expressionDialogButton}>
        {t('btn-open-expression-builder')}
      </Button>
      <FormLabel error={_.isEmpty(props.expression) || !_.isEmpty(errors)}>
        {_.isEmpty(expression) || !_.isEmpty(errors) ? t('expr-builder-unsaved') : t('expr-builder-saved')}
      </FormLabel>
      <Dialog 
        maxWidth='md'
        fullWidth={true}
        className={classes.dialogContainer}
        onClose={handleClose} 
        aria-labelledby='expression-builder-title' 
        open={open}
        PaperProps={{
          className: 'appBarShift',
        }}
      >
        <DialogTitle className={classes.builderTitle} id='expression-builder-title' disableTypography>
          <Typography className={classes.builderTitleText} variant='h6'>{t('expression-builder')}</Typography>
          <IconButton aria-label='close' className={props.classes.closeButton} onClick={handleClose}>
            <Close />
          </IconButton>
          
        </DialogTitle>
        <DialogContent dividers>
          <ExpressionBuilder
            expression={expression}
            targetType={props.targetType}
            handleChange={handleExpressionBuilderChange}
            setIsValid={props.setIsValid}
          />
        </DialogContent>
        <DialogActions>
          <Button
            variant='contained'
            size='large'
            color='primary'
            className={props.classes.submitButton}
            onClick={handleSave}
          >
            {props.t('btn-save')}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

ExpressionBuilderDialog.propTypes = {
  errors: PropTypes.object,
  expression: PropTypes.object,
  targetType: PropTypes.string,
  handleChange: PropTypes.func,
};

export default compose(
  withTranslation(),
  withStyles(styles),
)(ExpressionBuilderDialog);