import { CERTS } from '../actions/actionTypes.js';
import IDG_CONSTANTS from 'src/utils/constants.js';

const initialState = {
  type: IDG_CONSTANTS.CERT_TYPE.USER,
  status: IDG_CONSTANTS.CERT_STATUS.ACTIVE,
  results: [],
  pageNumber: 1,
  pageSize: 10,
  sortBy: 'name',
  sortDir: 'asc',
  q: '',
  querying: false,
};

export default (state = initialState, action) => {
  switch(action.type) {
  case CERTS.CHANGE_CERT_TYPE:
    return {
      ...state,
      type: action.payload,
    };
  case CERTS.CHANGE_CERT_STATUS:
    return {
      ...state,
      status: action.payload,
    };
  case CERTS.UPDATE_PAGINATION:
    let { pageNumber, pageSize, sortBy, status } = action.payload;
    return {
      ...state,
      pageNumber,
      pageSize,
      sortBy,
      status,
    };
  case CERTS.CHANGE_SORTING:
    return {
      ...state,
      sortBy: action.payload.sortBy,
      sortDir: action.payload.isDesc ? 'desc' : 'asc',
    };
  case CERTS.CHANGE_SEARCH_KEY:
    return {
      ...state,
      q: action.payload.q,
    };
  case CERTS.QUERY_CERTS:
    return {
      ...state,
      querying: true,
    };
  case CERTS.QUERY_CERTS_SUCCESS:
    return {
      ...state,
      querying: false,
      results: action.payload,
    };
  case CERTS.QUERY_CERTS_FAILURE:
    return {
      ...state,
      querying: false,
      results: [],
    };
  default:
    return state;
  }
};
