import {GENERIC} from '../actions/actionTypes.js';

const initialState = {
  notifications: [],
  message: '',
  error: false,
  activeGovernanceDialog: null,
  isDrawerOpen: false,
  isCreateModalOpen: false,
  isDialogOpen: false,
  createModalType: null,
  dialogType: null,
  dialogMessage: null,
  createModalMode: null,
  createModalTarget: null,
  takingAction: false,
};

export default (state = initialState, action) => {
  switch(action.type) {
  case GENERIC.CHANGE_CONFIG:
    return {
      ...state,
      config: action.payload,
    };
  case GENERIC.ENQUEUE_SNACKBAR:
    return {
      ...state,
      notifications: [
        ...state.notifications,
        {
          key: action.key,
          ...action.notification,
        },
      ],
    };
  case GENERIC.CLOSE_SNACKBAR:
    return {
      ...state,
      notifications: state.notifications.map(notification => (
        (action.dismissAll || notification.key === action.key)
          ? { ...notification, dismissed: true }
          : { ...notification }
      )),
    };
  case GENERIC.REMOVE_SNACKBAR:
    let newNotifications = state.notifications.filter(
      notification => notification.key !== action.key,
    );
    if (newNotifications.length === state.notifications.length) {
      newNotifications = [];
    }
    return {
      ...state,
      notifications: newNotifications,
    };
  case GENERIC.GET_CONFIG:
    return {
      ...state,
      config: action.payload,
    };
  case GENERIC.CHANGE_IS_DRAWER_OPEN:
    return {
      ...state,
      isDrawerOpen: action.payload,
    };
  case GENERIC.CHANGE_IS_CREATE_MODAL_OPEN:
    return {
      ...state,
      isCreateModalOpen: action.payload,
    };
  case GENERIC.CHANGE_IS_DIALOG_OPEN:
    return {
      ...state,
      isDialogOpen: action.payload,
    };
  case GENERIC.CHANGE_CREATE_MODAL_TYPE:
    const { createModalType, createModalMode, createModalTarget } = action.payload;
    return {
      ...state,
      createModalMode,
      createModalType,
      createModalTarget,
    };
  case GENERIC.CHANGE_ACTIVE_GOVERNANCE_DIALOG:
    return {
      ...state,
      activeGovernanceDialog: action.payload,
    };
  case GENERIC.CHANGE_DIALOG_MESSAGE:
    return {
      ...state,
      dialogMessage: action.payload,
    };
  case GENERIC.CHANGE_DIALOG_TYPE:
    return {
      ...state,
      dialogType: action.payload,
    };
  case GENERIC.CHANGE_MODAL_OBJECT:
    return {
      ...state,
      modalObject: action.payload,
    };
  case GENERIC.QUERY_MODAL_OBJECT:
    return {
      ...state,
      queryingModalObject: true,
    };
  case GENERIC.QUERY_MODAL_OBJECT_SUCCESS:
    return {
      ...state,
      queryingModalObject: false,
      modalObject: action.payload,
    };
  case GENERIC.QUERY_MODAL_OBJECT_FAILURE:
    return {
      ...state,
      queryingModalObject: false,
      modalObject: null,
    };
  case GENERIC.UPDATE_TAKING_ACTION:
    return {
      ...state,
      takingAction: action.payload,
    };
  default:
    return state;
  }
};
